<template>
  <div class="boxInTab">
    <div id="placeHolderPath" style="display: none" v-show="selectedCount == 0">
      <p style="text-indent: 3em;"></p>
    </div>
    <div id="propertiesNavPath" v-show="selectedCount > 0">
      <div>
        <div class="itemPropertyLabel" ></div>
        <el-select v-model="dirValue" placeholder="" @change="onChangePathDirection">
          <el-option
                  v-for="item in dirOptions"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
          </el-option>
        </el-select>
      </div>
      <div style="padding-top: 6px">
        <div class="itemPropertyLabel" ></div>
        <el-select v-model="pathValue" placeholder="" @change="onChangePathType">
          <el-option
                  v-for="item in pathOptions"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
          </el-option>
        </el-select>
      </div>
    </div>
  </div>
</template>

<script>
  import ElSelect from '@/components/element/select';
  import ElOption from '@/components/element/select/src/option';
  import MainScene from '@/3d/js/MainScene';
  import Layers from "../../../3d/js/Layers";
  import History from "../../../3d/js/undo/History";

  export default {
    name: "BoxSelectedPaths",
    data() {
      return {
        pathOptions: [{
          value: 0,
          label: ''
        }, {
          value: 1,
          label: ''
        }, {
          value: 2,
          label: ''
        }],
        pathValue: 0,

        dirOptions: [{
          value: 0,
          label: ''
        }, {
          value: 1,
          label: ''
        }, {
          value: 2,
          label: ''
        }, {
          value: 3,
          label: ''
        }],
        dirValue: 0,
        selectedCount: 0,

        undoObj: {}
      }
    },
    mounted() {
      Layers.navPathLayer.addEventListener('selectedPathChange', () => {
        let itemPaths = MainScene.pathTool().getSelectedPathItems();
        this.selectedCount = itemPaths.length;
        // console.log("selectedPathChange, ", itemPaths);
        if (this.selectedCount < 1) {
          return;
        }
        let pathItem = itemPaths[0];
        this.pathValue = pathItem.getPathType();
        this.dirValue = pathItem.getPathDirection();
        this.setUndoObj(itemPaths);
      } );
    },
    methods: {
      setUndoObj(itemPaths) {
        let undo = this.undoObj;
        undo.preDirection = null, undo.preType = null;
        undo.pathHolders = itemPaths.slice();
        undo.pathTypes = [], undo.pathDirections = [];
        itemPaths.forEach(holder => {
          undo.pathTypes.push(holder.getPathType() );
          undo.pathDirections.push(holder.getPathDirection() );
        });
      },
      onChangePathDirection(val) {
        // console.log('change path direction', val);
        MainScene.pathTool().changeSelectedPathsDirections(val);
        let undo = this.undoObj;
        let undoContext = {pathHolders: undo.pathHolders, pathDirections: undo.pathDirections,
          unifyDirection: val, preDirection: undo.preDirection};
        History.addAction("unify paths direction", undoContext);

        undo.preDirection = val;
      },
      onChangePathType(val) {
        // console.log('change path type', val);
        MainScene.pathTool().changeSelectedPathsTypes(val);

        let undo = this.undoObj;
        let undoContext = {pathHolders: undo.pathHolders, pathTypes: undo.pathTypes,
          unifyType: val, preType: undo.preType};
        History.addAction("unify paths type", undoContext);

        undo.preType = val;
      }
    },
    components: {
      ElSelect, ElOption
    }
  }
</script>

<style scoped>

</style>
