import {Group, Vector3} from 'three';
import PathPoint from "./model/PathPoint";
import PathEdge from "./model/PathEdge";
import Config from "./configurable/Config";

let Singleton = function(name) {
  let eventLayersChanged = {type: "layers changed"};
  this.name = name;

  let floor = new Group();
  let background = new Group();
  let referLayer = new Group(), referMesh;
  let buildings = new Group();
  let markers = new Group();
  let navPaths = new Group();
  let editingLayer = new Group();
  this.indicatorLayer = new Group();
  // floor.name = "floor", background.name = "background", referLayer.name = "refer";
  // buildings.name = "buildings", markers.name = "marker", navPaths.name = "path", editingLayer.name = "editing";
  let customLayers = [], customLayerGroups = [];
  let _scene;

  let oldRefPos = new Vector3();

  let layers = [referLayer, floor, background, buildings, markers, navPaths, editingLayer, this.indicatorLayer];
  for (let i = 0; i < layers.length; i++) {
    layers[i].renderOrder = i;
    layers[i].userData.isLayer = true;
  }
  navPaths.visible = false;

  let refLayerSaving;
  this.referLayer = referLayer;
  this.editingLayer = editingLayer;
  this.markLayer = markers;
  this.navPathLayer = navPaths;
  this.buildingsLayer = buildings;
  this.floorLayer = floor;
  this.backgroundLayer = background;

  let previewPath = {pathEdges: [], pathPoints: []};
  function getMeshesInLayer(layer) {
    let arr = [];
    layer.children.forEach(it => {
      if (it.holder && it.holder.getMesh) {
        arr.push(it.holder.getMesh() );
      }
    });
    return arr;
  }
  function getMarkersInLayer(layer) {
    let arr = [];
    layer.children.forEach(it => {
      if (it.holder && it.holder.isMarker) {
        arr.push(it.holder.sprite() );
      }
    });
    return arr;
  }

  this.initLayers = function () {
    this.layers = {
      refer: {hidden: false, locked: true, id:"refer", name: "",
        nameEditable: false, removable: false, group: referLayer},
      background: {hidden: false, locked: true, id:"background", name: "",
        nameEditable: false, removable: false, group: background},
      floor: {hidden: false, locked: true, id:"floor", name: "",
        nameEditable: false, removable: false, group: floor},
      buildings: {hidden: false, locked: false, id:"buildings", name: "",
        nameEditable: false, removable: false, group: buildings},
      markers: {hidden: false, locked: true, id:"markers", name: "",
        nameEditable: false, removable: false, group: markers},

      nav: {group: navPaths, id:"nav"},
      editing: {group: editingLayer, id: "editing"}
    };
    for (let key in this.layers) {
      let obj = this.layers[key];
      obj.group.layerId = obj.id;
    }
  };
  this.syncVisibilities = function() {
    this.getAllLayers().forEach(it => {
      if (it !== this.layers.nav)
        it.group.visible = !it.hidden;
    })
  }
  this.reorderLayers = function (layerItems) {
    let groups = [], layerObjs = [];
    if (layerItems == undefined) {
      layerObjs = this.getAllLayers();
      for (let i = 0; i < layerObjs.length; i++) {
        groups.push(layerObjs[i].group);
      }
    } else {
      for (let item of layerItems) {
        let obj = this.getLayerById(item.id);
        if (obj)
          layerObjs.push(obj);
      }
      let lst = layerObjs.reverse();
      if (lst.indexOf(this.layers.background) < 0) {
        let index = lst.indexOf(this.layers.floor);
        lst.splice(index+1, 0, this.layers.background);
      }
      for (let i = 0; i < lst.length; i++) {
        groups.push(lst[i].group);
      }
      if (groups.indexOf(this.layers.refer) < 0) {
        groups.unshift(this.layers.refer.group);
      }
      groups.push(navPaths, editingLayer);
    }

    for (let i = 0; i < groups.length; i++) {
      groups[i].renderOrder = i;
    }
  };
  this.getLayerById = function (id) {
    for (let key in this.layers) {
      let obj = this.layers[key];
      if (obj.id == id)
        return obj;
    }
    for (let layerObj of customLayers) {
      if (layerObj.id == id)
        return layerObj;
    }
    return null;
  };
  this.renameLayer = function (id, newName) {
    let layerObj = this.getLayerById(id);
    if (layerObj)
      layerObj.name = newName;
  };
  this.initLayers();
  this.getDefaultFillColorArr = function () {
    if (!this.layers.floor.locked && !this.layers.floor.hidden) {
      return Config.ui.defaultFillColorFloor;
    }
    return Config.ui.defaultFillColor;
  }
  this.getDefaultFillColor = function () {
    if (!this.layers.floor.locked && !this.layers.floor.hidden) {
      return Config.ui.defaultFillColorFloorStr();
    }
    return Config.ui.defaultFillColorStr();
  }
  this.setDefaultFillColor = function (colorNum, opacity) {
    if (!this.layers.floor.locked && !this.layers.floor.hidden) {
      Config.ui.defaultFillColorFloor[0] = colorNum;
      Config.ui.defaultFillColorFloor[1] = opacity;
    } else {
      Config.ui.defaultFillColor[0] = colorNum;
      Config.ui.defaultFillColor[1] = opacity;
    }
  }
  this.getDefaultLineColorArr = function () {
    if (!this.layers.floor.locked && !this.layers.floor.hidden) {
      return Config.ui.defaultLineColorFloor;
    }
    return Config.ui.defaultLineColor;
  }
  this.getDefaultLineColor = function () {
    if (!this.layers.floor.locked && !this.layers.floor.hidden) {
      return Config.ui.defaultLineColorFloor[0].toString(16).toUpperCase();
    }
    return Config.ui.defaultLineColor[0].toString(16).toUpperCase();
  }
  this.setDefaultLineColor = function (colorNum) {
    if (!this.layers.floor.locked && !this.layers.floor.hidden) {
      Config.ui.defaultLineColorFloor[0] = colorNum;
    } else {
      Config.ui.defaultLineColor[0] = colorNum;
    }
  }
  this.getDefaultHeight = function () {
    if (!this.layers.floor.locked && !this.layers.floor.hidden) {
      return Config.ui.defaultHeightFloor;
    }
    return Config.ui.defaultHeight;
  }
  this.setDefaultHeight = function (val) {
    if (!this.layers.floor.locked && !this.layers.floor.hidden) {
      Config.ui.defaultHeightFloor = val;
    }
    Config.ui.defaultHeight = val;
  }

  this.addLayer = function (option) {
    let group = new Group();
    _scene.add(group);
    customLayerGroups.push(group);
    let num = customLayerGroups.length;

    let opt = option || {hidden: false, locked: false, id: null, order: undefined, name: ""+num};
    group.userData.isLayer = true;
    group.layerId = opt.id || new Date().getTime() + "";

    let layerObj = {hidden: opt.hidden, locked: opt.locked, id: group.layerId, name: opt.name,
      nameEditable: true, removable: true, group: group};
    group.name = layerObj.name;
    customLayers.push(layerObj);
    if (opt.order != undefined) {
      group.renderOrder = opt.order;
    }
    if (option == undefined)
      this.reorderLayers();
    _scene.dispatchEvent(eventLayersChanged);
    return layerObj;
  }
  this.removeLayer = function (layerItem) {
    let layerObj = this.getLayerById(layerItem.id);
    let index = customLayers.indexOf(layerObj);
    if (index < 0)
      return;
    customLayers.splice(index, 1);
    customLayerGroups.splice(index, 1);
    _scene.remove(layerObj.group);
    _scene.dispatchEvent(eventLayersChanged);
  }

  this.toggleHide = function(layerItem) {
    layerItem.hidden = !layerItem.hidden;
    let layerObj = this.getLayerById(layerItem.id);
    layerObj.hidden = layerItem.hidden;
    layerObj.group.visible = !layerObj.hidden;
    _scene.dispatchEvent({type: 'lock change', layerObj: layerObj});
  }
  this.toggleLock = function(layerItem) {
    layerItem.locked = !layerItem.locked;
    let layerObj = this.getLayerById(layerItem.id);
    layerObj.locked = layerItem.locked;
    _scene.dispatchEvent({type: 'lock change', layerObj: layerObj});
    // layerObj.group.dispatchEvent({type: 'lock change', locked: layerObj.locked, hidden: layerObj.hidden});
  }
  this.setLock = function(layerItem, locked) {
    if (layerItem.locked !== locked) {
      this.toggleLock(layerItem);
    }
  }
  this.attachLayersToScene = function (scene) {
    _scene = scene;
    scene.add(background);
    scene.add(referLayer);
    scene.add(floor);
    scene.add(buildings);
    scene.add(markers);
    scene.add(editingLayer);
    scene.add(navPaths);
    scene.add(this.indicatorLayer);
  }
  this.updateRefPos = function () {
    if (referMesh == null) {
      return;
    }
    oldRefPos.copy(referMesh.position);
  }
  this.oldRefPos = function () {
    return oldRefPos;
  }
  this.referMesh = function () {
    return referMesh;
  }
  this.moveRef = function(vector) {
    let pos = oldRefPos.clone().add(vector);
    referMesh.position.set(pos.x, pos.y, referMesh.position.z);
  }
  this.setReferLayer = function(mesh) {
    referMesh = mesh;
    referMesh.inRefLayer = true;
    this.clearLayer(referLayer);
    referLayer.add(mesh);
    this.initRefItem();
  }
  this.initRefItem = function () {
    if (refLayerSaving && referMesh ) {
      referMesh.position.fromArray(refLayerSaving.pos);
      referMesh.scale.fromArray(refLayerSaving.scale);
      referMesh.rotation.z = refLayerSaving.rotation;
      referMesh.material.opacity = refLayerSaving.opacity;
      refLayerSaving = null;
    }
  }
  this.setReferLayerSaving = function (saving) {
    refLayerSaving = saving;
    this.initRefItem();
  }
  this.availableMeshes = function (needAll, buildingOnly) {
    let layerObjs = this.getSelectableLayers(buildingOnly);
    let arr = [];
    for (let layerObj of layerObjs) {
      if (needAll || (!layerObj.locked && !layerObj.hidden) ) {
        arr = arr.concat(getMeshesInLayer(layerObj.group) );
      }
    }
    return arr;
  }
  this.availableMarkers = function(needAll) {
    let layerObjs = this.getSelectableLayers(false);
    let arr = [];
    for (let layerObj of layerObjs) {
      if (needAll || (!layerObj.locked && !layerObj.hidden) ) {
        arr = arr.concat(getMarkersInLayer(layerObj.group) );
      }
    }
    return arr;
  }
  this.getLayer = function() {
    let layerObjs = this.getSelectableLayers();
    for (let layerObj of layerObjs) {
      if (!layerObj.locked && !layerObj.hidden) {
        return layerObj.group;
      }
    }
    return null;
  }

  this.getSelectableLayers = function (buildingOnly) {
    let target = [this.layers.floor, this.layers.buildings, this.layers.markers].concat(customLayers);
    if (buildingOnly) { //PolygonMesh
      let minRenderOrder = this.layers.buildings.group.renderOrder;
      target = target.filter(obj => obj.group.renderOrder >= minRenderOrder);
    }
    return target;
  }
  this.clearCustomLayers = function () {
    customLayers.slice().forEach(it => this.removeLayer(it) );
  }
  this.getAllLayers = function() {
    // customLayers = customLayers.sort((a, b) => a.group.renderOrder - b.group.renderOrder);
    return [this.layers.refer, this.layers.floor, this.layers.background, this.layers.buildings]
        .concat(customLayers)
        .concat([this.layers.markers, this.layers.nav, this.layers.editing]);
  }
  this.getObjLayer = function (obj) {
    let layer = obj.parent;
    while (layer.parent) {
      if (layer.userData.isLayer) {
        return layer;
      }
      layer = layer.parent;
    }
    return layer;
  }
  this.changeItemsLayer = function (meshes, layerId) {
    let layerObj = this.getLayerById(layerId);
    meshes.forEach(mesh => layerObj.group.add(mesh.holder.getHolderGroup() ) );
  }
  this.changeMarkersLayer = function (markers, layerId) {
    let layerObj = this.getLayerById(layerId);
    markers.forEach(marker => marker.holder.changeParent(layerObj.group) );
  }
  this.clearLayer = function (layer) {
    let children = layer.children.slice();
    for (let child of children) {
      layer.remove(child);
    }
  }
  this.getPreviewPath = function () {
    return previewPath;
  }
  this.initPreviewPath = function () {
    let rings = this.getPathSegmentRings();
    let points = [];
    previewPath.pathEdges = [];
    for (let ring of rings) {
      points.push(new PathPoint(ring.position));
    }
    let holders = this.getPathSegmentHolders();
    for (let holder of holders) {
      holder.updateCost();
      previewPath.pathEdges.push(new PathEdge().setFromPathSegment(holder, rings, points));
    }
    previewPath.pathPoints = points;
  }
  this.navPathTabSelected = function () {
    return navPaths.visible;
  }
  this.onNavPathTabSelected = function () {
    let meshLst = this.availableMeshes(true);
    meshLst.forEach((it, index) => {
      let mesh = it.holder.getMesh();
      mesh.userData.bacOpacity = mesh.material.opacity;
      mesh.material.opacity = 0.2;
    });
    if (referMesh) {
      referMesh.userData.bacOpacity = referMesh.material.opacity;
      referMesh.material.opacity = Config.ui.refOpacityPathTab;
    }
    navPaths.visible = true;
  }
  this.onNavPathTabUnselected = function () {
    let meshLst = this.availableMeshes(true);
    meshLst.forEach((it, index) => {
      let mesh = it.holder.getMesh();
      mesh.material.opacity = mesh.userData.bacOpacity;
    });
    if (referMesh) {
      referMesh.material.opacity = referMesh.userData.bacOpacity;
    }
    navPaths.visible = false;
  }

  this.getPathSegmentHolders = function(excludeRing) {
    let holders = [];
    navPaths.children.forEach(it => {
      if (it.holder) {
        if (excludeRing==null || it.holder.toCircle() != excludeRing && it.holder.fromCircle() != excludeRing) {
          holders.push(it.holder);
        }
      }
    });
    return holders;
  }
  this.removeUnusedSegmentRings = function () {
    let rings = this.getPathSegmentRings();
    let usedRings = [];
    navPaths.children.forEach(it => {
      if (it.holder) {
        if (usedRings.indexOf(it.holder.toCircle()) < 0) {
          usedRings.push(it.holder.toCircle());
        }
        if (usedRings.indexOf(it.holder.fromCircle()) < 0) {
          usedRings.push(it.holder.fromCircle());
        }
      }
    });
    for (let ring of rings) {
      if (usedRings.indexOf(ring) < 0) {
        ring.parent.remove(ring);
      }
    }
  }
  this.getPathSegmentRings = function() {
    let rings = [];
    navPaths.children.forEach(it => {
      if (it.userData.isRing) {
        rings.push(it);
      }
    });
    return rings;
  }
  this.getUnrelatedPathSegments = function(rings) {
    let holders = this.getPathSegmentHolders();
    let unrelated = [];
    holders.forEach(it => {
      let related = false;
      for (let ring of rings) {
        if (!ring) continue;
        if (it.fromCircle() === ring || it.toCircle() === ring) {
          related = true;
          break;
        }
      }
      if (!related) {
        unrelated.push(it);
      }
    });
    return unrelated;
  }
  this.getRelatedPathSegments = function(rings) {
    let holders = this.getPathSegmentHolders();
    let related = [];
    holders.forEach(it => {
      if (rings.indexOf(it.fromCircle()) >= 0 || rings.indexOf(it.toCircle()) >= 0) {
        related.push(it);
      }
    });
    return related;
  }

  this.applyRotationFromCoordBinding = function (rotation) {
    let arr = [floor, background, buildings, editingLayer, navPaths].concat(customLayerGroups);
    arr.forEach(it => it.rotation.z = rotation);
  }

};
// 
Singleton.getInstance = function(name) {
  // 
  if(!this.instance) {
    this.instance = new Singleton(name);
  }
  // 
  return this.instance;
}

let Layers = Singleton.getInstance('layerGroup');
export default Layers;
