<template>
  <div class="boxInTab">
    <div><input type="button" value="" @click="onclickAddLight"></div>

    <ul class="light-list" :class="{show: showAddLightMenu}">
      <li class="light-item bottom-line" @click="addPointLight"></li>
      <li class="light-item bottom-line" @click="addSpotLight"></li>
      <li class="light-item bottom-line" @click="addDirectionLight"></li>
      <li class="light-item" @click="addHemiSphereLight"></li>
    </ul>

    <light-items :lights="lights" ref="lightList"/>
    <div ref="lightProperties" v-show="lightSelected">
      <div class="label"></div>
      <div class="item-row">
        <div class="itemPropertyLabel" ></div> {{lightTypeDesc}}
      </div>
      <div class="item-row">
        <div class="itemPropertyLabel" ></div>
        <simple-number-input class="w50" v-model="lightPosX" @change="updateLightPosition($event, 0)"/>
        <simple-number-input class="w50" v-model="lightPosY" @change="updateLightPosition($event, 1)"/>
        <simple-number-input class="w50" v-model="lightPosZ" @change="updateLightPosition($event, 2)"/>
      </div>
      <div class="item-row">
        <div class="itemPropertyLabel" ></div>
        <simple-number-input class="w130" v-model="lightIntensity" @change="updateLightIntensity($event)"/>
      </div>
      <div class="item-row">
        <div class="itemPropertyLabel" >{{lightColorDesc}}</div>
        <div class="inputWidth" style="width: 130px">
          <el-input ref="eleLightColor" v-model="lightColor" @colorChange="updateLightColor"
                    :colorEnabled="true"/>
        </div>
      </div>
      <div class="item-row" v-show="hasGroundColor">
        <div class="itemPropertyLabel" ></div>
        <div class="inputWidth" style="width: 130px">
          <el-input ref="eleLightGroundColor" v-model="lightGroundColor" @colorChange="updateLightGroundColor"
                    :colorEnabled="true"/>
        </div>
      </div>
      <div class="item-row" v-show="hasLightDistance">
        <div class="itemPropertyLabel" ></div>
        <simple-number-input class="w130" v-model="lightDistance" @change="updateLightDistance"/>
      </div>
      <div class="item-row" v-show="hasLightAngle">
        <div class="itemPropertyLabel" ></div>
        <simple-number-input class="w130" v-model="lightAngle" @change="updateLightAngle"/>
      </div>
      <!--    <div class="item-row">-->
      <!--      <div class="itemPropertyLabel" ></div>-->
      <!--      <switch-item active-color="#409eff" inactive-color="#ff4949" v-model="valueVisible" @change="toggleVisible"/>-->
      <!--    </div>-->
    </div>
  </div>
</template>

<script>
  import * as Const from "../../../3d/js/consts/Constants";
  import MainScene from "../../../3d/js/MainScene";
  import SwitchItem from '@/components/element/switch/component';
  import ElInput from '@/components/element/input';
  import SimpleNumberInput from "../gadget/SimpleNumberInput";
  import SimpleColorInput from "../gadget/SimpleColorInput";
  import LightItems from "../gadget/LightItems";
  import {convertLightObject} from "../../../3d/js/util/StaticMethods";

  export default {
    name: "BoxLights",
    components: {LightItems, SimpleColorInput, SimpleNumberInput, SwitchItem, ElInput},
    mounted() {
      this.lights = MainScene.lightTool().allLights().map(it => convertLightObject(it));
      let _this = this;
      MainScene.scene().addEventListener("light removed", (event) => {
        if (_this.curLight && _this.curLight.id == event.id)
          _this.curLight = null;
        let index = _this.findLightById(event.id);
        if (index > -1)
          _this.lights.splice(index, 1);
      });
      MainScene.scene().addEventListener("light moved", (event) => {
        // _this.curLight = convertLightObject(event.light);
        _this.curLight = _this.$refs.lightList.selectLightBy(event.light.uuid, true);
        _this.curLight.position.copy(event.light.position);
        let pos = _this.curLight.position;
        _this.lightPosX = pos.x.toFixed(2);
        _this.lightPosY = pos.y.toFixed(2);
        _this.lightPosZ = pos.z.toFixed(2);
      });

      MainScene.scene().addEventListener("light selected", (event) => {
        if (!event.light) {
          _this.$refs.lightList.refreshSelection();
          _this.curLight = null;
          return;
        }
        let ind = _this.findLightById(event.light.uuid);
        if (ind < 0) {
          let lightNew = convertLightObject(event.light);
          _this.lights.push(lightNew);
        }
        _this.curLight = _this.$refs.lightList.selectLightBy(event.light.uuid, true);

        if (_this.hasGroundColor) {
          _this.lightColorDesc = "";
        } else {
          _this.lightColorDesc = "";
        }
        let light = _this.curLight, pos = _this.curLight.position;
        _this.lightColor = light.color.getHexString();
        _this.$refs.eleLightColor.updateColor(_this.lightColor);
        if (light.groundColor != undefined) {
          _this.lightGroundColor = light.groundColor.getHexString();
          _this.$refs.eleLightGroundColor.updateColor(_this.lightGroundColor);
        }
        _this.lightPosX = pos.x.toFixed(2);
        _this.lightPosY = pos.y.toFixed(2);
        _this.lightPosZ = pos.z.toFixed(2);
        _this.lightIntensity = light.intensity.toFixed(2);
        if (light.distance != undefined)
          _this.lightDistance = light.distance.toFixed(2);
        if (light.angle != undefined)
          _this.lightAngle = light.angle.toFixed(3);
      });
    },
    data() {
      return {
        lightColorDesc: "",
        showAddLightMenu: false,
        valueVisible: true,
        curLight: null,
        lights: [],

        lightColor: "ffffff",
        lightGroundColor: "000000",
        lightPosX: "0",
        lightPosY: "0",
        lightPosZ: "0",
        lightIntensity: "1.00",
        lightDistance: "0",
        lightAngle: "0.374"
      };
    },
    computed: {
      lightTypeDesc() {
        return this.curLight ? this.curLight.name : "";
      },
      lightSelected() {
        return this.curLight != null;
      },
      hasGroundColor() {
        return this.curLight && this.curLight.type == Const.lightTypeHemiSphere;
      },
      hasLightAngle() {
        return this.curLight && this.curLight.type == Const.lightTypeSpot;
      },
      hasLightDistance() {
        return this.curLight && (this.curLight.type == Const.lightTypeSpot ||
            this.curLight.type == Const.lightTypePoint);
      }
    },
    methods: {
      findLightById(id) {
        for (let i = 0; i < this.lights.length; i++) {
          if (this.lights[i].id == id) {
            return i;
          }
        }
        return -1;
      },
      onclickAddLight() {
        this.showAddLightMenu = !this.showAddLightMenu;
      },
      addPointLight() {
        let light = MainScene.lightTool().addLight(Const.lightTypePoint);
        this.showAddLightMenu = false;
      },
      addSpotLight() {
        let light = MainScene.lightTool().addLight(Const.lightTypeSpot);
        this.showAddLightMenu = false;
      },
      addDirectionLight() {
        let light = MainScene.lightTool().addLight(Const.lightTypeDirection);
        this.showAddLightMenu = false;
      },
      addHemiSphereLight() {
        let light = MainScene.lightTool().addLight(Const.lightTypeHemiSphere);
        this.showAddLightMenu = false;
      },
      updateLightColor(jsColorStr) {
        let colorNum = parseInt("0x" + jsColorStr);
        this.curLight.color.setHex(colorNum);
        MainScene.lightTool().updateLight(this.curLight.id, {color: this.curLight.color});
      },
      updateLightGroundColor(jsColorStr) {
        let colorNum = parseInt("0x" + jsColorStr);
        this.curLight.groundColor.setHex(colorNum);
        MainScene.lightTool().updateLight(this.curLight.id, {groundColor: this.curLight.groundColor});
      },
      updateLightPosition(event, field) {
        // console.log("this.lightPosX: " + this.lightPosX + ", ", event.target.value);
        if (field == 0) {
          this.curLight.position.x = parseFloat(event.target.value);
        } else if (field == 1) {
          this.curLight.position.y = parseFloat(event.target.value);
        } else {
          this.curLight.position.z = parseFloat(event.target.value);
        }
        MainScene.lightTool().updateLight(this.curLight.id, {position: this.curLight.position});
      },
      updateLightIntensity(event) {
        // this.curLight.intensity = parseFloat(this.lightIntensity);
        this.curLight.intensity = parseFloat(event.target.value);
        MainScene.lightTool().updateLight(this.curLight.id, {intensity: this.curLight.intensity});
      },
      updateLightDistance(event) {
        this.curLight.distance = parseFloat(event.target.value);
        MainScene.lightTool().updateLight(this.curLight.id, {distance: this.curLight.distance});
      },
      updateLightAngle(event) {
        this.curLight.angle = parseFloat(event.target.value);
        MainScene.lightTool().updateLight(this.curLight.id, {angle: this.curLight.angle});
      }
    }
  }
</script>

<style scoped>
  .light-list {
    position: absolute;
    z-index: 99;
    left: 66px;
    top: 180px;
    border: solid 1px #999;
    box-shadow:3px 3px 11px 0px rgba(0, 0, 0, 0.3);
    transform: scaleY(0);
    transform-origin: top;
    transition: 0.3s;
  }
  .show {
    transform: scaleY(1);
  }
  .light-item {
    list-style: none;
    font-size: 15px;
    color: black;

    display: flex;
    justify-content: center;
    background-color: white;
    line-height: 1.2;
  }
  .bottom-line {
    border-bottom: solid 1px #999;
  }
  .light-item:hover {
  color: white;
  background-color: #999;
}
.label {
  font-size: 15px;
  font-weight: bold;
}
.item-row {
}
  .w50 {
    width: 50px;
  }
  .w130 {
    width: 130px;
  }
</style>
