import {Vector3} from 'three'

import Main from '../MainScene';
import Layers from "../Layers";
import {getPathCtrlPoint, getPathSegment} from '../util/StaticMethods'
import PathSegment from "../instance/PathSegment";
import * as Config from '../consts/Constants'
import TriangleCut from "../lib/TriangleCut";
import NavigatorTool from "./NavigatorTool";
import History from "../undo/History";

let NavPathTool = function () {
  let dom = Main.getDom();
  let eventSelectPathChange = {type: 'selectedPathChange'};
  let currentPointObj = getPathCtrlPoint();

  let selectedPaths = [];
  let startPoint = null;
  let currentRing = null, startRing = null;
  let adhereHolder = null;
  let lineMesh = null;
  let editingLayer = Layers.editingLayer;
  let curPathType = 0;
  let STATUS_ADDING = 1;
  let STATUS_EDITING = 2;
  let STATUS_FREE = 3;
  let STATUS_PATH_FINDING = 4;
  const MIN_PATH_LENGTH = 0.1;   //
  const MIN_PATH_LENGTH_SQ = MIN_PATH_LENGTH * MIN_PATH_LENGTH;
  let status = STATUS_FREE;
  let pathNavigator = NavigatorTool(Main);

  Layers.navPathLayer.addEventListener('selectionBoxUpdated', mouseup4SelectionBox);
  dom.addEventListener("mousedown", mousedown4SelectPath);

  function mousemove4PathFinding(event) {
    let lst = Main.getIntersectsOnPlane(event.clientX, event.clientY);
    if (lst.length > 0) {
      let curVector = lst[0].point;
      let circle = getSelectedCircle(curVector);
      if (circle) {
        curVector.copy(circle.position);
      }
      currentPointObj.position.copy(curVector);
      if (!currentPointObj.parent) {
        Layers.editingLayer.add(currentPointObj);
      }
    }
  }
  function mouseclick4PathFinding(event) {
    if (status !== STATUS_PATH_FINDING) {
      return;
    }
    if (!pathNavigator.startPoint()) {
      pathNavigator.setMarkerPos(event.clientX, event.clientY, false, true);
    } else {
      pathNavigator.setMarkerPos(event.clientX, event.clientY, true, false);
      pathNavigator.onPathFinding();
    }
  }

  function mousedown4SelectPath(event) {
    if (!Layers.navPathTabSelected()) {
      return;
    }
    if (status == STATUS_PATH_FINDING) {
      if (event.button == 0 && pathNavigator.startPoint() && pathNavigator.endPoint() ) {
        endPathFinding();
      }
      return;
    }
    if (status !== STATUS_FREE) {
      return;
    }
    let lst = Main.getIntersectsOnPlane(event.clientX, event.clientY);
    if (lst.length > 0) {
      let curVector = lst[0].point;
      let pathItem = getSelectedPathItem(curVector);
      setSelectedPaths(pathItem);
    }
    Layers.navPathLayer.dispatchEvent(eventSelectPathChange);
  }

  function mouseup4SelectionBox(event) {
    let selectedRings = event.selectBox.selectRings(Layers.getPathSegmentRings());
    let holders = Layers.getRelatedPathSegments(selectedRings);
    selectedPaths.forEach(it => it.unselect() );
    holders.forEach(it => it.select() );
    selectedPaths = holders;
    Layers.navPathLayer.dispatchEvent(eventSelectPathChange);
  }
  function setSelectedPaths(pathItem) {
    if (pathItem == null) {
      if (!Main.pressingShift()) {
        hideSelectedPaths();
      }
      return;
    }
    if (Main.pressingShift()) {
      pathItem.select();
      if (selectedPaths.indexOf(pathItem) < 0) {
        selectedPaths.push(pathItem);
      } else {
        pathItem.unselect();
        selectedPaths.splice(selectedPaths.indexOf(pathItem), 1);
      }
      Layers.navPathLayer.dispatchEvent(eventSelectPathChange);
    } else {
      if ( selectedPaths.indexOf(pathItem) < 0 ) {
        selectedPaths.forEach(it => it.unselect() );
        selectedPaths.splice(0, selectedPaths.length);
        pathItem.select();
        selectedPaths[0] = pathItem;
        Layers.navPathLayer.dispatchEvent(eventSelectPathChange);
      }
    }
  }
  function hideSelectedPaths() {
    selectedPaths.forEach(it => it.unselect() );
    selectedPaths.splice(0, selectedPaths.length);
    Layers.navPathLayer.dispatchEvent(eventSelectPathChange);
  }

  let ringMoving, relatedHolders;
  let oldRingPos = new Vector3();
  function mousedown4EditingPoint(event) {
    if (event.button == 2) {
      endEditingPoint();
      return;
    }
    hideSelectedPaths();
    if (currentRing != null) {
      ringMoving = currentRing;
      oldRingPos.copy(ringMoving.position);
      relatedHolders = Layers.getRelatedPathSegments([ringMoving]);
      dom.addEventListener('mouseup', mouseup4EditingPoint);
    }
  }
  function mousemove4EditingPoint(event) {
    let lst = Main.getIntersectsOnPlane(event.clientX, event.clientY);
    if (lst.length > 0) {
      let curVector = getAdheredPosition(lst[0].point);
      currentPointObj.position.copy(curVector);
      if (ringMoving == null) {
        if (!currentPointObj.parent) {
          Layers.editingLayer.add(currentPointObj);
        }
        return;
      }
      ringMoving.position.copy(curVector);
      relatedHolders.forEach(it => it != null && it.updateLine());
    }
  }
  function mouseup4EditingPoint(event) {
    let lst = Main.getIntersectsOnPlane(event.clientX, event.clientY);
    let lineSegment;
    if (lst.length > 0) {
      let undoContext;
      let curVector = getAdheredPosition(lst[0].point);
      if (currentRing == null) {
        ringMoving.position.copy(curVector);
        if (adhereHolder != null) {
          undoContext = {addLines:[], removeLines: [], relatedHolders: relatedHolders.slice(), ring: ringMoving,
            oldPosition: oldRingPos.clone(), position: curVector};

          let line = adhereHolder.getLine();
          line.parent.remove(line);
          undoContext.removeLines.push(line);

          let lineType = adhereHolder.getPathType();
          let exists = findPathHoldersByCircles(adhereHolder.fromCircle(), ringMoving);
          if (exists.length == 0) {
            lineSegment = addLineSegment(adhereHolder.fromCircle(), ringMoving, lineType);
            relatedHolders.push(lineSegment);
            if (lineSegment) {
              undoContext.addLines.push(lineSegment.getLine() );
            }
          }
          exists = findPathHoldersByCircles(ringMoving, adhereHolder.toCircle());
          if (exists.length == 0) {
            lineSegment = addLineSegment(ringMoving, adhereHolder.toCircle(), lineType);
            relatedHolders.push( lineSegment );
            if (lineSegment) {
              undoContext.addLines.push(lineSegment.getLine() );
            }
          }
          History.addAction("add paths", undoContext);
        } else {
          relatedHolders.forEach(it => it != null && it.updateLine() );
          undoContext = {pathPointObj: ringMoving, oldPosition: oldRingPos.clone(), position:curVector};
          undoContext.relatedHolders = relatedHolders.slice();
          History.addAction("update path point", undoContext);
        }
      } else {
        // ringMoving.position.copy(oldRingPos);
        undoContext = {relatedHolders: relatedHolders.slice(), ringGroup: ringMoving.parent,
          replaced: ringMoving, replacement: currentRing, oldPosition: oldRingPos };
        relatedHolders.forEach(it => it != null && it.replaceCircle(ringMoving, currentRing));
        ringMoving.parent.remove(ringMoving);
        ringMoving = currentRing;
        currentRing = null;
        relatedHolders = Layers.getRelatedPathSegments([ringMoving]);

        History.addAction("replace paths point", undoContext);
      }
    }

    dom.removeEventListener('mouseup', mouseup4EditingPoint);
    ringMoving = null;
  }

  function mousemove4PathAdd(event) {
    let lst = Main.getIntersectsOnPlane(event.clientX, event.clientY);
    if (lst.length > 0) {
      let curVector = getAdheredPosition(lst[0].point);
      if (!currentPointObj.parent) {
        Layers.editingLayer.add(currentPointObj);
      }
      currentPointObj.position.copy(curVector);
      if (startPoint != null) {
        if (lineMesh == null) {
          // lineMesh = getPolygonLineMesh([startPoint, curVector], null, false);
          lineMesh = getPathSegment(startPoint, curVector, Config.pathTypeAdding);
          editingLayer.add(lineMesh);
        } else {
          lineMesh.geometry.setPositions([startPoint.x, startPoint.y, 0, curVector.x, curVector.y, 0]);
          lineMesh.computeLineDistances();
        }
      }
    }
  }

  function addLineSegment(start, end, segmentType) {
    if (start === end) {
      return null;
    }
    let p0 = start instanceof Vector3 ? start : start.position;
    let p1 = end instanceof Vector3 ? end : end.position;
    let dd = p0.distanceToSquared(p1);
    if (dd <= MIN_PATH_LENGTH_SQ) {
      return null;
    }
    if (start instanceof Vector3) {
      let circle = findCircleByPoint(start);
      if (circle) start = circle;
    }
    if (end instanceof Vector3) {
      let circle = findCircleByPoint(end);
      if (circle) end = circle;
    }
    let pathSegment = PathSegment(start, end, segmentType != undefined ? segmentType : curPathType, Main.currentCameraScale());
    let items = pathSegment.getItems();
    items.forEach(it => Layers.navPathLayer.add(it));
    // console.log("addLineSegment success, distance: " + dd);
    return pathSegment;
  }
  function clearTempMesh() {
    if (lineMesh != null) {
      editingLayer.remove(lineMesh);
      lineMesh = null;
      startPoint = null;
      startRing = null;
    }
  }
  function endPathFinding() {
    status = STATUS_FREE;
    if (currentPointObj.parent) {
      currentPointObj.parent.remove(currentPointObj);
    }
    pathNavigator.clearPathAnimation();

    dom.removeEventListener('click', mouseclick4PathFinding);
    dom.removeEventListener('mousemove', mousemove4PathFinding);
  }

  function endEditingPoint() {
    status = STATUS_FREE;
    if (currentPointObj.parent) {
      currentPointObj.parent.remove(currentPointObj);
    }
    dom.removeEventListener('mousemove', mousemove4EditingPoint, false);
    dom.removeEventListener('mousedown', mousedown4EditingPoint, false);
    ringMoving = null;
  }

  function endAddPath() {
    status = STATUS_FREE;
    if (currentPointObj.parent) {
      currentPointObj.parent.remove(currentPointObj);
    }
    if (lineMesh != null) {
      lineMesh.parent.remove(lineMesh);
      lineMesh = null;
      startPoint = null;
    }
    dom.removeEventListener('mousemove', mousemove4PathAdd, false);
    dom.removeEventListener('click', mouseclick4LineSegmentAdd, false);
    dom.removeEventListener('mousedown', mousedown4LineSegmentAdd, false);
  }

  function mousedown4LineSegmentAdd(event) {
    if (event.button === 2) {
      endAddPath();
    }
  }
  function mouseclick4LineSegmentAdd(event) {
    if (startPoint == null) {
      startPoint = currentPointObj.position.clone();
      startRing = currentRing;
    } else {
      let start = startRing || startPoint;
      let end = currentRing || currentPointObj.position;
      let newItem = addLineSegment(start, end);
      if (newItem == null) {
        return;
      }
      clearTempMesh();
      let undoContext = {removeLines: [], addLines: [newItem.getLine()]};
      splitSegmentsByNewItem(newItem, undoContext);
      History.addAction("add paths", undoContext);
    }
  }

  function splitSegmentsByNewItem(newItem, undoContext) {
    let circleFrom = newItem.fromCircle(), circleTo = newItem.toCircle();
    let newCircles = [circleFrom, circleTo];
    let unrelatedItems = Layers.getUnrelatedPathSegments(newCircles);
    let from = circleFrom.position, to = circleTo.position;
    let splitedCount = 0;
    let tmp = new Vector3();
    for (let item of unrelatedItems) {
      item.splitCircles = [], item.splitBy = [];
      let p0 = item.fromCircle().position, p1 = item.toCircle().position;
      let cross = TriangleCut.lineSegmentCross(p0, p1, from, to);
      if (!cross) {
        cross = TriangleCut.lineSegmentCross(p0, p1, from, to);
        continue;
      }
      splitedCount++;
      tmp.set(cross.x, cross.y, 0);
      let ptObj = findCircleByPoint(tmp);
      let crossObj = ptObj;
      if (crossObj == null) {
        crossObj = getPathCtrlPoint(tmp, Config.pointTypeRing);
      }
      if (ptObj == null || newCircles.indexOf(ptObj) >= 0 ) {
        item.splitCircles[item.splitBy.length] = crossObj;
        item.splitBy.push(new Vector3(cross.x, cross.y, 0) );
      }

      if (newCircles.indexOf(ptObj) < 0) {
        newItem.splitCircles[newItem.splitBy.length] = crossObj;
        newItem.splitBy.push(new Vector3(cross.x, cross.y, 0) );
        undoContext.addLines.pop();
      }
    }
    let arr = unrelatedItems.concat(newItem);
    for (let item of arr) {
      if (item.splitBy.length < 1) {
        continue;
      }
      let line = item.getLine();
      line.parent.remove(line);
      if (item !== newItem) {
        undoContext.removeLines.push(line);
      }
      let segments = item.processSplitBy();
      for (let segmentItem of segments) {
        let pathSegment = addLineSegment(segmentItem.fromCircle(), segmentItem.toCircle(), segmentItem.getPathType());
        if (pathSegment) {
          pathSegment.setPathDirection(item.getPathDirection() );
          undoContext.addLines.push(pathSegment.getLine());
        }
      }
    }
  }

  function getSelectedPathItem(curPos) {
    let holders = Layers.getPathSegmentHolders();
    for (let holder of holders) {
      let adhereObj = holder.getAdhereObj(curPos);
      if (adhereObj.ctrlPoint != null || adhereObj.posOnEdge != null) {
        return holder;
      }
    }
    return null;
  }
  function getSelectedCircle(curPos) {
    let holders = Layers.getPathSegmentHolders();
    for (let holder of holders) {
      let adhereObj = holder.getAdhereObj(curPos, true);
      if (adhereObj.ctrlPoint != null) {
        return adhereObj.ctrlPoint;
      }
    }
    return null;
  }
  function getAdheredPosition(curPos) {
    let holders = Layers.getPathSegmentHolders(ringMoving);
    let pointAdheres = [], edgeAdheres = [];
    for (let holder of holders) {
      let adhereObj = holder.getAdhereObj(curPos);
      if (adhereObj.ctrlPoint != null) {
        pointAdheres.push(adhereObj);
      } else if (adhereObj.posOnEdge != null) {
        edgeAdheres.push(adhereObj);
      }
    }
    pointAdheres.sort((a,b) => a.distanceSq - b.distanceSq);
    if (pointAdheres.length > 0) {
      currentRing = pointAdheres[0].ctrlPoint;
      return pointAdheres[0].ctrlPoint.position;
    }
    currentRing = null;

    edgeAdheres.sort((a,b) => a.distanceSq - b.distanceSq);
    if (edgeAdheres.length > 0) {
      adhereHolder = edgeAdheres[0].holder;
      return edgeAdheres[0].posOnEdge;
    }
    adhereHolder = null;
    return curPos;
  }

  function findPathHoldersByCircles(fromCircle, toCircle) {
    let holders = Layers.getPathSegmentHolders();
    let result = [];
    holders.forEach(it => {
      if ((it.fromCircle() === fromCircle && it.toCircle() === toCircle) ||
        (it.fromCircle() === toCircle && it.toCircle() === fromCircle) ){
        result.push(it);
      }
    });
    return result;
  }
  function findCircleByPoint(point) {
    let circles = Layers.getPathSegmentRings();
    for (let circle of circles) {
      if (circle.position.distanceToSquared(point) <= MIN_PATH_LENGTH_SQ) {
        return circle;
      }
    }
    return null;
  }
  function prepareToFree() {
    if (status == STATUS_PATH_FINDING) {
      endPathFinding();
    } else if (status == STATUS_ADDING) {
      endAddPath();
    } else if (status == STATUS_EDITING) {
      endEditingPoint();
    }
  }

  let that = {
    prepareToFree: prepareToFree,
    startPathFinding() {
      if (status == STATUS_PATH_FINDING) {
        return;
      }
      prepareToFree();
      pathNavigator.buildPathFindingGraph();
      status = STATUS_PATH_FINDING;
      dom.addEventListener('click', mouseclick4PathFinding);
      dom.addEventListener('mousemove', mousemove4PathFinding);
    },
    navigator() {
      return pathNavigator;
    },
    // animatePathIndicator: animatePathIndicator,
    startAddPath(pathType) {
      status = STATUS_ADDING;
      curPathType = pathType;
      dom.addEventListener('mousemove', mousemove4PathAdd, false);
      dom.addEventListener('click', mouseclick4LineSegmentAdd, false);
      dom.addEventListener('mousedown', mousedown4LineSegmentAdd, false);
    },
    startEditingPoint() {
      status = STATUS_EDITING;
      dom.addEventListener('mousemove', mousemove4EditingPoint, false);
      dom.addEventListener('mousedown', mousedown4EditingPoint, false);
    },

    getSelectedPathItems() {
      return selectedPaths;
    },
    changeSelectedPathsDirections(direction) {
      selectedPaths.forEach(it => it.setPathDirection(direction));
    },
    changeSelectedPathsTypes(pathType) {
      selectedPaths.forEach(it => it.setPathType(pathType));
    },
    removeSelectedPaths() {
      if (selectedPaths.length == 0) {
        return;
      }
      let undoContext = {removeLines: [], addLines: []};
      selectedPaths.forEach(pathItem => {
        let ring0 = pathItem.fromCircle(), ring1 = pathItem.toCircle();
        let holders0 = Layers.getRelatedPathSegments([ring0]);
        let holders1 = Layers.getRelatedPathSegments([ring1]);
        if (holders0.length < 2 && ring0.parent) {
          ring0.parent.remove(ring0);
        }
        if (holders1.length < 2 && ring1.parent) {
          ring1.parent.remove(ring1);
        }
        pathItem.unselect();
        let line = pathItem.getLine();
        line.parent.remove(line);
        undoContext.addLines.push(line);
      });
      selectedPaths.splice(0, selectedPaths.length);
      Layers.navPathLayer.dispatchEvent(eventSelectPathChange);

      History.addAction("remove paths", undoContext);
    },
    removeAll() {
      let arr = Layers.navPathLayer.children.slice();
      arr.push(currentPointObj);
      arr.forEach(it => {
        if (it && it.parent) {
          it.parent.remove(it);
        }
      });
      selectedPaths = [];
      Layers.navPathLayer.dispatchEvent(eventSelectPathChange);
      pathNavigator.clearPathAnimation();
    }
  };
  return that;
};
export default NavPathTool;
