import {Vector3} from "three"

let PathPoint = function (vector3) {
  this.pos = new Vector3().copy(vector3);
  this.dstNum = 0; // 
  this.nearPlaces = []; //array of {to: edge.to (PathPoint type), cost: 12.23} : nearPlaces
  this.toPlaces = null; //array of {points: [array of PathPoint ], cost: 12.23}
  this.onFloor = true;
  this.abstract = false;
};

export default PathPoint;
