<template>
  <div class="tabbar">
    <tab-bar-item slot="label" index="0" :cur="currentIndex" @tabClick="onTabclick">
      <span class="iconfont iconzujian" slot="icon"></span>
      <span slot="txt"></span>
    </tab-bar-item>

    <tab-bar-item slot="label" index="1" :cur="currentIndex" @tabClick="onTabclick" v-show="!$store.state.editingFor3d">
      <span class="iconfont iconlujing" slot="icon"></span>
      <span slot="txt"></span>
    </tab-bar-item>

    <tab-bar-item slot="label" index="1" :cur="currentIndex" @tabClick="onTabclick" v-show="$store.state.editingFor3d">
      <span class="iconfont iconlujing" slot="icon"></span>
      <span slot="txt"></span>
    </tab-bar-item>

    <tab-bar-item slot="label" index="2" :cur="currentIndex" @tabClick="onTabclick">
      <span class="iconfont icontuceng" slot="icon"></span>
      <span slot="txt"></span>
    </tab-bar-item>

    <tab-bar-item slot="label" index="3" :cur="currentIndex" @tabClick="onTabclick">
      <span class="iconfont iconshezhi" slot="icon"></span>
      <span slot="txt"></span>
    </tab-bar-item>
  </div>
</template>

<script>
  import TabBar from "../common/tabBar/TabBar";
  import TabBarItem from "../common/tabBar/TabBarItem";

  export default {
    name: "MainTabBar",
    data() {
      return {
        currentIndex: '0'
      }
    },
    methods: {
      onTabclick(ind) {
        this.$emit('tabClick', {pre: ~~this.currentIndex, cur: ~~ind});
        this.currentIndex = ind;
      }
    },
    components: {
      TabBar, TabBarItem
    }
  }
</script>

<style scoped>

  .tabbar {
    display: flex;
    background-color: var(--color-tint);
    /*height: 66px;*/

    box-shadow: 0 -1px 1px rgba(100,100,100,.2);
  }
</style>
