<template>
  <div class="img-button" @click="btnClick">
    <span class="icon"> <slot name="icon"></slot> </span>
    <span class="txt"> <slot name="text"></slot> </span>
  </div>
</template>

<script>
  export default {
    name: "ImgButton",
    methods: {
      btnClick() {
        // console.log('img button click')
        this.$emit('btnClick');
      }
    }
  }
</script>

<style scoped>
  .img-button {
    display: flex;
    border-radius: 5px;
    background-color: #2381F6;
    text-align: center;
    padding: 8px;
    margin: 0px 3px;
    color: white;
    cursor: pointer;
    /*font-family:Alibaba PuHuiTi;*/
  }
  .img-button>.icon {
    font-size:12px;
  }
  .img-button>.txt {
    font-size:15px;
    margin-left: 5px;
  }
</style>
