<template>
  <div style="padding: 0 10px">
    <div id="placeHolderDiv" class="itemProperty" v-show="selectCount == 0">
      <p style="text-indent: 3em;"></p>
      <p style="text-indent: 3em;">(shift)</p>
    </div>
    <div id="propertiesSingleDiv" v-show="selectCount == 1">
<!--      <div class="itemProperty">-->
<!--        <div class="itemPropertyLabel" >ID</div>-->
<!--        <div class="inputWidth">-->
<!--          <el-input type="text" id="eleId" v-model="inputId" :readonly="true"/>-->
<!--        </div>-->
<!--      </div>-->
      <div class="itemProperty">
        <div class="itemPropertyLabel" ></div>
        <el-select v-model="layerVal" placeholder="" @change="onChangeItemsLayer">
          <el-option
                  v-for="item in layerOptions"
                  :key="item.id"
                  :label="item.name"
                  :value="item.id">
          </el-option>
        </el-select>
      </div>
      <div class="itemProperty">
        <div class="itemPropertyLabel" ></div>
        <div class="inputWidth">
          <el-input type="textarea" placeholder="" v-model="inputName"
                    :autosize="{ minRows: 1, maxRows: 2}" @input="onInputMeshesName"
                    @change="onInputMeshesNameEnd"
                    clearable/>
        </div>
      </div>
      <div class="itemProperty">
        <div class="itemPropertyLabel" ></div>
        <div class="inputWidth">
          <el-input type="text" id="eleHeight"  v-model="inputHeight" @input="onInputMeshesHeight"
                    @change="onInputMeshesHeightEnd" :numberOnly="true"/>
        </div>
      </div>
      <div class="itemProperty">
        <div class="itemPropertyLabel" ></div>
        <div class="inputWidth">
          <el-input type="text" id="eleTxtHeight"  v-model="inputTxtHeight" @input="onInputTxtHeight"
                    @change="onInputTxtHeightEnd" :numberOnly="true"/>
        </div>
      </div>
      <div class="itemProperty">
        <div class="itemPropertyLabel" ></div>
        <div class="inputWidth">
          <el-input ref="eleFillColor" v-model="inputFillColor" @colorChange="updateMeshesColor($event, false)"
                    @colorChangeEnd="updateMeshesColorEnd"
                    :colorAlphaEnabled="true"/>
        </div>
      </div>
      <div class="itemProperty">
        <div class="itemPropertyLabel" ></div>
        <div class="inputWidth">
          <el-input ref="eleLineColor" v-model="inputLineColor" @colorChange="updateMeshesColor($event, true)"
                    @colorChangeEnd="updateOutlinesColorEnd"
                    :colorEnabled="true"/>
        </div>
      </div>
    </div>
    <div id="propertiesMultiDiv" v-show="selectCount > 1">
      <div class="itemProperty">
        <div class="itemPropertyLabel" ></div>
        <el-select v-model="layerVal" placeholder="" @change="onChangeItemsLayer">
          <el-option
                  v-for="item in layerOptions"
                  :key="item.id"
                  :label="item.name"
                  :value="item.id">
          </el-option>
        </el-select>
      </div>
      <div class="itemProperty">
        <div class="itemPropertyLabel" ></div>
        <div class="inputWidth">
          <el-input type="text" placeholder="" v-model="inputName"
                    :autosize="{ minRows: 1, maxRows: 2}" @input="onInputMeshesName" @change="onInputMeshesNameEnd"
                    clearable/>
        </div>
      </div>
      <div class="itemProperty">
        <div class="itemPropertyLabel" ></div>
        <div class="inputWidth">
          <el-input id="elesHeight"  v-model="inputHeight" @input="onInputMeshesHeight" @change="onInputMeshesHeightEnd"
                    :numberOnly="true"/>
        </div>
      </div>
      <div class="itemProperty">
        <div class="itemPropertyLabel" ></div>
        <div class="inputWidth">
          <el-input ref="elesTxtHeight"  v-model="inputTxtHeight" @input="onInputTxtHeight" @change="onInputTxtHeightEnd"
                    :numberOnly="true"/>
        </div>
      </div>
      <div class="itemProperty">
        <div class="itemPropertyLabel" ></div>
        <div class="inputWidth">
          <el-input ref="elesFillColor" v-model="inputFillColor" @colorChange="updateMeshesColor($event, false)"
                    @colorChangeEnd="updateMeshesColorEnd"
                    :colorAlphaEnabled="true"/>
        </div>
      </div>
      <div class="itemProperty">
        <div class="itemPropertyLabel" ></div>
        <div class="inputWidth">
          <el-input ref="elesLineColor" v-model="inputLineColor" @colorChange="updateMeshesColor($event, true)"
                    @colorChangeEnd="updateOutlinesColorEnd"
                    :colorEnabled="true"/>
        </div>
      </div>

    </div>

    <div id="propertiesRef" v-show="selectCount == -1">
      <div class="itemPropertyLabel" ></div>
      <div class="itemProperty">
        <div class="itemPropertyLabel" >x: </div>
        <div class="inputWidth">
          <el-input ref="refPosX" v-model="refPosX" @input="updateRefPos" clearable/>
        </div>
      </div>
      <div class="itemProperty">
        <div class="itemPropertyLabel" >y: </div>
        <div class="inputWidth">
          <el-input ref="refPosY" v-model="refPosY" @input="updateRefPos" clearable/>
        </div>
      </div>
      <div class="itemProperty">
        <div class="itemPropertyLabel" >z: </div>
        <div class="inputWidth">
          <el-input ref="refPosZ" v-model="refPosZ" @input="updateRefPos" clearable/>
        </div>
      </div>
      <div class="itemPropertyLabel" ></div>
      <div class="itemProperty">
        <div class="itemPropertyLabel" > x: </div>
        <div class="inputWidth">
          <el-input ref="refScaleX" v-model="refScaleX" @input="updateRefScale" @clear="clearRefScaleX" clearable/>
        </div>
      </div>
      <div class="itemProperty">
        <div class="itemPropertyLabel" > y: </div>
        <div class="inputWidth">
          <el-input ref="refScaleY" v-model="refScaleY" @input="updateRefScale" @clear="clearRefScaleY" clearable/>
        </div>
      </div>

      <div class="itemProperty">
        <div class="itemPropertyLabel" >: </div>
        <div class="inputWidth">
          <el-input ref="refRotateZ" v-model="refRotateZ" @input="updateRefRotation" clearable/>
        </div>
        <span></span>
      </div>
      <div class="itemProperty">
        <div class="itemPropertyLabel" >: </div>
        <div class="inputWidth">
          <el-input ref="refAlpha" v-model="refAlpha" @input="updateRefAlpha" @clear="clearRefAlpha" clearable/>
        </div>
      </div>
    </div>

    <div id="propertiesMarkers" v-show="selectCount == -2">
      <div class="itemProperty">
        <div class="itemPropertyLabel" ></div>
        <el-select v-model="layerVal" placeholder="" @change="onChangeMarkersLayer">
          <el-option
            v-for="item in layerOptions"
            :key="item.id"
            :label="item.name"
            :value="item.id">
          </el-option>
        </el-select>
      </div>
      <div class="itemProperty">
        <div class="itemPropertyLabel" ></div>
        <div class="inputWidth">
          <el-input type="text" placeholder="" v-model="inputName"
                    :autosize="{ minRows: 1, maxRows: 2}" @input="onInputMarkersName"
                    clearable/>
        </div>
      </div>

      <div class="itemProperty">
        <div class="itemPropertyLabel" ></div>
        <div class="inputWidth">
          <el-input ref="elesTxtHeight"  v-model="inputTxtHeight" @input="onInputMarkersHeight"
                    :numberOnly="true"/>
        </div>
      </div>
      <div class="itemProperty">
        <div class="itemPropertyLabel" ></div>
        <div class="inputWidth">
          <el-input ref="markerTxtColor" v-model="textColor" @colorChange="updateMarkersColor($event, false)"
                    :colorEnabled="true"/>
        </div>
      </div>
      <div class="itemProperty">
        <div class="itemPropertyLabel" ></div>
        <div class="inputWidth">
          <el-input ref="markerOutlineColor" v-model="textOutlineColor" @colorChange="updateMarkersColor($event, true)"
                    :colorEnabled="true"/>
        </div>
      </div>

    </div>
  </div>
</template>

<script>
  import ElInput from '@/components/element/input';
  import ElSelect from '@/components/element/select';
  import ElOption from '@/components/element/select/src/option';
  import MainScene from '@/3d/js/MainScene';
  import History from "../../../3d/js/undo/History";
  import Layers from "../../../3d/js/Layers";
  import checkNumber from "../../element/input/src/checkNumber";
  import Common from "../../../3d/js/util/Common";
  import Config from "../../../3d/js/configurable/Config";
  import option from "../../element/select/src/option";

  let obj = {
    name: "BoxSelectedGraphic",
    data() {
      return {
        inputId: '',
        inputName: '',
        inputHeight: '',
        inputTxtHeight: '',
        inputFillColor: 'FFCC00 0.5',
        inputLineColor: 'FFCC00',
        textColor: 'FFCC00',
        textOutlineColor: 'FFCC00',
        selectCount: 0,
        //---- ref
        refPosX: 0,
        refPosY: 0,
        refPosZ: 0,
        refScaleX: 1,
        refScaleY: 1,
        refRotateZ: 0,
        refAlpha: 1,
        numInvalidInfo: "",
        //---- layer
        layerOptions: [],
        layerVal: null,
        //---- undo
        undoObj: {},
        dirtyName: false
      }
    },
    mounted() {
      let scene = MainScene.scene();
      let _this = this;
      scene.addEventListener('selectedCountChange', () => {
        if (_this.dirtyName) {
          _this.onInputMeshesNameEnd();
        }
        let meshLst = MainScene.polgyonTool().getSelectedMeshes();
        _this.selectCount = meshLst.length;
        if (meshLst.length == 0) {
          return;
        }
        let mesh = meshLst[0];
        _this.inputId = mesh.uuid;
        _this.inputName = mesh.holder.getName();
        if (meshLst.length > 1) {
          _this.inputName = "{xxx}";
        }
        _this.inputHeight = mesh.holder.getHeight();
        _this.inputTxtHeight = mesh.holder.getTextHeight();
        _this.inputFillColor = mesh.material.color.getHexString().toUpperCase() + ' ' + mesh.material.opacity;
        _this.inputLineColor = mesh.holder.getOutlines()[0].material.color.getHexString().toUpperCase();
        _this.undoObj.preTextHeight = _this.inputTxtHeight;
        _this.undoObj.preHeight = _this.inputHeight;
        if (_this.layerOptions.length == 0) {
          _this.layerOptions = Layers.getSelectableLayers().map(it => Common.convertLayerObj(it));
        }
        let layerGroup = Layers.getObjLayer(mesh);
        if (layerGroup.layerId != undefined)
          _this.layerVal = layerGroup.layerId;
        // console.log("selectedCountChange: ", this.inputFillColor);

        if (meshLst.length == 1) {
          _this.$refs.eleFillColor.updateColor(_this.inputFillColor);
          _this.$refs.eleLineColor.updateColor(_this.inputLineColor);
        } else {
          _this.$refs.elesFillColor.updateColor(_this.inputFillColor);
          _this.$refs.elesLineColor.updateColor(_this.inputLineColor);
        }
        _this.setUndoObj(meshLst);
      } );
      scene.addEventListener('refItemSelect', (evt) => {
        _this.selectCount = -1;
        // console.log('refItemSelect, ', evt.item);
        let mesh = evt.item;
        if (!mesh) {
          _this.selectCount = 0;
        } else {
          _this.selectCount = -1;
          _this.refPosX = mesh.position.x;
          _this.refPosY = mesh.position.y;
          _this.refPosZ = mesh.position.z;
          _this.refScaleX = mesh.scale.x;
          _this.refScaleY = mesh.scale.y;
          _this.refRotateZ = mesh.rotation.z * 180 / Math.PI;
          _this.refAlpha = mesh.material.opacity;
        }
      });
      scene.addEventListener('markers select change', () => {
        let markers = MainScene.markerTool().selectedMarkers();
        if (markers.length == 0) {
          return;
        }
        _this.selectCount = -2;
        let marker = markers[0];
        _this.inputName = marker.holder.text();
        _this.inputTxtHeight = marker.holder.getTextHeight();
        let layerGroup = Layers.getObjLayer(marker);
        if (layerGroup.layerId != undefined)
          _this.layerVal = layerGroup.layerId;

        let option = marker.holder.getOption();
        _this.textColor = option.textColor.toUpperCase().substr(1);
        _this.textOutlineColor = option.outlineColor.toUpperCase().substr(1);
        _this.$refs.markerTxtColor.updateColor(_this.textColor);
        _this.$refs.markerOutlineColor.updateColor(_this.textOutlineColor);
      });

      scene.addEventListener('layers changed', () => {
        _this.layerOptions = Layers.getSelectableLayers().map(it => Common.convertLayerObj(it));
      });
    },
    methods: {
      onInputMarkersName(val) {
        MainScene.markerTool().unifyMarkersName(val);
      },
      onInputMeshesName(val) {
        this.dirtyName = true;
        MainScene.polgyonTool().updateNameByRule(val);
      },
      onInputMeshesNameEnd() {
        this.dirtyName = false;
        let undo = this.undoObj;
        let undoContext = {meshes: undo.meshes, texts: undo.names,
          unifyText: this.inputName, preText: undo.preText};
        History.addAction("unify models names", undoContext);

        if (undo.meshes.length > 1)
          undo.preText = this.inputName;
      },
      onInputMeshesHeightEnd(val) {
        if ( !checkNumber(val) ) {
          return;
        }
        let heightVal = parseFloat(val);
        let undo = this.undoObj;
        if (heightVal == undo.preHeight)
          return;
        let undoContext = {meshes: undo.meshes, heights: undo.heights,
          unifyHeight: heightVal, preHeight: undo.preHeight};
        History.addAction("unify models height", undoContext);

        undo.preHeight = heightVal;
      },
      onInputTxtHeightEnd(val) {
        if ( !checkNumber(val) ) {
          return;
        }
        let heightVal = parseFloat(val);
        let undo = this.undoObj;
        if (heightVal == undo.preTextHeight)
          return;
        let undoContext = {meshes: undo.meshes, textHeights: undo.textHeights,
          unifyTextHeight: heightVal, preTextHeight: undo.preTextHeight};
        History.addAction("unify models text height", undoContext);

        undo.preTextHeight = heightVal;
      },
      onInputMeshesHeight(val) {
        let heightVal = parseFloat(val);
        let meshLst = MainScene.polgyonTool().getSelectedMeshes();
        if (isNaN(heightVal) || (meshLst.length == 0 && heightVal <= 0) ) {
          return;
        }
        let txtHeight = heightVal + Config.ui.txtHeightExtra;
        this.inputTxtHeight = txtHeight.toFixed(2);
        meshLst.forEach(mesh => {
          mesh.holder.setHeight(heightVal);
          mesh.holder.setTextHeight(txtHeight);
        });
      },
      onInputTxtHeight(val) {
        let heightVal = parseFloat(val);
        let meshLst = MainScene.polgyonTool().getSelectedMeshes();
        if (isNaN(heightVal) || (meshLst.length == 0 && heightVal <= 0)) {
          return;
        }
        meshLst.forEach(mesh => mesh.holder.setTextHeight(heightVal));
      },
      onInputMarkersHeight(val) {
        let heightVal = parseFloat(val);
        let markers = MainScene.markerTool().selectedMarkers();
        if (isNaN(heightVal) || (markers.length == 0 && heightVal <= 0)) {
          return;
        }
        markers.forEach(marker => marker.holder.setTextHeight(heightVal));
      },
      updateMarkersColor(jscolor, updateOutline) {
        let markers = MainScene.markerTool().selectedMarkers();
        let arr = jscolor.toString().split(' ');
        // let color = parseInt('0x' + arr[0]), opacity = arr[1];
        markers.forEach(marker => {
          let holder = marker.holder;
          let option = holder.getOption();
          if (updateOutline) {
            option.outlineColor = '#' + arr[0];
          } else {
            option.textColor = '#' + arr[0];
          }
          holder.updateText(holder.text(), option);
        });
      },
      updateMeshesColor(jscolor, updateOutline) {
        // console.log("updateMeshesColor", jscolor, updateOutline);
        let meshLst = MainScene.polgyonTool().getSelectedMeshes();
        let arr = jscolor.toString().split(' ');
        let color = parseInt('0x' + arr[0]), opacity = arr[1];
        // console.log("updateMeshesColor", meshLst.length);
        meshLst.forEach(mesh => {
          if (updateOutline) {
            let outlines = mesh.holder.getOutlines();
            if (outlines) {
              outlines.forEach(outline => outline.material.color.setHex(color) );
            }
          } else {
            mesh.material.color.setHex(color);
            mesh.material.opacity = opacity;
            mesh.userData.bacOpacity = opacity;
          }
        });
      },
      setUndoObj(meshLst) {
        let undo = this.undoObj;
        undo.meshes = meshLst.slice();
        undo.opacities = [], undo.colors = [], undo.lineColors = [], undo.names = [], undo.heights = [], undo.textHeights = [];
        undo.preColor = null, undo.preOpacity = null, undo.preLineColor = null;
        meshLst.forEach(mesh => {
          undo.opacities.push(mesh.material.opacity);
          undo.colors.push(mesh.material.color.clone() );
          undo.lineColors.push(mesh.holder.getOutlines()[0].material.color.getHex() );
          undo.names.push(mesh.holder.getName() );
          undo.heights.push(mesh.holder.getHeight() );
          undo.textHeights.push(mesh.holder.getTextHeight() );
        });
      },
      updateMeshesColorEnd(jscolor) {
        // console.log("updateMeshesColorEnd: " + jscolor);
        let arr = jscolor.toString().split(' ');
        let color = parseInt('0x' + arr[0]), opacity = arr[1];
        let undo = this.undoObj;
        let undoContext = {meshes: undo.meshes, colors: undo.colors, opacities: undo.opacities,
          unifyColor: color, unifyOpacity: opacity, preColor: undo.preColor, preOpacity: undo.preOpacity};
        History.addAction("unify model fillcolors", undoContext);

        undo.preColor = color, undo.preOpacity = opacity;
      },
      updateOutlinesColorEnd(jscolor) {
        let color = parseInt('0x' + jscolor);
        let undo = this.undoObj;
        let undoContext = {meshes: undo.meshes, lineColors: undo.lineColors,
          unifyColor: color, preColor: undo.preLineColor};
        History.addAction("unify model linecolors", undoContext);

        undo.preLineColor = color;
      },
      updateRefPos(val) {
        let refMesh = MainScene.polgyonTool().getSelectedRef();
        if (!refMesh) {
          return;
        }
        refMesh.position.x = parseFloat(this.refPosX) || 0;
        refMesh.position.y = parseFloat(this.refPosY) || 0;
        refMesh.position.z = parseFloat(this.refPosZ) || 0;
      },
      updateRefScale(val) {
        let refMesh = MainScene.polgyonTool().getSelectedRef();
        if (!refMesh) {
          return;
        }
        refMesh.scale.x = parseFloat(this.refScaleX);
        refMesh.scale.y = parseFloat(this.refScaleY);
      },
      clearRefScaleX() {
        let refMesh = MainScene.polgyonTool().getSelectedRef();
        refMesh.scale.x = 1;
        this.refScaleX = 1;
      },
      clearRefScaleY() {
        let refMesh = MainScene.polgyonTool().getSelectedRef();
        refMesh.scale.y = 1;
        this.refScaleY = 1;
      },
      updateRefRotation(val) {
        let refMesh = MainScene.polgyonTool().getSelectedRef();
        if (!refMesh) {
          return;
        }
        refMesh.rotation.z = (parseFloat(this.refRotateZ) || 0) /180 * Math.PI;
      },
      updateRefAlpha(val) {
        let refMesh = MainScene.polgyonTool().getSelectedRef();
        if (!refMesh) {
          return;
        }
        refMesh.material.opacity = parseFloat(this.refAlpha);
      },
      clearRefAlpha() {
        let refMesh = MainScene.polgyonTool().getSelectedRef();
        refMesh.material.opacity = 1;
        this.refAlpha = 1;
      },
      onChangeItemsLayer() {
        let meshLst = MainScene.polgyonTool().getSelectedMeshes();
        Layers.changeItemsLayer(meshLst, this.layerVal);
      },
      onChangeMarkersLayer() {
        let markers = MainScene.markerTool().selectedMarkers();
        Layers.changeMarkersLayer(markers, this.layerVal);
      },
    },
    components: {
      ElInput, ElSelect, ElOption
    }
  };
  export default obj;
</script>

<style scoped>

</style>
