<template>
  <div class="tabContentBoxes">
    <collapse v-model="activeNames" class="tabContent" role="buildings" @change="collapseChange">
      <collapse-item title="" name="select" :is-last = "$store.state.editingFor3d">
        <box-selected-graphics/>
      </collapse-item>
      <collapse-item title="" name="create" v-show="$store.state.showGraphicAdd && !$store.state.editingFor3d"
                     :is-last="true">
        <box-draw-graphic/>
      </collapse-item>
    </collapse>

    <collapse v-model="activeNames2" class="tabContent hide" role="paths" v-show="!$store.state.editingFor3d">
      <collapse-item title="" name="select">
        <box-selected-paths/>
      </collapse-item>
      <collapse-item title="" name="create" :is-last="true">
        <box-draw-path/>
      </collapse-item>
    </collapse>

    <collapse v-model="activeLights" class="tabContent hide" role="paths" v-show="$store.state.editingFor3d">
      <collapse-item title="" name="materials">
        <box-materials/>
      </collapse-item>
      <collapse-item title="" name="lights" v-show="$store.state.lightsEditable">
        <box-lights/>
      </collapse-item>
    </collapse>

    <div class="tabContent hide" role="layers">
      <box-layers/>
    </div>
    <collapse v-model="activeSettings" class="tabContent hide" role="settings">
      <collapse-item title="" name="mapEditorSetting">
        <box-settings/>
      </collapse-item>
      <collapse-item title="" name="refLayerDivide" v-show="!$store.state.editingFor3d">
        <box-ref-layer-divide/>
      </collapse-item>
      <collapse-item title="" name="geographyBind" v-show="!$store.state.editingFor3d">
        <box-coord-bind ref="boxCoordBind"/>
      </collapse-item>
      <collapse-item title="" name="misc" :is-last="true">
        <box-misc/>
      </collapse-item>
    </collapse>
  </div>
</template>

<script>
  // import {Collapse, CollapseItem} from 'element-ui';
  import Collapse from '../element/collapse/collapse';
  import CollapseItem from '../element/collapse/collapse-item';
  import BoxSelectedGraphics from "./tabdraw/BoxSelectedGraphics";
  import BoxDrawGraphic from "./tabdraw/BoxDrawGraphic";
  import BoxDrawPath from "./tabpath/BoxDrawPath";
  import BoxSelectedPaths from "./tabpath/BoxSelectedPaths";
  import BoxLayers from "./tablayer/BoxLayers";
  import BoxSettings from "./tabsettings/BoxSettings";
  import BoxCoordBind from "./tabsettings/BoxCoordBind";
  import BoxRefLayerDivide from "./tabsettings/BoxRefLayerDivide";
  import BoxMisc from "./tabsettings/BoxMisc";
  import MainScene from '@/3d/js/MainScene';
  import Main from "../../3d/Main";
  import BoxLights from "./tablights/BoxLights";
  import BoxMaterials from "./tablights/BoxMaterials";

  export default {
    name: "MainTabBox",
    data() {
      return {
        activeNames: ['select', 'create'],
        activeNames2: ['select', 'create'],
        activeSettings: ['mapEditorSetting'],
        activeLights: ['materials'],
      };
    },
    computed: {
      tabs() {//$store.state.editingFor3d
        let tabNodeLst = document.querySelectorAll('.tabContent');
        let arr = [];
        tabNodeLst.forEach(it => arr.push(it));
        if (this.$store.state.editingFor3d) {
          arr.splice(1, 1);
        } else {
          arr.splice(2, 1);
        }
        return arr;
      }
    },
    methods: {
      setTabs(obj) {
        // let tabs = document.querySelectorAll('.tabContent');
        // console.log(this.tabs);
        let oldIndex = obj.pre, newIndex = obj.cur;
        this.tabs[oldIndex].style.display = 'none';
        this.tabs[newIndex].style.display = 'block';
        if (oldIndex == 1) {
          MainScene.onNavPathTabUnselected();
        } else if (oldIndex == 3) {
          MainScene.onSettingsTabUnelected();
        }

        if (newIndex == 1) {
          MainScene.onNavPathTabSelected();
        } else if (newIndex == 3) {
          MainScene.onSettingsTabSelected();
          // this.$refs.boxCoordBind.updateUI();
        }
      },
      collapseChange() {
        // console.log("collapseChange: " + this.activeNames);
      }
    },
    components: {
      BoxMaterials,
      BoxLights,
      Collapse, CollapseItem, BoxSelectedGraphics, BoxDrawGraphic, BoxDrawPath, BoxSelectedPaths,
      BoxLayers, BoxSettings, BoxCoordBind, BoxRefLayerDivide, BoxMisc
    }
  }
</script>

<style scoped>
  .tabContentBoxes {

  }
  .tabContent {
    padding:0;
    overflow-y: auto;
    max-height: calc(100vh - 139px);
  }
  .hide {
    display: none;
  }

  .tabContent::-webkit-scrollbar {
    width: 6px;
    height: 6px;
    background-color: #fff;
  }
  .tabContent::-webkit-scrollbar-thumb {
    background-color: #93b9e3;
  }
  .tabContent::-webkit-scrollbar-track {
    background-color: #d9d9d9;
  }
</style>
