import pako from 'pako'

//https://www.cnblogs.com/chanwahfung/p/11391234.html
function handleCodePoints(array) {
  var CHUNK_SIZE = 0x8000; // arbitrary number here, not too small, not too big
  var index = 0;
  var length = array.length;
  var result = '';
  var slice;
  var arr = [];
  for (var i = 0, _i = array.length; i < _i; i++) {
        arr[i] = array[i];
     }
   while (index < length) {
         slice = arr.slice(index, Math.min(index + CHUNK_SIZE, length)); // `Math.min` is not really necessary here I think
         result += String.fromCharCode.apply(null, slice);
         index += CHUNK_SIZE;
     }
   return result;
}

// 
export function unzip(key, fromRemote) {
  if (key.charAt(0) === '{') {
    return key; //
  }
  // let charArr = key.split(''), pre0, pre1, pre2;
  // let charData = [];
  // for (let i = 0; i < charArr.length;)

  // 
  let charData = key.split('').map(function (x) {
    return x.charCodeAt(0);
  });
  // console.log(':', charData.join(","))

  // 
  var binData = new Uint8Array(charData);

  // 
  var data = pako.inflate(binData);

  // GunZip ByTAREARASCII
  // key = String.fromCharCode.apply(null, );
  key = handleCodePoints(new Uint16Array(data));

  //unescape(str)  --->
  return decodeURI(key);
}

// 
export function zip(str) {
  //escape(str)  --->
  var binaryString = pako.deflate(encodeURI(str), {to: 'string'});
  return binaryString;

}
