<template>
  <div id="compassBox">
    <img src="../../../assets/img/gadget/compass3.svg" @click="resetRotation" style="width: 40px; ">
  </div>
</template>

<script>
  import MainScene from '@/3d/js/MainScene';

  export default {
    name: "CompassItem",
    mounted() {
      let _this = this;
      let scene = MainScene.scene();
      scene.addEventListener('zRotate', rotateCompass);

      function rotateCompass(event) {
        // console.log('rotateCompass')
        _this.compassDom.style.transform = 'rotate(' + event.value + 'deg)';
      }
    },
    computed: {
      compassDom() {
        return document.getElementById('compassBox');
      }
    },
    methods: {
      resetRotation() {
        this.compassDom.style.transform = 'rotate(0deg)';
        MainScene.resetCameraRotate();
      }
    }
  }
</script>

