<template>
  <table class="simple-grid" border="0" cellspacing="1" cellpadding="1">
    <tr v-for="colArr in itemsRow">
      <td v-for="item in colArr" class="grid-cell"
          :class="{active: item.val === currentIndex}"
          @mousedown="selectGrid(item.val)">{{item.label}}</td>
    </tr>
  </table>
</template>

<script>
  import MainScene from "@/3d/js/MainScene";

  export default {
    name: "SimpleGrid",
    data() {
      return {
        itemsRow: [],
        currentIndex: -1
      }
    },
    mounted() {
      let _this = this;
      MainScene.scene().addEventListener("divide ref selected", (event) => {
        _this.currentIndex = event.value;
      });
    },
    methods: {
      selectGrid(index) {
        let result = MainScene.moveCameraToDivideIndex(index);
        if (result) {
          this.currentIndex = index;
        }
      },
      setRowCol(str) {
        this.itemsRow =[];
        let arr = str.split("x");
        let row = ~~arr[0], col = ~~arr[1];
        if (row < 2 && col < 2) {
          return;
        }
        for (let i = 0; i < row; i++) {
          let colArr = [];
          for (let j = 0; j < col; j++) {
            let dIndex = (row - i - 1) * row + j;
            let num = dIndex + 1;
            let obj = {label: num, val: dIndex};
            colArr.push(obj);
          }
          this.itemsRow.push(colArr);
        }
      }
    }
  }
</script>

<style scoped>
  .active {
    background-color: #55aaff;
    color: white;
  }
  .grid-cell {
    border-right: #999 1px solid;
    border-top: #999 1px solid;
  }
.simple-grid {
  table-layout: fixed;
  border: #443 3px solid;
  width: 280px;
  background-color:#fff;
  vertical-align: middle;
  border-collapse: collapse;
  text-align: center;
}
</style>
