<template>
  <ul class="light-items-list" :class="{show: this.show}">
    <li v-for="(item, index) in lights"
        class="light-item"
        :class="{active: index === currentIndex}"
        @click="selectLight(index)">
      <div class="flex1">{{item.name}}</div>
      <div class="iconfont pad-right btn-hover" :class="{'iconchakan2': !item.visible, 'iconchakan': item.visible}"
           @click="toggleItemVisible(index)"></div>
      <div class="iconfont iconshanchu1 btn-hover" @click="removeItem(index)"></div>
    </li>
  </ul>
</template>

<script>
  import MainScene from "../../../3d/js/MainScene";

  export default {
    name: "LightItems",
    props: {
      lights: {
        type: Array,
        default() {
          return []; //array of object (must not be THREE.Light type, but similar)
        }
      }
    },
    data() {
      return {
        currentLight: null,
        currentIndex: -1
      }
    },
    computed: {
      show() {
        return this.lights.length > 0;
      }
    },
    methods: {
      selectLightBy(uuid, fromEvent) {
        let index = this.currentIndex;
        for (let i = 0; i < this.lights.length; i++) {
          let obj = this.lights[i];
          if (obj.id == uuid) {
            index = i;
            break;
          }
        }
        if (this.currentIndex !== index) {
          this.currentLight = this.lights[index];
          if (!fromEvent)
            MainScene.lightTool().selectLightBy(uuid);
          this.currentIndex = index;
        }
        return this.lights[index];
      },
      selectLight(index) {
        if (index >= this.lights.length)
          return;
        let uuid = this.lights[index].id;
        this.selectLightBy(uuid);
      },
      toggleItemVisible(index) {
        let curLight = this.lights[index];
        curLight.visible = !curLight.visible;
        MainScene.lightTool().toggleLightVisible(curLight.id);
      },
      removeItem(index) {
        MainScene.lightTool().deleteLightBy(this.lights[index].id);
      },
      refreshSelection() {
        if (!this.currentLight || !MainScene.lightTool().selectedLight() ||
            this.lights.indexOf(this.currentLight) < 0 ||
          this.currentLight.id !== MainScene.lightTool().selectedLight().uuid ) {
          this.currentIndex = -1;
          this.currentLight = null
        }
      }
    }
  }
</script>

<style scoped>
  .light-items-list {
    display: block;
    max-height: 200px;
    overflow-x: hidden;
    overflow-y: auto;
    visibility: hidden;
  }
  .show {
    visibility: visible;
  }
  .light-item {
    list-style: none;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: white;
    color: black;
    padding: 0 10px;
    border-bottom: solid 1px #eee;
  }
  .light-item:hover {
    background-color: grey;
    color: white;
  }
  .active {
    background-color: white;
    color: #4b8bff;
  }
  .flex1 {
    flex: 1;
  }
  .btn-hover {
    cursor: pointer;
  }
  .pad-right{
    padding-right: 10px;
  }
</style>
