import {Object3D, BufferGeometry, InterleavedBuffer, InterleavedBufferAttribute, SpriteMaterial, Vector2} from 'three'

let TextSprite = function (material, rectData) {
  Object3D.call( this )
  this.type = 'Sprite';
  let _geometry = new BufferGeometry();

  let r = rectData;
  let uv0 = {x: r.x/r.size, y: r.y/r.size}, uv1 = {x: (r.x + r.width) / r.size, y: r.y/r.size};
  let uv2 = {x: (r.x + r.width)/r.size, y: (r.y + r.height)/r.size}, uv3 = {x: r.x/r.size, y: (r.y + r.height)/r.size};
  var float32Array = new Float32Array( [
    uv0.x-0.5, uv0.y-0.5, 0, uv0.x, uv0.y,
    uv1.x-0.5, uv1.y-0.5, 0, uv1.x, uv1.y,
    uv2.x-0.5, uv2.y-0.5, 0, uv2.x, uv2.y,
    uv3.x-0.5, uv3.y-0.5, 0, uv3.x, uv3.y
  ] );

  var interleavedBuffer = new InterleavedBuffer( float32Array, 5 );

  _geometry.setIndex( [ 0, 1, 2,	0, 2, 3 ] );
  _geometry.setAttribute( 'position', new InterleavedBufferAttribute( interleavedBuffer, 3, 0, false ) );
  _geometry.setAttribute( 'uv', new InterleavedBufferAttribute( interleavedBuffer, 2, 3, false ) );

  this.geometry = _geometry;
  this.material = ( material != undefined ) ? material : new SpriteMaterial();
  this.center = new Vector2( 0.5, 0.5 );
}
TextSprite.prototype = Object.assign( Object.create( Object3D.prototype ), {

  constructor: TextSprite,

  isSprite: true});

export default TextSprite;
