import {Vector3, Matrix4} from 'three';
import Layers from "../Layers";
import Main from "../MainScene";
import {getPositionMarker} from "../util/StaticMethods"
import {getLngLat, getMercator, wgs2bd} from "../lib_external/GeoCoord"
import Config from "../configurable/Config";

let CoordCorrelateTool = function () {
  let eventCoordChange = {type: 'coord change', index: 0, x: 0, y: 0};
  let eventMousemove = {type: 'mouse move', x: 0, y: 0, mx: 0, my:0, lng:0, lat:0};
  let editingLayer = Layers.editingLayer;
  let markers = [], coordBindings = [];
  for (let i = 0; i < 2; i++) {
    let marker = getPositionMarker(i+1);
    markers.push(marker);
    editingLayer.add(marker);
    coordBindings.push(0, 0);
  }
  markers[0].position.set(0, 3, 0);
  markers[1].position.set(3, 0, 0);
  let showing = false;
  let draggingMarker;
  let dragStartPos, markerPos0;
  // let scaleX = 0, scaleY = 0; // 3dxy
  let mercatorOrigin = {x: 0, y: 0}; //{x:0, y:0}
  let scale = 1; //
  let matTransform; // 3d * matTransform = 
  let matTransformInverse;
  let rotation = 0; // 
  let tmp = new Vector3();
  //test
  // let arr = wgs2bd(30.563638, 104.049875);
  // console.log('wgs2bd, ' + arr.join(', '));

  let dom = Main.getDom();
  dom.addEventListener('mousedown', mousedown4Drag);
  dom.addEventListener('mousemove', mousemove4Drag);
  dom.addEventListener('mouseup', mouseup4Drag);

  function mousedown4Drag(event) {
    if (!showing) {
      return;
    }
    let lst = Main.getIntersects(event.clientX, event.clientY, markers);
    if (lst.length == 0) {
      return;
    }
    draggingMarker = lst[0].object;
    eventCoordChange.index = markers.indexOf(draggingMarker);
    dragStartPos = lst[0].point;
    markerPos0 = draggingMarker.position.clone();

  }
  function mousemove4Drag(event) {
    if (!showing) {
      return;
    }
    let lst = Main.getIntersectsOnPlane(event.clientX, event.clientY);
    if (lst.length == 0) {
      return;
    }
    let curPos = lst[0].point;
    if (draggingMarker != null) {
      let vector = curPos.clone().sub(dragStartPos);
      let pos = markerPos0.clone().add(vector);
      draggingMarker.position.set(pos.x, pos.y, 0);
      eventCoordChange.x = pos.x;
      eventCoordChange.y = pos.y;
      Main.scene().dispatchEvent(eventCoordChange);
    } else {
      eventMousemove.x = curPos.x;
      eventMousemove.y = curPos.y;
      tmp.set(curPos.x, curPos.y, 0);
      tmp.applyMatrix4(matTransform);
      eventMousemove.mx = tmp.x;
      eventMousemove.my = tmp.y;
      let lnglat = getLngLat({x: eventMousemove.mx, y: eventMousemove.my});
      eventMousemove.lng = lnglat.x;
      eventMousemove.lat = lnglat.y;
      Main.scene().dispatchEvent(eventMousemove);
    }
  }
  function mouseup4Drag() {
    draggingMarker = null;
  }

  function initTransformation() {
    let markerPos1 = that.getCoord(0), markerPos2 = that.getCoord(1);
    let earthPos1 = new Vector3(coordBindings[0], coordBindings[1], 0);
    let earthPos2 = new Vector3(coordBindings[2], coordBindings[3], 0);

    let c1 = markerPos2.clone().sub(markerPos1), c2 = earthPos2.clone().sub(earthPos1);
    let scalar = c2.length() / c1.length();

    // let angle = c1.angleTo(c2);
    let angle = c1.getVector2().angleTo(c2.getVector2());
    // console.log("angle: " + angle);
    let matRotate = new Matrix4().makeRotationZ(angle);
    let matScale = new Matrix4().makeScale(scalar, scalar, 1);
    let mat = matRotate.clone().multiply(matScale);
    let tranlate = earthPos1.clone().sub(markerPos1.clone().applyMatrix4(mat));
    let matTranlate = new Matrix4().makeTranslation(tranlate.x, tranlate.y,0);
    mat.premultiply(matTranlate);

    matTransform = mat;
    matTransformInverse = new Matrix4().getInverse(mat);
    scale = scalar;
    rotation = angle;
    mercatorOrigin = new Vector3(0,0,0).applyMatrix4(mat);
    let rot = rotation * Config.mathConsts.radianToDegree;
    console.log("" + rot + "")
  }

  let that = {
    getScale() {
      return scale;
    },
    rotation() {
      return rotation;
    },
    clearBinding() {
      scale = 1;
      rotation = 0;
      matTransform = new Matrix4();
      coordBindings = [0,0,0,0];
      Main.scene().dispatchEvent(eventMousemove);
    },
    hide() {
      showing = false;
      markers.forEach(it => it.visible = false);
    },
    show() {
      showing = true;
      markers.forEach(it => it.visible = true);
    },
    getCoord(markerIndex) {
      return markers[markerIndex].position;
    },
    setCoord(markerIndex, x, y) {
      markers[markerIndex].position.set(x, y, 0);
    },
    setCoordBinds(index, x, y) {
      coordBindings[index * 2] = x;
      coordBindings[index * 2 + 1] = y;
    },
    getCoordBinds(index) {
      return {x: coordBindings[index * 2], y: coordBindings[index * 2 + 1]};
    },
    getRenderPos(lng, lat) {  //wcs3dxy
      let mercator;
      if (Config.code.lngLatBind) {
        mercator = {x: lng, y: lat};
      } else {
        let arr = [lat, lng];
        if (Config.code.coordType == Config.code.TYPE_BaiDu) {
          arr = wgs2bd(lat, lng); //wcs  
        }
        mercator = getMercator({x: arr[1], y: arr[0]} );
      }
      tmp.set(mercator.x, mercator.y, 0).applyMatrix4(matTransformInverse);
      return tmp;
    },
    serialize() {
      let arr = [], obj = {lngLatBind: Config.code.lngLatBind};
      markers.forEach(it => arr.push({x: it.position.x, y: it.position.y}) );
      obj.posLst = arr;
      obj.bindLst = coordBindings;
      return obj;
    },
    deserialize(obj) {
      Config.code.lngLatBind = obj.lngLatBind;
      coordBindings = obj.bindLst;
      obj.posLst.forEach((it, index) => {
        if (markers[index]) {
          markers[index].position.x = it.x;
          markers[index].position.y = it.y;
        }
      });
      initTransformation();
      Main.scene().dispatchEvent(eventMousemove);
    }
  };
  if (!showing) that.hide();
  return that;
};

export default  CoordCorrelateTool;
