<template>
  <div class="boxInTab2">
    <div class="settingItem">
      <div></div>
      <switch-item active-color="#409eff" inactive-color="#ff4949" v-model="valueShowNames" @change="toggleModelsNames"/>
    </div>
    <div class="settingItem">
      <div></div>
      <switch-item active-color="#409eff" inactive-color="#ff4949" v-model="valueShowGrid" @change="toggleGrid"/>
    </div>
    <div class="settingItem">
      <div></div>
      <switch-item active-color="#409eff" inactive-color="#ff4949" v-model="valueDrawAdhere" @change="toggleAdhere"/>
    </div>
<!--    <div class="settingItem">-->
<!--      <input type="button" value="" @click="resetCamera()" id="camReset">-->
<!--    </div>-->
    <div class="settingItem">
      <div class="itemPropertyLabel" ></div>
      <div class="inputWidth" style="width: 200px">
        <el-input v-model="sceneBackColor" @colorChange="updateBackgroundColor"
                  ref="backColor" :colorAlphaEnabled="true"/>
      </div>
    </div>
  </div>
</template>

<script>
  import ElInput from '@/components/element/input';
  import SwitchItem from '@/components/element/switch/component'
  import MainScene from '@/3d/js/MainScene';
  import Config from "../../../3d/js/configurable/Config";
  import {Ref_Flag} from "../../../store/mutations-types";
  import Layers from "../../../3d/js/Layers";

  export default {
    name: "BoxSettings",
    mounted() {
      MainScene.scene().addEventListener('reset ui', () => {
        this.valueShowNames = Config.ui.modelTextVisible;
        this.valueShowGrid = !Layers.layers.background.hidden;
        this.valueDrawAdhere = Config.ui.enableEditPointAdhere;
        this.$store.commit(Ref_Flag, false);
      });
      this.sceneBackColor = MainScene.getClearColor();
      this.$refs.backColor.updateColor(this.sceneBackColor);
    },
    data() {
      return {
        valueShowNames: true,
        valueShowGrid: true,
        valueDrawAdhere: true,
        sceneBackColor: "ffffff 1"
      }
    },
    methods: {
      toggleModelsNames(val) {
        Config.ui.modelTextVisible = val;
        MainScene.toggleModelsNames();
      },
      toggleGrid() {
        Layers.toggleHide(Layers.layers.background);
      },
      toggleAdhere(val) {
        Config.ui.enableEditPointAdhere = val;
      },
      resetCamera() {
        MainScene.resetCamera();
      },
      updateBackgroundColor(jscolor) {
        let arr = jscolor.split(' ');
        let color = parseInt('0x' + arr[0]);
        let opacity = parseFloat(arr[1]);
        MainScene.setClearColor(color, opacity);
      }
    },
    components: {
      SwitchItem, ElInput
    }
  }
</script>

<style scoped>
  .settingItem {
    padding: 5px 0 0 0;
    display: flex;
    font-size: 14px;
    color: #333;
    justify-content: space-between;
  }
  .boxInTab2 {
    padding: 0 10px;
  }
</style>
