import {
  Group, MeshBasicMaterial,
  Mesh, MeshLambertMaterial, Vector3, Shape, Path, FrontSide
} from 'three'
import {generateTextCanvas, makeCanvasTextSprite, createMesh4Heatmap, vectorsToObjXY,
  getHeatmapMaterial, createModel3D} from "../util/StaticMethods";
import TriangleCut from "../lib/TriangleCut";
import Config from "../configurable/Config";
import Box2 from "../lib/Box2";

let Polygon3d = function (vectorLst, holeNums) {
  //vectices holesVectices 
  let vectices = [];
  let holeIndices = [], holesVectices = [], holes =[];
  let name;
  let zHeight, txtHeight;
  let textSprite;
  let convexLst2;
  let selectedMaterial = new MeshBasicMaterial({color: "#d28e13", transparent: true, opacity: 1});
  let oldMaterial;
  let inSelectionAnim = false, opacityDiff = -0.03;
  let holderGroup = new Group();
  let model3d;
  let originBox;
  let modelPos = new Vector3();
  let mesh4Heatmap, data4Heatmap, range4Heatmap;

  if (vectorLst != null && vectorLst.length > 2) {
    init(vectorLst, holeNums);
  }
  function init(vecLst, holeNums) {
    if (holeNums != null && holeNums.length > 0) {
      holeIndices = holeNums.slice();
    } else {
      vectices = vecLst.slice();
    }
    if (holeIndices.length > 0) {
      for (let i = 0; i < holeIndices.length; i++) {
        if (i == 0) {
          vectices = vecLst.slice(0, holeIndices[i]);
        } else {
          let arr = vecLst.slice(holeIndices[i-1], holeIndices[i]);
          holesVectices.push(arr);
        }
      }
      holesVectices.push(vecLst.slice(holeIndices[holeIndices.length-1], vecLst.length));
    }
  }

  function setTextSprite() {
    let canvas = generateTextCanvas(name);
    textSprite = makeCanvasTextSprite(canvas);
    textSprite.renderOrder = 2;
    return textSprite;
  }

  function initConvexLst2(meshMatrix) {
    convexLst2 = [];
    [vectices].concat(holesVectices).forEach(arr => {
      let lst = [];
      arr.forEach(vector => {
        let point = vector.clone().applyMatrix4(meshMatrix);
        lst.push(point);
      });
      let objsXY = vectorsToObjXY(lst);
      let convexLst = [];
      TriangleCut.concavToConvex(objsXY, convexLst);
      convexLst2.push(convexLst);
    });
  }

  let that = {
    getTextSprite() {
      return textSprite;
    },
    contains(pointXY) {
      return TriangleCut.isPointInPolygonWithHoles(pointXY, convexLst2);
    },
    bindPathPoints: [],
    getModelPos() {
      if (that.bindPathPoints.length > 0) {
        modelPos.copy(that.bindPathPoints[0].pos);
      } else {
        let pos = model3d.position;
        modelPos.set(pos.x, 0, -pos.y);
      }
      return modelPos;
    },
    name() {
      return name;
    },
    getMesh() {
      return model3d;
    },
    inSelectionAnim() {
      return inSelectionAnim;
    },
    updateSelectionAnim() {
      selectedMaterial.opacity += opacityDiff;
      if (selectedMaterial.opacity < 0.2) {
        opacityDiff = 0.06;
      }
      if (selectedMaterial.opacity > 1) {
        selectedMaterial.opacity = 1;
        inSelectionAnim = false;
      }
    },
    startSelectAnimation() {
      if (inSelectionAnim)
        return;
      model3d.material = selectedMaterial;
      opacityDiff = -0.06;
      inSelectionAnim = true;
    },
    clearSelection() {
      selectedMaterial.opacity = 1;
      inSelectionAnim = false;
      model3d.material = oldMaterial;
    },
    selected() {
      return model3d.material === selectedMaterial;
    },

    showHeatmap(dataFromRemote) {
      if (!mesh4Heatmap) {
        let material = null;
        if (dataFromRemote) {
          let maxValue = -1;
          let sumVal = 0;
          for (let i = 0; i < dataFromRemote.length; i++) {
            let item = dataFromRemote[i];
            item.x = item.x * 1;
            item.y = item.y * 1;  //convert string to number
            sumVal += item.value;
            if (maxValue < item.value)
              maxValue = item.value;
          }
          console.log("heatmap max value: " + maxValue);
          let squarePos = originBox.minSquarePos();
          data4Heatmap = dataFromRemote;
          range4Heatmap = {min: 1, max: sumVal, cur: ~~(sumVal/2)};
          range4Heatmap.bili = (range4Heatmap.cur - range4Heatmap.min) / (range4Heatmap.max - range4Heatmap.min)
          material = getHeatmapMaterial(material, dataFromRemote, squarePos, range4Heatmap.cur);
        }
        mesh4Heatmap = createMesh4Heatmap(vectices, holesVectices, model3d.matrix, zHeight + 0.01, material);
        mesh4Heatmap.renderOrder = 9999;
      }
      holderGroup.add(mesh4Heatmap);
    },
    updateMax4Heatmap(max) {
      getHeatmapMaterial(mesh4Heatmap.material, data4Heatmap, originBox.minSquarePos(), max );
    },
    range4Heatmap() {
      return range4Heatmap;
    },
    hideHeatmap() {
      if (mesh4Heatmap)
        holderGroup.remove(mesh4Heatmap);
    },
    originBox() {
      return originBox;
    },
    deserialize(obj) {
      let allVertices = [];
      for (let arr of obj.allVertices) {
        let vector = new Vector3().fromArray(arr);
        allVertices.push(vector);
      }
      name = obj.name;
      zHeight = obj.height;
      txtHeight = obj.txtHeight;
      init(allVertices, obj.holeNums);

      //height, vectices, holesVectices, opacity, meshColor(arr), position(arr), scale(arr), quaternion(arr), lineColor
      obj.vectices = vectices, obj.holesVectices = holesVectices;

      model3d = createModel3D(obj);
      oldMaterial = model3d.material;

      holderGroup.add(model3d);
      if (name && name.trim().length > 0) {
        setTextSprite();
        holderGroup.add(textSprite);
        textSprite.position.set(model3d.position.x, model3d.position.y, txtHeight);
      }
      model3d.updateMatrix();
      initConvexLst2(model3d.matrix);
      // let dd = convexLst2.length;
      let points = [];
      // console.log(model3d.position);
      vectices.forEach(vector => points.push(vector.clone().applyMatrix4(model3d.matrix)));
      originBox = new Box2().setFromPoints(points);

      holderGroup.holder = that;
      model3d.holder = that;
      return holderGroup;
    }
  };

  return that;
};

export default Polygon3d
