<template>
  <div class="boxInTab">
    <simple-grid ref="grid"/>
    <div class="btns">
      <div class="btn-item">
        <form name="form1">
          <input type="file" name="picpath" style="display:none;" @change="selectImage" accept="image/*">
          <input type="button" value="" @click="imgBtnClicked">
        </form>
      </div>

      <div><input type="button" value="" @click="onclickMerge()"></div>
    </div>

  </div>
</template>

<script>
  import SimpleGrid from "../gadget/SimpleGrid";
  import MainScene from "@/3d/js/MainScene";
  import {Ref_Flag} from "../../../store/mutations-types";
  import Notification from "../../element/notification";

  export default {
    name: "BoxRefLayerDivide",
    mounted() {
      MainScene.scene().addEventListener("divide ref layer", (event) => {
        this.$refs.grid.setRowCol(event.value);
      });
    },
    components: {
      SimpleGrid
    },
    methods: {
      imgBtnClicked() {
        document.form1.picpath.click()
      },
      selectImage() {
        let file = document.form1.picpath.files;
        // console.log('selectImage, ', file);
        if (!file || !file.length) {
          return;
        }
        let _this = this;
        let reader = new FileReader();
        reader.onload = function (evt) {
          // refLayerItem.style.display = 'block';
          // navPathRefDiv.style.display = 'block';
          // refPathAlpha.value = Singleton.getRefOpacityPath();
          let image = document.createElement('img');
          image.src = evt.target.result;
          image.onload = function () {
            MainScene.setReferImg(image, isJPEG);
            _this.$store.commit(Ref_Flag, true);
          };
        }
        let fileObj = file[0];
        let isJPEG = fileObj.name.search( /\.jpe?g($|\?)/i ) > 0 || fileObj.type.search( /^image\/jpeg/ ) === 0;
        reader.readAsDataURL(fileObj);

      },
      onclickMerge() {
        try {
          MainScene.startMergeAnimation();
        } catch (err) {
          Notification.error({message:err, position:'top-left'});
        }
      }
    }
  }
</script>

<style scoped>
.btns {
  display: flex;
}
  .btn-item {
    margin-right: 10px;
  }
</style>
