import {Vector3} from 'three'
import {makeCanvasTextSprite, generateTextCanvas, generateSpriteBox} from "../util/StaticMethods"
// import Config from "../configurable/Config";

let TextMark = function (txt, option) {
  let extraWidth = 18, extraHeight= 12;
  let text = txt;
  let oldPos = new Vector3();
  let sprite;
  if (txt != null) {
    sprite = makeCanvasTextSprite(generateTextCanvas('', option));
  }
  let textHeight = 0;
  let spriteBox;
  let opt = option || {};

  function renewSpriteBox() {
    if (spriteBox && spriteBox.parent)
      spriteBox.parent.remove(spriteBox);
    if (!sprite.parent)
      return;
    let w = sprite.userData.poi.w + extraWidth, h = sprite.userData.poi.h + extraHeight;
    spriteBox = generateSpriteBox(w, h, 4, '#1730ff');
    spriteBox.visible = false;
    sprite.parent.add(spriteBox);
    spriteBox.position.copy(sprite.position);
  }
//{size: 40, textColor: '#000', outlineColor: '#fff'}
  let that = {
    isMarker: true,
    getOption() {
      return {
        size: opt.size || 40,
        textColor: opt.textColor || '#000000',
        outlineColor: opt.outlineColor || '#ffffff',
      }
    },
    text() {
      return text;
    },
    getOldPos() {
      return oldPos;
    },
    updatePosition() {
      oldPos.copy(sprite.position);
    },
    move(vector) {
      let pos = oldPos.clone().add(vector);
      sprite.position.copy(pos);
      spriteBox.position.copy(pos);
    },
    setPosition(pos) {
      sprite.position.copy(pos);
      if (spriteBox)
        spriteBox.position.copy(pos);
      oldPos.copy(pos);
    },
    updateText(name, option, published) {
      if (option) {
        if (option.size != null)
          opt.size = option.size;
        if (option.textColor != null)
          opt.textColor = option.textColor;
        if (option.outlineColor != null)
          opt.outlineColor = option.outlineColor;
      }

      let parent;
      if (sprite) {
        parent = sprite.parent;
        if (parent) {
          parent.remove(sprite);
        } else {
          return;
        }
      }
      if (name != null) {
        let canvas = generateTextCanvas(name, that.getOption() );
        let textSprite = makeCanvasTextSprite(canvas);
        if (parent) {
          parent.add(textSprite);
          textSprite.position.copy(sprite.position);
        }
        sprite = textSprite;
        text = name;
        sprite.holder = that;
        if (!published)
          renewSpriteBox();
        // textSprite.visible = Config.ui.modelTextVisible;
      }
    },
    sprite() {
      return sprite;
    },
    getTextHeight() {
      return textHeight;
    },
    setTextHeight(val) {
      textHeight = val.toFixed(2)*1;
    },
    select() {
      if (spriteBox)
        spriteBox.visible = true;
    },
    unselect() {
      if (spriteBox)
        spriteBox.visible = false;
    },
    changeParent(parent) {
      parent.add(sprite);
      if (spriteBox)
        parent.add(spriteBox);
    },
    attach(layer) {
      layer.add(sprite);
      renewSpriteBox();
    },
    detach() {
      if (sprite.parent) {
        sprite.parent.remove(sprite);
        spriteBox.parent.remove(spriteBox);
      }
    },
    serialize() {
      let target = {
        isMarker: true,
        name: text,
        th: textHeight,
        pos: sprite.position.toArrayFixed(2)
      };
      Object.assign(target, opt);
      return target;
    },
    deserialize(obj, published) {
      that.updateText(obj.name, obj, published);
      let pos = new Vector3().fromArray(obj.pos);
      if (published)
        pos.z = obj.th;
      that.setPosition(pos);
      textHeight = obj.th;
      return that;
    },
  }
  if (sprite)
    sprite.holder = that;
  return that;
}

export default TextMark;
