import Main from "../MainScene";
import TextMark from "../instance/TextMark";
import Layers from "../Layers";
import Config from "../configurable/Config";
import History from "../undo/History";

let MarkerTool = function (dom) {
  let eventMarkersSelectChange = {type: 'markers select change'};

  let isAddingTextMark = false;
  let contextMenuEnabled = true;

  let currentTextMark = new TextMark('');
  let editingLayer = Layers.editingLayer;
  let selectedMarkers = [];
  let scene = Main.scene();
  let mouseDownPos;

  scene.addEventListener('selectionBoxUpdated', mouseup4SelectionBox);
  scene.addEventListener('lock change', onLockChange);

  function onLockChange(event) {
    if (isAddingTextMark) {
      endAddTextMark();
    }
    let locked = event.layerObj.locked, hidden = event.layerObj.hidden, layer = event.layerObj.group;
    let lst = layer.children;
    for (let i = 0; i < lst.length; i++) {
      if (lst[i].holder == undefined || !lst[i].holder.isMarker)
        continue;
      let sprite = lst[i].holder.sprite();
      if (locked || hidden) {
        let index = selectedMarkers.indexOf(sprite);
        if (index >= 0) {
          selectedMarkers[index].holder.unselect();
          selectedMarkers.splice(index, 1);
        }
      }
    }
  }

  function getLayer() {
    let markLayer = Layers.markLayer;
    if (markLayer.hidden || markLayer.locked) {
      return Layers.getLayer()
    }
    return markLayer;
  }
  function startAddTextMark() {
    isAddingTextMark = true;
    contextMenuEnabled = false;
    dom.addEventListener('mousemove', mousemove4MarkAdding, false);
    dom.addEventListener('mousedown', mousedown4MarkAdding, false);
  }
  function endAddTextMark() {
    isAddingTextMark = false;
    dom.removeEventListener('mousemove', mousemove4MarkAdding, false);
    dom.removeEventListener('mousedown', mousedown4MarkAdding, false);
    if (currentTextMark.sprite().parent) {
      currentTextMark.sprite().parent.remove(currentTextMark.sprite());
    }
  }

  function mouseup4SelectionBox(event) {
    if (isAddingTextMark || Layers.navPathTabSelected() )
      return;
    let markers = Layers.availableMarkers(false);
    if (markers.length == 0) {
      return;
    }
    let allSelected = event.selectBox.selectRings(markers);
    selectedMarkers.forEach(it => it.holder.unselect() );
    allSelected.forEach(it => it.holder.select() );
    selectedMarkers = allSelected;
    scene.dispatchEvent(eventMarkersSelectChange);
  }

  function mousemove4MarkAdding(event) {
    if (!currentTextMark.sprite().parent)
      editingLayer.add(currentTextMark.sprite());
    let lst = Main.getIntersectsOnPlane(event.clientX, event.clientY);
    if (lst.length > 0) {
      currentTextMark.sprite().position.copy(lst[0].point);
    }
  }
  function mousedown4MarkAdding(event) {
    event.cancelBubble = true;
    event.stopPropagation();

    if (event.button === 2) {
      // 
      endAddTextMark();
      setTimeout(function () {
        contextMenuEnabled = true;
      }, 100);
    } else {
      addMark();
    }
  }

  function mousedown4Select(event) {
    if (isAddingTextMark)
      return true;
    let arr = Layers.availableMarkers(false);
    if (arr.length == 0)
      return false;
    let lst = Main.getIntersects(event.clientX, event.clientY, arr);
    if (lst.length == 0) {
      if (!Main.pressingShift()) setSelectMark(null);
      return false;
    }
    let mark = lst[0].object;
    setSelectMark(mark);
    mouseDownPos = lst[0].point;
    selectedMarkers.forEach(it => it.holder.updatePosition() );
    dom.addEventListener('mousemove', mousemove4Drag);
    document.addEventListener('mouseup', mouseup4Drag);
    return true;
  }

  function mousemove4Drag(event) {
    if (event.buttons == 2) {
      return;
    }
    let lst = Main.getIntersectsOnPlane(event.clientX, event.clientY);
    if (lst.length == 0) {
      return;
    }
    let curPos = lst[0].point;
    let vector = curPos.clone().sub(mouseDownPos);
    selectedMarkers.forEach(marker => marker.holder.move(vector) );
  }
  function mouseup4Drag() {
    let undoContext = {meshes:[], positions:[], oldPositions:[] };
    selectedMarkers.forEach(it => {
      undoContext.meshes.push(it);
      undoContext.oldPositions.push(it.holder.getOldPos().clone() );
      it.holder.updatePosition();
      undoContext.positions.push(it.holder.getOldPos().clone() );
    } );

    if (undoContext.positions.length > 0 &&
      undoContext.positions[0].manhattanDistanceTo(undoContext.oldPositions[0]) >= Config.code.moveSlightlyStep) {
      History.addAction("update models position", undoContext);
    }

    dom.removeEventListener('mousemove', mousemove4Drag);
    document.removeEventListener('mouseup', mouseup4Drag);
  }

  function setSelectMark(sprite) {
    if (sprite == null) {
      selectedMarkers.forEach(it => it.holder.unselect() );
      selectedMarkers = [];
      scene.dispatchEvent(eventMarkersSelectChange);
      return;
    }
    if (Main.pressingShift() && !isAddingTextMark) {
      sprite.holder.select();
      if (selectedMarkers.indexOf(sprite) < 0) {
        selectedMarkers.push(sprite);
      } else {
        sprite.holder.unselect();
        selectedMarkers.splice(selectedMarkers.indexOf(sprite), 1);
      }
      scene.dispatchEvent(eventMarkersSelectChange);
    } else {
      if (isAddingTextMark || selectedMarkers.indexOf(sprite) < 0) {
        selectedMarkers.forEach(it => it.holder.unselect() );
        selectedMarkers.splice(0, selectedMarkers.length);
        sprite.holder.select();
        selectedMarkers[0] = sprite;
        scene.dispatchEvent(eventMarkersSelectChange);
      }
    }
  }

  function addMark() {
    let layer = getLayer();
    let mark = new TextMark(currentTextMark.text());
    mark.setPosition(currentTextMark.sprite().position);
    mark.attach(layer);
  }

  function removeMark(holder) {
    let index = selectedMarkers.indexOf(holder);
    if (index >= 0) {
      selectedMarkers.splice(index, 1);
    }
    holder.detach();
  }

  let that = {
    mousedown4Select: mousedown4Select,

    startAddTextMark: startAddTextMark,
    endAddTextMark: endAddTextMark,
    contextMenuEnabled() {
      return contextMenuEnabled;
    },
    selectedMarkers() {
      return selectedMarkers;
    },
    unifyMarkersName(name) {
      selectedMarkers.forEach(marker => marker.holder.updateText(name))
    },
    removeSelected() {
      if (selectedMarkers.length == 0 || isAddingTextMark) {
        return;
      }

      selectedMarkers.forEach(mark => mark.holder.detach() );
      selectedMarkers = [];
      scene.dispatchEvent(eventMarkersSelectChange);
    },
    removeAll() {
      let markers = Layers.availableMarkers(true);
      markers.forEach(it => {
        if (it.parent)
          it.parent.remove(it)
      })
      selectedMarkers = [];
      scene.dispatchEvent(eventMarkersSelectChange);
    },
    setSelection(sprite) {
      setSelectMark(sprite)
    },
  };
  return that;
}
export default MarkerTool;
