export default /* glsl */`
uniform vec3 color;
uniform float opacity;
uniform float radius_outer; //0.5
uniform float radius_inner;

void main() {
    gl_FragColor = vec4( color, opacity );
    vec2 diff = vec2(0.5, 0.5) - gl_PointCoord;
    float distanceSquared = dot(diff, diff); 
    float outer = radius_outer * radius_outer;
    float inner = radius_inner * radius_inner;
    if ( distanceSquared > outer || distanceSquared < inner ) discard;
}
    `
