<template>
  <li class="layerItem" :id="item.id">
    <div class="left common flex1"><p :contenteditable="item.nameEditable" class="layer-title"
      @mousedown="mousedown4LayerName" @keydown="keydown4LayerName" @blur="blur4LayerName">{{item.name}}</p></div>
    <div class="btn-hover">
      <div class="iconfont p10" :class="{'iconsuo21': !item.locked, 'iconsuo1': item.locked,}"
           @click="lockChange($event)" id="lockMarkers"></div>
      <div class="iconfont iconchakan2 p10" :class="{'iconchakan2': item.hidden, 'iconchakan': !item.hidden,}"
           @click="hideChange($event)" id="eyeMarkers"></div>
      <div style="width: 18px; display: inline-block">
        <div class="iconfont iconshanchu1" v-show="item.removable" @click="onclickRemove"></div>
      </div>
    </div>

  </li>
</template>

<script>
  import Layers from "../../../3d/js/Layers";
  import * as M from "../../../store/mutations-types";

  export default {
    name: "LayerItem",
    props: {
      item: Object
    },
    data() {
      return {
        changingName: false
      }
    },
    methods: {
      lockChange() {
        Layers.toggleLock(this.item);
        Layers.floorLayer.dispatchEvent({type: 'default color change', item: null} );
        this.$store.commit(M.Show_GraphicAdd, Layers.getLayer() != null);//
      },
      hideChange() {
        Layers.toggleHide(this.item);
        Layers.floorLayer.dispatchEvent({type: 'default color change', item: null} );
        this.$store.commit(M.Show_GraphicAdd, Layers.getLayer() != null);//
      },
      onclickRemove() {
        this.$emit("removeLayer", {item: this.item} );
      },
      mousedown4LayerName(event) {
        if (event.target.contentEditable == "false")
          return;
        let selection = window.getSelection();
        if (this.changingName) {
          let existRange = selection.getRangeAt(0);
          if (!existRange.collapsed) {
            selection.removeAllRanges();
          }
          // console.log("existRange:", existRange);
          return;
        }
        let range = document.createRange();
        range.selectNodeContents(event.target);
        selection.removeAllRanges();
        selection.addRange(range);
        this.changingName = true;
      },
      keydown4LayerName(event) {
        if (event.keyCode === 13) {
          // 
          event.preventDefault();
        }
        this.changingName = true;
      },
      blur4LayerName(event) {
        window.getSelection().removeAllRanges();
        this.changingName = false;
        this.item.name = event.target.innerText;
        Layers.renameLayer(this.item.id, this.item.name);
        // console.log("layer name: " + this.item.name);
      }
    }
  }
</script>

<style scoped>
  .layerItem {
    list-style: none;
    border-bottom: solid 1px #ddd;
    border-top: solid 1px #ddd;
    line-height: 2.2;
    display: flex;
    align-items: center;
    justify-content: space-between;
    color: #000;
    padding: 0 10px;
  }
  flex1 {
    flex: 1
  }
  .layer-title {
    min-width: 20px;
    max-width: 180px;
    text-overflow: clip;
    overflow: hidden;
    white-space: nowrap;
  }
  .common {
    display: inline-block;
  }
  .btn-hover {
    cursor: pointer;
  }
  .btn-hover>div {
    display: inline-block;
  }
  .p10 {
    padding-right: 10px;
  }
</style>
