<template>
  <top-bar>
    <page-title slot="left">
<!--      <span slot="icon" class="iconfont iconfanhui hand"></span>-->
      <span slot="title"></span>
    </page-title>
    <div slot="center" style="display: flex; margin-right: auto; align-items: center">
      <el-dropdown @command="onSearchByDropdownClose">
        <div class="search-pre-box">
          <i class="iconfont icongengduo2"></i>
        </div>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item :command="0" :selected="searchBySelectIndex==0">{{searchByLst[0]}}</el-dropdown-item>
          <el-dropdown-item :command="1" :selected="searchBySelectIndex==1">{{searchByLst[1]}}</el-dropdown-item>
          <el-dropdown-item :command="2" :selected="searchBySelectIndex==2">{{searchByLst[2]}}</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>

      <el-select v-model="searchTxt" @change="onSearchItemClick" size="small" :placeholder="searchPlaceholder" filterable noIconRotate
                 input-style="border-radius: 0 5px 5px 0" remote :remote-method="filterFromEditor"
                 customIcon="iconfont iconsousuo searchIcon">
        <el-option
          v-for="item in searchOptions"
          :key="item.id"
          :label="item.name"
          :value="item.id">
          <span style="float: left">{{ item.name }}</span>
          <span style="float: right; color: #8492a6; font-size: 13px">{{ item.type }}</span>
        </el-option>
      </el-select>
    </div>
    <div slot="right" style="display: flex">
      <div class="btnGroup0">
        <i class="iconfont iconchexiao1 bareIcon" :class="{'enabled': undoEnabled}" @click="performUndo"></i>
        <div style="width: 10px"></div>
        <i class="iconfont iconhuanyuan bareIcon" :class="{'enabled': redoEnabled}" @click="performRedo"></i>
      </div>
      <div class="btnGroup1">
        <img-button @btnClick="onClickSave">
          <span slot="icon" class="iconfont iconbaocun"></span>
          <span slot="text"></span>
        </img-button>

        <img-button @btnClick="onClickLoad">
          <span slot="icon" class="iconfont iconfanhui"></span>
          <span slot="text"></span>
        </img-button>
        <input type="file" id="jsonInput" style="display:none;" @input="selectMapJson" accept="application/json">
      </div>
      <div class="btnGroup2">
        <img-button @btnClick="publishMap">
          <span slot="icon" class="iconfont iconfabu"></span>
          <span slot="text"></span>
        </img-button>

        <img-button @btnClick="onClickPreview">
          <span slot="icon" class="iconfont iconyulan"></span>
          <span slot="text"></span>
        </img-button>

        <img-button @btnClick="onClickPreview(true)">
          <span slot="icon" class="iconfont iconyulan"></span>
          <span slot="text">pc</span>
        </img-button>

        <img-button @btnClick="showDownloadPrompt">
          <span slot="icon" class="iconfont iconxiazai"></span>
          <span slot="text"></span>
        </img-button>

<!--        <img-button @btnClick="showDeveloping">-->
<!--          <span slot="icon" class="iconfont icongengduo"></span>-->
<!--          <span slot="text"></span>-->
<!--        </img-button>-->
      </div>

      <div class="username">
        {{userName}}
      </div>
    </div>
  </top-bar>
</template>

<script>
  import ElInput from "@/components/element/input"
  import ElDropdown from "@/components/element/dropdown"
  import ElDropdownMenu from "@/components/element/dropdown/src/dropdown-menu"
  import ElDropdownItem from "@/components/element/dropdown/src/dropdown-item"
  import ElSelect from '@/components/element/select'
  import ElOption from '@/components/element/select/src/option'
  import TopBar from "../common/TopBar";
  import ImgButton from "../common/ImgButton";
  import PageTitle from "../common/PageTitle";
  import MainScene from "../../3d/js/MainScene";
  import Serializer from "../../3d/js/util/Serialization";
  import Notification from "../element/notification";
  import FileSaver from 'file-saver';
  import History from "../../3d/js/undo/History";
  import Config from "../../3d/js/configurable/Config";
  import Common from "../../3d/js/util/Common";
  import {zip, unzip} from "../../3d/js/lib_external/CompressTool";

  export default {
    name: "MainTopBar",
    data() {
      return {
        searchTxt: "",
        searchOptions: [],
        searchPlaceholder: ": ",
        searchBySelectIndex: 0,
        searchByLst: ["", "ID", "ID"],
        undoEnabled: false,
        redoEnabled: false
      }
    },
    mounted() {
      History.addEventListener("stack change", (event) => {
        this.undoEnabled = event.undoLen > 0;
        this.redoEnabled = event.redoLen > 0;
      });
      if (Config.remote.token != null) {
        this.loadFromRemote();
      }
    },
    methods: {
      onSearchItemClick(uuid) {
        MainScene.setSelectByUuid(uuid);
      },
      filterFromEditor(query) {
        if (query.trim().length > 0)
          this.searchOptions = MainScene.search(query.trim())
      },
      onSearchByDropdownClose(command) {
        this.searchBySelectIndex = command;
        this.searchPlaceholder = ": " + this.searchByLst[command];
      },
      onClickSave() {
        let str = Serializer.saveToLoal();
        // Notification.success({message:'', position:'bottom-right'});
        if (Config.remote.token != null) {
          // this.saveToRemote(zip(str) );
          this.saveToRemote(str);
        }
      },
      saveToRemote(savingStr) {
        let fileName = ".json";
        let formData = new FormData();
        let file = new File([savingStr], fileName, {
          type: "text/plain;charset=utf-8"
        });
        formData.append("file", file); //files
        formData.append("headerFilename", "");
        formData.append("mapId", Config.remote.mapId); //
        formData.append("saveType", "1"); //
        formData.append("longitude", "116.397128"); //
        formData.append("latitude", "39.916527"); //

        this.axios.post('/merchant/merchantMap/save', formData).then((response) => {
          console.log(response.data);
          if (response.data.code == 0) {
            Notification.success({message:'', position:'bottom-right'});
          } else {
            Notification.info({message:response.data.message, position:'bottom-right'});
          }
        }).catch(error => {
          console.log(error);
        });
      },
      loadFromRemote() {
        let _this = this;
        this.axios({
          url: '/merchant/merchantMap/download',
          method: 'post',
          responseType:'blob',
          data: {
            mapId: Config.remote.mapId,
            version: Config.remote.version,
          }
        }).then((response) => {
          // console.log(response.data);
          if (response.data) {
            let mapContent = response.data; //unzip(response.data.tdata, true);
            _this.parseRemoteMap(mapContent);
          }
        }).catch(error => {
          console.log(error);
        });
      },
      parseRemoteMap(mapContent) {
        const resultJson = mapContent.slice(0, 1024);
        const reader = new FileReader();
        reader.readAsText(resultJson);
        reader.onload = function (event) {
          const content = Common.clearNull(reader.result);
          const headerObj = JSON.parse(content);
          // console.log("parseRemoteMap, headerObj: ", headerObj);
          for (let i = 0; i < headerObj.file.length; i++) {
            const mapPartObj = headerObj.file[i];
            const start = mapPartObj.start - 1;
            const end = mapPartObj.end;
            // console.log("parseRemoteMap, part: " + i, mapPartObj);
            if (i > 0) {
              continue;
            }
            const mapPartStr = mapContent.slice(start, end);
            const readerTmp = new FileReader();
            readerTmp.readAsText(mapPartStr);
            readerTmp.onload = function(event) {
              Serializer.deserialize(readerTmp.result);
            }
          }
        }
      },
      onClickLoad() {
        // Serializer.loadFromLocal(false);
        // Notification.success({message:'', position:'bottom-right'});
        this.jsonInput.click();
      },
      onClickPreview(forPc) {
        Serializer.saveToLoal(true);
        // this.$router.replace('/preview');
        let routeUrl = this.$router.resolve({
          path: forPc ? "/preview4pc" : "/preview",
          query: { fromEditor: true }
        });
        window.open(routeUrl.href, '_blank');
      },
      publishMap() {
        let jsonObj = {mapId: Config.remote.mapId, info: ""};
        let headerObj =  {
          headers: {
            'Content-Type': "application/json"
          }
        };
        this.axios.post('/merchant/merchantMapRelease/add', jsonObj, headerObj).then((response) => {
          console.log(response.data);
          if (response.data.code == 0) {
            Notification.info({message:'', position:'bottom-right'});
          }
        }).catch(error => {
          console.log(error);
        });
      },
      showDeveloping() {
        Notification.info({message:'', position:'bottom-right'});
      },
      showDownloadPrompt() {
        let name = prompt("", "map demo");
        if (name && name.trim()) {
          // console.log(name);
          let content = Serializer.serialize();
          let zipped = content; //zip(content);
          let blob = new Blob([zipped ], {type: "text/plain;charset=utf-8"});
          FileSaver.saveAs(blob, name + ".json");
        }
      },
      selectMapJson() {
        let file = this.jsonInput.files;
        console.log('selectMapJson, ', file);
        if (!file || !file.length) {
          return;
        }

        let reader = new FileReader();
        reader.onload = function (evt) {
          // console.log(evt.target.result);
          let unzipped = unzip(evt.target.result);
          Serializer.deserialize(unzipped);
        }
        let fileObj = file[0];
        reader.readAsText(fileObj);
      },
      performUndo() {
        // console.log("perform undo");
        if (this.undoEnabled) {
          History.undoAction();
        }
      },
      performRedo() {
        // console.log("perform redo");
        if (this.redoEnabled) {
          History.redoAction();
        }
      }
    },
    computed: {
      jsonInput() {
        return document.getElementById('jsonInput');
      },
      userName() {
        if (this.$route.query.username != null)
          return this.$route.query.username;
        return "";
      }
    },
    components: {
      TopBar, ImgButton, PageTitle, ElInput, ElDropdown, ElDropdownItem, ElDropdownMenu,ElSelect,ElOption
    }
  }
</script>

<style scoped>
.hand {
  cursor: pointer;
}
  .searchIcon {
    font-size: 14px;
    color: #55aaff;
    line-height: 34px;
    cursor: pointer;
  }

  .username {
    color: #2381F6;
    max-width: 140px;
    white-space: nowrap;
    text-overflow:ellipsis;
    overflow:hidden;

    font-size: 14px;
    line-height: 33px;
    padding: 0 10px;
    border-left: solid 1px #ccc;
  }

  .btnGroup2 {
    padding: 0 10px;
    display: flex;
    border-left: solid 1px #ccc;
  }

.btnGroup1 {
  padding: 0 10px;
  display: flex;
  border-left: solid 1px #ccc;
}
.btnGroup0 {
  padding: 0 10px;
  display: flex;
  justify-content: center;
  align-items: center;
}
  .bareIcon {
    color: #999;
    font-size: 20px;
    cursor: pointer;
  }
  .enabled {
    color: black;
  }
  .search-pre-box {
    width: 32px;
    height: 32px;
    border-radius: 5px 0 0 5px;
    color: white;
    background-color: #2381F6;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
  }
</style>
