<template>
  <div class="editor-toolbar">
    <div style="width: 77px; margin-left: 10px">
      <el-select v-model="scaleValue" @change="onChangeScale" size="mini" :hideBorder="true"
        customIcon="iconfont iconxiala2">
        <el-option
                v-for="item in scaleOptions"
                :key="item.value"
                :label="item.label"
                :value="item.value">
        </el-option>
      </el-select>
    </div>
    <div class="div-divider">
      <div class="vertital-divide"/>
    </div>
    <div class="btnGroup">
      <div class="singleBtnBox" v-tip.dark="tips[0]" @click="startAddTextMark">
        <i class="iconfont iconA bareIcon"></i>
      </div>

      <el-dropdown trigger="click" @command="onMeasureAction">
        <div class="singleBtnBox" v-tip.dark="tips[1]">
          <i class="iconfont iconbiaochi bareIcon"></i>
        </div>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item :command="2"></el-dropdown-item>
          <el-dropdown-item :command="1"></el-dropdown-item>
          <el-dropdown-item :command="0"></el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>

      <div class="singleBtnBox" v-tip.dark="fullScreenTip" @click="toggleFull">
        <i class="iconfont iconquanping bareIcon"></i>
      </div>
      <div class="singleBtnBox" v-tip.dark="tips[3]">
        <i class="iconfont iconchakan bareIcon"></i>
      </div>
    </div>
    <div class="div-divider">
      <div class="vertital-divide"/>
    </div>
    <span></span>
    <div style="width: 65px; ">
      <el-select v-model="divideValue" @change="onChangeDivide" size="mini"
                 customIcon="iconfont iconxiala2" :hideBorder="true">
        <el-option
                v-for="item in divideOptions"
                :key="item.value"
                :label="item.label"
                :value="item.value">
        </el-option>
      </el-select>
    </div>

    <div class="div-divider">
      <div class="vertital-divide"/>
    </div>
    <span></span>
    <div style="width: 55px; margin-right: 10px">
      <el-select v-model="floorValue" @change="onChangeFloor" size="mini"
                 customIcon="iconfont iconxiala2" :hideBorder="true">
        <el-option
                v-for="item in floorOptions"
                :key="item.value"
                :label="item.label"
                :value="item.value">
        </el-option>
      </el-select>
    </div>
  </div>
</template>

<script>
  import ElSelect from '@/components/element/select';
  import ElOption from '@/components/element/select/src/option';
  import ElDropdown from '@/components/element/dropdown';
  import ElDropdownMenu from '@/components/element/dropdown/src/dropdown-menu';
  import ElDropdownItem from '@/components/element/dropdown/src/dropdown-item';
  import MainScene from "@/3d/js/MainScene";
  import Notification from "../../element/notification";
  import {fullScreen, exitFullscreen, isFullScreen} from "../../../external_lib/fullscreen";

  export default {
    name: "EditorToolbar",
    components: {
      ElSelect, ElOption, ElDropdown, ElDropdownMenu, ElDropdownItem
    },
    mounted() {
      let _this = this;
      document.addEventListener("fullscreenchange", function (e) {
        if (document.fullscreenElement) {
          _this.fullScreenTip = ""
        } else {
          _this.fullScreenTip = ""
        }
      })

      MainScene.cameraControl().addEventListener("change scale", onCameraScaling);

      function onCameraScaling(event) {
        let val = parseInt((1/event.scaleValue) * 100);
        let res = _this.scaleOptions.find((value, index, arr) => value.value == val);
        if (res) {
          _this.scaleValue = val;
        } else {
          _this.scaleValue = val + "%";
        }
      }
      MainScene.scene().addEventListener("divide ref layer", (event) => {
        for (let dOption of _this.divideOptions) {
          if (dOption.label == event.value) {
            _this.divideValue = dOption.value;
            return;
          }
        }
      });
    },
    data() {
      return {
        tips: ["", "", "", "", ""],
        fullScreenTip: '',
        scaleOptions: [{
          value: 25,
          label: '25%'
        }, {
          value: 50,
          label: '50%'
        }, {
          value: 100,
          label: '100%'
        }, {
          value: 200,
          label: '200%'
        }, {
          value: 400,
          label: '400%'
        }, {
          value: 800,
          label: '800%'
        }],
        scaleValue: 100,

        floorOptions: [{
          value: 0,
          label: 'b1'
        }, {
          value: 1,
          label: 'b2'
        }, {
          value: 2,
          label: 'b3'
        }],
        floorValue: 0,

        divideOptions: [{
          value: 0,
          label: '1x1'
        }, {
          value: 1,
          label: '2x2'
        }, {
          value: 2,
          label: '3x3'
        }, {
          value: 3,
          label: '4x4'
        }, {
          value: 4,
          label: '5x5'
        }],
        divideValue: 0
      }
    },
    methods: {
      onMeasureAction(command) {
        switch (command) {
          case 2:
            MainScene.measureTool().startMeasureLength();
            break;
          case 1:
            MainScene.measureTool().startMeasureArea();
            break;
          case 0:
            MainScene.measureTool().clearMeasures();
            break;
        }
      },
      startAddTextMark() {
        MainScene.markerTool().startAddTextMark();
      },
      onChangeScale() {
        MainScene.cameraControl().zoomCameraToPercentage(this.scaleValue);
      },
      onChangeDivide() {
        let divideStr = this.divideOptions[this.divideValue].label;
        try {
          MainScene.divideRefImg(divideStr);
        } catch (err) {
          Notification.error({message:err, position:'top-left'});
        }
      },
      toggleFull() {
        if (isFullScreen()) {
          exitFullscreen();
        } else {
          fullScreen(document.body);
        }
      },
      onChangeFloor() {

      }
    }
  }
</script>

<style scoped>
  .editor-toolbar {
    display: flex;
    background-color: white;
    box-shadow:0px 2px 13px 0px rgba(0, 0, 0, 0.2);
    align-items: center;
    /*width: 458px;*/
    /*height: 54px;*/
  }
  .btnGroup {
    display: flex;
    justify-content: center;
    align-items: center;
    /*padding: 0 10px;*/

  }
  .vertital-divide {
    border-left: solid 1px #ddd;
    height: 24px;
  }
  .div-divider {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 20px;
  }
  .singleBtnBox {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .singleBtnBox:hover {
    background-color: #ddd;
  }
  .bareIcon {
    font-size: 20px;
    cursor: pointer;
    padding: 10px;
    color: black;
  }
</style>
