import {WebGLRenderer, Scene, PerspectiveCamera, PointLight, PlaneBufferGeometry, MeshBasicMaterial, AxesHelper,
  LinearToneMapping, RGBAFormat, RGBFormat, FrontSide, Raycaster, DirectionalLight, SpotLight, HemisphereLight,
  BoxHelper, Vector3, Group, Mesh} from 'three';
// import {OrbitControls} from 'three/examples/jsm/controls/OrbitControls'
import MapControls from "./lib/MapControls";
import Stats from 'three/examples/jsm/libs/stats.module'

import Layers from './Layers';
import Polygon3dReader from "./util/Polygon3dReader";
import {makeGpsIndicatorBase, makeGpsIndicatorPlane} from './util/StaticMethods'
import MapScalar from "./model/MapScalar";
import NavigatorTool from "./tool/NavigatorTool";
import OrbitControls from "./lib/OrbitControls";
import Config from "./configurable/Config";
import * as Const from "./consts/Constants";
import PathTrack from "./instance/PathTrack";
import kdbush from "./lib_external/kdbush";

let PublishedScene = function () {
  let eventCameraChange = {type: 'camera change', rotate: 0};
  let eventRangeHeatmap = {type: 'range heatmap', obj: {} };
  let orbitControls;
  let renderer;
  let scene, camera, stats;
  let planeHelper;
  let gpsLocIndicatorBase = makeGpsIndicatorBase();
  let gpsLocIndicator = makeGpsIndicatorPlane();
  let coordTool, navigatorTool;
  let watcher_id = null;

  let camDistanceSquared;
  let resetingCamera = false;
  let resetingFrames = 14;
  let curFrame = 1;
  let resetRotating = false, resetMoving = false, zooming = false, phiing = false;
  let tmpVector = new Vector3();
  let originPhi = 0;
  let reader = Polygon3dReader();
  let lights = [];
  let pathTrack;
  let heatmapShowing = false;
  let meshesAll = [];

  function start_watchPosition() {
    // watchPosition
    watcher_id = navigator.geolocation.watchPosition(getSuccess, getError, getOptions);
  }

  function endResetCamera() {
    resetingCamera = false;
    orbitControls.enabled = true;
    resetRotating = false, resetMoving = false, zooming = false, phiing = false;
  }

  function initLights(scene) {
    let ambiColor = "#ffffff";
    let light0 = new PointLight(ambiColor);
    light0.position.set( -30, 30, 10);
    scene.add(light0);
    let light1 = new PointLight(ambiColor);
    light1.position.set( 30, -30, 20);
    scene.add(light1);
    let light2 = new PointLight(ambiColor);
    light2.position.set( 50, 40, 20);
    scene.add(light2);
    lights.push(light0, light1, light2);
  }

  function initControl() {
    orbitControls = new MapControls(camera, renderer.domElement);
    // orbitControls = new OrbitControls(camera, renderer.domElement);
    orbitControls.enableRotate = false;
    orbitControls.screenSpacePanning = false;
    orbitControls.maxPolarAngle = Math.PI * 75 / 180;
    orbitControls.minDistance = 2.5;
    orbitControls.maxDistance = 118;
    orbitControls.addEventListener( "change", onCameraChange);
    orbitControls.addEventListener( "change scale", onCameraScaling);
    orbitControls.addEventListener( "change phi", onCameraPhiChange);
    // originPhi = orbitControls.currentPhi();
    originPhi = Math.PI/4;
    // console.log("originPhi: " + originPhi);
  }

  //three
  function toScreenPos(position) {
    let standardVector = tmpVector.copy(position).project(camera);//
    let a =  window.innerWidth / 2;
    let b =  window.innerHeight / 2;
    let x = Math.round(standardVector.x * a + a);//
    let y = Math.round(-standardVector.y * b + b);//
    return { x: x, y: y};
  }

  function onCameraChange() {
    eventCameraChange.rotate = camera.rotation.z;
    scene.dispatchEvent(eventCameraChange);
    updateTextSprites();
    // updateTextSpritesDebounce();
  }
  function onCameraScaling(event) {
    let scale = event.scaleValue;
    // console.log("scale from camera: " + scale + ", y: " + camera.position.y);
    gpsLocIndicator.scale.set(scale, scale, 1);
    navigatorTool.updatePathIndicatorScale(scale);
  }
  function onCameraPhiChange(event) {
    navigatorTool.rotatePathIndicator(event.value);
  }

  function getX(point) {
    return point.x;
  }
  function getY(point) {
    return point.y;
  }

  let updateTextSpritesDebounce = debounce(updateTextSprites, 50);
  function debounce(method, delay) {
    let timer = null;
    let _this = this;
    return function (arg) {
      let self = _this;
      clearTimeout(timer);
      timer = setTimeout(function () {
        method.call(self, arg);
      }, delay);
    }
  }
  function updateTextSprites() {
    let arr = [], points = [];
    meshesAll.forEach(it => {
      if (it.holder) {
        let sprite =it.holder.getTextSprite();
        if (sprite) {
          sprite.visible = true;
          sprite.screenPos = toScreenPos(sprite.globalPos);
          arr.push(sprite);
          sprite.screenPos.host = sprite;
          points.push(sprite.screenPos);
        }
      }
    });
    points.sort((a,b) => a.x - b.x);
    let index = kdbush(points, getX, getY, 64, Int32Array);

    for (let i = 0; i < points.length; i++) {
      let coord = points[i];
      let sprite = coord.host;
      if (!sprite.visible)
        continue;
      let image = sprite.material.map.image;
      let width = image.width, height = image.height * 0.5;
      let minX = coord.x - width;
      let minY = coord.y - height;
      let maxX = coord.x + width;
      let maxY = coord.y + height;

      let neighbors = index.range(minX, minY, maxX, maxY);
      for (let j = 0; j < neighbors.length; j++) {
        let ind = neighbors[j];
        if (ind === i)
          continue;
        points[ind].host.visible = false;
      }
    }
  }

  let getOptions = {
    //GPStrue
    enableHighAccuracy:true,
    //0
    timeout:5000,
    //
    //0
    //Infinity
    maximumAge:0
  };
  let count0 = 0, count1 = 0;
  //
  function getSuccess(position){
    // getCurrentPositiongetSuccessposition
    // positioncoordstimeStamp
    // timeStamp
    // coords
    let lat = position.coords.latitude;
    let lng = position.coords.longitude;
    // 
    // console.log(position.coords.heading);
    // /
    // console.log(position.coords.speed);
    let markerPos = coordTool.getRenderPos(lng, lat);
    gpsLocIndicator.position.x = markerPos.x;
    gpsLocIndicator.position.y = markerPos.y;
    gpsLocIndicatorBase.position.copy(gpsLocIndicator.position);
    if (navigatorTool) navigatorTool.onGpsPostitionChange();
    if (count0 == 0)
      alert(lng + ", " + lat);
    count0 ++;
  }
  //
  function getError(error) {
    // error
    // errorcodeTIMEOUTPERMISSION_DENIEDPOSITION_UNAVAILABLE
    // code
    let errorMsg = "unknown";
    switch(error.code){
      case error.TIMEOUT:
        errorMsg = '';
        console.log('');
        break;
      case error.PERMISSION_DENIED:
        errorMsg = '';
        console.log('');
        break;
      case error.POSITION_UNAVAILABLE:
        errorMsg = '';
        console.log("");
        break;
      default:
        break;
    }
    if (count1 == 0)
      alert(errorMsg);
    count1 ++;
  }

  function setGlobalPos4TextLabels() {
    meshesAll = Layers.availableMeshes(true, true);
    meshesAll.forEach(mesh => {
      if (mesh.holder) {
        let sprite = mesh.holder.getTextSprite();
        if (sprite) {
          if (!sprite.globalPos)
            sprite.globalPos = new Vector3();
          sprite.globalPos = sprite.globalPos.copy(sprite.position).applyMatrix4(mesh.parent.parent.matrix);
        }
      }
    });
    updateTextSprites();
  }

  function orientationHandler(event) {
    // console.log("is global earth orientation: " + event.absolute);
    let alpha = 360 - Math.round(event.alpha);
    gpsLocIndicator.rotation.z = -alpha/180 * Math.PI;
  }

  function init(fromEditor) {
    window.addEventListener('deviceorientationabsolute', orientationHandler, false);
    scene = new Scene();
    // helpers
    let helperGeometry = new PlaneBufferGeometry(999, 999, 2, 2);
    let helperMaterial = new MeshBasicMaterial({color: 0x00cc00, wireframe: true, transparent: true, opacity: 0, depthWrite: false});
    planeHelper = new Mesh(helperGeometry, helperMaterial);
    scene.add(planeHelper);
    // scene.add(new AxesHelper(200));

    // create a render and set the size
    renderer = new WebGLRenderer({ antialias: true });
    // renderer.setClearColor( 0xa0a0a0, 1 );
    renderer.setClearColor( '#ffffff', 1 );
    // renderer.setPixelRatio( window.devicePixelRatio );
    renderer.setSize(window.innerWidth, window.innerHeight);
    renderer.autoClear = false;
    renderer.toneMapping = LinearToneMapping;

    camera = new PerspectiveCamera( 70, window.innerWidth / window.innerHeight, 1, 1000 );
    camera.up.set(0, 0, 1);
    camera.position.set(0, 0, 35);
    camDistanceSquared = camera.position.y * (camera.position.y);

    coordTool = MapScalar();
    navigatorTool = NavigatorTool(that);
    navigatorTool.addListeners(renderer.domElement);

    Layers.attachLayersToScene(scene, null);
    Layers.backgroundLayer.visible = false;

    let fromRemote = Config.remote.previewMapId != null;
    if (fromEditor) {
      reader.loadFromLocal();
      setGlobalPos4TextLabels();
      if (!fromRemote) {
        Layers.indicatorLayer.add(gpsLocIndicatorBase);
        Layers.indicatorLayer.add(gpsLocIndicator);
        // Singleton.buildingsLayer.add(new AxesHelper(200));
      }
    }
    // start_watchPosition();
    navigatorTool.onMapChange();
    // gpsLocIndicator.position.x = -10;
    // gpsLocIndicatorBase.position.x = -10;
    // let markerPos = coordTool.getRenderPos(112.87632, 28.23165);
    // gpsLocIndicator.position.x = markerPos.x;
    // gpsLocIndicator.position.y = markerPos.y;
    // gpsLocIndicatorBase.position.copy(gpsLocIndicator.position);

    document.getElementById("WebGL-output").appendChild(renderer.domElement);
    if (Config.ui.showFps) {
      stats = new Stats();
      document.getElementById("WebGL-output").appendChild( stats.dom );
    }
    initControl();
    render();
    function render() {
      requestAnimationFrame(render);
      if (Config.ui.showFps)
        stats.update();
      renderer.clear();
      renderer.render( scene, camera);

      let animItems =  meshesAll.filter(group => group.holder && group.holder.inSelectionAnim() );
      animItems.forEach(group => group.holder.updateSelectionAnim() );
      if (resetingCamera) {
        curFrame++;
        if (curFrame > resetingFrames) {
          endResetCamera();
        } else {
          if (resetRotating) {
            orbitControls.resetingRotation();
          }
          if (resetMoving) {
            orbitControls.resetingMove(curFrame == resetingFrames);
          }
          if (zooming) {
            orbitControls.zoomingCamera();
          }
          if (phiing) {
            orbitControls.phiingCamera();
          }
        }
      }
      navigatorTool.animatePathIndicator();

      if (pathTrack && pathTrack.playing() ) {
        pathTrack.update();
      }
    }
  }

  window.addEventListener( 'resize', function () {
    if (!camera)
      return;
    camera.aspect = window.innerWidth / window.innerHeight;
    camera.updateProjectionMatrix();

    renderer.setSize( window.innerWidth, window.innerHeight );
  }, false );

  let that = {
    init: init,
    deserialize(jsonStr) {
      reader.initializeScene(jsonStr);
      setGlobalPos4TextLabels();
      Layers.indicatorLayer.add(gpsLocIndicatorBase);
      Layers.indicatorLayer.add(gpsLocIndicator);
      navigatorTool.onMapChange();
      if (pathTrack)
        pathTrack.detach();
      pathTrack = null;
      heatmapShowing = false;
    },
    updateMax4Heatmap(max) {
      let floorGroupItems = Layers.floorLayer.children;
      if (floorGroupItems.length > 0) {
        floorGroupItems[0].holder.updateMax4Heatmap(max);
      }
    },
    //dataFromRemote null
    //dataFromRemote: [{x: "-5.790", y: "12.680", value: 31}, ...]
    showHeatmap(dataFromRemote) {
      let floorGroupItems = Layers.floorLayer.children;
      if (floorGroupItems.length > 0) {
        floorGroupItems[0].holder.showHeatmap(dataFromRemote);
        if (dataFromRemote && dataFromRemote.length > 0) {
          eventRangeHeatmap.obj = floorGroupItems[0].holder.range4Heatmap();
          scene.dispatchEvent(eventRangeHeatmap);
        }
      }

      meshesAll.forEach(it => {
        if (!it.holder)
          return;
        let model3d = it.holder.getMesh();
        model3d.userData.bacOpacity = model3d.material.opacity;
        model3d.material.opacity = 0.2;
      });
      heatmapShowing = true;
    },
    hideHeatmap() {
      let floorGroupItems = Layers.floorLayer.children;
      floorGroupItems.forEach(it => {
        it.holder.hideHeatmap();
      });

      meshesAll.forEach(it => {
        if (!it.holder)
          return;
        let model3d = it.holder.getMesh();
        model3d.material.opacity = model3d.userData.bacOpacity;
      });
      heatmapShowing = false;
    },
    heatmapShowing() {
      return heatmapShowing;
    },
    setEnabeRotate(val) {
      orbitControls.enableRotate = val;
    },
    coordTool() {
      return coordTool;
    },
    mapScale() {//
      return coordTool.getScale();
    },
    currentCameraScale() {
      return orbitControls.currentZoom();
    },
    currentCameraPhi() {
      return orbitControls.currentPhi();
    },
    scene() {
      return scene;
    },
    dom() {
      return renderer.domElement;
    },
    getIntersects(x, y, objects) {
      let vector = new Vector3(( x / window.innerWidth ) * 2 - 1, -( y / window.innerHeight ) * 2 + 1, 0.5);
      vector = vector.unproject(camera);

      let raycaster = new Raycaster(camera.position, vector.sub(camera.position).normalize());

      return raycaster.intersectObjects(objects);
    },
    getIntersectsWith(vector, objects) {
      tmpVector.copy(vector);
      let raycaster = new Raycaster(camera.position, tmpVector.sub(camera.position).normalize());

      return raycaster.intersectObjects(objects);
    },
    getIntersectsOnPlane(x, y) {
      return that.getIntersects(x, y, [planeHelper]);
    },
    navigatorTool() {
      return navigatorTool;
    },
    toggleNavigatingSwitch(isNavigating) {
      navigatorTool.toggleNavigatingSwitch(isNavigating);
    },
    endNavigating() {
      navigatorTool.endNavigating();
    },
    getGpsLocIndicatorPos() {
      let indicatorPos = gpsLocIndicator.position;
      return indicatorPos;
    },
    initCoordBinding(bindingObj) {
      coordTool.initByJsonObj(bindingObj);
      Layers.applyRotationFromCoordBinding(coordTool.rotation());
    },
    addLight(lightType, position) {
      let light;
      let defaultColor = 0xffffff;
      let intensity = 1;
      if (lightType == Const.lightTypePoint) {
        light = new PointLight(defaultColor);
      } else if (lightType == Const.lightTypeDirection) {
        light = new DirectionalLight(defaultColor, intensity);
      } else if (lightType == Const.lightTypeSpot) {
        let distance = 0;
        let angle = Math.PI * 0.1;
        let penumbra = 0;
        light = new SpotLight( defaultColor, intensity, distance, angle, penumbra );
      } else if (lightType == Const.lightTypeHemiSphere) {
        let groundColor = 0x0;
        light = new HemisphereLight( defaultColor, groundColor, intensity );
      }
      light.position.copy(position);
      scene.add(light);
      return light;
    },
    initLightsByDefault() {
      if (lights.length == 0)
        initLights(scene);
    },
    deserializeLights(obj) {
      renderer.setClearColor(obj.clearColor, obj.clearAlpha);
      let tmp0 = new Vector3();
      for (let objL of obj.lights) {
        tmp0.fromArray(objL.pos);
        let light = that.addLight(objL.type, tmp0);
        light.intensity = objL.intensity;
        light.color.setHex(objL.color), light.visible = objL.visible;

        if (objL.groundColor !== undefined) light.groundColor.setHex(objL.groundColor);
        if (objL.distance !== undefined) light.distance = objL.distance;
        if (objL.angle !== undefined) light.angle = objL.angle;
      }
    },
    startResetingCamera() {
      let indicatorPos = that.getGpsLocIndicatorPos();
      if (!orbitControls.enabled) {
        return;
      }
      that.startMovingCamera(indicatorPos);
    },
    startMovingCamera(position) {
      orbitControls.startReseting(resetingFrames, position);
      resetingCamera = true;
      curFrame = 0;
      // resetRotating =
      resetMoving = true;
    },
    startResetCameraRotation() {
      if (!resetingCamera && camera.rotation.z !== 0) {
        resetRotating = resetingCamera = true;
        orbitControls.enabled = false;
        curFrame = 0;
        orbitControls.startResetRotation(resetingFrames);
      }
    },
    startResetCameraOffset() {
      let indicatorPos = that.getGpsLocIndicatorPos();
      let offset = orbitControls.target.clone().sub(indicatorPos);
      if (!resetingCamera && (offset.manhattanLength() > 0.1 ||
          orbitControls.currentZoom() !== 1)) {
        resetMoving = resetingCamera = true;
        orbitControls.enabled = false;
        curFrame = 0;
        orbitControls.startMoveCamera(resetingFrames, indicatorPos);
      }
    },
    startZoomCamera(zoomPercentage) {
      if (!orbitControls.enabled) {
        return;
      }
      orbitControls.startZoomCamera(zoomPercentage, resetingFrames);
      orbitControls.enabled = false;
      zooming = resetingCamera = true;
      curFrame = 0;
    },
    changePhiCamera(isTo2D) {
      orbitControls.enablePhiChange = !isTo2D;
      if (!orbitControls.enabled) {
        return;
      }
      orbitControls.startPhiCamera(isTo2D ? 0 : originPhi, resetingFrames);
      orbitControls.enabled = false;
      phiing = resetingCamera = true;
      curFrame = 0;
    },
    showRandomTrack(needReplay, positionsFromRemote) {
      if (pathTrack && pathTrack.playing() )
        return;
      if (needReplay && pathTrack) {
        pathTrack.reset();
      } else {
        let positions = positionsFromRemote;
        if (!positions) {
          positions = navigatorTool.getRandomTrack(false, 10, 0.18);
          if (positions.length == 0)
            return;
        }
        let box2;
        if (Layers.floorLayer.children.length > 0) {
          box2 = Layers.floorLayer.children[0].holder.originBox();
        }
        pathTrack = new PathTrack(Layers.editingLayer, positions, 40, box2);
        pathTrack.attach();
      }
    }
  };
  return that;
} ();

export default PublishedScene;
