import {Vector3, CurvePath, LineCurve3} from 'three'

export default function Path3( points ) {

    CurvePath.call( this );

    this.type = 'Path';

    this.currentPoint = new Vector3();

    if ( points ) {

        this.setFromPoints( points );

    }

}

Path3.prototype = Object.assign( Object.create( CurvePath.prototype ), {

    constructor: Path3,

    setFromPoints: function (points) {

        this.moveTo(points[0].x, points[0].y, points[0].z);

        for (var i = 1, l = points.length; i < l; i++) {

            this.lineTo(points[i].x, points[i].y, points[i].z);

        }

    },

    moveTo: function (x, y, z) {

        this.currentPoint.set(x, y, z); // TODO consider referencing vectors instead of copying?

    },

    lineTo: function (x, y, z) {

        var curve = new LineCurve3(this.currentPoint.clone(), new Vector3(x, y, z));
        this.curves.push(curve);

        this.currentPoint.set(x, y, z);

    }
});
