<template>
  <div class="el-collapse-item"
    :class="{'is-active': isActive, 'is-disabled': disabled }">
    <div
      role="tab"
      :aria-expanded="isActive"
      :aria-controls="`el-collapse-content-${id}`"
      :aria-describedby ="`el-collapse-content-${id}`"
    >
      <div
        class="el-collapse-item__header2"
        @click="handleHeaderClick"
        role="button"
        style="padding-left: 23px; font-size: 14px"
        :id="`el-collapse-head-${id}`"
        :tabindex="disabled ? undefined : 0"
        @keyup.space.enter.stop="handleEnterClick"
        :class="{
          'focusing': focusing,
          'is-active': isActive,
          'borderBottom': !isLast
        }"
        @focus="handleFocus"
        @blur="focusing = false"
      >
        <slot name="title">{{title}}</slot>
        <div
          class="el-collapse-item__arrow2"
          :class="{'is-active': isActive}">
        </div>
      </div>
    </div>
    <el-collapse-transition>
      <div
        class="el-collapse-item__wrap2"
        v-show="isActive"
        role="tabpanel"
        :aria-hidden="!isActive"
        :aria-labelledby="`el-collapse-head-${id}`"
        :id="`el-collapse-content-${id}`"
      >
        <div class="el-collapse-item__content2">
          <slot></slot>
        </div>
      </div>
    </el-collapse-transition>
  </div>
</template>
<script>
  import ElCollapseTransition from '../transitions/collapse-transition';
  // import ElCollapseTransition from 'element-ui/lib/transitions/collapse-transition';
  import Emitter from '../mixins/emitter';
  import { generateId } from '../utils/util';

  export default {
    name: 'ElCollapseItem',

    componentName: 'ElCollapseItem',

    mixins: [Emitter],

    components: { ElCollapseTransition },

    data() {
      return {
        contentWrapStyle: {
          height: 'auto',
          display: 'block'
        },
        contentHeight: 0,
        focusing: false,
        isClick: false,
        id: generateId()
      };
    },

    inject: ['collapse'],

    props: {
      title: String,
      name: {
        type: [String, Number],
        default() {
          return this._uid;
        }
      },
      isLast: Boolean,
      disabled: Boolean
    },

    computed: {
      isActive() {
        return this.collapse.activeNames.indexOf(this.name) > -1;
      },
      getContentDom() {
        return document.getElementById('el-collapse-content-' + this.id);
      }
    },

    methods: {
      handleFocus() {
        setTimeout(() => {
          if (!this.isClick) {
            this.focusing = true;
          } else {
            this.isClick = false;
          }
        }, 50);
      },
      handleHeaderClick() {
        if (this.disabled) return;
        this.dispatch('ElCollapse', 'item-click', this);
        this.focusing = false;
        this.isClick = true;
      },
      handleEnterClick() {
        this.dispatch('ElCollapse', 'item-click', this);
      }
    }
  };
</script>
<style scoped>
  .el-collapse-item__header2 {
    display: flex;
    align-items: center;
    height: 32px;
    line-height: 32px;
    background-color: #999;
    color: white;
    cursor: pointer;
    font-size: 13px;
    font-weight: 500;
    transition: border-bottom-color .3s;
    outline: 0;
    padding-left: 10px;
  }
  .borderBottom {
    border-bottom: 1px solid #ddd;
  }

  is-active {
    border-bottom-color: transparent;
  }

  .el-collapse-item__arrow2.is-active {
    transform: rotate(90deg);
  }

 .el-collapse-item__arrow2 {
   margin: 0 8px 0 auto;
   /*width: 40px;*/
   /*height: 40px;*/
   transition: .3s;

   border-left:solid 8px white;
   border-top:solid 6px rgba(0,0,0,0);
   border-bottom:solid 6px rgba(0,0,0,0);
 }

  .el-collapse-item__wrap2 {
    will-change: height;
    background-color: #FFF;
    overflow: hidden;
    box-sizing: border-box;
    /*border-bottom: 1px solid #ddd;*/
    transition: height 0.3s ease-in-out;
  }

  .el-collapse-item__content2 {
    padding-bottom: 10px;
    font-size: 13px;
    color: #303133;
    background-color: #fff;
    line-height: 1.769230769230769;
  }
</style>
