<template>
  <input class="color-simple jscolor" :class="{'jscolor {alphaEnabled: true}': colorAlphaEnabled}"
         ref="colorInput">
</template>

<script>
  import jsColor from "../../../external_lib/jscolor";

  export default {
    name: "SimpleColorInput",
    data() {
      return {
        event: {target: null}
      }
    },
    props: {
      colorAlphaEnabled: {
        type: Boolean,
        default: false
      }
    },

    mounted() {
      let el = this.$refs.colorInput;
      this.event.target = el;
      if (!this.$refs.colorInput._jscLinkedInstance)
        jsColor.jsc().jscolor(el, {});
      el.onFineChange = (jscolor) => {
        this.$emit('colorChange', this.event);
      };
      el.addEventListener('change', () => {
        this.$emit('colorChangeEnd', this.event);
      });
      if (this.colorEnabled || this.colorAlphaEnabled) {
        // if (!this.$refs.colorInput._jscLinkedInstance)

          // jsColor.jsc().tryInstallOnElements([this.$refs.colorInput], "color-simple")

          // this.$refs.colorInput._jscLinkedInstance = new jscolor(this.$refs.colorInput, {});
      }
    },
    methods: {
      updateColor(color) {
        if (this.colorEnabled || this.colorAlphaEnabled) {
          this.$refs.colorInput.value = color;
          // console.log('input value changed! ' + this.$refs.input.value);
          this.$refs.colorInput._jscLinkedInstance.importColor();
        }
      }
    }
  }
</script>

<style scoped>
.color-simple {
  border: none;
  outline: none;
}
</style>
