<template>
  <div class="boxLayers">
    <button style="margin-left: 10px" @click="onclickAddLayer"></button>
    <ul ref="layerContainer">
      <layer-item v-for="(item, index) in layers" :item="item" :key="item.id" @removeLayer="onRemoveLayer"/>
    </ul>
  </div>
</template>

<script>
  import LayerItem from "./LayerItem";
  import Layers from "../../../3d/js/Layers";
  import MainScene from '@/3d/js/MainScene';
  import Common from "../../../3d/js/util/Common";

  export default {
    name: "BoxLayers",
    mounted() {
      this.initLayerItems();
      // console.log('initLayerItems() in mounted BoxLayer.vue')
      MainScene.scene().addEventListener('reset ui', () => {
        this.initLayerItems();
      });
      MainScene.scene().addEventListener('add ref layer', () => {
        let hasRefLayer = this.layers.filter(it => it.id == "refer").length > 0;
        if (!hasRefLayer) {
          let layerObj = Layers.getLayerById("refer");
          this.layers.push( Common.convertLayerObj(layerObj) );
        }
      });

      // let insertIndicator = document.createElement('div');
      // insertIndicator.style.width = '300px';
      // insertIndicator.style.height = '1px';
      // insertIndicator.style.background = 'red';

      //
      function getMousePos(e){
        return {
          x : e.pageX || e.clientX + document.body.scrollLeft,
          y : e.pageY || e.clientY + document.body.scrollTop
        }
      }
      //
      function getElementPos(el){
        let parentObj;
        if (el.offsetParent) {
          parentObj = getElementPos(el.offsetParent)
        }
        return {
          x : el.offsetParent ? el.offsetLeft + parentObj['x'] : el.offsetLeft,
          y : el.offsetParent ? el.offsetTop + parentObj['y'] : el.offsetTop
        }
      }
      //
      function getElementSize(el){
        return {
          width : el.offsetWidth,
          height : el.offsetHeight
        }
      }

      let list = this.$refs.layerContainer.getElementsByTagName('li');
      let outer_wrap = this.$refs.layerContainer;
      let insertBeforeName = null, insertBeforeId = null;
      let insertIndicator;
      let _this = this;
      outer_wrap.onmousedown = function(event){
        //
        event = event || window.event;
        let draggingItem = event.target || event.srcElement;
        let index = 0;
        for(let i = 0; i < list.length; i++){
          list[i]['pos'] = getElementPos(list[i]);
          list[i]['size'] = getElementSize(list[i]);
          if (draggingItem === list[i]) {
            index = i;
          }
        }
        draggingItem.classList.add("dragging-item");
        insertIndicator = draggingItem;
        // outer_wrap.removeChild(draggingItem);

        if(draggingItem.tagName.toLowerCase() == 'li') {
          insertIndicator.style.opacity = '0.0';
          let p = getMousePos(event);
          let el = draggingItem.cloneNode(true);
          el.style.position = 'absolute';
          el.style.left = draggingItem.pos.x + 'px';
          el.style.top = draggingItem.pos.y + 'px';
          el.style.width = draggingItem.size.width + 'px';
          el.style.height = draggingItem.size.height + 'px';
          el.style.border = '1px solid #d4d4d4';
          el.style.background = '#4e76ee';
          el.style.opacity = '1.0';
          document.body.appendChild(el);

          document.onmousemove = function(event) {
            event = event || window.event;
            let current = getMousePos(event);
            el.style.left = draggingItem.pos.x + current.x - p.x + 'px';
            el.style.top = draggingItem.pos.y + current.y - p.y+ 'px';
            // document.body.style.cursor = 'move';

            //
            for(let i = 0; i < list.length; i++){
              if (draggingItem == list[i])
                continue;
              if (current.x > list[i].pos.x && current.x < list[i].pos.x + list[i].size.width) {
                if (current.y > list[i].pos.y && current.y < list[i].pos.y + list[i].size.height/2) {
                  outer_wrap.insertBefore(insertIndicator, list[i]);
                  if (list[i].id != draggingItem.id)
                    insertBeforeName = list[i].innerText, insertBeforeId = list[i].id;
                  // console.log("insertBefore: " + insertBeforeName);
                  _this.dragging = true;
                } else if (current.y > list[i].pos.y + list[i].size.height/2 && current.y < list[i].pos.y + list[i].size.height) {
                  outer_wrap.insertBefore(insertIndicator, list[i].nextSibling);
                  if (list[i].nextSibling == null)
                    insertBeforeName = null, insertBeforeId = null;
                  else {
                    if (list[i].nextSibling.id != draggingItem.id)
                      insertBeforeName = list[i].nextSibling.innerText, insertBeforeId = list[i].nextSibling.id;
                  }
                  // console.log("insertBefore sibling: " + insertBeforeName);
                  _this.dragging = true;
                }
              }
            }
          }
          //
          document.onmouseup = function(event){
            draggingItem.classList.remove("dragging-item");
            document.onmousemove = null;
            if (_this.dragging) {
              // outer_wrap.replaceChild(draggingItem, insertIndicator);
              _this.dragging = false;
              // console.log("insertBefore end: " + insertBeforeName);
              _this.reorderDragged(draggingItem.id, insertBeforeId);
            }
            if (insertIndicator)
              insertIndicator.style.opacity = '1.0';
            document.body.removeChild(el);
            el = null;
            // document.body.style.cursor = 'normal';
            document.onmouseup = null;
          }
        }
      }
    },
    data() {
      return {
        layers: [],
        dragging: false
      }
    },
    methods: {
      onclickAddLayer() {
        // console.log("");
        let layerObj = Layers.addLayer();
        this.layers.splice(1, 0, Common.convertLayerObj(layerObj) );
      },
      initLayerItems() {
        this.layers = [];
        let objLst = Layers.getSelectableLayers();
        objLst = objLst.sort((a, b) => b.group.renderOrder - a.group.renderOrder);
        for (let i = 0; i < objLst.length; i++) {
          let obj = Common.convertLayerObj(objLst[i]);
          if (obj) {
            this.layers.push(obj);
          }
        }
      },
      onRemoveLayer(event) {
        let layerItem = event.item;
        let index = this.layers.indexOf(layerItem);
        this.layers.splice(index, 1);
        Layers.removeLayer(layerItem);
      },
      reorderDragged(draggingId, insertBeforeId) {
        if (draggingId == insertBeforeId)
          return;
        let ind = -1;
        for (let i = 0; i < this.layers.length; i++) {
          let id = this.layers[i].id;
          if (id == draggingId) {
            ind = i;
            break;
          }
        }
        let movingItem = this.layers.splice(ind, 1) [0];
        if (insertBeforeId == null) {
          this.layers.push(movingItem);
        } else {
          for (let i = 0; i < this.layers.length; i++) {
            let id = this.layers[i].id;
            if (id == insertBeforeId) {
              ind = i;
              break;
            }
          }
          this.layers.splice(ind, 0, movingItem);
        }
        Layers.reorderLayers(this.layers);
      }
    },
    components: {
      LayerItem
    }
  }
</script>

<style scoped>
.boxLayers {
  background-color: white;
  padding-top: 10px;
}
  .dragging-item {
    background-color: #4b8bff;
    color: white;
  }
</style>
