export default /* glsl */`
varying vec2 vUv;
uniform mat3 uvTransform;

attribute vec3 core;
uniform float radius;

void main() {
    vUv = ( uvTransform * vec3( uv, 1 ) ).xy;
    vec3 pos = core + normal*radius;
    gl_Position = projectionMatrix * modelViewMatrix * vec4( pos, 1.0 );
}
    `;
