let undoTypes = function () {
  let addPolygons = "add polygons",
      removePolygons =  "remove polygons",
      addPaths = "add paths",
      removePaths = "remove paths",
      updateModelsTexts =  "update models names",
      unifyModelsTexts =  "unify models names",
      updateModelFillColors =  "update model fillcolors",
      unifyModelFillColors =  "unify model fillcolors",
      updateModelLineColors =  "update model linecolors",
      unifyModelLineColors =  "unify model linecolors",
      updateModelsHeight = "update models height",
      unifyModelsHeight = "unify models height",
      updateModelsTextHeight = "update models text height",
      unifyModelsTextHeight = "unify models text height",

      updateModelShape = "update model shape",
      updateModelShapeUndo = "update model shape undo",
      updateModelShapeSlightly = "update model shape slightly",
      updateModelShapeSlightlyUndo = "update model shape slightly undo",
      updateModelsPos = "update models position",
      updateModelsPosUndo = "update models position undo",
      updateModelScale = "update model scale",
      updateModelScaleUndo = "update model scale undo",
      updateModelRotate = "update model rotation",
      updateModelRotateUndo = "update model rotation undo",

      shortcutMove = "shortcut move",
      shortcutMoveUndo = "shortcut move undo",
      shortcutRotate = "shortcut rotate",
      shortcutRotateUndo = "shortcut rotate undo",

      mergeModels = "merge models",
      mergeModelsUndo = "merge models undo",
      sliceModel = "slice model",
      sliceModelUndo = "slice model undo",

      updatePathPoint = "update path point",
      updatePathPointUndo = "update path point undo",

      replacePathsPoint = "replace paths point",
      replacePathsPointUndo = "replace paths point undo",

      updatePathsType = "update paths type",
      unifyPathsType = "unify paths type",
      updatePathsDirection = "update paths direction",
      unifyPathsDirection = "unify paths direction";

  let lst0 = [addPolygons, removePolygons, addPaths, removePaths, updateModelShapeSlightly, updateModelShapeSlightlyUndo,
    updateModelsTexts, unifyModelsTexts, updateModelFillColors, unifyModelFillColors, shortcutMove, shortcutMoveUndo,
    shortcutRotate, shortcutRotateUndo,
    updateModelLineColors, unifyModelLineColors, updateModelsHeight, unifyModelsHeight,
    updateModelsTextHeight, unifyModelsTextHeight, updatePathsType, unifyPathsType, updatePathsDirection, unifyPathsDirection,
    updatePathPoint, updatePathPointUndo, replacePathsPoint, replacePathsPointUndo,
    updateModelShape, updateModelShapeUndo, updateModelsPos, updateModelsPosUndo, updateModelScale, updateModelScaleUndo,
    updateModelRotate, updateModelRotateUndo, mergeModels, mergeModelsUndo, sliceModel, sliceModelUndo];

  let lst1 = [removePolygons, addPolygons, removePaths, addPaths, updateModelShapeSlightlyUndo, updateModelShapeSlightly,
    unifyModelsTexts, updateModelsTexts, unifyModelFillColors, updateModelFillColors, shortcutMoveUndo, shortcutMove,
    shortcutRotateUndo, shortcutRotate,
    unifyModelLineColors, updateModelLineColors, unifyModelsHeight, updateModelsHeight,
    unifyModelsTextHeight, updateModelsTextHeight, unifyPathsType, updatePathsType, unifyPathsDirection, updatePathsDirection,
    updatePathPointUndo, updatePathPoint, replacePathsPointUndo, replacePathsPoint,
    updateModelShapeUndo, updateModelShape, updateModelsPosUndo, updateModelsPos, updateModelScaleUndo, updateModelScale,
    updateModelRotateUndo, updateModelRotate, mergeModelsUndo, mergeModels, sliceModelUndo, sliceModel];

  function strToCamelStr(str) {
    let arr0 = str.split(" ");
    let arr1 = [arr0[0] ];
    for (let i = 1; i < arr0.length; i++) {
      arr1[i] = arr0[i].charAt(0).toUpperCase() + arr0[i].substr(1);
    }
    return arr1.join("");
  }

  let scope = this;
  function init() {
    for (let i = 0; i < lst0.length; i++) {
      let str = lst0[i];
      let obj = {label: str, num: i};
      obj.undoNum = lst1.indexOf(str);
      scope[strToCamelStr(str)] = obj;
      scope[str.split(" ").join("_")] = i;
    }
  }
  init();

  this.getActionName = function(index) {
    return lst0[index];
  };
  this.getAction = function (actionName) {
    return scope[strToCamelStr(actionName)];
  };
  this.getUndoAction = function (actionName) {
    let index = lst0.indexOf(actionName);
    let undoActionName = lst1[index];
    return scope[strToCamelStr(undoActionName)];
  }
};
let UndoTypes = new undoTypes();
export default UndoTypes;
