import {Vector3} from 'three';
import Layers from "../Layers";
import Polygon3d from "../instance/Polygon3d";
import PublishedScene from "../PublishedScene";
import PathPoint from "../model/PathPoint";
import PathEdge from "../model/PathEdge";
import Config from "../configurable/Config";
import TextMark from "../instance/TextMark";

let Polygon3dReader = function () {
  let saveName = Config.code.defaultSaveForPreview;

  function getBindedPolygon3dItem(pos) {
    let meshLst = Layers.availableMeshes(true, true);
    for (let mesh of meshLst) {
      if (mesh.holder.contains(pos)) {
        return mesh.holder;
      }
    }
    return null;
  }

  function deserializeNavPathLayer(obj) {
    let previewPath = Layers.getPreviewPath();
    previewPath.pathPoints = [];
    previewPath.pathEdges = [];

    for (let pos of obj.rings) {
      let vector = new Vector3(pos.x, pos.y, 0);
      let point = new PathPoint(vector);
      previewPath.pathPoints.push(point);
      let polygon3d = getBindedPolygon3dItem(pos);
      if (polygon3d) {
        polygon3d.bindPathPoints.push(point);
        point.onFloor = false;
        // if (polygon3d.bindPathPoints.length > 1)
        //     console.log("items contains multi-points, " + polygon3d.name(), polygon3d.bindPathPoints.length);
      }
    }
    for (let segment of obj.segments) {
      let pathEdge = new PathEdge().initFromSerilization(segment, previewPath.pathPoints);
      previewPath.pathEdges.push(pathEdge);
    }
  }

  let that = {
    initializeScene(jsonStr) {
      Layers.clearLayer(Layers.navPathLayer);
      Layers.clearLayer(Layers.floorLayer);
      Layers.clearLayer(Layers.buildingsLayer);
      Layers.clearLayer(Layers.referLayer);
      Layers.clearLayer(Layers.markLayer);
      Layers.clearCustomLayers();
      let obj = JSON.parse(jsonStr);
      if (obj.layers == undefined) { //
        obj.layers = [{id: 'floor', hidden: false, locked: false, order: 1},
          {id: 'buildings', hidden: false, locked: false, order: 3}];
      }
      for (let layer of obj.layers) {
        if (!Layers.layers.hasOwnProperty(layer.id) ) {
          Layers.addLayer(layer);
        }

        let elementArr;
        if (obj.eles != undefined) {
          elementArr = obj.eles[layer.id];  //
        } else {
          elementArr = obj[layer.id];
        }
        if (elementArr == undefined)
          continue;
        for (let itemObj of elementArr) {
          let group = Layers.getLayerById(layer.id).group;
          if (itemObj.isMarker) {
            let marker = TextMark().deserialize(itemObj)
            marker.attach(group);
          } else {
            group.add(Polygon3d().deserialize(itemObj));
          }
        }
      }
      Layers.getSelectableLayers().forEach(obj => {
        obj.group.updateMatrix();
      });

      deserializeNavPathLayer(obj.navPaths);
      if (obj.coordBinding) {
        PublishedScene.initCoordBinding(obj.coordBinding);
      }
      if (obj.lightList !== undefined && obj.lightList.lights  && obj.lightList.lights.length > 0)  // && obj.lightList.lights.length > 0
        PublishedScene.deserializeLights(obj.lightList);
      else
        PublishedScene.initLightsByDefault();
    },
    loadFromLocal() {
      let saving = window.localStorage[saveName];
      if (saving == undefined) {
        console.log("no saving for " + saveName + " founded!");
        return;
      }
      that.initializeScene(saving);
    }
  };
  return that;
};

export default Polygon3dReader;
