<template>
  <div class="boxInTab2">
    <div class="posCoordDiv">
      <div class="posCoordRow">
        <el-input placeholder="1 x" class="posCoord" v-model="pos1x" @input="onInputPosition(0)" clearable/>
        <el-input placeholder="1 y" class="posCoord itemRight" v-model="pos1y" @input="onInputPosition(0)" clearable/>
      </div>
      <div class="posCoordRow">
        <el-input :placeholder="tipP1x" class="posCoord" v-model="pos1MecatorX" @input="onInputMercatorPosition(0)" clearable/>
        <el-input :placeholder="tipP1y" class="posCoord itemRight" v-model="pos1MecatorY" @input="onInputMercatorPosition(0)" clearable/>
      </div>
    </div>
    <div class="posCoordDiv">
      <div class="posCoordRow">
        <el-input placeholder="2 x" class="posCoord" v-model="pos2x" @input="onInputPosition(1)" clearable/>
        <el-input placeholder="2 y" class="posCoord itemRight" v-model="pos2y" @input="onInputPosition(1)" clearable/>
      </div>
      <div class="posCoordRow">
        <el-input :placeholder="tipP2x" class="posCoord" v-model="pos2MecatorX" @input="onInputMercatorPosition(1)" clearable/>
        <el-input :placeholder="tipP2y" class="posCoord itemRight" v-model="pos2MecatorY" @input="onInputMercatorPosition(1)" clearable/>
      </div>
    </div>
    <div style="border-bottom: solid 1px #888">
      <label><input type="checkbox" @click="toggleMouseInfo()"></label>
    </div>

    <div class="posCoordDiv" v-show="showMouseCoord">
      <div class="posCoordRow">
        <el-input placeholder=" x" class="posCoord" v-model="pos_x"  readonly/>
        <el-input placeholder=" y" class="posCoord itemRight" v-model="pos_y" readonly/>
      </div>
      <div class="posCoordRow">
        <el-input :placeholder="tipP_x" class="posCoord" v-model="posMecatorX" readonly/>
        <el-input :placeholder="tipP_y" class="posCoord itemRight" v-model="posMecatorY" readonly/>
      </div>
    </div>
  </div>
</template>

<script>
  import ElInput from '@/components/element/input';
  import MainScene from '@/3d/js/MainScene';
  import Config from "../../../3d/js/configurable/Config";

  export default {
    name: "BoxCoordBind",
    components: {
      ElInput
    },
    mounted() {
      if (Config.code.lngLatBind) {
        this.tipP1x = "1 ";
        this.tipP1y = "1 ";
        this.tipP2x = "2 ";
        this.tipP2y = "2 ";
      }
      MainScene.scene().addEventListener('coord change', onCoordChange);
      let _this = this;
      function onCoordChange(event) {
        if (event.index == 0) {
          _this.pos1x = event.x;
          _this.pos1y = event.y;
        } else if (event.index == 1) {
          _this.pos2x = event.x;
          _this.pos2y = event.y;
        }
      }
      MainScene.scene().addEventListener('mouse move', this.updateUI);
    },
    data() {
      return {
        pos1x: "0",
        pos1y: "3",
        pos1MecatorX: '',
        pos1MecatorY: '',
        pos2x: "3",
        pos2y: "0",
        pos2MecatorX: '',
        pos2MecatorY: '',

        pos_x: "0",
        pos_y: "0",
        posMecatorX: '',
        posMecatorY: '',

        tipP1x: '1 x',
        tipP1y: '1 y',
        tipP2x: '2 x',
        tipP2y: '2 y',
        tipP_x: '  x',
        tipP_y: '  y',
        showMouseCoord: false,
      }
    },
    methods: {
      updateUI(event) {
        let coordTool = MainScene.coordTool();
        let pos1 = coordTool.getCoord(0), pos2 = coordTool.getCoord(1);
        let posMecator1 = coordTool.getCoordBinds(0), posMecator2 = coordTool.getCoordBinds(1);
        this.pos1x = pos1.x, this.pos1y = pos1.y;
        this.pos2x = pos2.x, this.pos2y = pos2.y;
        this.pos1MecatorX = posMecator1.x, this.pos1MecatorY = posMecator1.y;
        this.pos2MecatorX = posMecator2.x, this.pos2MecatorY = posMecator2.y;

        this.pos_x = event.x, this.pos_y = event.y;
        this.posMecatorX = event.mx, this.posMecatorY = event.my;
      },
      getCoordXY(index) {
        let arr2 = [[this.pos1x, this.pos1y], [this.pos2x, this.pos2y]];
        return arr2[index];
      },
      getMecatorXY(index) {
        let arr2 = [[this.pos1MecatorX, this.pos1MecatorY], [this.pos2MecatorX, this.pos2MecatorY]];
        return arr2[index];
      },
      onInputPosition(index) {
        let coord = this.getCoordXY(index);
        MainScene.coordTool().setCoord(index, parseFloat(coord[0]) || 0,
            parseFloat(coord[1]) || 0)
      },
      onInputMercatorPosition(index) {
        let coord = this.getMecatorXY(index);
        MainScene.coordTool().setCoordBinds(index, parseFloat(coord[0]) || 0,
            parseFloat(coord[1]) || 0)
      },
      toggleMouseInfo() {
        this.showMouseCoord = !this.showMouseCoord;
      }
    }
  }
</script>

<style scoped>
  .boxInTab2 {
    padding: 5px;
    background-color: white;
  }
  .posCoord {
    flex: 1;
  }

  .posCoordDiv {
    border-radius: 5px;
    border: solid 1px #888;
    margin-top: 5px;
    display: flex;
    justify-content: space-between;
  }
  .posCoordRow {
    margin: 5px;
  }
  .itemRight {
    /*margin-left: 5px;*/
  }
</style>
