import {EventDispatcher} from "three";
import Action from "./Action";

function equalMeshes(meshes0, meshes1) {
  if (meshes0.length != meshes1.length) {
    return false;
  }
  for (let mesh of meshes0) {
    if (meshes1.indexOf(mesh) < 0) {
      return false;
    }
  }
  return true;
}

let history = function () {
  this.actionModified = false;
  this.stackLen = 100;
  this.stack = [];
  this.eventStackChange = {type: "stack change", undoLen: 0, redoLen: 0};
};

history.prototype = Object.assign( Object.create( EventDispatcher.prototype ), {
  constructor: history,
  dispatchStackChange() {
    this.eventStackChange.undoLen = this.stack.length;
    this.eventStackChange.redoLen = this.redoStack ? this.redoStack.length : 0;
    this.dispatchEvent(this.eventStackChange)
  },
  removeAll() {
    this.stack.splice(0, this.stack.length);
    if (this.redoStack) {
      this.redoStack.splice(0, this.redoStack.length);
    }
    this.actionModified = false;
    this.dispatchStackChange();
  },
  addAction(actionName, context) {
    let action = new Action(actionName, context);
    this.redoStack = [];
    this.stack.push(action);
    if (this.stack.length > this.stackLen) {
      this.stack.shift();
    }
    this.actionModified = true;
    this.dispatchStackChange();
  },
  addOrUpdateAction(actionName, context) {
    let latestAction;
    if (this.stack.length > 0) {
      latestAction = this.stack[this.stack.length - 1];
    }
    let updated = false;
    if (latestAction && latestAction.name == actionName ) {
      if (actionName == "shortcut move" && equalMeshes(latestAction.context().meshes, context.meshes) ) {
        latestAction.context().moveVector.add(context.moveVector);
        updated = true;
      } else if (actionName == "shortcut rotate" && latestAction.context().mesh == context.mesh) {
        latestAction.context().degree += context.degree;
        updated = true;
      }
    }
    if (!updated) this.addAction(actionName, context);
  },
  undoAction() {
    if (this.stack.length == 0) {
      return;
    }
    let action = this.stack.pop();
    this.redoStack.push(action);
    this.dispatchStackChange();

    action.undo();
  },

  redoAction() {
    if (this.redoStack.length == 0) {
      return;
    }
    let action = this.redoStack.pop();
    this.stack.push(action);
    this.dispatchStackChange();

    action.redo();
  }
});

let History = new history();
export default History
