import {Vector3} from 'three'
import {LineMaterial} from 'three/examples/jsm/lines/LineMaterial'
import {LineGeometry} from 'three/examples/jsm/lines/LineGeometry'
import {Line2} from 'three/examples/jsm/lines/Line2'
import {flattenToArr, getPositionMarker, getTrackPosMarker} from "../util/StaticMethods";
import Config from "../configurable/Config";

let PathTrack = function (parentObj3d, trackPositions, trackLen, box2) {
  let trackRemain = true;
  let progress = 0, length = trackPositions.length;
  if (box2) {
    trackPositions = trackPositions.filter(it => {
      return box2.contains(new Vector3(it.x, -it.z, 0))
    })
    length = trackPositions.length
  }
  let curLen = 0;
  let positions;  // = trackPositions.slice(progress, trackLen);
  let skipCount = -1;
  let lineMaterial = new LineMaterial( {
    color: "#bb0000",
    linewidth: 3, // in pixels
    transparent: true,
    dashed: false,
    depthWrite: false,
    depthTest: false
  } );
  lineMaterial.resolution.set( window.innerWidth, window.innerHeight );
  let lineGeo = new LineGeometry();
  let arr =[];
  if (trackRemain) {
    for (let i = 0; i < length; i++) {
      let pos = trackPositions[i];
      arr.push(pos.x, pos.y, pos.z);
    }
  } else {
    for (let i = 0; i < trackLen * 3; i++) {
      arr.push(0);
    }
  }
  lineGeo.setPositions( arr );

  let lineSegments = new Line2(lineGeo, lineMaterial);
  lineSegments.computeLineDistances();
  lineSegments.scale.set( 1, 1, 1 );
  // let marker = getStartEndIcon();
  // let marker = getPositionMarker("", null, "#000");
  let trackOwner = "";
  if (Config.remote.trackOwner) {
    trackOwner = Config.remote.trackOwner;
  }
  let marker = getTrackPosMarker("");//getTrackPosMarker(trackOwner)
  let playing = false;

  let that = {
    playing() {
      // return marker.parent != null;
      return playing;
    },
    attached() {
      return lineSegments.parent != null;
    },
    attach() {
      parentObj3d.add(lineSegments);
      parentObj3d.add(marker);
      marker.visible = false;
      playing = true;
    },
    detach() {
      parentObj3d.remove(lineSegments);
      parentObj3d.remove(marker);
    },
    lineSegments() {
      return lineSegments;
    },
    update() {
      skipCount++;
      if (skipCount % 5 !== 0)
        return;

      if (curLen < trackLen)
        curLen++;
      else
        progress++;
      positions = trackPositions.slice(progress, curLen + progress);
      while (positions.length < trackLen) {
        positions.push(positions[positions.length - 1]);
      }
      marker.position.copy(positions[positions.length - 1]);
      marker.visible = true;
      if (!trackRemain) {
        lineSegments.geometry.setPositions(flattenToArr(positions) );
        lineSegments.computeLineDistances();
      }
      if (that.reachEnd()) {
        // parentObj3d.remove(marker);
        playing = false;
      }
    },
    reset() {
      curLen = 0, progress = 0, marker.visible = false;
      that.attach();
    },
    reachEnd() {
      if (trackLen >= length)
        return true;
      return progress == length - trackLen;
    },
    ended() {
      return progress >= length - 1;
    }
  };
  return that;
};

export default PathTrack;
