<template>
  <input class="num" ref="numberInput" @input="emitChange">
</template>

<script>
  export default {
    name: "SimpleNumberInput",
    props: {
      value: [String, Number],
    },
    data() {
      return {
        min: - Infinity,
        max: Infinity,
        precision: 3,
        unit: "",
        step: 1,
        distance: 0,
        onMouseDownValue: 0,
        pointer: [0, 0],
        prevPointer: [0, 0],
        event: {target: null}
      }
    },
    mounted() {
      let dom = this.$refs.numberInput;
      this.event.target = dom;
      dom.value = this.value;
      dom.addEventListener( 'mousedown', (event) => {
        event.preventDefault();
        this.distance = 0;

        this.onMouseDownValue = parseFloat(this.$refs.numberInput.value);

        this.prevPointer = [ event.clientX, event.clientY ];

        document.addEventListener( 'mousemove', this.onMouseMove, false );
        document.addEventListener( 'mouseup', this.onMouseUp, false );
      }, false );
    },
    watch: {
      value(val) {
        this.setNativeInputValue();
      }
    },
    methods: {
      setNativeInputValue() {
        this.$refs.numberInput.value = this.value;
      },
      setValue( value ) {

        if ( value !== undefined ) {

          value = parseFloat( value );

          if ( value < this.min ) value = this.min;
          if ( value > this.max ) value = this.max;

          this.$refs.numberInput.value = value.toFixed( this.precision );

          if ( this.unit !== '' ) this.$refs.numberInput.value += ' ' + this.unit;
        }
      },
      emitChange() {
        this.$emit("change", this.event);
      },
      onMouseMove( event ) {
        let currentValue = this.$refs.numberInput.value;

        this.pointer = [ event.clientX, event.clientY ];

        this.distance += ( this.pointer[ 0 ] - this.prevPointer[ 0 ] ) - ( this.pointer[ 1 ] - this.prevPointer[ 1 ] );

        let value = this.onMouseDownValue + ( this.distance / ( event.shiftKey ? 5 : 50 ) ) * this.step;
        value = Math.min( this.max, Math.max( this.min, value ) );

        if ( currentValue !== value ) {
          this.setValue( value );
          this.$emit("change", this.event);
        }

        this.prevPointer = [ event.clientX, event.clientY ];

      },

      onMouseUp() {

        document.removeEventListener( 'mousemove', this.onMouseMove, false );
        document.removeEventListener( 'mouseup', this.onMouseUp, false );

        if ( Math.abs( this.distance ) < 2 ) {

          this.$refs.numberInput.focus();
          this.$refs.numberInput.select();

        }

      }
    }
  }
</script>

<style scoped>
  .num {
    font-size: 12px;
    border: 0px;
    padding: 2px;
    cursor: col-resize;
  }
</style>
