import {Vector3, Line3} from "three"
import * as Consts from "../consts/Constants"

let PathEdge = function () {
  this.from = null;// = new PathPoint();
  this.to = null;
  this.pathType = Consts.pathTypeBold;
  this.pathDirection = Consts.pathDirBoth;
  this.cost = 0;
  let line3;
  let tmp = new Vector3();

  this.setFromPathSegment = function (segment, rings, pathPoints) {
    let fromIndex = rings.indexOf(segment.fromCircle());
    let toIndex = rings.indexOf(segment.toCircle());
    this.from = pathPoints[fromIndex];
    this.to = pathPoints[toIndex];
    line3 = new Line3(this.from.pos, this.to.pos);
    this.cost = segment.getCost();
    this.pathType = segment.getPathType();
    this.pathDirection = segment.getPathDirection();
    return this;
  };

  this.initFromSerilization = function (obj, pathPoints) {
    this.from = pathPoints[obj.from];
    this.to = pathPoints[obj.to];
    line3 = new Line3(this.from.pos, this.to.pos);
    if (obj.cost == undefined) {
      this.cost = this.from.pos.distanceTo(this.to.pos);
    } else {
      this.cost = obj.cost;
    }
    this.pathType = obj.pathType;
    this.pathDirection = obj.pathDirection;
    return this;
  };

  this.getClosestPointOnEdge = function (point) {
    let t = line3.closestPointToPointParameter(point, false);
    if (t > 1 || t < 0) {
      return null;
    }
    line3.closestPointToPoint(point, false, tmp);
    return tmp;
  }
};

export default PathEdge;
