export default /* glsl */`
varying vec2 vUv;
uniform float opacity;

  void main() {
    vec2 coord = vUv * 11.0;
    float i = mod(coord.x - fract(coord.x), 2.0);
    float j = mod(coord.y - fract(coord.y), 2.0);
    float val = i*j + (1.0 - i)*(1.0 - j);

    vec3 color = vec3(1.0) * val;   
    // vec3 color = vec3(vUv.x, 0.3, vUv.y);

    gl_FragColor = vec4(color, opacity);
  }
    `
