<template>
  <div class="bili" v-show="showing">
    <div>{{biliNum}}m</div>
    <div class="segment" ref="biliSeg"></div>
  </div>
</template>

<script>
  import MainScene from '@/3d/js/MainScene';

  export default {
    name: "BiliText",
    mounted() {
      let width = MainScene.biliSegmentWidth();
      let height = parseInt(width/5);
      this.$refs.biliSeg.style.width = width + 'px';
      this.$refs.biliSeg.style.height = height + 'px';
      let _this = this;
      this.showing = MainScene.coordTool().getScale() > 0;
      MainScene.scene().addEventListener('reset ui', () => {
        _this.showing = MainScene.coordTool().getScale() > 0;
      })
      MainScene.cameraControl().addEventListener('change scale', (event) => {
        _this.biliNum = parseInt(MainScene.coordTool().getScale() * 2 * event.scaleValue);
      })
    },
    data() {
      return {
        biliNum: 100,
        showing: true
      }
    },
    methods: {
    }
  }
</script>

<style scoped>
.bili {
  color: black;
  position: absolute;
  bottom: 24px;
  left: 55px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
  .segment {
    border-bottom: 1px solid black;
    border-left: 1px solid black;
    border-right: 1px solid black;
  }
</style>
