export function getPathSegment(from, to, pathType) {
    let lineMaterial = new LineMaterial( {
        color: 0x165816,
        linewidth: 4, // in pixels
        dashed: false
    } );
    if (pathType == Consts.pathTypeMiddle) {
        lineMaterial.linewidth = 3;
        lineMaterial.color.setStyle('#419b4e');
    } else if (pathType == Consts.pathTypeThin) {
        lineMaterial.linewidth = 2;
        lineMaterial.color.setStyle('#a4f17f');
    } else if (pathType == Consts.pathTypeSelected) {
        lineMaterial.linewidth = 5;
        lineMaterial.color.setStyle('#ff0000');
    } else if (pathType == Consts.pathTypeFinded) {
        lineMaterial.linewidth = 6;
        lineMaterial.color.setStyle('#4dc168');
    } else if (pathType == Consts.pathTypeAdding) {
        lineMaterial.linewidth = 2;
        lineMaterial.color.setStyle('#519AF1');
    }
    lineMaterial.resolution.set( window.innerWidth, window.innerHeight );
    let lineGeo = new LineGeometry();
    lineGeo.setPositions( [from.x, from.y, from.z, to.x, to.y, to.z] );

    let lineSegment = new Line2(lineGeo, lineMaterial);
    lineSegment.computeLineDistances();
    lineSegment.scale.set( 1, 1, 1 );
    return lineSegment;
}

function fillIndices(start, end, indices) {
    for (let i = start; i < end; i++) {
        let next = i >= end - 1 ? start : i + 1;
        indices.push(i, next);
    }
}
export function createLines3d(param) {
    let verticesAll = param.vectices.slice();
    let indices = [];
    fillIndices(0, verticesAll.length, indices);
    for (let arr of param.holesVectices) {
        fillIndices(verticesAll.length, verticesAll.length + arr.length, indices);
        verticesAll.push(...arr);
    }
    let offset = verticesAll.length;
    let indices2 = indices.map(it => it + offset);
    indices = indices.concat(indices2);
    for (let i = 0; i < offset; i++) {
        indices.push(i, i + offset);
    }

    let verticesAll2 = verticesAll.map(it => {
        let vector = it.clone();
        vector.z += param.height;
        return vector;
    });
    verticesAll = verticesAll.concat(verticesAll2);

    let geometry = new BufferGeometry();
    geometry.setIndex( indices );
    geometry.setAttribute( 'position', new BufferAttribute( flatten(verticesAll), 3 ) );

    let lineSegments = new LineSegments(geometry, new LineBasicMaterial( { color: '#090', toneMapped: false } ));
    lineSegments.material.color.fromArray(param.meshColor);
    lineSegments.position.fromArray(param.position);
    return lineSegments;
}

//height, vectices, holesVectices, opacity, meshColor(arr), position(arr), scale(arr), quaternion(arr), lineColor
export function createModel3D(param) {
    let extrudeSettings = {
        depth: param.height,
        steps: 1,
        bevelEnabled: false
    };
    let shape = new Shape( param.vectices );
    for (let arr of param.holesVectices) {
        shape.holes.push(new Path(arr));
    }

    let geometry = new ExtrudeBufferGeometry( shape, extrudeSettings );

    let material;
    if (Config.ui.noLight) {
        material = new MeshBasicMaterial( { transparent: true, opacity: param.opacity} );
    } else {
        material = new MeshLambertMaterial( {transparent: true, opacity: param.opacity, side: FrontSide, flatShading: true } );
    }
    material.color.fromArray(param.meshColor);

    let model3d = new Mesh( geometry, material );
    model3d.position.fromArray(param.position);
    model3d.scale.fromArray(param.scale);
    model3d.quaternion.fromArray(param.quaternion);

    let outline = getPolygonLineMesh(param.vectices, null, true, new Color().fromArray(param.lineColor) );
    //create holes' outlines
    let holes = [outline];
    param.holesVectices.forEach(arr => {
        let holeOutline = getPolygonLineMesh(arr, null, true, new Color().fromArray(param.lineColor) );
        holes.push(holeOutline);
    });

    holes.forEach(it => {
        model3d.add(it);
        it.position.z = param.height;
        it.material.color.fromArray(param.lineColor)
    });
    return model3d;
}

export function updateUvAttribute(mesh) {
    let attr = mesh.geometry.attributes.position.clone();
    attr.applyMatrix4(mesh.matrixWorld);
    let vertices = bufferAttributeToVertices(attr);

    let originBox = new Box2();
    originBox.setFromPoints(vertices);
    let w = originBox.max.x - originBox.min.x;
    let h = originBox.max.y - originBox.min.y;
    let val = w > h ? w : h;
    let offset = {x:0, y:0};
    w > h ? offset.y = (w-h)/2 : offset.x = (h-w)/2;
    let vector2Lst = [];
    vertices.forEach(vector3 => {
        vector3.sub(originBox.min);
        let ux = (vector3.x + offset.x) / val, uy = (vector3.y + offset.y) / val;
        vector2Lst.push(new Vector2(ux, uy));
    });
    mesh.geometry.setAttribute("uv", new BufferAttribute(flatten(vector2Lst), 2));
}

function getRandHeatmapDataList(size, width, height, max) {
    let target = [];
    for (let i = 0; i < size; i++) {
        let x = Math.random() * width, y = Math.random() * height;
        let obj = {x: ~~x, y: ~~y, value: ~~(max * Math.random()) };
        target.push(obj);
    }
    return target;
}

function convertHeatmapData4Canvas(posDataLst, squarePos) {
    let dataLst = [], scale = 256.0 / squarePos.z;
    posDataLst.forEach(it => {
        dataLst.push({value: it.value, x: ~~((it.x - squarePos.x) * scale),
            //ycanvas
            //uv x180uv
            y: 512 - ~~((it.y - squarePos.y) * scale)});
    });
    return dataLst;
}

export function getHeatmapMaterial(material, posDataLst, squarePos, maxVal) {
    let heatmapDataLst = convertHeatmapData4Canvas(posDataLst, squarePos);
    if (material) {
        material.userData.heatmapInstance.setData({data:heatmapDataLst, max: maxVal});
        material.map.needsUpdate = true;
    } else {
        let div = document.createElement('div');
        let heatmapInstance = h337.create({
            container: div,
            width: 512, height: 512
        });
        heatmapInstance.setData({data:heatmapDataLst, max: maxVal});
        let canvas = div.children[0];
        let texture = new Texture(canvas);
        texture.needsUpdate = true;
        material = new MeshBasicMaterial({transparent: true, opacity: 1, map: texture, side:FrontSide});
        material.depthWrite = material.depthTest = false;
        material.userData.heatmapInstance = heatmapInstance;
    }
    return material;
}

export function getRandHeatmapMaterial() {
    // let canvas = document.createElement( 'canvas' );
    // canvas.width = 512;
    // canvas.height = 512;
    let div = document.createElement('div');
    let maxVal = 101;
    let dataList = getRandHeatmapDataList(100, 512, 512, maxVal);
    // let drawer = new HeatMapDraw(canvas, 47);
    // drawer.setData(dataList);

    let heatmapInstance = h337.create({
        container: div,
        width: 512, height: 512
    });
    heatmapInstance.setData({data:dataList, max: maxVal});
    let canvas = div.children[0];

    let texture = new Texture(canvas);
    texture.needsUpdate = true;
    let material = new MeshBasicMaterial({transparent: true, opacity: 1, map: texture, side:FrontSide});
    material.depthWrite = material.depthTest = false;
    return material;
}

export function createMesh4Heatmap(vectices, holesVectices, matrix4, height, materialProvided) {
    let arr2d = [];
    let allVertices = vectices.slice();
    let holeIndices = [];
    if (holesVectices.length > 0) {
        let cur = vectices.length;
        holesVectices.forEach(arr => {
            allVertices.push(...arr);
            holeIndices.push(cur);
            cur += arr.length;
        });
    }

    allVertices.forEach(it => {
        arr2d.push(it.x, it.y);
        it.z = height;
    });
    let triangles = Earcut.triangulate(arr2d, holeIndices);

    let geometry = new BufferGeometry();
    geometry.setAttribute("position", new BufferAttribute(flatten(allVertices), 3));
    geometry.attributes.position.applyMatrix4(matrix4);
    geometry.setIndex(triangles);
    let material = materialProvided;
    if (!material)
        material = getRandHeatmapMaterial();
    let mesh = new Mesh(geometry, material);
    updateUvAttribute(mesh);
    return mesh;
}

export function drawLocationBaseCircle(radius, lineWidth) {
    let canvas = document.createElement( 'canvas' );
    let ctx = canvas.getContext( '2d' );
    canvas.width = (radius+lineWidth)*2;
    canvas.height = (radius+lineWidth)*2; // fontsize * 1.5

    ctx.beginPath();
    ctx.fillStyle = "rgba(101,89,255,0.6)";
    ctx.strokeStyle = "#1d2b88";
    ctx.lineWidth = lineWidth;
    ctx.arc(canvas.width/2, canvas.height/2, radius, Math.PI*2, false);
    ctx.fill();
    ctx.stroke();
    return canvas;
}

export function drawLocationMarker(radius, lineWidth, color) {
    let canvas = document.createElement( 'canvas' );
    let ctx = canvas.getContext( '2d' );
    canvas.width = (radius + lineWidth) * 4;
    canvas.height = (radius + lineWidth) * 4; // fontsize * 1.5
    let hw = canvas.width / 2;
    let p0 = {x: radius+lineWidth*2, y: hw};
    let p1 = {x: 3*radius+lineWidth*2, y: hw}, p2 = {x: hw, y: lineWidth};
    ctx.beginPath();
    ctx.moveTo(p0.x, p0.y);
    ctx.lineTo(p1.x, p1.y);
    ctx.lineTo(p2.x, p2.y);
    ctx.closePath();
    ctx.fillStyle = color;
    ctx.fill();

    ctx.beginPath();
    ctx.fillStyle = color;
    ctx.strokeStyle = "#fff";
    ctx.lineWidth = lineWidth;
    ctx.arc(canvas.width/2, canvas.height/2, radius, Math.PI*2, false);
    ctx.fill();
    ctx.stroke();
    return canvas;
}

