function makeMobileCanvasSprite(canvas) {
    let sprite = makeCanvasSprite(canvas);
    sprite.material.depthWrite = false;
    sprite.material.depthTest = false;
    let runOnMobile = browser.runOnMobile();
    if (runOnMobile) {
        sprite.scale.set(canvas.width/(window.innerWidth*3), canvas.height/(window.innerWidth*3), 1.0);
    }
    return sprite;
}

export function makeOutlineSprite(canvas) {
    let texture = new Texture(canvas);
    texture.minFilter = LinearFilter;
    texture.needsUpdate = true;
    let uniforms = { 'opacity': {value: 1}, "center": {value: new Vector2(0.5, 0.5)},
        "map": {value:texture}, "outline_color": {value: new Color('#fff')}, "u_gamma": {value: 0.05}
        , "outline_width": {value: 0.55}, "fill_color": {value: new Color('#000')}  };

    let shaderMaterial = new ShaderMaterial( {
        uniforms: uniforms,
        vertexShader: sdf_vertex,
        fragmentShader: sdf_fragment,
        side: DoubleSide
    } );
    shaderMaterial.transparent = true;
    //  three/src/renderers/webgl/WebGLProgram.js shader
    //'#extension GL_OES_standard_derivatives : enable', fwidth 
    shaderMaterial.extensions.derivatives = true;
    shaderMaterial.map = texture;

    let sprite = new TextSprite(shaderMaterial, canvas.rectData);
    let scalar = canvas.rectData.size/window.innerHeight * 1.6;
    sprite.scale.set(scalar, scalar, 1.0);
    return sprite;
}
//canvassprite
export function makeCanvasSprite(canvas) {
    let texture = new Texture(canvas);
    texture.needsUpdate = true;
    let spriteMaterial = new SpriteMaterial({map : texture, sizeAttenuation: false});
    let sprite = new Sprite(spriteMaterial);
    sprite.center = new Vector2(0.5, 0.5);
    let poi = {w: canvas.width, h: canvas.height};
    if (browser.runOnMobile() ) {
        sprite.scale.set(poi.w/(window.innerWidth*2), poi.h/(window.innerWidth*2), 1.0);
    } else {
        sprite.scale.set(poi.w/window.innerWidth, poi.h/window.innerWidth, 1.0);
    }
    return sprite;
}

export function makeCanvasTextSprite(canvas) {
    let texture = new Texture(canvas);
    texture.minFilter = LinearFilter;
    texture.needsUpdate = true;
    let spriteMaterial = new SpriteMaterial({map : texture, sizeAttenuation: false});
    let sprite = new Sprite(spriteMaterial);
    sprite.center = new Vector2(0.5, 0.5);
    let poi = {w: canvas.width, h: canvas.height};
    sprite.scale.set(poi.w/(window.innerHeight*1.8), poi.h/(window.innerHeight*1.8), 1.0);
    sprite.material.depthWrite = false;
    sprite.material.depthTest = false;
    sprite.userData.poi = poi;
    return sprite;
}

