//  p0 ---------- p3
//  |             |
//  |             |
//  p1 ---------- p2
export function isRectangle(vertices) {
    if (vertices.length !== 4)
        return false;
    let p0 = vertices[0], p1 = vertices[1], p2 = vertices[2], p3 = vertices[3];
    if (p0.x == p1.x && p2.x == p3.x && p0.y == p3.y && p1.y == p2.y)
        return true;
    return false;
}
export function createGuideLine(vertices, isHorizontal, scalarCamera) {
    scalarCamera = scalarCamera || 1;
    let min = Infinity, max = -Infinity, minVector, maxVector;
    vertices.forEach(vector => {
        if (vector) {
            if (isHorizontal) {
                if (min > vector.x) {
                    min = vector.x, minVector = vector;
                }
                if (max < vector.x) {
                    max = vector.x, maxVector = vector;
                }
            } else {
                if (min > vector.y) {
                    min = vector.y, minVector = vector;
                }
                if (max < vector.y) {
                    max = vector.y, maxVector = vector;
                }
            }
        }
    });
    if (minVector == maxVector) {
        return null;
    }
    let span = minVector.distanceTo(maxVector), scalar = 1 + Config.code.extraSpan/span * scalarCamera;
    let minVector2 = maxVector.clone().lerp(minVector, scalar);
    let maxVector2 = minVector.clone().lerp(maxVector, scalar);
    let lGeometry = new BufferGeometry();
    let lineMaterial = new LineDashedMaterial( { color: '#ff28c8', dashSize: 0.7, gapSize: 0.1 } );
    lineMaterial.transparent = true;
    let typedArr = flatten([minVector2, maxVector2]);
    lGeometry.setAttribute("position", new BufferAttribute(typedArr, 3));
    let lineMesh= new Line(lGeometry, lineMaterial);
    lineMesh.computeLineDistances();
    lineMesh.name = "guide line";
    lineMesh.userData.guideVal = isHorizontal ? minVector.y : minVector.x;
    return lineMesh;
}

