<template>
  <div class="boxInTab">
    <span class="itemPropertyLabel"></span>
    <square-buttons-group v-model="activePath" @change="onClickAddPath">
      <square-button icon-class="iconxianduan3" value="0" v-tip.dark="tips[0]"></square-button>
      <square-button icon-class="iconxianduan2" value="1" v-tip.dark="tips[1]"></square-button>
      <square-button icon-class="iconxianduan1" value="2" v-tip.dark="tips[2]"></square-button>

    </square-buttons-group>
<!--    <label><input name="pathType" type="radio" value="" @click="onClickAddPath($event, 0)"> </label>-->
<!--    <label><input name="pathType" type="radio" value="" @click="onClickAddPath($event, 1)"> </label>-->
<!--    <label><input name="pathType" type="radio" value="" @click="onClickAddPath($event, 2)"> </label>-->
    <div>
      <input type="button" value="" @click="onclickPathFinding()">
      <input type="button" value="" @click="onclickPathTracking()">
    </div>
  </div>
</template>

<script>
  import SquareButtonsGroup from "@/components/common/SquareButtonsGroup";
  import SquareButton from "@/components/common/SquareButton";
  import MainScene from '@/3d/js/MainScene';
  import Main from "../../../3d/Main";

  export default {
    name: "BoxDrawPath",
    data() {
      return {
        activePath: '',
        tips: ["", "", ""]
      }
    },
    methods: {
      onClickAddPath() {
        MainScene.pathTool().startAddPath(~~this.activePath);
        let mousedown = (event) => {
          // console.log("event.button : " + event.button);
          if (event.button === 2) {
            MainScene.getDom().removeEventListener('mousedown', mousedown);
            this.activePath = '';
          }
        }
        MainScene.getDom().addEventListener('mousedown', mousedown);
      },
      onclickPathFinding() {
        MainScene.pathTool().startPathFinding();
      },
      onclickPathTracking() {
        MainScene.showRandomTrack();
      }
    },
    components: {
      SquareButtonsGroup, SquareButton
    }
  }
</script>

