import Vue from 'vue'
import Vuex from '../external_lib/vuex.esm.browser.min.js'
import * as M from "./mutations-types";

Vue.use(Vuex)

const store =  new Vuex.Store({
  state: {
    hasRefImage: false,
    editingFor3d: false,
    showCompassItem: false,
    showBottomBox: false,
    showBottomInfo: false,
    showGraphicAdd: true,
    isNavigating: false,
    lightsEditable: true,
  },
  mutations: {
    [M.Ref_Flag](state, flag) {
      state.hasRefImage = flag;
    },
    [M.Show_CompassItem](state, flag) {
      state.showCompassItem = flag;
    },
    [M.Show_BottomBox](state, flag) {
      state.showBottomBox = flag;
    },
    [M.Show_BottomInfo](state, flag) {
      state.showBottomInfo = flag;
    },
    [M.Show_GraphicAdd](state, flag) {
      state.showGraphicAdd = flag;
    },
    [M.Is_Navigating](state, flag) {
      state.isNavigating = flag
    },
    [M.Editing_For_3d](state, flag) {
      state.editingFor3d = flag
    },
    [M.Lights_Editable](state, flag) {
      state.lightsEditable = flag
    }
  },
  actions: {
  },
  modules: {
  }
});

export default store;
