import {Vector2, Vector3, PointLight, PointLightHelper, DirectionalLightHelper, MeshBasicMaterial, Mesh,
  SpotLightHelper, HemisphereLightHelper, SphereBufferGeometry, DirectionalLight, SpotLight, HemisphereLight} from 'three';
import {TransformControls} from "../lib_external/TransformControls";
import MainScene from "../MainScene";
import Layers from "../Layers";
import * as Const from "../consts/Constants";

let LightTool = function () {
  let eventLightSelected = {type: 'light selected', light:null};
  let eventLightMoved = {type: 'light moved', light:null};
  let eventLightRemoved = {type: 'light removed', id: null};
  let enabled = true;

  let lights = [], lightPickers = [], dom = MainScene.getDom();
  let transformControls = new TransformControls( MainScene.getCamera(), dom );
  let scene = MainScene.scene();
  let trackballControls = MainScene.cameraControl();
  scene.add( transformControls );
  transformControls.addEventListener( "change", () => {
    if (transformControls.object) {
      transformControls.object.userData.helper.update();
      eventLightMoved.light = transformControls.object;
      scene.dispatchEvent(eventLightMoved);
    }
  });

  let onDownPos = new Vector2(), onUpPos = new Vector2();
  function mousedown4PickLight(event) {
    trackballControls.enabled = !transformControls.dragging;
    if (!enabled)
      return;
    if (transformControls.dragging)
      return;
    let lst = MainScene.getIntersects(event.clientX, event.clientY, lightPickers);
    if (lst.length == 0) {
      onDownPos.set(event.clientX, event.clientY);
      return;
    }
    let lightPicker = lst[0].object;
    transformControls.attach(lightPicker.userData.object);
    eventLightSelected.light = transformControls.object;
    scene.dispatchEvent(eventLightSelected);
  }
  function mouseup4PickLight(event) {
    onUpPos.set(event.clientX, event.clientY);
    if (onDownPos.manhattanDistanceTo(onUpPos) == 0) {
      transformControls.detach();
      trackballControls.enabled = true;
      eventLightSelected.light = null;
      scene.dispatchEvent(eventLightSelected);
    }
  }
  function selectLight(light) {
    if (transformControls && light && light !== transformControls.object) {
      eventLightSelected.light = light;
      scene.dispatchEvent(eventLightSelected);
      transformControls.attach(light);
    }
  }

  let that = {
    toggle3dEditing(val) {
      trackballControls.lockFromZ = !val;
      let lst = Layers.availableMeshes(true, false);
      lst.forEach(it => it.holder.displayAs3D(val) );
      if (val) {
        MainScene.coordTool().hide();
        dom.addEventListener('mousedown', mousedown4PickLight);
        dom.addEventListener('mouseup', mouseup4PickLight);
        lights.forEach(light => {
          light.userData.helper.visible = true;
          light.userData.helper.update();
        });
        trackballControls.staticMoving = true;
      } else {
        dom.removeEventListener('mousedown', mousedown4PickLight);
        dom.removeEventListener('mouseup', mouseup4PickLight);
        transformControls.detach();
        eventLightSelected.light = null;
        scene.dispatchEvent(eventLightSelected);
        lights.forEach(light => light.userData.helper.visible = false);
        trackballControls.enabled = true;
        trackballControls.staticMoving = false;
        MainScene.resetCamera();
      }
    },
    visible(val) {
      enabled = val;
      if (!enabled)
        transformControls.detach();
      lights.forEach(light => light.userData.helper.visible = val);
    },
    allLights() {
      return lights;
    },
    selectedLight() {
      return transformControls.object;
    },
    selectLightBy(uuid) {
      let light = that.getLightById(uuid);
      selectLight(light);
    },
    getLightById(uuid) {
      for (let light of lights) {
        if (light.uuid === uuid)
          return light;
      }
      return null;
    },
    updateLight(lightId, updateObj) {
      let light = that.getLightById(lightId);
      if (light) {
        if (updateObj.position)
          light.position.copy(updateObj.position);
        if (updateObj.color){
          light.color.copy(updateObj.color);
        }
        if (updateObj.groundColor){
          light.groundColor.copy(updateObj.groundColor);
        }
        if (updateObj.intensity != null) {
          light.intensity = updateObj.intensity;
        }
        if (updateObj.distance != null) {
          light.distance = updateObj.distance;
        }
        if (updateObj.angle != null) {
          light.angle = updateObj.angle;
        }
        light.userData.helper.update();
      }
    },
    deleteLightBy(uuid) {
      let light = that.getLightById(uuid);
      if (light)
        that.deleteLight(light);
    },
    deleteLight(light) {
      if (transformControls.object === light) {
        transformControls.detach();
      }
      scene.remove(light);
      scene.remove(light.userData.helper);
      let index = lights.indexOf(light);
      lights.splice(index, 1);
      lightPickers.splice(index, 1);
      eventLightRemoved.id = light.uuid;
      scene.dispatchEvent(eventLightRemoved);
    },
    addLight(lightType, position, skipSelect) {
      let light, helper;
      let defaultColor = 0xffffff;
      let intensity = 1;
      if (lightType == Const.lightTypePoint) {
        light = new PointLight(defaultColor);
        helper = new PointLightHelper( light, 1 );
      } else if (lightType == Const.lightTypeDirection) {
        light = new DirectionalLight(defaultColor, intensity);
        light.position.set( 5, 10, 7.5 );
        helper = new DirectionalLightHelper( light, 1 );
      } else if (lightType == Const.lightTypeSpot) {
        let distance = 0;
        let angle = Math.PI * 0.1;
        let penumbra = 0;
        light = new SpotLight( defaultColor, intensity, distance, angle, penumbra );
        helper = new SpotLightHelper(light, 1);
      } else if (lightType == Const.lightTypeHemiSphere) {
        let groundColor = 0x0;
        light = new HemisphereLight( defaultColor, groundColor, intensity );
        light.position.set( 0, 10, 0 );
        helper = new HemisphereLightHelper(light, 1);
      }
      let geometry = new SphereBufferGeometry( 2, 4, 2 );
      let material = new MeshBasicMaterial( { color: 0xff0000, visible: false } );
      let picker = new Mesh( geometry, material );
      picker.name = 'picker';
      picker.userData.object = light;
      helper.add( picker );
      lightPickers.push(picker);

      light.userData.helper = helper;
      lights.push(light);
      scene.add(light);
      scene.add(helper);
      helper.visible = MainScene.isEditingIn3D();
      if (position) {
        light.position.copy(position);
        if (!skipSelect)
          helper.update();
      }

      if (!skipSelect)
        selectLight(light);
      return light;
    },
    deleteSelectedLight() {
      if (!transformControls.object)
        return;
      that.deleteLight(transformControls.object);
    },
    toggleLightVisible(lightId) {
      let light = that.getLightById(lightId);
      if (light) {
        let visible = !light.visible;
        light.userData.helper.visible = visible;
        light.visible = visible;
      }
    },
    deleteAllLights() {
      let lights0 = lights.slice();
      for (let light of lights0) {
        that.deleteLight(light);
      }
    },
    serializeLights() {
      let obj = {clearColor: MainScene.getRenderer().getClearColor().getHex(), clearAlpha: MainScene.getRenderer().getClearAlpha(), lights: []};
      for (let light of lights) {
        let lightType = Const.getLightType(light);
        let objL = {type: lightType, pos: light.position.toArray(), intensity: light.intensity, visible: light.visible };
        objL.color = light.color.getHex();
        if ( light.groundColor !== undefined ) objL.groundColor = light.groundColor.getHex();

        if ( light.distance !== undefined ) objL.distance = light.distance;
        if ( light.angle !== undefined ) objL.angle = light.angle;
        obj.lights.push(objL);
      }
      return obj;
    },
    deserializeLights(obj) {
      this.deleteAllLights();
      MainScene.setClearColor(obj.clearColor, obj.clearAlpha);
      let tmp = new Vector3();
      for (let objL of obj.lights) {
        tmp.fromArray(objL.pos);
        let light = that.addLight(objL.type, tmp, true);
        light.intensity = objL.intensity;
        light.color.setHex(objL.color), light.visible = objL.visible;

        if (objL.groundColor !== undefined) light.groundColor.setHex(objL.groundColor);
        if (objL.distance !== undefined) light.distance = objL.distance;
        if (objL.angle !== undefined) light.angle = objL.angle;
        light.userData.helper.update();
      }
    }
  };
  return that;
};

export default LightTool;
