import {
  Group, BufferGeometry, BufferAttribute, Mesh, Vector3, Line3, FrontSide,
  MeshBasicMaterial, LineBasicMaterial, MeshNormalMaterial, MeshLambertMaterial, MeshPhongMaterial
} from 'three'

import Earcut from '../lib_external/Earcut'
import * as Consts from "../consts/Constants"
import Box2 from "../lib/Box2";
import Config from "../configurable/Config";
import MainScene from "../MainScene"
import Layers from "../Layers";

import {flatten, bufferAttributeToVertices, getPolygonLineMesh, getDashedOutLine, createLines3d,
  getPolygonCtrlPoint, vectorsToObjXY, makeCanvasTextSprite, generateTextCanvas, createModel3D} from "../util/StaticMethods"

let PolygonMesh = function (vectorLst, holeNums) {
  //vectices holesVectices 
  //
  let vectices = [];
  let holeIndices = [], holesVectices = [], holes = [];

  if (vectorLst != null && vectorLst.length > 2) {
    init(vectorLst, holeNums);
  }

  let fixedPointColor = 0x3652A9;
  const ctrlPointHeight = 0;  //0.00001;
  const topOrder = 99999;

  let ctrlPoints = [];    //
  const edgeAdhere = Config.code.adhereEdgeDistance;
  const adhereDistance = Config.code.adhereRadiusForEditing;
  let edgeAdhereDistanceSq = edgeAdhere * edgeAdhere;
  let adhereDistanceSq = adhereDistance * adhereDistance;
  let holderGroup = new Group();
  let mesh, outline, mesh3d, mesh3dMat = Config.ui.noLight ? "MeshBasicMaterial" : "MeshLambertMaterial";
  let lineSegments3d;
  let mode = 'global';
  let name = "" + Consts.getPolygonCount();

  let zHeight = Layers.getDefaultHeight(), txtHeight = (zHeight + Config.ui.txtHeightExtra).toFixed(2)*1;
  let originBox = Box2(), expandedBox = Box2();
  let textSprite;
  let instanceRectScale;
  function initInstanceRectScale(vertices, holeNums) {
    if (vertices.length != 4 || holeNums.length > 1)
      return;
    let p0 = vertices[0], p1 = vertices[1], p2 = vertices[2], p3 = vertices[3];
    if (p0.x == p1.x && p2.x == p3.x && p0.y == p3.y && p1.y == p2.y) {
      let rectWidth = Math.abs(p3.x - p0.x), rectHeight = Math.abs(p1.y - p0.y);
      instanceRectScale = new Vector3(rectWidth, rectHeight, 1);
    }
  }

  function setTextSprite() {
    let textParent;
    if (textSprite) {
      textParent = textSprite.parent;
      if (textParent) {
        textParent.remove(textSprite);
      }
    }
    if (name && name.trim().length > 0) {
      let canvas = generateTextCanvas(name);
      textSprite = makeCanvasTextSprite(canvas);
      (textParent || holderGroup).add(textSprite);
      textSprite.position.copy(mesh.position);
      textSprite.renderOrder = topOrder;
      textSprite.visible = Config.ui.modelTextVisible;
    }
  }

  function init(vecLst, holeNums) {
    if (holeNums != null && holeNums.length > 0) {
      holeIndices = holeNums.slice();
    } else {
      vectices = vecLst.slice();
    }
    if (holeIndices.length > 0) {
      for (let i = 0; i < holeIndices.length; i++) {
        if (i == 0) {
          vectices = vecLst.slice(0, holeIndices[i]);
        } else {
          let arr = vecLst.slice(holeIndices[i - 1], holeIndices[i]);
          holesVectices.push(arr);
        }
      }
      holesVectices.push(vecLst.slice(holeIndices[holeIndices.length - 1], vecLst.length));
    }
  }

  createMeshAndOutlines(true);
  updateBox2();

  function createMeshAndOutlines(addItemsToMesh, meshParent) {
    if (vectices.length == 0) {
      return;
    }
    // console.log("createMeshAndOutlines called " );
    let arr2d = [];
    let allVertices = vectices.slice();
    holeIndices = [];
    if (holesVectices.length > 0) {
      let cur = vectices.length;
      holesVectices.forEach(arr => {
        allVertices.push(...arr);
        holeIndices.push(cur);
        cur += arr.length;
      });
    }
    originBox.setFromPoints(vectices);
    let centroid;
    if (addItemsToMesh) {
      centroid = getPosition();
      allVertices.forEach(it => it.sub(centroid));
    }
    allVertices.forEach(it => arr2d.push(it.x, it.y));
    let triangles = Earcut.triangulate(arr2d, holeIndices);

    let geometry = new BufferGeometry();
    geometry.setAttribute("position", new BufferAttribute(flatten(allVertices), 3));
    geometry.setIndex(triangles);
    let arrStyle = Layers.getDefaultFillColorArr();
    let lineColorStyle = Layers.getDefaultLineColorArr();
    let material = new MeshBasicMaterial({
      color: arrStyle[0],
      transparent: true,
      opacity: arrStyle[1],
      wireframe: false
    });
    mesh = new Mesh(geometry, material);
    mesh.userData.bacOpacity = material.opacity;
    // let shareGeometry = (holesVectices.length == 0 && isRectangle(vectices)) ? geometry : null;
    outline = getPolygonLineMesh(vectices, null, true, lineColorStyle[0]);
    outline.renderOrder = topOrder - 1;
    //create holes' outlines
    holes = [];
    holesVectices.forEach(arr => {
      let holeOutline = getPolygonLineMesh(arr, null, true, lineColorStyle[0]);
      holeOutline.renderOrder = topOrder - 2;
      holes.push(holeOutline);
    });
    if (addItemsToMesh) {
      mesh.add(outline);
      holes.forEach(hole => mesh.add(hole));
      mesh.position.set(centroid.x, centroid.y, 0);
      mesh.updateMatrixWorld();
      holderGroup.add(mesh);
      setTextSprite();
      mesh.renderOrder = 0;
      mode = 'local';
    } else {
      meshParent.add(mesh);
      meshParent.add(outline);
      holes.forEach(hole => meshParent.add(hole));
      mode = 'global';
    }
    ctrlPoints = [];
    let arr2 = [vectices].concat(holesVectices);
    arr2.forEach(arr => {
      let ctrl = [];
      arr.forEach((it, index) => {
        let ptObj = getPolygonCtrlPoint(index, it, fixedPointColor);
        ptObj.renderOrder = topOrder - 3;
        ptObj.position.z = ctrlPointHeight;
        ctrl.push(ptObj);
        if (!addItemsToMesh) meshParent.add(ptObj);
      });
      ctrlPoints.push(ctrl);
    });
    updateVertices();
  }

  function updateVertices() {
    [outline].concat(holes).forEach((it, index) => {
      let attr = it.geometry.attributes.position.clone();
      attr.applyMatrix4(mesh.matrixWorld)
      let arr = bufferAttributeToVertices(attr);
      if (index == 0) {
        vectices = arr;
      } else {
        holesVectices[index - 1] = arr;
      }
    });
  }

  function getArr2() {
    let arr2 = [];
    [outline].concat(holes).forEach(it => {
      let attr = it.geometry.attributes.position.clone();
      attr.applyMatrix4(mesh.matrixWorld)
      let arr = bufferAttributeToVertices(attr);
      arr2.push(arr);
    });
    return arr2;
  }

  function localToGlobal(scene) {
    if (mode == 'global') {
      return;
    }
    if (textSprite) textSprite.visible = false;
    //update vectices and holesVectices
    holesVectices = [];
    [outline].concat(holes).forEach((it, index) => {
      scene.add(it);
      let attr = it.geometry.attributes.position;
      attr.applyMatrix4(mesh.matrixWorld);
      attr.needsUpdate = true;
      let vLst = bufferAttributeToVertices(attr);
      ctrlPoints[index].forEach((it, index) => {
        scene.add(it);
        it.position.set(vLst[index].x, vLst[index].y, ctrlPointHeight);
      });
      if (index == 0) {
        vectices = vLst;
      } else {
        holesVectices.push(vLst);
      }
    });
    let attrMesh = mesh.geometry.attributes.position;
    attrMesh.applyMatrix4(mesh.matrixWorld);
    attrMesh.needsUpdate = true;
    mesh.position.set(0, 0, 0);
    mesh.quaternion.set(0, 0, 0, 1);
    mesh.scale.set(1, 1, 1);
    mesh.updateMatrixWorld();
    mesh.geometry.computeBoundingBox();
    mesh.geometry.computeBoundingSphere();
    mode = 'global';
  }

  function globalToLocal() {
    if (mode == 'local') {
      return;
    }
    if (textSprite) textSprite.visible = true;
    let center = getPosition(), meshPos = mesh.position.clone();
    let attr = mesh.geometry.attributes.position;
    let vLst = bufferAttributeToVertices(attr);
    vLst.forEach(it => it.sub(center));
    attr.copyArray(flatten(vLst));
    attr.needsUpdate = true;
    mesh.position.copy(center).add(meshPos);
    if (mesh.position.length() == 0) {
      console.log("globalToLocal error happen");
    }
    mesh.geometry.computeBoundingBox();
    mesh.geometry.computeBoundingSphere();

    [outline].concat(holes).forEach((it, index) => {
      it.position.set(0, 0, 0);
      let lineAttr = it.geometry.attributes.position;
      let lst = bufferAttributeToVertices(lineAttr);
      lst.forEach(it => it.sub(center));
      lineAttr.copyArray(flatten(lst));
      lineAttr.needsUpdate = true;
      mesh.add(it);
      ctrlPoints[index].forEach(pt => {
        pt.position.sub(center).sub(meshPos);
        mesh.add(pt);
      });
    });
    mode = 'local';
  }

  function updateBox2() {
    if (!mesh) {
      return;
    }
    let lst = [];
    mesh.updateMatrixWorld();
    ctrlPoints[0].forEach(it => {
      if (mode == 'local') {
        let pt = it.position.clone().applyMatrix4(mesh.matrixWorld);
        lst.push(pt);
      } else {
        lst.push(it.position.clone());
      }
    });
    originBox.setFromPoints(lst);
    expandedBox = originBox.expandBy(edgeAdhere);
  }

  function renewObjects(holder) {
    that.unselect();
    outline.parent.remove(outline);
    mesh.parent.remove(mesh);
    holes.forEach(hole => hole.parent.remove(hole));

    let tmp = {meshColor: mesh.material.color, meshOpacity: mesh.material.opacity, lineColor: outline.material.color};
    createMeshAndOutlines(false, holderGroup);
    updateBox2();
    mesh.holder = holder;
    mesh.material.color.copy(tmp.meshColor);
    mesh.material.opacity = tmp.meshOpacity;
    outline.material.color.copy(tmp.lineColor);
    holderGroup.holder = holder;
  }

  function getPosition() {
    if (outline == null) {
      let sum = new Vector3();
      vectices.forEach(it => sum.add(it));
      return sum.divideScalar(vectices.length);
    }
    if (mode === 'local') {
      return mesh.position;
    }
    let target = new Vector3();
    let vLst = bufferAttributeToVertices(outline.geometry.attributes.position);
    vLst.forEach(it => target.add(it));
    target.divideScalar(vLst.length);
    return target.add(outline.position);
  }

  function getIndexOfMesh(position, meshItem) {
    let vLst = bufferAttributeToVertices(meshItem.geometry.attributes.position);
    vLst.forEach(it => it.z = 0);
    let pos = position.clone();
    pos.z = 0;
    for(let i = 0; i < vLst.length; i++) {
      if (vLst[i].manhattanDistanceTo(pos) < 0.0001) {
        return i;
      }
    }
    return -1;
  }

  function updateCurPointIndex(pObj) {
    indexOnMesh = getIndexOfMesh(pObj.position, mesh);
    for (let i = 0; i < ctrlPoints.length; i++) {
      let arr = ctrlPoints[i];
      let index = getPointIndexInCircle(arr, pObj);
      if (index >= 0) {
        indexOfGroup = i, indexInGroup = index;
        break;
      }
    }
  }

  function getPointIndexInCircle(arr, pObj) {
    let index = arr.indexOf(pObj);
    if (index >= 0) {
      return index;
    }
    for (let i = 0; i < arr.length; i++) {
      if (arr[i].position.manhattanDistanceTo(pObj.position) < 0.0001) {
        return i;
      }
    }
    return -1;
  }

  function getCtrlPointObj(pos) {
    for (let i = 0; i < ctrlPoints.length; i++) {
      let arr = ctrlPoints[i];
      for (let j = 0; j < arr.length; j++) {
        if (arr[j].position.equals(pos) ) {
          return arr[j];
        }
      }
    }
    console.log("getCtrlPointObj unexpected input!");
    return ctrlPoints[0][0];
  }

  function getMaterialByType(name) {
    let mat = mesh.material;
    if (name == "MeshNormalMaterial") {
      return new MeshNormalMaterial({ transparent: true, side: FrontSide, opacity: mat.opacity});
    } else if (name == "MeshLambertMaterial") {
      return new MeshLambertMaterial({ transparent: true, side: FrontSide, color: mat.color, opacity: mat.opacity});
    } else if (name == "MeshPhongMaterial") {
      return new MeshPhongMaterial({ transparent: true, color: mat.color, emissive: 0x072534, side: FrontSide, flatShading: true});
    }
    return new MeshBasicMaterial({ transparent: true, side: FrontSide, color: mat.color, opacity: mat.opacity});
  }

  let indexOnMesh, indexOfGroup, indexInGroup;
  let oldPos = new Vector3();
  let that = {
    getOldPos() {
      return oldPos;
    },
    getHeight() {
      return zHeight;
    },
    setHeight(value) {
      zHeight = value;
    },
    setTextHeight(height) {
      txtHeight = height.toFixed(2) * 1;
    },
    getTextHeight() {
      return txtHeight;
    },
    getName() {
      return name;
    },
    setName(nameStr) {
      if (nameStr != null && nameStr != name) {
        name = nameStr;
        setTextSprite();
      }
    },
    getMesh() {
      return mesh;
    },
    getHolderGroup() {
      holderGroup.holder = that;
      return holderGroup;
    },
    getOutlines() {
      return [outline].concat(holes);
    },
    updateScaleFactor(scalar) {
      edgeAdhereDistanceSq = edgeAdhere * edgeAdhere * scalar * scalar;
      adhereDistanceSq = adhereDistance * adhereDistance * scalar * scalar;
      expandedBox = originBox.expandBy(edgeAdhere * scalar);
    },
    select() {
      if (ctrlPoints.length > 0) {
        ctrlPoints.forEach(arr => {
          arr.forEach(it => mesh.add(it));
        });
      }
    },
    unselect() {
      if (ctrlPoints.length > 0) {
        ctrlPoints.forEach(arr => {
          arr.forEach(it => {
            if (it.parent) it.parent.remove(it);
          });
        });
      }
    },
    remove() {
      that.unselect();
      if (holderGroup.parent) {
        holderGroup.parent.remove(holderGroup);
      }
    },
    move(vector) {
      if (mode === 'local') {
        let pos = oldPos.clone().add(vector);
        mesh.position.set(pos.x, pos.y, mesh.position.z);
        if (textSprite) textSprite.position.copy(mesh.position);
        updateBox2();
      }
    },
    getPosition: getPosition,
    updatePosition() {
      if (mode === 'local') {
        oldPos.copy(mesh.position);
      }
    },
    getArr4Merge() {
      let indices = [], allVertices = [];
      let count = 0;
      [outline].concat(holes).forEach(it => {
        let attr = it.geometry.attributes.position.clone();
        attr.applyMatrix4(mesh.matrixWorld);
        let arr = bufferAttributeToVertices(attr);
        allVertices.push(...arr);
        count += arr.length;
        indices.push(count);
      });
      indices.pop();
      return [vectorsToObjXY(allVertices), indices];
    },
    startEditCtrlPoint(scene) {
      localToGlobal(scene);
    },
    endEditCtrlPoint() {
      globalToLocal();
      oldPos.copy(mesh.position);
    },
    updateCurPointIndex: updateCurPointIndex,
    moveCtrlPoint(pObj) {
      let pos = pObj.position;
      let pp = [vectices].concat(holesVectices)[indexOfGroup][indexInGroup];
      pp.set(pos.x, pos.y, pp.z);
      //update shape of outline or hole
      let line = [outline].concat(holes)[indexOfGroup];
      let lineAttr = line.geometry.attributes.position;
      lineAttr.setXY(indexInGroup, pos.x, pos.y);
      lineAttr.needsUpdate = true;

      //update shape of mesh
      // let indexAttr = mesh.geometry.getIndex();
      let attr = mesh.geometry.attributes.position;
      if (mesh.position.lengthSq() != 0) {
        let arr1 = bufferAttributeToVertices(attr);
        arr1.forEach(it => it.add(mesh.position));
        mesh.position.set(0, 0, 0);
        attr = new BufferAttribute(flatten(arr1), 3);
      }
      attr.setXY(indexOnMesh, pos.x, pos.y);
      if (attr !== mesh.geometry.attributes.position) {
        mesh.geometry.attributes.position.copy(attr);
        attr = mesh.geometry.attributes.position;
      }
      attr.needsUpdate = true;
      let pts = bufferAttributeToVertices(attr);
      let arr2d = [];
      pts.forEach(it => arr2d.push(it.x, it.y));
      let triangles = Earcut.triangulate(arr2d, holeIndices);
      mesh.geometry.setIndex(triangles);
      mesh.geometry.index.needsUpdate = true;
      updateBox2();
    },
    removeCtrlPoint(adhereObj) {
      let pObj = adhereObj.ctrlPoint;
      updateCurPointIndex(pObj);
      if (indexOfGroup == 0 && vectices.length < 4) {
        return;
      }
      let arr = [vectices].concat(holesVectices)[indexOfGroup];
      arr.splice(indexInGroup, 1);
      if (indexOfGroup > 0 && arr.length < 3) {   //
        holesVectices.splice(indexOfGroup - 1, 1);
      }
      renewObjects(that);
      adhereObj.posOnEdge = adhereObj.ctrlPoint.position;
    },
    addCtrlPoint(adhereObj) {
      let pos = adhereObj.posOnEdge, index = adhereObj.index, inGroup = adhereObj.inGroup;
      if (pos == null) {
        return;
      }
      let arr = [vectices].concat(holesVectices)[inGroup];
      if (index == arr.length - 1) {
        arr.unshift(pos);
      } else {
        arr.splice(index + 1, 0, pos);
      }
      renewObjects(that);
      adhereObj.ctrlPoint = getCtrlPointObj(pos);
    },
    getBox2() {
      return originBox;
    },
    getPointOnEdges(point, onlyVertices) {
      let target = {ctrlPoint: null, posOnEdge: null, index: -1, inGroup: -1};
      if (!expandedBox.contains(point)) {
        return target;
      }
      let arr2 = getArr2();
      let tmp = new Vector3(), lines2 = [];
      for (let i = 0; i < arr2.length; i++) {
        let arr = arr2[i], lines = [];
        for (let j = 0; j < arr.length; j++) {
          let pos = arr[j], nextPos, nextPtObj;
          let distanceSq = pos.distanceToSquared(point);
          if (distanceSq <= adhereDistanceSq) {
            // console.log("getPointOnEdges, point distance less than: " + adhereDistance);
            target.ctrlPoint = ctrlPoints[i][j];  //getPolygonCtrlPoint
            target.ctrlPos = pos;   //Vector3 
            target.index = j - 1;
            target.inGroup = i;
            target.distanceSq = distanceSq;
            return target;
          }
          if (onlyVertices) {
            continue;
          }
          if (j == arr.length - 1) {
            nextPos = arr[0];
            nextPtObj = ctrlPoints[i][0];
          } else {
            nextPos = arr[j + 1];
            nextPtObj = ctrlPoints[i][j+1];
          }
          let line = new Line3(pos, nextPos);
          line.startPt = ctrlPoints[i][j], line.endPt = nextPtObj;
          lines.push(line);
        }
        lines2.push(lines);
      }
      if (onlyVertices) {
        return target;
      }
      for (let i = 0; i < lines2.length; i++) {
        for (let j = 0; j < lines2[i].length; j++) {
          let line = lines2[i][j];
          let t = line.closestPointToPointParameter(point, false);
          if (t > 1 || t < 0) {
            continue;
          }
          line.closestPointToPoint(point, false, tmp);
          let distanceSq = tmp.distanceToSquared(point);
          if (distanceSq <= edgeAdhereDistanceSq) {
            target.posOnEdge = tmp;
            target.index = j;
            target.inGroup = i;
            target.edge = [line.startPt, line.endPt];
            return target;
          }
        }
      }
      return target;
    },
    getGuideLines4Editing(vector, excludes) {
      let span = Config.code.smartGuideSpan * MainScene.currentCameraScale();
      let target = [{line: null, distance: 0}, {line: null, distance: 0}];
      let minH = Infinity, minV = Infinity;
      let hPos, vPos;
      for (let arr of ctrlPoints) {
        for (let ptObj of arr) {
          if (excludes.indexOf(ptObj) >= 0) {
            continue;
          }
          let pos = ptObj.position;
          if (mode === 'local') {
            pos = pos.clone().applyMatrix4(mesh.matrixWorld);
          }
          let hVal = Math.abs(pos.x - vector.x);
          let vVal = Math.abs(pos.y - vector.y);
          if (hVal <= span && minH > hVal) {
            minH = hVal;
            hPos = pos;
          }
          if (vVal <= span && minV > vVal) {
            minV = vVal;
            vPos = pos;
          }
        }
      }
      if (vPos) {
        target[1].line = {start: vPos, end: new Vector3(vector.x, vPos.y, ctrlPointHeight)};
        target[1].distance = vector.y - vPos.y;
      }
      if (hPos) {
        target[0].line = {start: hPos, end: new Vector3(hPos.x, vector.y, ctrlPointHeight)};
        target[0].distance = vector.x - hPos.x;
      }
      return target;
    },
    rotateByKeyboard() {
      let angleInRadian = Config.code.rotateStep / 180 * Math.PI;
      mesh.rotation.z += angleInRadian;
    },
    displayAs3D(val) {
      if (val) {
        let obj = that.serialize();
        let allVertices = [];
        for (let arr of obj.allVertices) {
          let vector = new Vector3().fromArray(arr);
          allVertices.push(vector);
        }
        init(allVertices, obj.holeNums);
        obj.vectices = vectices, obj.holesVectices = holesVectices;
        mesh3d = createModel3D(obj);
        lineSegments3d = createLines3d(obj);
        holderGroup.remove(mesh);
        holderGroup.add(mesh3d);
        holderGroup.add(lineSegments3d);
        lineSegments3d.visible = false;
        if (textSprite)
          textSprite.position.z = txtHeight;
      } else {
        holderGroup.remove(mesh3d);
        holderGroup.remove(lineSegments3d);
        holderGroup.add(mesh);
        if (textSprite)
          textSprite.position.z = 0;
      }
    },
    change3dMaterial(materialType) {
      if (materialType == mesh3dMat)
        return;
      mesh3dMat = materialType;
      if (mesh3dMat == "LineBasicMaterial") {
        lineSegments3d.visible = true;
        mesh3d.visible = false;
      } else {
        lineSegments3d.visible = false;
        mesh3d.visible = true;
        mesh3d.material = getMaterialByType(materialType);
      }
    },
    //called only in local mode
    toPasteItem() {
      let group = new Group();
      let items = [outline].concat(holes);
      let allVertices = [], holeNums = [];
      items.forEach(it => {
        let vLst = bufferAttributeToVertices(it.geometry.attributes.position);
        let dashedOutline = getDashedOutLine(vLst);
        group.add(dashedOutline);
        allVertices.push(...vLst);
        if (holeNums.length == 0) {
          holeNums.push(vLst.length);
        } else {
          let cur = holeNums[holeNums.length - 1] + vLst.length;
          holeNums.push(cur);
        }
      });
      holeNums.pop();
      group.scale.copy(mesh.scale);
      group.quaternion.copy(mesh.quaternion);
      group.lineColor = outline.material.color;
      group.meshColor = mesh.material.color;
      // group.meshOpacity = mesh.material.opacity;
      group.meshOpacity = mesh.userData.bacOpacity;
      group.allVertices = allVertices;
      group.holeNums = holeNums;
      group.name = name;
      group.zVal = zHeight;
      group.txtHeight = txtHeight;
      return group;
    },
    initFromPasteItem(item, pos) {
      if (pos != undefined && pos instanceof Vector3) {
        item.allVertices.forEach(it => it.add(pos));
      }
      name = item.name;
      zHeight = item.zVal;
      txtHeight = item.txtHeight;
      init(item.allVertices, item.holeNums);
      createMeshAndOutlines(true);
      mesh.quaternion.copy(item.quaternion);
      mesh.scale.copy(item.scale);
      mesh.material.color.copy(item.meshColor);
      mesh.material.opacity = item.meshOpacity;
      [outline].concat(holes).forEach(it => it.material.color.copy(item.lineColor));
      mesh.holder = that;
      return that;
    },
    serialize() {
      if (mode == 'global') {
        globalToLocal();
      }
      let pasteItem = that.toPasteItem();
      let target = {
        allVertices: [],
        holeNums: pasteItem.holeNums,
        opacity: pasteItem.meshOpacity,
        meshColor: pasteItem.meshColor.toArray(),
        lineColor: pasteItem.lineColor.toArray(),
        scale: pasteItem.scale.toArray(),
        quaternion: pasteItem.quaternion.toArray(),
        name: name,
        height: zHeight,
        txtHeight: txtHeight,
        position: mesh.position.toArrayFixed(4)
      };
      for (let vector3 of pasteItem.allVertices) {
        target.allVertices.push(vector3.toArrayFixed(3));
      }
      return target;
    },
    deserialize(obj) {
      let allVertices = [];
      for (let arr of obj.allVertices) {
        let vector = new Vector3().fromArray(arr);
        allVertices.push(vector);
      }
      // initInstanceRectScale(allVertices, obj.holeNums);
      name = obj.name;
      zHeight = obj.height;
      txtHeight = obj.txtHeight;
      init(allVertices, obj.holeNums);
      createMeshAndOutlines(true);
      mesh.quaternion.fromArray(obj.quaternion);
      mesh.scale.fromArray(obj.scale);
      mesh.material.color.fromArray(obj.meshColor);
      mesh.material.opacity = obj.opacity;
      mesh.userData.bacOpacity = obj.opacity;
      mesh.position.fromArray(obj.position);
      if (textSprite) textSprite.position.fromArray(obj.position);
      [outline].concat(holes).forEach(it => it.material.color.fromArray(obj.lineColor));
      mesh.holder = that;
      oldPos.copy(mesh.position);
      updateBox2();
      return that;
    },
    instanceRectScale() {
      return instanceRectScale;
    },
    setTextVisibility(visible) {
      if (!textSprite) {
        return;
      }
      textSprite.visible = visible;
    },
    getTextSprite() {
      return textSprite;
    }
  };
  if (mesh) {
    mesh.holder = that;
  }
  return that;
};
export default PolygonMesh;
