import {Vector2} from "three"
import Layers from "../Layers";
import {getStartEndIcon, getMinNum, getPathIndicator, scalePathIndicator} from "../util/StaticMethods"
import PathPoint from "../model/PathPoint";
import Config from "../configurable/Config";
import browser from "../util/Browser";
import * as Consts from "../consts/Constants"
import PublishedScene from "../PublishedScene";
import fr from "../../../components/element/locale/lang/fr";

let NavigatorTool = function (sceneHolder) {
  let eventRemainMeters = {type: 'remain meters', meters: 0};
  let eventRefindPath = {type: 're-find path'};
  let eventRecaculatePath = {type: 'recaculate path', count: 0};
  //
  let eventMapPosSelect = {type: 'map pos select', start: '', end: ''};
  //
  let eventBuildingSelected = {type: 'building selected', modelName: '', posObj: null, destination: false};

  let pathPoints, pathPointsOnFloor, pathPointsOnBuilding, pathEdges;
  let editingLayer = Layers.editingLayer;
  let startPointObj = getStartEndIcon( true), endPointObj = getStartEndIcon(false);
  let startPoint, endPoint;
  let pathIndicator;
  let span = 2;
  let thresh = span * span;
  let buildings = [];
  let enablePathFinding = true;
  let startPosAdjust = false, endPosAdjust = true;
  let needClearAbstractEdges = false;
  let findPathResult;
  let STATUS_NAVIGATING = 1, STATUS_NAVIGATING_END = 2, STATUS_NAVIGATE_FREE = 0;
  let status = STATUS_NAVIGATE_FREE;
  let pathRefreshInterval = 3, curPathRefreshCount = 0;
  let preSelectedMesh;

  let settingStartMarker, settingEndMarker;
  let touchEnd = new Vector2();

  function addAbstractEdge(from, to) {
    from.nearPlaces.push({to: to, cost: from.pos.distanceTo(to.pos), abstract:true });
    if (!from.abstract) {
      needClearAbstractEdges = true;
    }
  }

  function clearExtraEdges() {
    if (needClearAbstractEdges) {
      for (let pathPoint of pathPoints) {
        pathPoint.nearPlaces = pathPoint.nearPlaces.filter(it => !it.abstract);
      }
      needClearAbstractEdges = false;
    }
  }

  function getPathPointsInArr(vector3, arr, len) {
    if (arr.length <= len) {
      return arr;
    }
    let distances = [], target = [];
    for (let pathPoint of arr) {
      let distanceSq = pathPoint.pos.distanceToSquared(vector3);
      distances.push(distanceSq);
    }
    let sorted = distances.slice().sort((a, b) => a - b);
    for (let distance of sorted) {
      let index = distances.indexOf(distance);
      target.push(arr[index]);
      if (target.length == len) {
        break;
      }
    }
    return target;
  }
  // arr vector3
  //vector3  isEnd = true : isEnd = false
  function getPathPointInArr(vector3, arr, noSpan, isEnd) {
    let min = Infinity, target = null;
    for (let pathPoint of arr) {
      if (pathPoint.nearPlaces.length == 0 && !isEnd) {
        continue; //vector3 
      }
      if (pathPoint.dstNum == 0 && isEnd) {
        continue; //vector3 
      }
      let distanceSq = pathPoint.pos.distanceToSquared(vector3);
      if ( (noSpan || distanceSq <= thresh) && min > distanceSq) {
        min = distanceSq;
        target = pathPoint;
      }
    }
    return target;
  }

  function buildPathFindingGraph() {
    if (!Config.ui.isPublished()) {
      Layers.initPreviewPath();
    }
    pathEdges = Layers.getPreviewPath().pathEdges;
    pathPoints = Layers.getPreviewPath().pathPoints;
    for (let  pathPoint of pathPoints) {
      pathPoint.dstNum = 0;
    }
    for (let  pathPoint of pathPoints) {
      pathPoint.nearPlaces = [];
      pathPoint.toPlaces = []; //{points:[], cost: 10}
      for (let edge of pathEdges) {
        let dir = edge.pathDirection;
        if ( pathPoint == edge.from) {
          if (dir == Consts.pathDirFromTo || dir == Consts.pathDirBoth) {
            pathPoint.nearPlaces.push({to: edge.to, cost: edge.cost});
            edge.to.dstNum++;
          }
        } else if ( pathPoint == edge.to) {
          if (dir == Consts.pathDirToFrom || dir == Consts.pathDirBoth) {
            pathPoint.nearPlaces.push({to: edge.from, cost: edge.cost});
            edge.from.dstNum++;
          }
        }
      }
    }
    pathPointsOnFloor = pathPoints.filter(it => it.onFloor);
    pathPointsOnBuilding = pathPoints.filter(it => !it.onFloor);
  }

  function findPathSegment(pathPoint0, pathPoint1) {
    for (let edge of pathEdges) {
      let to = edge.to, from = edge.from;
      if ((pathPoint0 == to && pathPoint1 == from) || (pathPoint1 == to && pathPoint0 == from)) {
        return edge;
      }
    }
    return null;
  }

  //
  function findPath(from, to) {
    if (from == null || to == null) {
      console.log("null ");
      return null;
    }
    if (from === to) {
      // console.log("");
      return null;
    }
    let finded = [from];
    from.toPlaces = [];
    let nearPlaces = from.nearPlaces.slice(), toPlaces = from.toPlaces;
    if (nearPlaces.length < 1) {
      console.log("special circle, no neighbor");
      return null;
    }
    // cost 
    nearPlaces.sort((a, b) => a.cost - b.cost);
    for (let i = 0; i < nearPlaces.length; i++) {
      let edge = nearPlaces[i];
      toPlaces.push({points: [edge.to], cost: edge.cost});
    }//from.toPlaces 
    let curPath = toPlaces[0];

    while (true) {
      let midCircle = curPath.points[curPath.points.length - 1];
      finded.push(midCircle);
      if (midCircle === to) {
        // console.log("finded shortest path, costs: " + curPath.cost);
        let arr = [from].concat(curPath.points);
        let target = {points: [], edges: [], pathPoints: [], cost: curPath.cost};
        for (let i = 0; i < arr.length - 1; i++) {
          let holder = findPathSegment(arr[i], arr[i+1]);
          target.edges.push(holder);
        }
        arr.forEach(it => {
          target.points.push(it.pos.clone());
          target.pathPoints.push(it);
        });
        // clearExtraEdges();
        return target;
      }
      let placesFromMid = midCircle.nearPlaces.filter(it => finded.indexOf(it.to) < 0);
      placesFromMid.sort((a, b) => a.cost - b.cost);

      for (let index in placesFromMid) {
        let edge = placesFromMid[index];
        let curCost = curPath.cost + edge.cost;
        let filtered = toPlaces.filter(it => it.points[it.points.length - 1] === edge.to);
        if (filtered.length > 0) {
          let existCost = filtered[0].cost;
          let ind = toPlaces.indexOf(filtered[0]);
          if (existCost > curCost) {
            toPlaces[ind] = {points: curPath.points.concat(edge.to), cost: curCost};
          }
        } else {
          let newPath = {points: curPath.points.concat(edge.to), cost: curCost};
          toPlaces.push(newPath);
        }
      }
      let filtered = toPlaces.filter(it => finded.indexOf(it.points[it.points.length - 1]) < 0);
      if (filtered.length > 0) {
        filtered.sort((a, b) => a.cost - b.cost);
        curPath = filtered[0];
      } else {
        // console.log("");
        // clearExtraEdges();
        return null;
      }
    }
  }

  function setStartIcon(position) {
    if (!startPointObj.parent) {
      editingLayer.add(startPointObj);
    }
    startPointObj.position.copy(position);
  }
  function setEndIcon(position) {
    if (!endPointObj.parent) {
      editingLayer.add(endPointObj);
    }
    endPointObj.position.copy(position);
  }
  function setIconByName(name, isStart) {
    let lst = getMeshsByName(name);
    if (lst.length == 0)
      return;
    if (isStart) {
      setStartIcon(lst[0].holder.getModelPos());
      if (lst[0].holder.bindPathPoints.length == 0) {
        console.log("");
        return;
      }
      startPoint = lst[0].holder.bindPathPoints[0];
      startPoint.obj = null;
    } else {
      // setEndIcon(lst[0].holder.getModelPos());
      if (lst[0].holder.bindPathPoints.length == 0) {
        console.log("");
        return;
      }
      eventBuildingSelected.destination = true;
      eventBuildingSelected.posObj = {pathPoint: lst[0].holder.bindPathPoints[0], skipExtra: true};
      eventBuildingSelected.modelName = name;
      sceneHolder.scene().dispatchEvent(eventBuildingSelected);
    }
    if (preSelectedMesh) {
      preSelectedMesh.holder.clearSelection();
    }
    PublishedScene.startMovingCamera(lst[0].holder.getModelPos() );
    lst[0].holder.startSelectAnimation();
    preSelectedMesh = lst[0];
  }

  function clearPathIndicator() {
    if (pathIndicator && pathIndicator.parent) {
      pathIndicator.parent.remove(pathIndicator);
    }
  }
  function reversePathIndicator() {
    if (pathIndicator && pathIndicator.parent) {
      pathIndicator.parent.remove(pathIndicator);
      offsetX = 0;
      pathIndicator = getPathIndicator(findPathResult.points.reverse(), findPathResult.cost, getPathIndicatorBack(),
          sceneHolder.currentCameraScale(), sceneHolder.currentCameraPhi());
      editingLayer.add(pathIndicator);
    }
  }

  function clearPathAnimation() {
    clearExtraEdges();
    settingEndMarker = settingStartMarker = false;
    if (startPointObj.parent) {
      startPointObj.parent.remove(startPointObj);
    }
    if (endPointObj.parent) {
      endPointObj.parent.remove(endPointObj);
    }
    clearPathIndicator();

    startPoint = endPoint = null;
    status = STATUS_NAVIGATE_FREE;
  }

  function pathFindingOnGpsChange() {
    eventRecaculatePath.count++;
    sceneHolder.scene().dispatchEvent(eventRecaculatePath);
    let gpsPos = sceneHolder.getGpsLocIndicatorPos();
    let target = {};
    selectPointOnMapByVector3(gpsPos, target);
    startPoint = new PathPoint(target.rawPoint);
    startPoint.obj = target;
    startPoint.onFloor = !target.inBuilding;
    target.rawPathPoint = startPoint;
    setStartIcon(startPoint.pos);
    createExtraEdges(true);
    let oldPathPoints = [];
    if (findPathResult) {
      oldPathPoints = findPathResult.pathPoints;
    }
    findPathResult = findPath(startPoint, endPoint);
    if (findPathResult != null) {
      eventRemainMeters.meters = findPathResult.cost * sceneHolder.mapScale();
      sceneHolder.scene().dispatchEvent(eventRemainMeters);
      refreshPathIndicator();
      comparePaths(oldPathPoints, findPathResult.pathPoints);
    }
  }

  function comparePaths(oldPoints, currentPoints) {
    if (oldPoints.length == 0) {
      return;
    }
    let diffCount = 0;
    for (let pathPoint of currentPoints) {
      if (pathPoint.abstract) continue;
      if (oldPoints.indexOf(pathPoint) < 0) {
        diffCount++;
      }
    }
    if (diffCount > 1) {
      sceneHolder.scene().dispatchEvent(eventRefindPath);
    }
  }

  function getPickedMesh(vector) {
    let pickedMeshes = sceneHolder.getIntersectsWith(vector, buildings);
    if (pickedMeshes.length > 0) {
      return pickedMeshes[0].object;
    }
    return null;
  }
  function getMeshsByName(name) {
    let target = [];
    buildings.forEach(mesh => {
      if (mesh.holder.name() == name) {
        target.push(mesh);
      }
    });
    return target;
  }

  function setMarkerPos(clientX, clientY, isEnd, isStart) {
    if (!enablePathFinding) {
      return;
    }
    if (status === STATUS_NAVIGATING) {
      return;
    }
    let obj = selectPointOnMap(clientX, clientY, isEnd);
    if (!settingStartMarker && !settingEndMarker) {
      eventBuildingSelected.posObj = obj;
      eventBuildingSelected.modelName = '';
      if (obj.inBuilding) {
        eventBuildingSelected.modelName = obj.name;
      }
      eventBuildingSelected.destination = isEnd;
      sceneHolder.scene().dispatchEvent(eventBuildingSelected);
    }
    //
    if (obj.pathPoint) {
      let pos = obj.pathPoint.pos;
      if (isEnd) {
        if (!endPosAdjust) {
          pos = obj.rawPoint;
        }
        setEndIcon(pos);
        if (!endPosAdjust) {
          obj.rawPathPoint = new PathPoint(obj.rawPoint);
          endPoint = obj.rawPathPoint;
        } else {
          endPoint = obj.pathPoint;
        }
        endPoint.obj = obj;

        eventMapPosSelect.end = obj.name;
        sceneHolder.scene().dispatchEvent(eventMapPosSelect);
      } else if (isStart) {
        if (!startPosAdjust) {
          pos = obj.rawPoint;
        }
        setStartIcon(pos);
        if (!startPosAdjust) {
          obj.rawPathPoint = new PathPoint(obj.rawPoint);
          startPoint = obj.rawPathPoint;
        } else {
          startPoint = obj.pathPoint;
        }
        startPoint.obj = obj;

        eventMapPosSelect.start = obj.name;
        sceneHolder.scene().dispatchEvent(eventMapPosSelect);
      }
    }
  }
  //
  function selectPointOnMap(clientX, clientY, isEnd) {
    let target = {};
    if (dataInvaild() ) {
      return target;
    }

    let lst = sceneHolder.getIntersectsOnPlane(clientX, clientY);
    if (lst.length > 0) {
      selectPointOnMapByVector3(lst[0].point, target, isEnd);
    }
    return target;
  }

  function selectPointOnMapByVector3(curVector, target, isEnd) {
    target.rawPoint = curVector;
    if (preSelectedMesh) {
      preSelectedMesh.holder.clearSelection();
    }
    let selectedMesh = getPickedMesh(curVector);
    if (selectedMesh) {
      preSelectedMesh = selectedMesh;
      selectedMesh.holder.startSelectAnimation();
      let bindPathPoints = selectedMesh.holder.bindPathPoints;
      let points = getPathPointsInArr(curVector, bindPathPoints, 1);
      target.pathPoint = points[0], target.nearPoints = bindPathPoints, target.inBuilding = true;
      target.name = selectedMesh.holder.name();
    } else {
      target.name = '';
      let edgeDistances = [], pointsOnEdge = [], edges = [];
      pathEdges.forEach((pathEdge, index) => {
        if (pathEdge.pathDirection == Consts.pathDirBan) {
          return;
        }
        let pt = pathEdge.getClosestPointOnEdge(curVector);
        if (pt) {
          let distanceSq = curVector.distanceToSquared(pt);
          if (distanceSq <= Config.code.pathAdhereSpanSq) {
            edgeDistances.push(distanceSq);
            pointsOnEdge.push(pt);
            edges.push(index);
          }
        }
      });

      if (edgeDistances.length > 0) {
        let minDistance = getMinNum(edgeDistances);
        let index = edgeDistances.indexOf(minDistance);
        target.pathPoint = new PathPoint(pointsOnEdge[index]), target.onEdge = pathEdges[ edges[index] ];
      } else {
        let closestPathPoint = getPathPointInArr(curVector, pathPoints, true, isEnd);
        target.pathPoint = closestPathPoint, target.out = true;
      }
    }
  }

  function createExtraEdges(isStart) {
    let obj = isStart ? startPoint.obj : endPoint.obj;
    if (!obj || obj.skipExtra)
      return;
    let rawPathPoint = obj.rawPathPoint, pathPoint = obj.pathPoint;
    if (obj.inBuilding) {
      if (isStart) {
        if (rawPathPoint) addAbstractEdge(rawPathPoint, pathPoint);
      } else {
        if (rawPathPoint) addAbstractEdge(pathPoint, rawPathPoint);
      }
      return;
    }
    if (obj.onEdge) {
      let edge = obj.onEdge;
      if (isStart) {
        if (edge.pathDirection == Consts.pathDirBoth || edge.pathDirection == Consts.pathDirToFrom) {
          addAbstractEdge(pathPoint, edge.from);
        }
        if (edge.pathDirection == Consts.pathDirBoth || edge.pathDirection == Consts.pathDirFromTo) {
          addAbstractEdge(pathPoint, edge.to);
        }
        if (!startPosAdjust) {
          addAbstractEdge(rawPathPoint, pathPoint);
        }
      } else {
        if (edge.pathDirection == Consts.pathDirBoth || edge.pathDirection == Consts.pathDirFromTo) {
          addAbstractEdge(edge.from, pathPoint);
        }
        if (edge.pathDirection == Consts.pathDirBoth || edge.pathDirection == Consts.pathDirToFrom) {
          addAbstractEdge(edge.to, pathPoint);
        }
        if (!endPosAdjust) {
          addAbstractEdge(pathPoint, rawPathPoint);
        }
      }
    } else {
      if (isStart) {
        if (!startPosAdjust) addAbstractEdge(rawPathPoint, pathPoint);
      } else {
        if (!endPosAdjust) {
          addAbstractEdge(pathPoint, rawPathPoint);
        }
      }
    }
  }

  function onPathFinding() {
    if (dataInvaild() ) {
      return;
    }
    if (status == STATUS_NAVIGATING_END) {
      status = STATUS_NAVIGATE_FREE;
    }
    if (endPoint == null) {
      console.log("");
      return;
    }
    createExtraEdges(false);
    if (!startPoint) {
      pathFindingOnGpsChange();
    } else {
      createExtraEdges(true);
    }

    findPathResult = findPath(startPoint, endPoint);
    if (findPathResult != null) {
      let msgs = getNavigateMsgs(findPathResult.points);
      msgs.forEach(msg => console.log(msg.getDesc(sceneHolder.mapScale() ) ) );
      eventRemainMeters.meters = findPathResult.cost * sceneHolder.mapScale();
      sceneHolder.scene().dispatchEvent(eventRemainMeters);
      clearPathIndicator();
      offsetX = 0;
      pathIndicator = getPathIndicator(findPathResult.points, findPathResult.cost, getPathIndicatorBack(),
          sceneHolder.currentCameraScale(), sceneHolder.currentCameraPhi());
      editingLayer.add(pathIndicator);
    }
  }


  function getPathIndicatorBack() {
    let backColor = null;
    if (status == STATUS_NAVIGATING) {
      backColor = "#4b8bff";
    } else if (status == STATUS_NAVIGATING_END) {
      backColor = "#999";
    }
    return backColor;
  }

  function touchstart4PathFinding(event) {
    touchEnd.set(event.touches[ 0 ].pageX, event.touches[ 0 ].pageY);
    sceneHolder.dom().addEventListener("touchend", touchend4PathFinding);
    sceneHolder.dom().addEventListener("touchmove", touchmove4PathFinding);
  }
  function touchmove4PathFinding(event) {
    touchEnd.set(event.touches[ 0 ].pageX, event.touches[ 0 ].pageY);
    sceneHolder.dom().removeEventListener("touchend", touchend4PathFinding);
    sceneHolder.dom().removeEventListener("touchmove", touchmove4PathFinding);

    // console.log("touchmove4PathFinding, manhattanDistanceTo = " + touchEnd.manhattanDistanceTo(touchStart));
  }
  function touchend4PathFinding(event) {
    setMarkerPos(touchEnd.x, touchEnd.y, settingEndMarker, settingStartMarker);
  }

  function mousedown4PathFinding(event) {
    sceneHolder.dom().addEventListener("mousemove", mousemove4PathFinding);
    sceneHolder.dom().addEventListener("mouseup", mouseup4PathFinding);
  }
  function mousemove4PathFinding(event) {
    sceneHolder.dom().removeEventListener("mousemove", mousemove4PathFinding);
    sceneHolder.dom().removeEventListener("mouseup", mouseup4PathFinding);
  }

  function mouseup4PathFinding(event) {
    setMarkerPos(event.clientX, event.clientY, settingEndMarker, settingStartMarker);
  }

  let offsetX = 0;

  function refreshPathIndicator(scalar, phi) {
    scalar = scalar || sceneHolder.currentCameraScale();
    phi = phi || sceneHolder.currentCameraPhi();
    if (pathIndicator && pathIndicator.parent) {
      let parentObj = pathIndicator.parent;
      pathIndicator.parent.remove(pathIndicator);
      pathIndicator = getPathIndicator(findPathResult.points, findPathResult.cost, getPathIndicatorBack(), scalar, phi);
      parentObj.add(pathIndicator);
    }
  }

  function dataInvaild() {
    return !pathPoints || !pathEdges || pathPoints.length == 0 || pathEdges.length == 0
  }
  function interpolateLinear(from, to, span) {
    let target = [from], current = from;
    let distance = current.distanceTo(to);
    while (distance > span) {
      current = current.clone().lerp(to, span / distance);
      target.push(current);
      distance -= span;
    }
    return target;
  }

  // 
  let go_straight = 0, go_along_left = 1, go_left = 2, go_left_back = 3;
  let go_turn_around = 7, go_along_right = 4, go_right = 5, go_right_back = 6;
  function getNavigateMsgs(vector3s) {
    let msgs = []; // {type: 1, distance: 11}
    let vector2s = vector3s.map(it => new Vector2(it.x, it.y));
    if (vector2s.length < 2)
      return msgs;
    let point = vector2s[0];
    let keyPoints = [point];
    for (let i = 1; i < vector2s.length; i++) {
      let cur = vector2s[i];
      if (point.distanceTo(cur) >= 1) {
        keyPoints.push(cur);
        point = cur;
      }
    }
    let directions = [], angles = [];
    for (let i = 1; i < keyPoints.length; i++) {
      let dir = keyPoints[i].clone().sub(keyPoints[i - 1]);
      directions.push(dir);
    }
    for (let i = 0; i < directions.length - 1; i++) {
      let angle = directions[i].angleTo(directions[i+1]) * Config.mathConsts.radianToDegree;
      angles.push(angle)
    }
    console.log("keyPoints:",keyPoints)
    // console.log("directions:",directions)
    console.log("angles:", angles)
    if (directions.length == 0)
      return msgs;
    let currentMsg = new NavigateInfo(0, directions[0].length());
    msgs.push(currentMsg);
    for (let i = 1; i < directions.length; i++) {
      let navInfo = new NavigateInfo(angles[i-1], directions[i].length());
      let merged = currentMsg.merge(navInfo);
      if (!merged) {
        msgs.push(navInfo);
        currentMsg = navInfo;
      }
    }
    return msgs;
  }

//20  
//2070 
//70100 
//100170 
/// 
//
  let naviMsgs = ["", "", "", "", "", "", "", ""];
  function NavigateInfo(angleByDegree, distance) {
    let goType = 0
    if (angleByDegree >= 20 && angleByDegree < 70) {
      goType = go_along_left
    } else if (angleByDegree >= 70 && angleByDegree < 100) {
      goType = go_left
    } else if (angleByDegree >= 100 && angleByDegree < 170) {
      goType = go_left_back
    } else if ( (angleByDegree >= 170 && angleByDegree <= 180) ||
        angleByDegree <= -170 && angleByDegree >= -180 ) {
      goType = go_turn_around
    } else if (angleByDegree <= -20 && angleByDegree > -70) {
      goType = go_along_right
    } else if (angleByDegree <= -70 && angleByDegree > -100) {
      goType = go_right
    } else if (angleByDegree <= -100 && angleByDegree > -170) {
      goType = go_right_back
    }
    let target = {type: goType, distance: distance}

    target.getDesc = function (scalar) {
      return naviMsgs[target.type] + ~~(target.distance * scalar) + "";
    }
    target.merge = function (otherInfo) {
      if (otherInfo.type !== go_straight)
        return false;
      target.distance += otherInfo.distance;
      return true;
    }
    return target;
  }

  let that = {
    buildPathFindingGraph: buildPathFindingGraph,
    findPath: findPath,
    setMarkerPos: setMarkerPos,
    getRandomTrack(inEditor, minimumDistance, span) {
      let pahtPointLst = pathPointsOnBuilding;
      let distanceSquared = minimumDistance * minimumDistance;
      if (inEditor) {
        buildPathFindingGraph();
        pahtPointLst = pathPoints;
      }
      if (pahtPointLst.length < 3) {
        console.log("");
        return [];
      }
      let result, count = 0, dd;
      let startP, endP;
      do {
        let startNum = ~~ (pahtPointLst.length * Math.random() );
        let endNum = ~~ (pahtPointLst.length * Math.random() );
        startP = pahtPointLst[startNum], endP = pahtPointLst[endNum];
        dd = startP.pos.distanceToSquared(endP.pos);
        count++;
      } while (count < 900 && dd < distanceSquared);
      result = findPath(startP, endP);
      if (!result) {
        console.log("getRandomTrack failed");
        return [];
      }
      console.log("getRandomTrack, count: " + count + ", cost: " + result.cost);
      let target = [], points = result.points;
      for (let i = 0; i < points.length - 1; i++) {
        let from = points[i], to = points[i + 1];
        let arr = interpolateLinear(from, to, span);
        target.push(...arr);
      }
      return target;
    },
    startPoint() {
      return startPoint;
    },
    endPoint() {
      return endPoint;
    },
    findPathFromCircles(fromCircle, toCircle) {
      let ringLst = Layers.getPathSegmentRings();
      let pathPoints = Layers.getPreviewPath().pathPoints;
      if (pathPoints.length == 0) {
        return null;
      }
      let fromPoint = pathPoints[ ringLst.indexOf(fromCircle) ];
      let toPoint = pathPoints[ ringLst.indexOf(toCircle) ];
      return that.findPath(fromPoint, toPoint);
    },
    initBuildings() {
      buildings = [];
      let meshLst = Layers.availableMeshes(true, true);
      meshLst.forEach(mesh => {
        if (mesh.holder && mesh.holder.bindPathPoints.length > 0) buildings.push(mesh);
      } );
    },
    onMapChange() {
      clearPathAnimation();
      buildPathFindingGraph();

      that.initBuildings();
      if (Config.remote.track == 2) {
        PublishedScene.showRandomTrack(true);
      }
    },
    switchMarker() {
      if (startPoint && endPoint) {
        setEndIcon(startPoint.pos);
        setStartIcon(endPoint.pos);
        // console.log(startPoint.pos, endPoint.pos)
        let temp = startPoint;
        startPoint = endPoint;
        endPoint = temp;
      }

      reversePathIndicator();
    },
    setDistination() {
      let obj = eventBuildingSelected.posObj;
      setEndIcon(obj.pathPoint.pos);
      endPoint = obj.pathPoint;
      endPoint.obj = obj;
      // settingStartMarker = true; //
      eventMapPosSelect.end = obj.name;
    },
    setIconByName: setIconByName,
    startSetEndPosition(val) {
      // enablePathFinding = val;
      settingEndMarker = val;
    },
    onPathFinding: onPathFinding,
    clearPathAnimation: clearPathAnimation,
    animatePathIndicator() {
      if (pathIndicator && pathIndicator.parent) {
        offsetX -= 0.03;
        let Tube = pathIndicator.userData.tubeSetting;
        pathIndicator.material.map.offset.set( offsetX, Tube.offsetY );
      }
    },
    updatePathIndicatorScale(scalar) {
      if (pathIndicator && pathIndicator.parent) {
        scalePathIndicator(pathIndicator, scalar);
      }
    },
    rotatePathIndicator(phi) {
      if (pathIndicator && pathIndicator.parent) {
        pathIndicator.userData.tubeSetting.offsetY = (phi/ (Math.PI/2)) * 0.5;
      }
    },
    addListeners() {
      if (browser.runOnMobile() ) {
        sceneHolder.dom().addEventListener('touchstart', touchstart4PathFinding);
      } else {
        sceneHolder.dom().addEventListener('mousedown', mousedown4PathFinding);
      }
    },
    onGpsPostitionChange() {
      if (status !== STATUS_NAVIGATING) {
        return;
      }
      curPathRefreshCount++;
      if (curPathRefreshCount >= pathRefreshInterval) {
        curPathRefreshCount = 0;
        pathFindingOnGpsChange();
      }
    },
    toggleNavigatingSwitch(isNavigating) {
      if (dataInvaild() ) {
        return;
      }
      if (isNavigating) {
        status = STATUS_NAVIGATING;
      } else {
        status = STATUS_NAVIGATE_FREE;
      }
      eventRecaculatePath.count = 0;
      refreshPathIndicator();
    },
    endNavigating() {
      status = STATUS_NAVIGATING_END;
      eventRecaculatePath.count = 0;
      refreshPathIndicator();
    }
  };
  return that;
};

export default NavigatorTool;
