<template>
  <div class="home" v-title :data-title="pageTitle">
    <main3d/>
    <main-top-bar/>
    <tab-view>
      <main-tab-bar @tabClick="onTabClick"/>
      <main-tab-box ref="tabBoxes"/>
    </tab-view>
    <guide-box ref="guide"/>
    <compass-item class="compassIndicator"/>
    <location-reset-item class="locationIndicator"/>
    <editor-toolbar class="editor-float-bar"/>
    <bili-text/>
  </div>
</template>

<script>
  // @ is an alias to /src
  import MainTopBar from "../components/content/MainTopBar";
  import TabView from "../components/common/TabView";
  import MainTabBar from "../components/content/MainTabBar";
  import MainTabBox from "../components/content/MainTabBox";
  import GuideBox from "../components/content/guide/GuideBox";
  import CompassItem from "../components/content/gadget/CompassItem";
  import LocationResetItem from "../components/content/gadget/LocationResetItem";
  import EditorToolbar from "../components/content/gadget/EditorToolbar";
  import Main3d from "@/3d/Main";
  import Config from "../3d/js/configurable/Config";

  import jsColor from "@/external_lib/jscolor";

  import Vue from 'vue'
  import Vtip from '../external_lib/vtip/index.com'
  import '../external_lib/vtip/index.min.css'
  import axios from 'axios'
  import VueAxios from 'vue-axios'
  import BiliText from "../components/content/gadget/BiliText";

  Vue.use(VueAxios, axios)
  // 
  Vue.use(Vtip.directive)

  export default {
    name: 'Home',
    beforeCreate() {
      jsColor.init();
      if (this.$route.query.fps != undefined)
        Config.ui.showFps = this.$route.query.fps == 1;
      Config.remote.token = this.$route.query.token;
      Config.remote.mapId = this.$route.query.mapId;
      axios.defaults.baseURL = Config.remote.baseUrl;
      axios.defaults.headers.common['token'] = Config.remote.token;
      if (Config.remote.token)
        Config.code.loadSavingFromStart = false;
    },
    data() {
      return {
        pageTitle: ""
      }
    },
    methods: {
      onTabClick(obj) {
        this.$refs.tabBoxes.setTabs(obj);
        this.$refs.guide.setTipIndex(obj.cur);
      }
    },
    components: {
      BiliText,
      MainTopBar, TabView, MainTabBar, MainTabBox, GuideBox, CompassItem, LocationResetItem,
      Main3d, EditorToolbar
    }
  }
</script>

<style scoped>
  .home {
    overflow: hidden;
    width: 100vw;
    height: 100vh;
  }
  .compassIndicator {
    position: fixed;
    top: 56px;
    right: 24px;

    width: 36px;
    height: 36px;
    cursor: pointer;
  }
  .locationIndicator {
    position: fixed;
    top: 96px;
    right: 24px;

    width: 36px;
    height: 36px;
    cursor: pointer;
  }
  .editor-float-bar {
    position: absolute;
    bottom: 20px;
    right: 20px;
    z-index: 1;
  }
</style>
