let Common = {
  clearNull(str) {
    let output = "";
    for (let i = 0; i < str.length; i++) {
      if (str.charCodeAt(i) <= 127 && str.charCodeAt(i) > 0) {
        output += str.charAt(i);
      }
    }
    return output
  },
  convertLayerObj(layerObj) {
    let obj = {
      id: layerObj.id,
      hidden: layerObj.hidden, locked: layerObj.locked, name: layerObj.name,
      nameEditable: layerObj.nameEditable, removable: layerObj.removable,
    };
    return obj;
  }
};

export default Common;
