import {Vector3, Line3} from 'three';
import Config from "../configurable/Config";
import MainScene from "../MainScene";

let Box2 = function () {
    function isBetween(m, a, b) {
        if (m >= a && m <= b) {
            return true;
        }
        if (m >= b && m <= a) {
            return true;
        }
        let ma = Math.abs(m - a), mb = Math.abs(m-b);
        if (ma < 0.001 && mb < 0.001) {
            return true;
        }
        return false;
    }

    // let minOld = new Vector3(), maxOld = new Vector3();
    let surroundLines = [], midLines = [];
    function clearLines() {
        surroundLines = [], midLines = [];
    }
    function updateLines(zVal) {
        if (surroundLines.length == 0) {
            let topLeft = new Vector3(that.min.x, that.max.y, zVal);
            let bottomRight = new Vector3(that.max.x, that.min.y, zVal);
            let top = new Line3(topLeft, that.max), right = new Line3(that.max, bottomRight);
            let bottom = new Line3(bottomRight, that.min), left = new Line3(that.min, topLeft);
            surroundLines = [top, right, bottom, left];
            top.represent = that.max.y, bottom.represent = that.min.y;
            right.represent = that.max.x, left.represent = that.min.x;

            let mLeft = new Vector3(that.min.x, (that.min.y + that.max.y)/2, zVal);
            let mRight = new Vector3(that.max.x, (that.min.y + that.max.y)/2, zVal);
            let mTop = new Vector3((that.min.x + that.max.x)/2, that.max.y, zVal);
            let mBottom = new Vector3((that.min.x + that.max.x)/2, that.min.y, zVal);
            let lineX = new Line3(mLeft, mRight), lineY = new Line3(mTop, mBottom);
            midLines = [lineX, lineY];
            lineX.represent = (that.min.y + that.max.y)/2;  //x
            lineY.represent = (that.min.x + that.max.x)/2;  //y
        } else if (surroundLines[0].start.z != zVal) {
            surroundLines.concat(midLines).forEach(line3 => {
                line3.start.z = zVal;
                line3.end.z = zVal;
            });
        }
    }

    //return {lineY: line3, horizontal: 0.3, hVector: vector3, lineX: line3, vertical: 0.3, vVector: vector3 }
    function getClosestLines(point) {
        let target = {};
        updateLines(point.z);
        let leftLine = surroundLines[3], bottomLine = surroundLines[2];
        let spanX = that.max.x - that.min.x, spanY = that.max.y - that.min.y;
        let tmp = new Vector3(leftLine.start.x, point.y, point.z);
        let dx = point.x - tmp.x;
        if (dx <= 0) {
            target.lineY = leftLine, target.horizontal = -dx;
        } else {
            if (dx <= spanX * 0.25) {
                target.lineY = leftLine, target.horizontal = dx;
            } else if (dx <= spanX * 0.75) {
                target.lineY = midLines[1], target.horizontal = Math.abs(dx - spanX*0.5);
            } else {
                target.lineY = surroundLines[1], target.horizontal = Math.abs(dx - spanX);
            }
        }
        target.hVector = new Vector3(target.lineY.start.x, point.y, point.z);

        tmp = new Vector3(point.x, bottomLine.start.y, point.z);
        let dy = point.y - tmp.y;
        if (dy <= 0) {
            target.lineX = bottomLine, target.vertical = -dy;
        } else {
            if (dy <= spanY * 0.25) {
                target.lineX = bottomLine, target.vertical = dy;
            } else if (dy <= spanY * 0.75) {
                target.lineX = midLines[0], target.vertical = Math.abs(dy - spanY*0.5);
            } else {
                target.lineX = surroundLines[0], target.vertical = Math.abs(dy - spanY);
            }
        }
        target.vVector = new Vector3(point.x, target.lineX.start.y, point.z);
        return target;
    }

    function compareLines(hostLine, guestLine, obj, target) {
        let val = hostLine.represent - guestLine.represent;
        val = val >= 0 ? val : -val;
        if (obj.min > val) {
            obj.min = val;
            target.hostLine = hostLine;
            target.guestLine = guestLine;
            target.distance = val;
        }
    }
    function findMinPair(arrHost, arrGuest, lessCompare) {
        let minObj = {min: Infinity};
        let target = {hostLine: null, guestLine: null, distance: 0};
        if (lessCompare) {
            for (let i = 0; i < arrHost.length; i++) {
                compareLines(arrHost[i], arrGuest[i], minObj, target);
            }
        } else {
            for (let i = 0; i < arrHost.length - 1; i++) {
                for (let j = 0; j < arrGuest.length - 1; j++) {
                    compareLines(arrHost[i], arrGuest[j], minObj, target);
                }
            }
            compareLines(arrHost[2], arrGuest[2], minObj, target);
        }
        return target;
    }

    function getClosestLinesWithBox(box2) {
        let linesHostHorizontal = [surroundLines[0], surroundLines[2], midLines[0]];
        let linesHostVertical = [surroundLines[1], surroundLines[3], midLines[1]];
        let surroundings = box2.surroundLines(), middles = box2.midLines();
        let linesGuestHorizontal = [surroundings[0], surroundings[2], middles[0]];
        let linesGuestVertical = [surroundings[1], surroundings[3], middles[1]];
        let hObj = findMinPair(linesHostHorizontal, linesGuestHorizontal, !that.xCrossed(box2));
        let vObj = findMinPair(linesHostVertical, linesGuestVertical, !that.yCrossed(box2));

        return {h: hObj, v: vObj};
    }

    let squarePos;
    let that = {
        min: new Vector3(),
        max: new Vector3(),
        width() {
            return that.max.x - that.min.x;
        },
        height() {
            return that.max.y - that.min.y;
        },
        getCenter( target ) {

            if ( target === undefined ) {
                target = new Vector3();
            }

            return that.isEmpty() ? target.set( 0, 0, 0 ) : target.addVectors( that.min, that.max ).multiplyScalar( 0.5 );

        },
        xCrossed(box2) {
            let span = Config.code.smartGuideSpan * MainScene.currentCameraScale();
            let x0 = that.min.x - span, x1 = that.max.x + span;
            let x2 = box2.min.x - span, x3 = box2.max.x + span;
            return isBetween(x2, x0, x1) || isBetween(x3, x0, x1) ||
                isBetween(x0, x2, x3) || isBetween(x1, x2, x3);
        },
        yCrossed(box2) {
            let span = Config.code.smartGuideSpan * MainScene.currentCameraScale();
            let y0 = that.min.y - span, y1 = that.max.y + span;
            let y2 = box2.min.y - span, y3 = box2.max.y + span;
            return isBetween(y2, y0, y1) || isBetween(y3, y0, y1) ||
                isBetween(y0, y2, y3) || isBetween(y1, y2, y3);
        },
        getClosestLines: getClosestLines,
        getClosestLinesWithBox: getClosestLinesWithBox,
        surroundLines() {
            return surroundLines;
        },
        midLines() {
            return midLines;
        },
        isEmpty: function () {
            return ( that.max.x < that.min.x ) || ( that.max.y < that.min.y ) ;
        },
        makeEmpty: function () {

            that.min.x = that.min.y = + Infinity;
            that.max.x = that.max.y = - Infinity;
            clearLines();
            squarePos = null;
            return that;

        },
        expandByPoint( point ) {
            that.min.min( point );
            that.max.max( point );
            clearLines();
            squarePos = null;
            return that;
        },
        setFromPoints ( points ) {
            this.makeEmpty();

            for ( let i = 0, il = points.length; i < il; i ++ ) {

                this.expandByPoint( points[ i ] );

            }
            updateLines(points[0].z);
            return that;

        },
        contains(point) {
            if (isBetween(point.x, that.min.x, that.max.x) && isBetween(point.y, that.min.y, that.max.y)) {
                return true;
            }
            return false;
        },
        expandBy(scalar) {
            let diff = new Vector3(scalar, scalar, 0);
            let target = Box2();
            target.min = that.min.clone().sub(diff);
            target.max = that.max.clone().add(diff);
            squarePos = null;
            return target;
        },
        minSquarePos() {
            if (squarePos == null) {
                let w = that.width(), h = that.height();
                let size = w > h ? w : h;
                squarePos = {
                    x: (that.max.x + that.min.x - size)/2,
                    y: (that.max.y + that.min.y - size)/2,
                    z: size/2
                };
            }
            return squarePos;
        },
    };
    return that;
};

export default Box2;
