<template>
  <div class="boxInTab">
    <div>
      <div class="itemPropertyLabel"></div>
      <el-select v-model="matValue" placeholder="" @change="onChangeMaterial">
        <el-option
                v-for="item in matOptions"
                :key="item.value"
                :label="item.label"
                :value="item.value">
        </el-option>
      </el-select>
    </div>
  </div>
</template>

<script>
  import ElSelect from '@/components/element/select';
  import ElOption from '@/components/element/select/src/option';
  import MainScene from "../../../3d/js/MainScene";
  import {Lights_Editable} from "../../../store/mutations-types";
  import EventBus from "../../../store/EventBus";

  export default {
    name: "BoxMaterials",
    mounted() {
      EventBus.$on('editing for 3d', (val) => {
        if (!val) {
          this.matValue = 3;
        }
      })
    },
    data() {
      return {
        matOptions: [{
          value: 0,
          type: 'MeshBasicMaterial',
          label: ""
        }, {
          value: 1,
          type: 'LineBasicMaterial',
          label: ""
        }, {
          value: 2,
          type: 'MeshNormalMaterial',
          label: ""
        }, {
          value: 3,
          type: 'MeshLambertMaterial',
          label: ""
        }, {
          value: 4,
          type: 'MeshPhongMaterial',
          label: ""
        }],
        matValue:3
      }
    },
    methods: {
      onChangeMaterial() {
        let opt = this.matOptions[this.matValue];
        MainScene.changeGlobalMaterial(opt.type);
        if (this.matValue < 3) {
          MainScene.lightTool().visible(false);
          this.$store.commit(Lights_Editable, false);
        } else {
          MainScene.lightTool().visible(true);
          this.$store.commit(Lights_Editable, true);
        }
      }
    },
    components: {
      ElSelect, ElOption
    }
  }
</script>

