let polygonCount = 0;  //global variable
export function getPolygonCount() {
  polygonCount++;
  return polygonCount;
}

export function getLightType(light) {
  if (light instanceof  DirectionalLight)
    return lightTypeDirection;
  if (light instanceof  PointLight)
    return lightTypePoint;
  if (light instanceof  SpotLight)
    return lightTypeSpot;
  if (light instanceof  HemisphereLight)
    return lightTypeHemiSphere;
  return  -1;
}

export function getLightTypeName(light) {
  if (!light)
    return "";
  if (light instanceof DirectionalLight)
    return "";
  if (light instanceof PointLight)
    return "";
  if (light instanceof SpotLight)
    return "";
  if (light instanceof HemisphereLight)
    return "";
  return "";
}
