import {Vector3} from 'three';
import {getPathCtrlPoint} from "./StaticMethods";
import PathSegment from "../instance/PathSegment";
import PolygonMesh from "../instance/PolygonMesh";
import Layers from "../Layers";
import MainScene from '../MainScene';
import * as Consts from "../consts/Constants";
import Config from "../configurable/Config";
import History from "../undo/History";
import TextMark from "../instance/TextMark";

let serializeTool = function () {
  let defaultSaveName = Config.code.defaultSaveForEditor;
  // obj.eles.buildings[0].allVertices [25.843, -12.357, 0]
  // obj.eles.buildings[0].holeNums []
  function test0(obj) {
    let arr = []
    obj.eles.buildings.forEach(item => {
      arr.push({vertices:item.allVertices, holeNums:item.holeNums})
    })
    let str = JSON.stringify(arr)
    console.log(str)
  }
  function findHolder(holders, from, to) {
    for(let holder of holders) {
      if (holder.fromCircle() == from && holder.toCircle() == to) {
        return holder;
      }
      if (holder.fromCircle() == to && holder.toCircle() == from) {
        return holder;
      }
    }
    return null;
  }
  function removeDuplicate(holders) {
    let target = [];
    for (let holder of holders) {
      if (!findHolder(target, holder.fromCircle(), holder.toCircle())) {
        target.push(holder);
      }
    }
    return target;
  }
  function retrieveRings(holders) {
    let target = [];
    for (let holder of holders) {
      let from = holder.fromCircle(), to = holder.toCircle();
      if (target.indexOf(from) < 0) {
        target.push(from);
      }
      if (target.indexOf(to) < 0) {
        target.push(to);
      }
    }
    return target;
  }

  function serializeNavPathLayer() {
    let holders = Layers.getPathSegmentHolders();
    holders = removeDuplicate(holders);
    let rings = retrieveRings(holders);
    let target = {rings: [], segments: []};
    for (let ring of rings) {
      target.rings.push({x: ring.position.x, y: ring.position.y});
    }
    for (let holder of holders) {
      let segment = {from: rings.indexOf(holder.fromCircle()), to: rings.indexOf(holder.toCircle()),
        pathType: holder.getPathType(), pathDirection: holder.getPathDirection()};
      target.segments.push(segment);
    }
    return target;
  }
  function deserializeNavPathLayer(obj) {
    let ringObjs = [], vector;
    let previewPath = Layers.getPreviewPath();
    previewPath.pathPoints = [];
    previewPath.pathEdges = [];

    for (let pos of obj.rings) {
      vector = new Vector3(pos.x, pos.y, 0);
      let ringObj = getPathCtrlPoint(vector, Consts.pointTypeRing);
      ringObjs.push(ringObj);
      Layers.navPathLayer.add(ringObj);
    }
    for (let segment of obj.segments) {
      let from = ringObjs[segment.from], to = ringObjs[segment.to];
      let holder = PathSegment(from, to, segment.pathType);
      holder.setPathDirection(segment.pathDirection);
      Layers.navPathLayer.add(holder.getLine());
    }
  }

  let that = {
    serialize() {
      //let target = {floor:[], buildings:[], navPaths: serializeNavPathLayer(), lightList: [], layers:[] };
      let target = {eles: {}, navPaths: serializeNavPathLayer(), lightList: [], layers:[] };
      Layers.getAllLayers().forEach(obj => {
        target.layers.push({id: obj.id, hidden: obj.hidden, locked: obj.locked,
          name: obj.name, order: obj.group.renderOrder
        });
        target[obj.id] = [];
      } );
      for (let obj of target.layers) {
        let group = Layers.getLayerById(obj.id).group;
        for (let mesh of group.children) {
          if (!mesh.holder || !mesh.holder.serialize) continue;
          if (target.eles[obj.id] == undefined)
            target.eles[obj.id] = [];
          target.eles[obj.id].push(mesh.holder.serialize());
        }
      }

      target.coordBinding = MainScene.coordTool().serialize();
      let referMesh = Layers.referMesh();
      if (referMesh != null) {
        target.refLayer = {};
        target.refLayer.pos = referMesh.position.toArrayFixed(3);
        target.refLayer.scale = referMesh.scale.toArrayFixed(2);
        target.refLayer.rotation = referMesh.rotation.z;
        target.refLayer.opacity = referMesh.material.opacity;
      }
      target.lightList = MainScene.lightTool().serializeLights();
      return JSON.stringify(target);
    },
    deserialize(jsonStr) {
      Layers.clearLayer(Layers.navPathLayer);
      Layers.clearLayer(Layers.floorLayer);
      Layers.clearLayer(Layers.buildingsLayer);
      Layers.clearLayer(Layers.referLayer);
      Layers.clearLayer(Layers.markLayer);
      Layers.clearCustomLayers();
      if (!jsonStr)
        return;
      let obj = JSON.parse(jsonStr);
      test0(obj)

      if (obj.layers == undefined) { //
        obj.layers = [{id: 'floor', hidden: false, locked: false, order: 1},
          {id: 'buildings', hidden: false, locked: false, order: 3}];
      }

      for (let layer of obj.layers) {
        if (Layers.layers.hasOwnProperty(layer.id)) {
          Layers.layers[layer.id].hidden = layer.hidden;
          Layers.layers[layer.id].locked = layer.locked;
          Layers.layers[layer.id].group.renderOrder = layer.order;
        } else {
          Layers.addLayer(layer);
        }
        let elementArr;
        if (obj.eles != undefined) {
          elementArr = obj.eles[layer.id];  //
        } else {
          elementArr = obj[layer.id];
        }
        if (elementArr == undefined)
          continue;
        for (let itemObj of elementArr) {  //for (let itemObj of obj[layer.id]) {
          let group = Layers.getLayerById(layer.id).group;
          if (itemObj.isMarker) {
            let marker = TextMark().deserialize(itemObj);
            marker.attach(group);
          } else {
            let polygonMesh = PolygonMesh().deserialize(itemObj);
            group.add(polygonMesh.getHolderGroup());
          }
        }
      }

      deserializeNavPathLayer(obj.navPaths);
      MainScene.updateMeshesScale();
      MainScene.coordTool().clearBinding();
      if (obj.coordBinding) {
        MainScene.initCoordBinding(obj.coordBinding);
      }
      Layers.setReferLayerSaving(obj.refLayer);

      MainScene.lightTool().deleteAllLights();
      if (obj.lightList != undefined)
        MainScene.lightTool().deserializeLights(obj.lightList);

      MainScene.pathTool().prepareToFree();
      Layers.syncVisibilities();
      // console.log('reset ui in Serialization.js')
      History.removeAll();
    },

    saveToLoal(saveForPreview) {
      let saving = that.serialize();
      if (saveForPreview) {
        window.localStorage[Config.code.defaultSaveForPreview] = saving;
      } else {
        window.localStorage[defaultSaveName] = saving;
      }
      // console.log(window.localStorage[defaultSaveName]);
      // document.getElementById('mapInput').value = window.localStorage[defaultSaveName];
      return saving;
    },
    loadFromLocal() {
      let saving = window.localStorage[defaultSaveName];
      if (saving == undefined) {
        console.log("no saving for " + defaultSaveName + " founded!");
        return;
      }
      that.deserialize(saving);
    }
  };
  return that;
};
let Serializer = serializeTool();
export default Serializer;
