import {WebGLRenderer, Scene, PerspectiveCamera, GridHelper, PlaneBufferGeometry, MeshBasicMaterial,
  Texture, RGBAFormat, RGBFormat, FrontSide, Raycaster, LinearToneMapping, SphereGeometry,
  BoxHelper, Vector3, Vector2, Group, Mesh} from 'three';
import {LCS} from "../../external_lib/util";
import Stats from 'three/examples/jsm/libs/stats.module'

import Config from "./configurable/Config";
import Layers from './Layers';
import TrackballControls from "./lib/TrackballControls";
import PolygonTool from "./tool/PolygonTool";
import NavPathTool from "./tool/NavPathTool";
import CoordCorrelateTool from "./tool/CoordCorrelateTool";
import {getRectMesh, getRectOutline, getGridOrigin, getRandHeatmapMaterial, updateUvAttribute} from "./util/StaticMethods";
import PathTrack from "./instance/PathTrack";
import LightTool from "./tool/LightTool";
import MeasureTool from "./tool/MeasureTool";
import Serializer from "./util/Serialization";
import History from "./undo/History";
import MarkerTool from "./tool/MarkerTool";
import SelectionBox from "./lib/SelectionBox";
import SelectionHelper from "./lib/SelectionHelper";

let MainScene = function () {
  let eventZRotateChange = {type: 'zRotate', value: 0};
  let eventResetUI = {type: 'reset ui'};
  let eventAddRefLayer = {type: 'add ref layer'};
  let eventDivideRef = {type: 'divide ref layer', value:"1x1"};
  let eventDividedRefSelected = {type: 'divide ref selected', value:-1};
  let eventSelectionBoxUpdated = {type: 'selectionBoxUpdated'};
  let trackballControls;
  let renderer;
  let scene, camera, stats;
  let planeHelper;
  let polygonTool, navPathTool, coordTool, lightTool, measureTool, markerTool;

  let pressingAlt = false, pressingCtrl = false, pressingShift = false;
  let initialScale;
  let zRotateInTotal = 0;
  let divideRects = [];
  let gridSize = 60;
  let panFrames = 20, zoomFrames = 40;
  let cameraAni;
  let gridOrigin;
  let editingIn3D = false;
  let pathTrack;

  //
  let selectionBox;
  let selectionBoxHelper;

  function addGrid(parentItem) {
    let grid = new GridHelper( gridSize, 60, 0x444444, 0x888888 );
    grid.rotation.x = Math.PI / 2;
    grid.material.transparent = true;
    let array = grid.geometry.attributes.color.array;

    for ( let i = 0; i < array.length; i += 60 ) {
      for ( let j = 0; j < 12; j ++ ) {
        array[ i + j ] = 0.26;
      }
    }
    parentItem.add( grid );
    gridOrigin = getGridOrigin(15);
    parentItem.add( gridOrigin );
  }

  function initControl() {
    trackballControls = new TrackballControls(camera);
    trackballControls.rotateSpeed = 2.0;
    trackballControls.zoomSpeed = 1.5;
    trackballControls.panSpeed = 1.2;
    trackballControls.noZoom = false;
    trackballControls.noPan = false;
    trackballControls.staticMoving = false;
    trackballControls.dynamicDampingFactor = 0.3;
    trackballControls.lockFromZ = true;
    trackballControls.addEventListener( "change", updateMeshesScale);
    trackballControls.addEventListener( "rotateByZ", cameraRotateByZAxis);
  }

  function init(divElement) {
    scene = new Scene();
    addGrid(Layers.backgroundLayer);
    // helpers
    let helperGeometry = new PlaneBufferGeometry(999, 999, 2, 2);
    let helperMaterial = new MeshBasicMaterial({color: 0x00cc00, wireframe: true, transparent: true, opacity: 0, depthWrite: false});
    planeHelper = new Mesh(helperGeometry, helperMaterial);
    scene.add(planeHelper);

    // create a render and set the size
    renderer = new WebGLRenderer({ antialias: true });
    // renderer.setClearColor( 0xa0a0a0, 1 );
    renderer.setClearColor( '#ffffff', 1 );
    // renderer.setPixelRatio( window.devicePixelRatio );
    renderer.setSize(window.innerWidth, window.innerHeight);
    renderer.setTransparentSort(transparentSort);
    renderer.toneMapping = LinearToneMapping;
    // renderer.autoClear = false;

    camera = new PerspectiveCamera( 70, window.innerWidth / window.innerHeight, 1, 1000 );
    camera.position.z = 20;
    initialScale = camera.position.z;
    initControl();

    selectionBox = new SelectionBox( camera, scene );
    selectionBoxHelper = new SelectionHelper( selectionBox, renderer, 'selectBox' );
    eventSelectionBoxUpdated.selectBox = selectionBox;

    document.getElementById("WebGL-output").appendChild(renderer.domElement);
    if (Config.ui.showFps) {
      stats = new Stats();
      document.getElementById("WebGL-output").appendChild( stats.dom );
    }
    window.onblur = function() {
      // console.log("canvas lose focus");
      pressingShift = pressingCtrl = pressingAlt = false;
      trackballControls.clearPressing();
    };

    render();
    function render() {
      trackballControls.update();
      requestAnimationFrame(render);
      if (Config.ui.showFps)
        stats.update();

      renderer.render( scene, camera);
      if (navPathTool) {
        navPathTool.navigator().animatePathIndicator();
      }
      if (cameraAni) {
        let nextCameraAni = cameraAni.animate();
        if (nextCameraAni == null) {
          if (cameraAni.isFinished() )
            endChangeCameraManually();
        } else {
          cameraAni = nextCameraAni;
        }
      }
      if (pathTrack && pathTrack.attached() ) {
        if (pathTrack.reachEnd() )  {//pathTrack.ended()
          pathTrack.detach();
        } else {
          pathTrack.update();
        }
      }
    }
    function endChangeCameraManually() {
      trackballControls.enabled = true;
      cameraAni = null;
    }

    polygonTool = PolygonTool(divElement);
    navPathTool = NavPathTool();
    coordTool = CoordCorrelateTool();
    lightTool = LightTool();
    measureTool = MeasureTool();
    markerTool = MarkerTool(renderer.domElement);
    renderer.domElement.addEventListener('mousedown', event => {
      if (pressingCtrl) {
        mousedown4SelectionBox(event);
      } else {
        let used = markerTool.mousedown4Select(event);
        if (!used)
          polygonTool.mousedown4Select(event);
      }
    });
    if (Config.code.autoSaveInterval > 0) {
      setInterval(() => {
        if (History.actionModified) {  //
          console.log("saveToLoal called")
          Serializer.saveToLoal(false);
          History.actionModified = false;
        }
      }, Config.code.autoSaveInterval * 1000);
    }
  }

  function mousedown4SelectionBox(event) {
    selectionBox.startPoint.set(
      ( event.clientX / window.innerWidth ) * 2 - 1,
      - ( event.clientY / window.innerHeight ) * 2 + 1,
      0.5 );
    document.addEventListener('mousemove', mousemove4SelectionBox);
    document.addEventListener('mouseup', mouseup4SelectionBox);
    selectionBoxHelper.setEnabled(true);
  }
  function mousemove4SelectionBox(event) {
    if ( selectionBoxHelper.isDown ) {
      selectionBox.endPoint.set(
        ( event.clientX / window.innerWidth ) * 2 - 1,
        - ( event.clientY / window.innerHeight ) * 2 + 1,
        0.5 );
    }
  }
  function mouseup4SelectionBox(event) {
    selectionBox.endPoint.set(
      ( event.clientX / window.innerWidth ) * 2 - 1,
      - ( event.clientY / window.innerHeight ) * 2 + 1,
      0.5 );
    if (!Layers.navPathTabSelected()) {
      scene.dispatchEvent(eventSelectionBoxUpdated);
    } else {
      Layers.navPathLayer.dispatchEvent(eventSelectionBoxUpdated);
    }

    document.removeEventListener('mousemove', mousemove4SelectionBox);
    document.removeEventListener('mouseup', mouseup4SelectionBox);
    selectionBoxHelper.setEnabled(false);
  }

  window.addEventListener( 'resize', function () {
    if (!camera)
      return;
    camera.aspect = window.innerWidth / window.innerHeight;
    camera.updateProjectionMatrix();

    renderer.setSize( window.innerWidth, window.innerHeight );
    trackballControls.handleResize();

  }, false );

  window.addEventListener('keydown', function (event) {
    pressingAlt = event.altKey;
    pressingCtrl = event.ctrlKey;
    pressingShift = event.shiftKey;
  });
  window.addEventListener('keyup', function (event) {
    // console.log('window keyup: ' + event.keyCode, event.altKey, event.ctrlKey, event.shiftKey );
    pressingAlt = false, pressingCtrl = false, pressingShift = false;
  });

  let _this = this;
  function updateMeshesScale() {
    // console.log("building num: " + Layers.buildingsLayer.children.length);
    // console.log("floor num: " + Layers.floorLayer.children.length);
    let scalar = _this.currentCameraScale();
    measureTool.updateScaleFactor(scalar);
    navPathTool.navigator().updatePathIndicatorScale(scalar);
    if (Layers.navPathTabSelected()) {
      let lineSegments = Layers.navPathLayer.children;
      lineSegments.forEach(it => {
        if (it.holder) {
          it.holder.updateScaleFactor(scalar);
        }
      });
    } else {
      Layers.availableMeshes(true, true).forEach(mesh => {
        if (mesh.holder) {
          mesh.holder.updateScaleFactor(scalar)
        }
      } );
    }
  }
  function cameraRotateByZAxis(event) {
    // console.log('cameraRotateByZAxis, ' + event.value);
    zRotateInTotal += event.value;
    let degreeInTotal = zRotateInTotal * 180 / Math.PI;
    // console.log('cameraRotateByZAxis, degreeInTotal = ' + degreeInTotal);
    eventZRotateChange.value = degreeInTotal;
    scene.dispatchEvent(eventZRotateChange);
  }
  function transToScreenCoord(vector) {
    var screenCoord = {};
    vector.project(camera);
    screenCoord.x = (0.5 + vector.x / 2) * window.innerWidth;
    screenCoord.y = (0.5 - vector.y / 2) * window.innerHeight;
    return screenCoord;
  }

  this.init = init;
  this.polgyonTool = function () {
    return polygonTool;
  };
  this.markerTool = function () {
    return markerTool;
  };
  this.contextMenuEnabled = function() {
    return polygonTool.getContextMenuEnabled() && markerTool.contextMenuEnabled();
  };
  this.removeAll = function () {
    polygonTool.removeAll();
    navPathTool.removeAll();
    coordTool.clearBinding();
    markerTool.removeAll();
    this.resetUI();
  };
  this.pathTool = function () {
    return navPathTool;
  };
  this.resetCamera = function () {
    zRotateInTotal = 0;
    trackballControls.reset();
  };
  this.resetCameraRotate = function () {
    zRotateInTotal = 0;
    trackballControls.resetRotation();
  };
  this.resetCameraPos = function () {
    trackballControls.resetPos();
  };
  let bacMaterials = [], meshes4Heatmap, heatmapMaterial;
  this.applyHeatmapToSelected = function() {
    meshes4Heatmap = polygonTool.getSelectedMeshes();
    if (meshes4Heatmap.length == 0)
      return;
    if (!heatmapMaterial)
      heatmapMaterial = getRandHeatmapMaterial();
    meshes4Heatmap.forEach(it => {
      updateUvAttribute(it);
      bacMaterials.push(it.material);
      it.material = heatmapMaterial;
    } );
  };
  this.unapplyHeatmapToSelected = function() {
    if (meshes4Heatmap.length == 0)
      return;
    meshes4Heatmap.forEach((it, index) => {
      it.material = bacMaterials[index];
    } );
  };

  this.showRandomHeatmap = function () {
    let width = 25, height = 25;
    let geometry = new PlaneBufferGeometry(width, height , 1, 1);
    heatmapMaterial = getRandHeatmapMaterial();
    let heatmapPlane = new Mesh(geometry, heatmapMaterial );
    Layers.clearLayer(Layers.referLayer);
    Layers.referLayer.add(heatmapPlane);
  };
  this.clearHeatmap = function () {
    Layers.clearLayer(Layers.referLayer);
  };
  this.setReferImg = function(imgDom, isJpeg) {
    let iw = imgDom.naturalWidth, ih = imgDom.naturalHeight;
    let size = 33;
    let texture = new Texture();
    texture.image = imgDom;
    texture.format = isJpeg ? RGBFormat : RGBAFormat;
    texture.needsUpdate = true;
    let width = size * (iw/ih), height = size;
    let geometry = new PlaneBufferGeometry(width, height , 1, 1);
    let material = new MeshBasicMaterial({transparent: true, opacity: 1, map: texture, side:FrontSide});
    // let material = new MeshBasicMaterial({color: 0x00bb00, wireframe: true, transparent: true, opacity: 1, depthWrite: false});
    let backImgPlane = new Mesh(geometry, material);
    backImgPlane.userData = {w: width, h: height};
    backImgPlane.name = "refImg";
    Layers.setReferLayer(backImgPlane);
    scene.dispatchEvent(eventAddRefLayer);
  };
  this.divideRefImg = function (divideStr) {
    let backImgPlane = Layers.referMesh();
    if (!backImgPlane) {
      throw "";
    }
    let childLst = backImgPlane.children.slice();
    childLst.forEach(child => child.parent.remove(child));
    divideRects = [];

    let arr = divideStr.split('x');
    let dw = parseInt(arr[0]), dh = parseInt(arr[1]);
    if (dw > 1 || dh > 1) {
      let sw = backImgPlane.userData.w / dw;
      let sh = backImgPlane.userData.h / dh;
      let halfW = backImgPlane.userData.w / 2;
      let halfH = backImgPlane.userData.h / 2;
      let rectOutline = getRectOutline(sw, sh, '#990000');
      for (let i = 0; i < dh; i++) {
        for (let j = 0; j < dw; j++) {
          let rect = rectOutline.clone();
          rect.position.set(sw*(j+0.5) - halfW, sh*(i+0.5) - halfH, 0);
          // rect.userData = {w: sw, h: sh, id: i * dh + j};
          let len = backImgPlane.children.length;
          backImgPlane.add(rect);
          let rectMesh = getRectMesh(sw, sh);
          rectMesh.position.copy(rect.position);
          rectMesh.userData = {w: sw, h: sh, id: len};
          divideRects.push(rectMesh);
        }
      }
      divideRects.forEach(mesh => backImgPlane.add(mesh) );
    }

    eventDivideRef.value = divideStr;
    scene.dispatchEvent(eventDivideRef);
  };

  function transparentSort(a, b) {
    let layerA = Layers.getObjLayer(a.object);
    let layerB = Layers.getObjLayer(b.object);
    if ( layerA !== layerB ) {

      return layerA.renderOrder - layerB.renderOrder;

    } else if ( a.renderOrder !== b.renderOrder ) {

      return a.renderOrder - b.renderOrder;

    } else if ( a.z !== b.z ) {

      return a.z - b.z;

    } else {

      return a.id - b.id;

    }
  }

  let ChainedAnimation = function (frames, initialFunc, updateFunc, endFunc) {
    let curFrame = 0;
    this.next = null;
    let cur = this;
    this.append = function () {
      let len = arguments.length - 1;
      for (let i = 0; i < len; i++) {
        arguments[i].next = arguments[i + 1];
      }
      if (arguments.length > 0) {
        let current = cur;
        while(current.next) {
          current = current.next;
        }
        current.next = arguments[0];
      }
    };
    this.animate = () => {
      curFrame++;
      if (curFrame > frames) {
        if (endFunc) {
          endFunc();
        }
        return this.next;
      } else {
        if (curFrame === 1) {
          initialFunc();
        }
        updateFunc();
        return null;
      }
    };
    this.isFinished = () => {
      return curFrame > frames;
    }

  };

  function startPanZoomCamera(pos, width, height, index) {
    if (!trackballControls.enabled) {
      return false;
    }
    cameraAni = new ChainedAnimation(panFrames, () => {
      trackballControls.startPanCamera(pos, panFrames);
      trackballControls.enabled = false;
    }, () => {
      trackballControls.panManually();
    } );

    cameraAni.next = new ChainedAnimation(zoomFrames, () => {
      let p0 = pos.clone(), p1 = pos.clone();
      p0.y -= height/2, p1.y += height/2;
      p0.x -= width/2, p1.x += width/2;
      prepareZoomCamera(p0, p1);
    }, () => {
      trackballControls.zoomManually();
    }, () => {
      if (index != undefined) {
        eventDividedRefSelected.value = index;
        scene.dispatchEvent(eventDividedRefSelected);
      }
    });

    return true;
  }

  function startMergeAnimation() {
    let backImgPlane = Layers.referMesh();
    if (!backImgPlane) {
      throw "";
    }
    if (cameraAni)
      return;
    let origin = new Vector3(0,0,0);
    cameraAni = new ChainedAnimation(panFrames, () => {
      trackballControls.startPanCamera(origin, panFrames);
      trackballControls.enabled = false;
    }, () => {
      trackballControls.panManually();
    } );

    let zoomAni0 = new ChainedAnimation(zoomFrames, () => {
      let p0 = new Vector3(-gridSize, -gridSize, 0), p1 = new Vector3(gridSize, gridSize, 0);
      prepareZoomCamera(p0, p1);
    }, () => {
      trackballControls.zoomManually();
    });

    let rotateAni = new ChainedAnimation(60, () => {
      trackballControls.startRotateCamera(Math.PI * 6, 60);
    }, () => {
      trackballControls.rotateManually();
    });

    let zoomAni1 = new ChainedAnimation(zoomFrames, () => {
      let p0 = new Vector3(-gridSize/2, -gridSize/2, 0), p1 = new Vector3(gridSize/2, gridSize/2, 0);
      prepareZoomCamera(p0, p1);
    }, () => {
      trackballControls.zoomManually();
    }, () => {
      let childLst = Layers.referMesh().children.slice();
      childLst.forEach(child => child.parent.remove(child));
      divideRects = [];
      eventDivideRef.value = "1x1";
      scene.dispatchEvent(eventDivideRef);
    });
    cameraAni.append(zoomAni0, rotateAni, zoomAni1);
  }

  function prepareZoomCamera(p0, p1) {
    let q0 = transToScreenCoord(p0), q1 = transToScreenCoord(p1);
    let height2 = q0.y - q1.y, scaleH = window.innerHeight / height2;
    let width2 = q1.x - q0.x, scaleW = window.innerWidth / width2;
    let scale = scaleW < scaleH ? scaleW : scaleH;
    trackballControls.startZoomCamera(scale, zoomFrames);
  }

  this.resetUI = function () {
    Layers.initLayers();
    Config.ui.modelTextVisible = true;
    Config.ui.enableEditPointAdhere = true;
    scene.dispatchEvent(eventResetUI);
  }
  this.transToScreenCoord = transToScreenCoord;
  this.getRenderer = function () {
    return renderer;
  }
  this.scene = function () {
    return scene;
  }
  this.getCamera = function () {
    return camera;
  }
  this.currentCameraScale = function() {
    return camera.position.z / initialScale;
  }
  this.currentCameraPhi = function () {
    return 0;
  }
  this.getGpsLocIndicatorPos = function () {
    return new Vector3();
  }
  this.cameraControl = function () {
    return trackballControls;
  }
  this.getDom = function () {
    return renderer.domElement;
  }
  this.biliSegmentWidth = function () {
    let p0 = new Vector3(), p1 = new Vector3(2,0,0);
    let s0 = transToScreenCoord(p0), s1 = transToScreenCoord(p1);
    return s1.x - s0.x
  }

  this.getIntersectsWith = (function () {
    let tmpVector = new Vector3();
    return function(vector, objects) {
      tmpVector.copy(vector);
      let raycaster = new Raycaster(camera.position, tmpVector.sub(camera.position).normalize());

      return raycaster.intersectObjects(objects);
    }
  } () );
  this.getIntersects = function (x, y, objects) {
    let screen = trackballControls.screen;
    if (screen.width == 0 || screen.height == 0) {
      return [];
    }

    let vector = new Vector3(( ( x - screen.left ) / screen.width ) * 2 - 1, -( ( y - screen.top ) / screen.height ) * 2 + 1, 0.5);
    vector = vector.unproject(camera);

    let raycaster = new Raycaster(camera.position, vector.sub(camera.position).normalize());
    raycaster.camera = camera;
    return raycaster.intersectObjects(objects);
  }
  this.getIntersectsOnPlane = function (x, y) {
    return this.getIntersects(x, y, [planeHelper]);
  }
  this.updateCameraByDivideRects = function (x, y) {
    if (trackballControls.currentScale() > 0.4 || divideRects.length < 2) {
      return false;
    }
    let lst = this.getIntersects(x, y, divideRects);
    console.log("updateCameraByDivideRects " + lst.length);
    if (lst.length == 0) {
      return false;
    }
    let rect = lst[0].object;
    startPanZoomCamera(rect.position.clone().applyMatrix4(rect.parent.matrix),
        rect.userData.w * rect.parent.scale.x,
        rect.userData.h * rect.parent.scale.y, divideRects.indexOf(rect) );
    return true;
  }
  this.onNavPathTabSelected = function() {
    if (editingIn3D)
      return;
    polygonTool.onNavPathTabSelected();
    Layers.onNavPathTabSelected();
    updateMeshesScale();
  }
  this.onNavPathTabUnselected = function () {
    if (editingIn3D)
      return;
    Layers.onNavPathTabUnselected();
    navPathTool.prepareToFree();
  }
  this.onSettingsTabSelected = function() {
    if (!editingIn3D)
      coordTool.show();
  }
  this.onSettingsTabUnelected = function() {
    coordTool.hide();
  }
  this.coordTool = function() {
    return coordTool;
  }
  this.initCoordBinding = function (objFromJson) {
    coordTool.deserialize(objFromJson);
    // Layers.applyRotationFromCoordBinding(coordTool.rotation())
  }

  this.mapScale = function() {//
    return coordTool.getScale() || 1;
  }
  this.moveCameraToDivideIndex = function (index) {
    let rect = divideRects[index];
    return startPanZoomCamera(rect.position.clone().applyMatrix4(rect.parent.matrix),
        rect.userData.w * rect.parent.scale.x,
        rect.userData.h * rect.parent.scale.y);
  }
  this.toggleModelsNames = function () {
    Layers.availableMeshes(true, true).forEach(it => it.holder.setTextVisibility(Config.ui.modelTextVisible) );
  }
  this.showRandomTrack = function () {
    if (pathTrack && pathTrack.attached() )
      return;
    let positions = navPathTool.navigator().getRandomTrack(true, 10, 0.3);
    if (positions.length == 0)
      return;
    pathTrack = new PathTrack(Layers.editingLayer, positions, 40);
    pathTrack.attach();
  }
  this.setClearColor = function (color, opacity) {
    renderer.setClearColor( color, opacity );
  }
  this.getClearColor = function () {
    let str = renderer.getClearColor().getHexString() + " " + renderer.getClearAlpha();
    // console.log("clear color: " + str);
    return str;
  }
  this.toggle3dEditing = function (val) {
    lightTool.toggle3dEditing(val);
    editingIn3D = val;
    polygonTool.set3D(val);
  }
  this.isEditingIn3D = function() {
    return editingIn3D;
  }
  this.lightTool = function () {
    return lightTool;
  }
  this.measureTool = function () {
    return measureTool;
  }
  this.changeGlobalMaterial = function (materialType) {
    let allMeshes = Layers.availableMeshes(true);
    allMeshes.forEach(mesh => mesh.holder.change3dMaterial(materialType) );
  };

  this.updateMeshesScale = updateMeshesScale;
  this.startMergeAnimation = startMergeAnimation;
  this.pressingCtrl = function () {
    return pressingCtrl;
  }
  this.pressingShift = function () {
    return pressingShift;
  }
  this.pressingAlt = function () {
    return pressingAlt;
  }
  // 
  this.search = function (keyword) {
    let target = [];
    let meshes = Layers.availableMeshes(true);
    let markers = Layers.availableMarkers(true);
    meshes.forEach(mesh => {
      let addressName = mesh.holder.getName();
      if (LCS(keyword, addressName) == keyword.length) {
        target.push({name: addressName, id: mesh.uuid, isMesh:true, type:"" });
      }
    });
    markers.forEach(mark => {
      let name = mark.holder.text();
      if (LCS(keyword, name) == keyword.length) {
        target.push({name: name, id: mark.uuid, isMark:true, type:"" });
      }
    })
    return target;
  }
  this.setSelectByUuid = function (uuid) {
    let meshes = Layers.availableMeshes(true);
    let list = meshes.filter(mesh => mesh.uuid === uuid);
    if (list.length > 0) {
      polygonTool.setSelection(list[0]);
      return;
    }
    let markers = Layers.availableMarkers(true);
    list = markers.filter(marker => marker.uuid === uuid);
    if (list.length > 0) {
      markerTool.setSelection(list[0]);
    }
  }
};
let main3d = new MainScene();
export default main3d;
