import el from "../../../components/element/locale/lang/el";

let TriangleCut = function () {
  const ON_LEFT_SIDE = 1;
  const ON_RIGHT_SIDE = 2;
  const ON_LINE_IN = 3;
  const ON_LINE_OUT = 4;
  const BETWEEN_IN = 1
  const BETWEEN_ON = 2
  const BETWEEN_NOT = 3
  const POLYGON_INSIDE = 1
  const POLYGON_ON_EDGE = 2
  const POLYGON_OUTSIDE = 3

  let precision = -1;
  let minimum = 0.0001;
  const EPS = 0.000001;
  const bool_union = 1  //
  const bool_a_sub_b = 2  //a  b
  const bool_intersection = 3 //a  b 

  function isBetween0 (m, a, b) {
    if (Math.abs(m - a) <= minimum || Math.abs(m - b) <= minimum) {
      return BETWEEN_ON //m  a  b  m == a  m == b
    }
    return ((m > a && m < b) || (m > b && m < a)) ? BETWEEN_IN : BETWEEN_NOT
  }
  function isBetween2 (m, a, b) {
    let target = isBetween0(m, a, b)
    return target == BETWEEN_ON || target == BETWEEN_IN
  }
  function isBetween3 (m, a, b, countBounds = true) {
    let a0 = a, b0 = b
    if (a > b) {
      a0 = b
      b0 = a
    }
    let target = isBetween0(m, a0, b0)
    if (countBounds) {
      return target == BETWEEN_ON || target == BETWEEN_IN
    } else {
      return target == BETWEEN_IN
    }
  }
  //pointp1, p2
  function rayUpSegmentCross(point, p1, p2) {
    if (point.y > p1.y + minimum && point.y > p2.y + minimum) {
      return null
    }

    let small, big
    if (p1.x > p2.x) {
      small = p2
      big = p1
    } else {
      small = p1
      big = p2
    }
    let res = isBetween0(point.x, small.x, big.x)
    if (res == BETWEEN_NOT) {
      return null
    } else if (res == BETWEEN_ON) {
      if (p1.x == p2.x) {
        if (small.y >= point.y - minimum) {
          return [small, big]
        } else if (big.y > point.y - minimum) {
          return [point]
        } else {
          return null
        }
      } else if (p1.x == point.x) {
        return [p1]
      } else {
        return [p2]
      }
    } else {  //res == BETWEEN_IN
      let rate = (point.x - small.x) / (big.x - small.x)  // lerp alpha
      let dx = big.x - small.x
      let dy = big.y - small.y
      let cross = {x: small.x + dx * rate, y: small.y + dy * rate}
      if (cross.y >= point.y - minimum) {  // ? cross.y === point.y
        return [ cross ]
      }
      return null
    }
  }

  //    false
  function isPointInEdges(point, edges) {
    let target = isPointInEdges0(point, edges)
    return target === POLYGON_INSIDE
  }
  function isPointInEdges0 (point, edges) {
    let crossedPoints = []
    let checked = []
    for (let i = 0; i < edges.length; i++) {
      let edge = edges[i]
      if (checked.indexOf(edge) >= 0) {
        continue
      }
      let arr = rayUpSegmentCross(point, edge.from, edge.to)
      if (!arr) {
        continue
      }

      let prev = i - 1, next = i + 1
      if (prev < 0) {
        prev = edges.length - 1
      }
      if (next > edges.length - 1) {
        next = 0
      }
      if (arr.length == 1) {
        let pt = arr[0]
        if (equals(point, pt, true)) {
          return POLYGON_ON_EDGE
        }
        if (pt != edge.from && pt != edge.to) {
          crossedPoints.push(pt)
        } else if (pt == edge.to) {
          let edgeNext = edges[next]
          checked.push(edgeNext)
          if (edgeNext.vertical()) {
            if (isBetween3(point.y, edgeNext.from.y, edgeNext.to.y)) {
              return POLYGON_ON_EDGE
            }
            next += 1
            if (next > edges.length - 1) {
              next = 0
            }
            edgeNext = edges[next]
            checked.push(edgeNext)
          }
          if (isBetween3(point.x, edge.from.x, edgeNext.to.x, false)) {
            crossedPoints.push(pt)
          }
        } else {  //pt == edge.from
          let edgePrev = edges[prev]
          checked.push(edgePrev)
          if (edgePrev.vertical()) {
            if (isBetween3(point.y, edgePrev.from.y, edgePrev.to.y) ) {
              return POLYGON_ON_EDGE
            }
            prev -= 1
            if (prev < 0) {
              prev = edges.length - 1
            }
            edgePrev = edges[prev]
            checked.push(edgePrev)
          }
          if (isBetween3(point.x, edgePrev.from.x, edge.to.x, false)) {
            crossedPoints.push(pt)
          }
        }
      } else if (arr.length == 2) {
        let edgeNext = edges[next]
        let edgePrev = edges[prev]
        if (isBetween2(point.y, arr[0], arr[1])) {
          return POLYGON_ON_EDGE
        }
        if (isBetween3(point.x, edgePrev.from.x, edgeNext.to.x, false)) {
          crossedPoints.push(arr[0])
        }
      }
    }
    return crossedPoints.length % 2 == 1 ? POLYGON_INSIDE : POLYGON_OUTSIDE
  }

  let Edge = function (from, to, belong, holeId) {
    let checked = false;
    this.from = from, this.to = to;
    this.belong = belong;
    this.holeId = holeId;
    this.splitBy = [];
    this.addSplitBy = function (point) {
      if (!equals(this.from, point, true) && !equals(this.to, point, true) && this.splitBy.indexOf(point) < 0) {
        this.splitBy.push(point);
        return point;
      }
      return null;
    };
    this.setChecked = function () {
      checked = true;
    };
    this.getChecked = function () {
      return checked;
    };
    this.reverseEdge = function () {
      if (this.reversedTo) {
        return this.reversedTo;
      }
      let edge = new Edge(this.to, this.from, belong, holeId);
      edge.reversedFrom = this;
      this.reversedTo = edge;
      return edge;
    };
    this.clone = function () {
      let cloned = new Edge(this.from, this.to, belong, holeId);
      return cloned;
    };
    this.setFrom = function (from) {
      this.from = from;
      return this;
    };
    this.setTo = function (to) {
      this.to = to;
      return this;
    };
    this.sliceBy = function (crossPoint) {
      return [this.clone().setTo(crossPoint), this.clone().setFrom(crossPoint)];
    };
    this.sourceId = function () {
      let maxX = Math.max(this.from.x, this.to.x);
      let minX = Math.min(this.from.x, this.to.x);
      let maxY = Math.max(this.from.y, this.to.y);
      let minY = Math.min(this.from.y, this.to.y);
      return maxX+','+minX+";"+maxY+","+minY;
    };
    this.sameSource = function (edge) {
      if (equals(this.from, edge.from) && equals(this.to, edge.to)) {
        return {similarType: 4};
      } else if (equals(this.from, edge.to) && equals(this.to, edge.from)) {
        return {similarType: 3};
      }
      return null;
    };
    this.subEdge = function (other) {
      if (this.sameSource(other)) {
        return [];
      }
      let arr = specialSegmentCross(this.from, this.to, other.from, other.to);
      if (arr.length == 0) {
        return [this];
      }

      if (arr.indexOf(this.from) >= 0 && arr.indexOf(this.to) >= 0) {
        return [];
      } else if (arr.indexOf(this.from) < 0 && arr.indexOf(this.to) < 0) {
        let d1 = getDistanceSquare(this.from, other.from), d2 = getDistanceSquare(this.from, other.to);
        if (d1 < d2) {
          return [new Edge(this.from, other.from, belong, holeId), new Edge(other.to, this.to, belong, holeId)];
        } else {
          return [new Edge(this.from, other.to, belong, holeId), new Edge(other.from, this.to, belong, holeId)];
        }
      } else {
        let pc = arr.indexOf(other.from) >= 0 ? other.from : other.to;
        if (arr.indexOf(this.from) >= 0) {
          return [new Edge(pc, this.to, belong, holeId) ];
        } else {
          return [new Edge(this.from, pc, belong, holeId) ];
        }
      }
    };
    this.getMidPoint = function () {
      return {x: (this.from.x + this.to.x) / 2, y: (this.from.y + this.to.y) / 2};
    };
    this.length = function () {
      return getDistance(this.from, this.to);
    };
    this.lerpToEnd = function (alpha) {
      let x = this.from.x + (this.to.x - this.from.x) * alpha;
      let y = this.from.y + (this.to.y - this.from.y) * alpha;
      return {x: x, y: y};
    }
    this.getNear = function (isFrom) {
      let alpha = minimum / this.length();
      return this.lerpToEnd(isFrom ? alpha : 1 - alpha);
    }
    this.vertical = function () {
      return this.from.x == this.to.x
    }
  };

  let Triangle = function (a, b, c) {
    let pa = a, pb = b, pc = c;
    let ia = a.id, ib = b.id, ic = c.id;
    let ab = {x: b.x - a.x, y: b.y - a.y}, ac = {x: c.x - a.x, y: c.y - a.y};
    let cross_product = ab.x * ac.y - ac.x * ab.y;  // ab x ac
    let isClockwise = cross_product < 0;

    this.toString = function () {
      return "(" + ia + ", " + ib + ", " + ic + ")";
    };
    this.getVertices = function () {
      return [pa, pb, pc];
    };
    this.getIndices = function () {
      return [ia, ib, ic];
    };
    this.isClockwise = function () {
      return isClockwise;
    };
    this.changeSequence = function () {
      let tmp = ib;
      ib = ic;
      ic = tmp;
      tmp = pb;
      pb = pc;
      pc = tmp;
      isClockwise = !isClockwise;
    };
  };

  function threePointInOneLine(v1, v2, v3) {
    if (v1.x == v2.x) {
      return v3.x == v2.x;
    }
    if (v2.x == v3.x) {
      return v1.x == v2.x;
    }
    let k12 = (v1.y - v2.y) / (v1.x - v2.x);
    let k23 = (v3.y - v2.y) / (v3.x - v2.x);
    return Math.abs(k12 - k23) < 0.0002;
  }

  //
  function lineSegmentCross(p1, p2, q1, q2) {
    if (p1 == q1 || p1 == q2) {
      return p1;
    }
    if (p2 == q1 || p2 == q2) {
      return p2;
    }
    let cross = getCrossPoint(p1, p2, q1, q2);
    if (cross == null) {
      //the two line is parallel
      if (getPointOnVectorSide(p1, p2, q1) == ON_LINE_IN) {
        return q1;
      }
      if (getPointOnVectorSide(p1, p2, q2) == ON_LINE_IN) {
        return q2;
      }
      return null;
    }

    if (isBetween(cross.x, p1.x, p2.x) && isBetween(cross.x, q1.x, q2.x) &&
        isBetween(cross.y, p1.y, p2.y) && isBetween(cross.y, q1.y, q2.y)) {
      let arr = [p1, p2, q1, q2];
      for (let pt of arr) {
        if (equals(pt, cross, true) ) {
          return pt;
        }
      }
      return cross;
    }
    return null;
  }

  //return point p's position regards vector v1 to v2, use 3d right hand's coordinate system
  function getPointOnVectorSide(v1, v2, p) {
    if (equals(v1, p) || equals(v2, p)) {
      return ON_LINE_IN;
    }
    let v1_v2 = {x: v2.x - v1.x, y: v2.y - v1.y};   //vector v1 to v2
    let v1_p = {x: p.x - v1.x, y: p.y - v1.y};      //vector v1 to p
    let cross_product = v1_v2.x * v1_p.y - v1_p.x * v1_v2.y;

    if (cross_product > EPS) {
      // return ON_RIGHT_SIDE;
      return ON_LEFT_SIDE;
    } else if (cross_product < -EPS) {
      // return ON_LEFT_SIDE;
      return ON_RIGHT_SIDE;
    } else {
      if (isBetween(p.x, v1.x, v2.x) && isBetween(p.y, v1.y, v2.y)) {
        return ON_LINE_IN;
      }
      return ON_LINE_OUT;
    }
  }

  //return true if number m is between a and b
  function isBetween(m, a, b) {
    if (m >= a && m <= b) {
      return true;
    }
    if (m >= b && m <= a) {
      return true;
    }
    let ma = Math.abs(m - a), mb = Math.abs(m - b);
    if (ma <= minimum || mb <= minimum) {
      return true;
    }
    return false;
  }

  //
  function getCrossPoint(start1, end1, start2, end2) {
    let a1 = end1.y - start1.y;
    let b1 = start1.x - end1.x;
    let c1 = end1.x * start1.y - start1.x * end1.y;
    let a2 = end2.y - start2.y;
    let b2 = start2.x - end2.x;
    let c2 = end2.x * start2.y - start2.x * end2.y;
    let denominator = (a2 * b1 - a1 * b2);
    if (denominator == 0) {
      return null;
    }
    let x = (b2 * c1 - b1 * c2) / denominator;
    let y = (a1 * c2 - a2 * c1) / denominator;
    return {x: x, y: y};
  }

  function getDistance(v1, v2) {
    return Math.sqrt(getDistanceSquare(v1, v2));
  }

  function getDistanceSquare(v1, v2) {
    let dx = v2.x - v1.x;
    let dy = v2.y - v1.y;
    return dx * dx + dy * dy;
  }

  function getAngleWithX_axis(v1, v2) {
    let distance = getDistance(v1, v2);
    let dx = v2.x - v1.x;
    let cosA = dx / distance;
    if (v2.y >= v1.y) {
      return Math.acos(cosA);
    } else {
      return 2 * Math.PI - Math.acos(cosA);
    }
  }

  function getAngle(a, b, c) {
    let a1 = getAngleWithX_axis(b, a);
    let a2 = getAngleWithX_axis(b, c);
    let ret = Math.abs(a1 - a2);
    if (ret > Math.PI) {
      return Math.PI * 2 - ret;
    } else {
      return ret;
    }
  }

  // 
  function getTriangles4ConvexPolygon(vertices, output) {
    if (vertices == null || vertices.length < 3) {
      console.log("invaild input parameter");
      return;
    }
    if (vertices.length == 3) {
      output.push(new Triangle(vertices[0], vertices[1], vertices[2]));
      return;
    }
    let removed = [];
    for (let i = 0; i < vertices.length - 2;) {
      let a = vertices[i], b = vertices[i + 1], c = vertices[i + 2];
      if (threePointInOneLine(a, b, c)) {
        i++;
        continue;
      }
      output.push(new Triangle(a, b, c));
      removed.push(vertices[i + 1]);
      i += 2;
    }
    getTriangles4ConvexPolygon(subtract(vertices, removed), output);
  }

  // 
  function getTriangles4ConcavPolygon(vertices) {
    let convexPolygons = [], output = [];
    concavToConvex(vertices, convexPolygons);
    for (let i = 0; i < convexPolygons.length; i++) {
      getTriangles4ConvexPolygon(convexPolygons[i], output);
    }
    return output;
  }

  function concavToConvex(vertices, output) {
    // let points = filterOutUselessVertex(vertices);
    let points = vertices.slice();
    let total = points.length;
    let isClockwise = isClockwiseSquence(points);
    let concavPoint = null;
    let concavIndex = -1;
    for (let i = 0; i < total; i++) {   //
      let v0 = points[i];
      let v1 = points[getValidIndex(i + 1, total)];
      let v2 = points[getValidIndex(i + 2, total)];
      if (threePointInOneLine(v0, v1, v2)) {
        continue;
      }
      let curSide = getPointOnVectorSide(v0, v1, v2);
      if (isClockwise && curSide == ON_LEFT_SIDE) {
        concavPoint = v1;
        concavIndex = getValidIndex(i + 1, total);
        break;
      } else if (!isClockwise && curSide == ON_RIGHT_SIDE) {
        concavPoint = v1;
        concavIndex = getValidIndex(i + 1, total);
        break;
      }
    }
    if (concavPoint != null) {
      let index = concavIndex;
      let p2 = getValidIndex(index + 1, total);
      let p1 = getValidIndex(index - 1, total);
      let angle = getAngle(points[p1], points[index], points[p2]);
      let halfAngle = (Math.PI * 2 - angle) / 2;
      let minAngle = 100;
      let divideIndex;
      for (let i = 0; i < total; i++) {
        if (i == index || i == p1 || i == p2) {
          continue;
        }
        let side = getPointOnVectorSide(points[p1], points[index], points[i]);
        if ((isClockwise && side == ON_RIGHT_SIDE) ||
            (!isClockwise && side == ON_LEFT_SIDE) ||
            side == ON_LINE_OUT) {
          if (!isCrossWithEdges(points, index, i)) {
            let curAngle = getAngle(points[p1], points[index], points[i]);
            let delta = Math.abs(curAngle - halfAngle);
            if (delta < minAngle) {
              minAngle = delta;
              divideIndex = i;
            }
          }
        }
      }
      if (divideIndex == undefined) {
        console.log("error! found in concavToConvex() ");
        return;
      }
      let arr = splitArray(points, index, divideIndex);
      concavToConvex(arr[0], output);
      concavToConvex(arr[1], output);
    } else {
      output.push(points);
    }
  }

  function splitArray(vertices, index1, index2) {
    let small = Math.min(index1, index2);
    let big = Math.max(index1, index2);
    let v1 = vertices[small];
    let v2 = vertices[big];
    let start = small + 1;
    let numbersBetween = Math.abs(index1 - index2) - 1;
    let newSeries;
    let delta = start + numbersBetween - vertices.length;
    if (delta > 0) {
      console.log("splitArray, attention! how is it possible to go here");
      for (let i = 0; i < delta; i++) {
        let item = vertices.shift();
        vertices.push(item);
        start--;
      }
    }
    newSeries = vertices.splice(start, numbersBetween);
    newSeries.push(v2, v1);
    return [newSeries, vertices];
  }

//[ vertices[index] vertices[checkingIndex] ]  verticestrue
  function isCrossWithEdges(vertices, index, checkingIndex) {
    let q1 = getValidIndex(checkingIndex - 1, vertices.length);
    let q2 = getValidIndex(checkingIndex + 1, vertices.length);
    let p1 = getValidIndex(index - 1, vertices.length);
    let p2 = getValidIndex(index + 1, vertices.length);
    let needSkip1 = [p1, index, p2];
    let needSkip2 = [q1, checkingIndex, q2];
    for (let i = 0; i < vertices.length; i++) {
      let edgeIndex = (i + 1) % vertices.length;
      if (needSkip1.indexOf(i) >= 0 && needSkip1.indexOf(edgeIndex) >= 0) {
        continue;
      }
      if (needSkip2.indexOf(i) >= 0 && needSkip2.indexOf(edgeIndex) >= 0) {
        continue;
      }
      if (lineSegmentCross(vertices[i], vertices[edgeIndex], vertices[index], vertices[checkingIndex])) {
        return true;
      }
    }
    return false;
  }

  function getValidIndex(index, total) {
    if (index >= total) {
      return index % total;
    }
    if (index < 0) {
      return total + index % total;
    }
    return index;
  }

  function isClockwiseSquence(vertices) {
    let leftAngleSum = 0;
    let rightAngleSum = 0;
    for (let i = 0; i < vertices.length; i++) {
      let v2Index = getValidIndex(i + 1, vertices.length);
      let pIndex = getValidIndex(i + 2, vertices.length);
      if (threePointInOneLine(vertices[i], vertices[v2Index], vertices[pIndex])) {
        continue;
      }
      let curSide = getPointOnVectorSide(vertices[i], vertices[v2Index], vertices[pIndex]);
      let angle = Math.PI - getAngle(vertices[i], vertices[v2Index], vertices[pIndex]);
      if (curSide == ON_LEFT_SIDE) {
        vertices[v2Index].mark = "left";
        leftAngleSum += angle;
      } else if (curSide == ON_RIGHT_SIDE) {
        vertices[v2Index].mark = "right";
        rightAngleSum += angle;
      }
    }
    if (rightAngleSum == 0 && leftAngleSum == 0) {
      console.log("vertices is invalid, all points on one line");
      return false;
    }
    //Math.abs(rightAngleSum - leftAngleSum)/2 equal with Math.PI
    if (rightAngleSum > leftAngleSum) {
      return true;
    }
    return false;
  }

  //return arr1 - arr2;
  function subtract(arr1, arr2) {
    let ret = [];
    for (let i = 0; i < arr1.length; i++) {
      let flag = true;
      for (let j = 0; j < arr2.length; j++) {
        if (arr1[i] == arr2[j]) {
          flag = false;
          break;
        }
      }
      if (flag) {
        ret.push(arr1[i]);
      }
    }
    return ret;
  }

  // vertices ,  
  function isInConvex(point, vertexLst) {
    let vertices = vertexLst;
    if (isClockwiseSquence(vertexLst)) {
      vertices = vertexLst.slice().reverse();
    }
    let i;
    for (i = 0; i < vertices.length - 1; i++) {
      let side = getPointOnVectorSide(vertices[i], vertices[i + 1], point);
      if (side === ON_LINE_IN) {
        return true;
      }
      if (side !== ON_LEFT_SIDE) {
        return false;
      }
    }
    let side = getPointOnVectorSide(vertices[i], vertices[0], point);
    if (side === ON_LEFT_SIDE || side === ON_LINE_IN) {
      return true;
    }
    return false;
  }

  function isInConvexPolygons(point, convexPolygons) {
    for (let i = 0; i < convexPolygons.length; i++) {
      let convex = convexPolygons[i];
      if (isInConvex(point, convex)) return true;
    }
    return false;
  }

  //  
  function removeNearDuplicate(pointsCircle) {
    let target = [], current = null;
    for (let point of pointsCircle) {
      if (current == null || current.x != point.x || current.y != point.y ) {
        target.push(point);
        current = point;
      }
    }
    if (equals(target[0], target[target.length - 1]) ) {
      target.pop();
    }
    return target;
  }
  // 
  function removeRags(pointsCircle) {
    let map = {}, keys = [];
    for (let i = 0; i < pointsCircle.length; i++) {
      let point = pointsCircle[i];
      let key = point.x + "," + point.y;
      if (map[key] == undefined) {
        map[key] = [i];
      } else {
        map[key].push(i);
        if (keys.indexOf(key) < 0) {
          keys.push(key);
        }
      }
    }
    for (let i = keys.length - 1; i > -1; i--) {
      let key = keys[i];
      let start = map[key][0] + 1, deleteCount = map[key][1] - map[key][0];
      if (deleteCount > 3)  // bug
        continue;
      let point = pointsCircle[start - 1];
      let key0 = point.x + "," + point.y;
      if (key0 == key) {
        pointsCircle.splice(start, deleteCount);
      }
    }
    return pointsCircle;
  }

  function createEdges4Circles(circles, polygonIndex) {
    let edges2 = [];
    let count = 0;
    for (let circle of circles) {
      let edges = createEdges(circle, polygonIndex, count++);
      edges2.push(edges);
    }
    return edges2;
  }

  function createEdges(vertices, polygonIndex, holeId) {
    let edges = [];
    let i;
    for (i = 0; i < vertices.length; i++) {
      if (i == vertices.length - 1) {
        edges[i] = new Edge(vertices[i], vertices[0], polygonIndex, holeId || 0);
      } else {
        edges[i] = new Edge(vertices[i], vertices[i + 1], polygonIndex, holeId || 0);
      }
    }

    return edges;
  }

  function processSplitBy(edge) {
    if (edge.splitBy.length == 0) {
      return [edge];
    }
    let distanceSqs = [];
    edge.splitBy.forEach(it => distanceSqs.push(getDistanceSquare(edge.from, it)));
    let distanceSqsSorted = distanceSqs.slice().sort((a, b) => a - b);
    let from = edge.from, to;
    let splited = [];
    for (let i = 0; i < distanceSqsSorted.length; i++) {
      let index = distanceSqs.indexOf(distanceSqsSorted[i]);
      to = edge.splitBy[index];
      // if (from.x == to.x && from.y == to.y) {
      //     console.log("strange edge 0 found");
      // }
      splited.push(new Edge(from, to, edge.belong, edge.holeId));
      from = to;
    }
    splited.push(new Edge(to, edge.to, edge.belong, edge.holeId));
    return splited;
  }

  function equals(pt0, pt1, notPrecise) {
    if (pt0 === pt1) {
      return true;
    }
    if (notPrecise) {
      return Math.abs(pt0.x - pt1.x) < minimum && Math.abs(pt0.y - pt1.y) < minimum;
    }
    return pt0.x == pt1.x && pt0.y == pt1.y;
  }

  function findBox2(vertices) {
    let max = {}, min = {};
    let arrX = [], arrY = [];
    vertices.forEach(it => {
      arrX.push(it.x), arrY.push(it.y);
    });
    max.x = Math.max(...arrX);
    max.y = Math.max(...arrY);
    min.x = Math.min(...arrX);
    min.y = Math.min(...arrY);
    return {
      max: max, min: min, contain(p) {  //preturn false;
        let yMid = p.y < this.max.y && p.y > this.min.y;
        let xMid = p.x < this.max.x && p.x > this.min.x;
        return ((xMid && yMid) ||
            (p.x == this.max.x && this.max.x == this.min.x && yMid) ||
            (p.y == this.max.y && this.max.y == this.min.y && xMid));
      },
      scale(scalar) {
        let center = {x: (this.min.x + this.max.x)/2, y: (this.min.y + this.max.y)/2};
        let dx = center.x - this.min.x, dy = center.y - this.min.y;
        dx *= scalar, dy *= scalar;
        this.min.x = center.x - dx, this.min.y = center.y - dy;
        this.max.x = center.x + dx, this.max.y = center.y + dy;
      }
    };
  }

  function findOutestPoint(vertices, box2) {
    let distances = [];
    let boxCenter = {x: (box2.max.x + box2.min.x) / 2, y: (box2.max.y + box2.min.y) / 2};
    for (let vertex of vertices) {
      if (box2.contain(vertex)) {
        distances.push(-1);
      } else {
        distances.push(getDistanceSquare(boxCenter, vertex));
      }
    }
    let max = Math.max(...distances);
    if (max < 0) {
      return null;
    }
    return vertices[distances.indexOf(max)];
  }

  function findStartEdgeOfHole(restEdges0, restEdges1) {
    let restEdges = restEdges0.concat(restEdges1);
    let filtered = restEdges.filter(arr => arr.length > 0);
    if (filtered.length == 0) {
      return null;
    }
    return filtered[0][0];
  }

  function findNextEdgeForHole(curEdge, restEdges0, restEdges1) {
    let flatten = [], candidates = [];
    restEdges0.concat(restEdges1).forEach(arr => flatten.push(...arr));
    flatten.forEach(edge => {
      if (edge.from == curEdge.to)
        candidates.push(edge);
    });
    if (candidates.length == 0) {
      console.log("error! findNextEdgeForHole failed to find next");
      return null;
    }
    for (let edge of candidates) {
      if (edge.belong !== curEdge.belong) {
        return edge;
      }
    }
    return candidates[0];
  }

  function findStartEdge(splited0, splited1) {
    let vertices0 = edges2Points(splited0), vertices1 = edges2Points(splited1);
    let box0 = findBox2(vertices0), box1 = findBox2(vertices1);
    let edges = splited1;
    let p = findOutestPoint(vertices1, box0);
    if (p == null) {
      edges = splited0;
      p = findOutestPoint(vertices0, box1);
    }
    for (let edge of edges) {
      if (edge.from == p) {
        return edge;
      }
    }
    for (let edge of edges) {
      if (edge.to == p) {
        return edge;
      }
    }
    console.log("findStartEdge shall not go here");
    return null;
  }

  function findNextEdge(curEdge, edges0, edges1) {
    let first, second;
    // if (edges0[0].belong < 0) {
    //   first = edges0, second = edges1;
    // } else if (edges1[0].belong < 0) {
    //   first = edges1, second = edges0;
    // } else
    if (edges0.length > 0 && curEdge.belong == edges0[0].belong) {
      first = edges1, second = edges0;
    } else {
      first = edges0, second = edges1;
    }
    for (let i = 0; i < first.length; i++) {
      let edge = first[i];
      if (edge.from === curEdge.to && curEdge.reverseEdge() != edge) {
        return edge;
      }
    }
    for (let i = 0; i < second.length; i++) {
      let edge = second[i];
      if (edge.from === curEdge.to) {
        return edge;
      }
    }
    console.log("findNextEdge shall never go here");
    return null;
  }

  // restEdges0 restEdges1output
  function findHoles(restEdges0, restEdges1, output) {
    let firstEdge = findStartEdgeOfHole(restEdges0, restEdges1);
    if (firstEdge == null) {
      return;
    }
    let holePoints = [];
    let curEdge = firstEdge;
    let holeStart = -1;
    holePoints.push(curEdge.from);
    while (true) {
      curEdge = findNextEdgeForHole(curEdge, restEdges1, restEdges0);
      if (curEdge != null) {
        holePoints.push(curEdge.from);
        //check if hole already found
        if (holePoints.length > 2) {
          holeStart = holePoints.indexOf(curEdge.to);
          if (holeStart >= 0) {
            holePoints = holePoints.slice(holeStart, holePoints.length);
            break;
          }
        }
      } else {
        break;
      }
    }
    if (curEdge == null) {
      return;
    }
    let restEdges00 = getRestEdges(restEdges0, holePoints);
    let restEdges01 = getRestEdges(restEdges1, holePoints);
    output.push(holePoints);
    findHoles(restEdges00, restEdges01, output);
  }

  function getRestEdges(edges2, excludePoints, specialPoints) {
    let restEdges2 = [];
    edges2.forEach(edges => {
      let restEdges = [];
      edges.forEach(edge => {
        if (excludePoints.indexOf(edge.from) < 0 && excludePoints.indexOf(edge.to) < 0) {
          restEdges.push(edge);
        }
        if (specialPoints && specialPoints.length > 0) {
          if (specialPoints.indexOf(edge.from) > -1 || specialPoints.indexOf(edge.to) > -1 ) {
            if (excludePoints.indexOf(edge.from) < 0 || excludePoints.indexOf(edge.to) < 0) {
              restEdges.push(edge);
            }
          }
        }
      });
      restEdges2.push(restEdges);
    });
    return restEdges2;
  }

  //point convexPolygons2 
  //[ n,  n, ...n n]
  function isPointInPolygonWithHoles(point, convexPolygons2) {
    let outestPolygons = convexPolygons2[0];
    let isInOutestCircle = isInConvexPolygons(point, outestPolygons);
    if (!isInOutestCircle || convexPolygons2.length === 1) {
      return isInOutestCircle;
    }
    for (let i = 1; i < convexPolygons2.length; i++) {
      let convexPolygonsOfHole = convexPolygons2[i];
      let isInHole = isInConvexPolygons(point, convexPolygonsOfHole);
      if (isInHole) {
        return false;
      }
    }
    return true;
  }

  function removeEdges(edges20, edges21, polygons20, polygons21, boolType) {
    removeOutlineEdges(edges20[0], edges21[0], polygons20, polygons21, boolType);
    let holeEdges0 = [], holeEdges1 = [];
    let holes0 = edges20.slice(1), holes1 = edges21.slice(1);
    holes0.forEach(edges => holeEdges0.push(...edges));
    holes1.forEach(edges => holeEdges1.push(...edges));

    let len0 = holeEdges0.length, len1 = holeEdges1.length;
    if (holeEdges0.length > 0 && holeEdges1.length > 0) {
      removeOutlineEdges(holeEdges0, holeEdges1, polygons20, polygons21, boolType);
      if (len0 > holeEdges0.length) {
        holes0.forEach(edges => arrAonB(edges, holeEdges0));
      }
      if (len1 > holeEdges1.length) {
        holes1.forEach(edges => arrAonB(edges, holeEdges1));
      }
    }
  }

  function removeOutlineEdges(outline0, outline1, polygons20, polygons21, boolType = bool_union) {
    let edge0in1 = [], edge1in0 = [];
    let edge0notIn1 = [], edge1notIn0 = [];
    let edges = outline0.concat(outline1);
    for (let i = 0; i < edges.length; i++) {
      let edge = edges[i];
      if (edge.getChecked())
        continue;
      let polygons2 = edge.belong == 0 ? polygons21 : polygons20;
      let isIn = isPointInPolygonWithHoles(edge.getMidPoint(), polygons2);
      if (isIn) {
        edge.belong == 0 ? edge0in1.push(edge) : edge1in0.push(edge);
      } else {
        edge.belong == 0 ? edge0notIn1.push(edge) : edge1notIn0.push(edge);
      }
    }

    if (boolType == bool_union || boolType == bool_a_sub_b) {
      if (edge0in1.length > 0) {
        arrAsubB(outline0, edge0in1);
      }
    }
    if (boolType == bool_union) {
      if (edge1in0.length > 0) {
        arrAsubB(outline1, edge1in0);
      }
    } else if (boolType == bool_a_sub_b) {
      if (edge1notIn0.length > 0) {
        arrAsubB(outline1, edge1notIn0);
      }
    }
    if (boolType == bool_intersection) {
      outline0.splice(0, outline0.length)
      outline1.splice(0, outline1.length)
      outline0.push(...edge0in1)
      outline1.push(...edge1in0)
    }
  }

  //  
  //, 1 {from: a, to: b}, 2 {from: a, to: b},  1  2
  //, 1 {from: a, to: b}, 2 {from: b, to: a}, 1  2
  function removeOverlapEdges(edges20, edges21) {
    for (let edges0 of edges20) {
      for (let edges1 of edges21) {
        removeOverlapOutlineEdges(edges0, edges1);
      }
    }
    let empty0 = edges20.filter(edges => edges.length === 0);
    let empty1 = edges21.filter(edges => edges.length === 0);
    if (empty0.length > 0)
      arrAsubB(edges20, empty0);
    if (empty1.length > 0)
      arrAsubB(edges21, empty1);
  }

  function removeOverlapOutlineEdges(outline0, outline1) {
    let sharedEdges0 = [], sharedEdges1 = [];
    for (let i = 0; i < outline0.length; i++) {
      let edge0 = outline0[i];
      let j = 0;
      for (; j < outline1.length; j++) {
        let edge1 = outline1[j];
        let obj = edge0.sameSource(edge1);
        if (obj) {
          if (obj.similarType == 3) { //
            sharedEdges0.push(edge0);
            sharedEdges1.push(edge1);
          } else {    //
            sharedEdges0.push(edge0);
            edge1.setChecked();
          }
          break;
        }
      }
    }
    if (sharedEdges0.length > 0) {
      arrAsubB(outline0, sharedEdges0);
      arrAsubB(outline1, sharedEdges1);
    }
  }

  //
  function findSpecialPoints(edges) {
    let map = {}, target = [];
    for (let edge of edges) {
      addSpecialPoint(map, edge.from, target);
      addSpecialPoint(map, edge.to, target);
    }
    return target;
  }
  function addSpecialPoint(map, point, target) {
    let pointId = point.x + ", " + point.y;
    if (map[pointId] == undefined)
      map[pointId] = 1;
    else
      map[pointId]++;
    if (map[pointId] > 3 && target.indexOf(point) < 0) {
      target.push(point);
    }
  }

  //
  function findCircles(splited0, splited1, circles0, circles1, boolType) {
    let outestCircles = [];
    let polygons20 = [], polygons21 = [];
    circles0.forEach(circle => {
      let polygons = [];
      concavToConvex(circle, polygons);
      polygons20.push(polygons);
    });
    circles1.forEach(circle => {
      let polygons = [];
      concavToConvex(circle, polygons);
      polygons21.push(polygons);
    });

    removeOverlapEdges(splited0, splited1);
    if (splited0.length == 0 || splited1.length == 0) {
      let edges4Circles = splited0.concat(splited1);
      return edgesLst2PointsCircles(edges4Circles);
    }
    removeEdges(splited0, splited1, polygons20, polygons21, boolType);

    let outlineEdgesA = splited0[0].slice(), outlineEdgesB = splited1[0].slice()
    let excludePoints = []
    while (outlineEdgesA.length > 0) {
      let outestCircle = [], usedEdges = []
      let startEdge = findStartEdge(outlineEdgesA, outlineEdgesB);
      let curEdge = startEdge;
      usedEdges.push(curEdge)
      outestCircle.push(startEdge.from);
      while (curEdge.to != startEdge.from) {
        curEdge = findNextEdge(curEdge, outlineEdgesA, outlineEdgesB);
        usedEdges.push(curEdge)
        if (outestCircle.indexOf(curEdge.from) > -1) {
          console.log("Error! findCircles() dead loop in.");
          return [];  //
        }
        outestCircle.push(curEdge.from);
      }
      arrAsubB(outlineEdgesA, usedEdges)
      arrAsubB(outlineEdgesB, usedEdges)
      outestCircles.push(outestCircle)
      outestCircle.forEach(it => excludePoints.push(it))
    }

    let pointsSharedByCircles = [];
    if (boolType == bool_union) {
      pointsSharedByCircles = findSpecialPoints(splited0[0].concat(splited1[0] ) )
    }
    let restEdges0 = getRestEdges(splited0, excludePoints, pointsSharedByCircles);
    let restEdges1 = getRestEdges(splited1, excludePoints, pointsSharedByCircles);
    let holes = [];
    findHoles(restEdges0, restEdges1, holes);
    if (outestCircles.length == 1) {
      let outestPoints = outestCircles[0]
      return [ [outestPoints].concat(holes) ]
    } else {
      let polygons = []
      outestCircles.forEach((circle, index) => {
        polygons.push({pointsArr: [circle], outlineEdges: createEdges(circle, index, 0)})
      })
      for (let i = 0; i < holes.length; i++) {
        let hole = holes[i]
        for (let j = 0; j < polygons.length; j++) {
          let polygonObj = polygons[j]
          let holePoint = {x: (hole[0].x + hole[1].x) * 0.5, y: (hole[0].y + hole[1].y) * 0.5}
          if (isPointInEdges(holePoint, polygonObj.outlineEdges)) {
            polygonObj.pointsArr.push(hole)
            break
          }
        }
      }
      let target = polygons.map(it => it.pointsArr)
      return target
    }
  }

  function mergeVertices(vertices0, vertices1) {
    let map = [], mergeCount = 0;
    for (let i = 0; i < vertices0.length; i++) {
      for (let j = 0; j < vertices1.length; j++) {
        if (!map[j] && equals(vertices1[j], vertices0[i], true)) {
          vertices1[j] = vertices0[i];
          vertices1[j].shared = true;
          map[j] = 1;
          mergeCount++;
        }
      }
    }
    if (equals(vertices1[0], vertices1[vertices1.length - 1], true)) {
      vertices1[vertices1.length - 1] = vertices1[0];
      vertices1[0].sharedBySelf = true;
    }
    return mergeCount;
  }

  // , 
  function simplifyCircle(pointsCircle) {
    let keyPoints = [];
    for (let i = 0; i < pointsCircle.length; i++) {
      let pre = i - 1, nxt = i + 1;
      if (pre < 0) {
        pre = pointsCircle.length - 1;
      }
      if (nxt > pointsCircle.length - 1) {
        nxt = 0;
      }
      let prePoint = pointsCircle[pre], curPoint = pointsCircle[i], nxtPoint = pointsCircle[nxt];
      let flag = getPointOnVectorSide(prePoint, nxtPoint, curPoint);
      if (flag != ON_LINE_IN) {
        keyPoints.push(curPoint);
      }
    }
    return keyPoints;
  }

  function mergeEdges(edges) {
    let points = edges2Points(edges);
    let mapPointsReplace = {}, flag = false;
    for (let i = 0; i < points.length - 1; i++) {//
      let pt0 = points[i];
      for (let j = i+1; j < points.length; j++) {
        let pt1 = points[j];
        let dx = Math.abs(pt1.x - pt0.x), dy = Math.abs(pt1.y - pt0.y);
        if (dx < minimum && dy < minimum) {
          let pointId = pt1.x+","+pt1.y;
          mapPointsReplace[pointId] = pt0;
          flag = true;
        }
      }
    }
    if (!flag)
      return edges;
    let target = [], edgeSourceIds = [], edgeMap = {}, prepareRemove=[];
    for (let edge of edges) { //
      let fromId = edge.from.x+","+edge.from.y;
      let toId = edge.to.x+","+edge.to.y;
      if (mapPointsReplace[fromId])
        edge.from = mapPointsReplace[fromId];
      if (mapPointsReplace[toId])
        edge.to = mapPointsReplace[toId];
      if (edge.from == edge.to)
        continue; //
      let sourceId = edge.sourceId();
      if (edgeSourceIds.indexOf(sourceId) > -1) {
        if (prepareRemove.indexOf(edgeMap[sourceId]) < 0)
          prepareRemove.push(edgeMap[sourceId] );
        continue;
      }
      edgeMap[sourceId] = edge;
      edgeSourceIds.push(sourceId);
      target.push(edge);
    }
    return arrAsubB(target, prepareRemove);
  }

  //  
  function parseToCircles(verticesHoles, noSimplify) {
    let vecLst = verticesHoles[0], holeIndices = verticesHoles[1];
    let circles = [], circles2 = [];
    if (holeIndices.length > 0) {
      for (let i = 0; i < holeIndices.length; i++) {
        let arr;
        if (i == 0) {
          arr = vecLst.slice(0, holeIndices[i]);
        } else {
          arr = vecLst.slice(holeIndices[i - 1], holeIndices[i]);
        }
        circles.push(arr);
      }
      circles.push(vecLst.slice(holeIndices[holeIndices.length - 1], vecLst.length));
    } else {
      circles = [vecLst];
    }
    circles = circles.map(circle => removeNearDuplicate(circle) );
    circles.forEach((circle, index) => {
      removeRags(circle);
      if (index == 0) {
        if (isClockwiseSquence(circle)) circle.reverse();
      } else {
        if (!isClockwiseSquence(circle)) circle.reverse();
      }
      if (!noSimplify) {
        circles2.push(simplifyCircle(circle));
      }
    });
    return noSimplify ? circles : circles2;
  }

  function splitTwoEdges(edges0, edges1) {
    let crossLst = [];
    for (let i = 0; i < edges0.length; i++) {
      let a = edges0[i];
      for (let j = 0; j < edges1.length; j++) {
        let b = edges1[j];
        let cross = lineSegmentCross(a.from, a.to, b.from, b.to);
        if (cross == null) {
          continue;
        }

        let pt = a.addSplitBy(cross);
        if (pt)
          crossLst.push(pt);
        pt = b.addSplitBy(cross);
        if (pt)
          crossLst.push(pt);
      }
    }
    return crossLst.length > 0;
  }

  function splitEdgesOfCircles(edges4Circles0, edges4Circles1) {
    let mergeSuccess = false;
    for (let edges0 of edges4Circles0) {
      for (let edges1 of edges4Circles1) {
        let splited = splitTwoEdges(edges0, edges1);
        mergeSuccess = mergeSuccess || splited;
      }
    }
    return mergeSuccess;
  }

  // 
  function processPrecision(vLst0, vLst1) {
    if (precision < 0) {
      return;
    }
    let objXyLst = vLst0;
    if (vLst1) {
      objXyLst = vLst0.concat(vLst1);
    }
    for (let i = 0; i < objXyLst.length; i++) {
      let obj = objXyLst[i];
      obj.x = parseFloat(obj.x.toFixed(precision));
      obj.y = parseFloat(obj.y.toFixed(precision));
    }
  }

  function edges2Points(edges) {
    let points = [];
    if (edges.length > 0) {
      edges.forEach(edge => {
        if (points.indexOf(edge.from) < 0)
          points.push(edge.from);
        if (points.indexOf(edge.to) < 0)
          points.push(edge.to);
      });
    }
    return points;
  }

  //A - BarrA arrB
  function arrAsubB(arrA, arrB) {
    arrB.forEach(it => {
      let ind = arrA.indexOf(it);
      if (ind >= 0) {
        arrA.splice(ind, 1);
      }
    });
    return arrA;
  }

  //A  B arrA arrB
  function arrAonB(arrA, arrB) {
    let shared = [];
    arrB.forEach(it => {
      let ind = arrA.indexOf(it);
      if (ind >= 0) {
        shared.push(it);
      }
    });
    arrA.splice(0, arrA.length);
    arrA.push(...shared);
    return arrA;
  }

  function boolPolygons(verticesHolesA, verticesHolesB, boolType) {
    processPrecision(verticesHolesA[0], verticesHolesB[0]);
    let map = mergeVertices(verticesHolesA[0], verticesHolesB[0]);
    let sharedCount = verticesHolesB[0].filter(it => it.shared).length;
    let circlesA = parseToCircles(verticesHolesA);
    let circlesB = parseToCircles(verticesHolesB);
    if (boolType == bool_a_sub_b) {
      circlesB.forEach(circle => circle.reverse())
    }
    let edges4Circles0 = createEdges4Circles(circlesA, 0);
    let edges4Circles1 = createEdges4Circles(circlesB, 1);
    let mergeSuccess = splitEdgesOfCircles(edges4Circles0, edges4Circles1);

    if (!mergeSuccess && sharedCount < 2) {
      let outlineA = circlesA[0], outlineB = circlesB[0]
      let edgesA = createEdges(outlineA, 0, 0)
      let edgesB = createEdges(outlineB, 1, 0)
      for (let i = 0; i < outlineB.length; i++) {
        let target = isPointInEdges0(outlineB[i], edgesA)
        if (target === POLYGON_OUTSIDE) {
          break
        } else if (target === POLYGON_INSIDE) { //polygon B totally inside polygon A
          if (boolType === bool_union) {
            return [verticesHolesA]
          } else if (boolType === bool_intersection) {
            return [verticesHolesB]
          } else if (boolType === bool_a_sub_b) {
            let holeIndices = verticesHolesA[1]
            holeIndices.push(verticesHolesA[0].length)
            verticesHolesA[0].push(...outlineB)
            let polygonsCreatedByHoles = []
            for (let j = 1; j < circlesB.length; j++) {
              let polygon = [circlesB[j], []]
              polygonsCreatedByHoles.push(polygon)
            }
            return [verticesHolesA].concat(polygonsCreatedByHoles)
          }
        }
      }

      for (let i = 0; i < outlineA.length; i++) {
        let target = isPointInEdges0(outlineA[i], edgesB)
        if (target === POLYGON_OUTSIDE) {
          break
        } else if (target === POLYGON_INSIDE) { //polygon A totally inside polygon B
          if (boolType === bool_union) {
            return [verticesHolesB]
          } else if (boolType === bool_intersection) {
            return [verticesHolesA]
          } else if (boolType === bool_a_sub_b) {
            return []
          }
        }
      }
      return [];
    }
    let splited0 = [], splited1 = [];
    edges4Circles0.forEach(edges4Circle => {
      let arr = [];
      edges4Circle.forEach(edge => arr.push(...processSplitBy(edge)));
      splited0.push(arr);
    });
    edges4Circles1.forEach(edges4Circle => {
      let arr = [];
      edges4Circle.forEach(edge => arr.push(...processSplitBy(edge)));
      splited1.push(arr);
    });
    let outlineAndHolesLst = findCircles(splited0, splited1, circlesA, circlesB, boolType);
    let target = []
    for (let j = 0; j < outlineAndHolesLst.length; j++) {
      let outlineAndHoles = outlineAndHolesLst[j]
      let holeIndices = [], allVertices = [];
      outlineAndHoles.forEach((it, index) => {
        let simplified = simplifyCircle(it)
        outlineAndHoles[index] = simplified
        allVertices = allVertices.concat(simplified)
      });
      if (outlineAndHoles.length > 1) {
        let curHoleIndex = 0;
        for (let i = 1; i < outlineAndHoles.length; i++) {
          let len = outlineAndHoles[i - 1].length;
          curHoleIndex += len;
          holeIndices.push(curHoleIndex);
        }
      }
      target.push([allVertices, holeIndices])
    }
    return target;
  }

  //vertices0 vertices1 
  //v0, v1, v3, v4, v5)
  // 1m0, m1, m2), 2n0, n1, n2, n3)
  //   [[v0, v1, v3, v4, v5, m0, m1, m2, n0, n1, n2, n3], [6, 9]]
  function mergePolygons(verticesHoles0, verticesHoles1) {
    let target = boolPolygons(verticesHoles0, verticesHoles1, bool_union)
    return target.length > 0 ? target[0] : target
  }

//--------------------------------------
  let Polygon = function (edges, isClockwise) {//
    this.getPoints = function () {
      let points = [];
      for (let edge of this.edges) {
        points.push(edge.from);
      }
      return points;
    };
    this.contain = function (point) {
      return isInConvexPolygons(point, convexPolygons);
    };
    this.containEdges = function(edges) {
      let points = edges2Points(edges);
      for (let point of points) {
        if (!this.contain(point)) {
          return false;
        }
      }
      return true;
    };

    this.edges = edges;
    if (isClockwise != undefined) {
      this.isClockwise = isClockwise;
    } else {
      this.isClockwise = isClockwiseSquence(this.getPoints());
    }
    let convexPolygons = [];
    concavToConvex(this.getPoints(), convexPolygons);
  };

  //
  function specialSegmentCross(p0, p1, q0, q1) {
    let target = [];
    let flag0 = getPointOnVectorSide(p0, p1, q0);
    let flag1 = getPointOnVectorSide(p0, p1, q1);
    let arr = [ON_LINE_OUT, ON_LINE_IN];
    if (arr.indexOf(flag0) < 0 || arr.indexOf(flag1) < 0) {
      return target;
    }
    let pBox = findBox2([p0, p1]);
    if (pBox.contain(q1) ) {
      target.push(q1);
    }
    if (pBox.contain(q0) ) {
      target.push(q0);
    }
    let qBox = findBox2([q0, q1]);
    if (qBox.contain(p1)) {
      target.push(p1);
    }
    if (qBox.contain(p0)) {
      target.push(p0);
    }
    return target;
  }

  //noSlice = true, latestEdge
  function sliceCircleEdges(segments, latestEdge, crossPoints, noSlice) {
    let beSliceObjs = [];
    let needUpdate = false;
    for (let edge of segments) {
      if (edge.to === latestEdge.from) {
        continue;
      }
      let cross = lineSegmentCross(edge.from, edge.to, latestEdge.from, latestEdge.to);
      if (cross == null) {
        continue;
      }
      needUpdate = true;
      crossPoints.push(cross);
      let pt = edge.addSplitBy(cross);
      if (pt)
        beSliceObjs.push({originEdge: edge, slicePoint: pt});
      latestEdge.addSplitBy(cross);
    }

    return {
      latestEdges: noSlice ? [latestEdge] : processSplitBy(latestEdge),
      beSlice: beSliceObjs,
      needUpdate: needUpdate
    };
  }

  function formatEdges2(edges2) {
    let allPoints = [], indices = [];
    let sum = 0;
    edges2.forEach(edges => {
      edges.forEach(edge => allPoints.push(edge.from));
      sum += edges.length;
      indices.push(sum);
    });
    indices.pop();
    return [allPoints, indices];
  }

  function getCrossPointsBySliceEdges(sliceEdges) {
    let crossArr = [];
    if (sliceEdges.length < 2) {
      return crossArr;
    }
    for (let i = 0; i < sliceEdges.length - 1; i++) {
      let beSlicing = sliceEdges[i];
      for (let j = i + 1; j < sliceEdges.length; j++) {
        let edge = sliceEdges[j];
        if (beSlicing.to === edge.from)
          continue;
        let cross = lineSegmentCross(beSlicing.from, beSlicing.to, edge.from, edge.to);
        // let arr = [];   //[beSlicing.from, beSlicing.to, edge.from, edge.to];
        if (cross) {
          crossArr.push(cross);
          beSlicing.addSplitBy(cross);
          edge.addSplitBy(cross);
        }
      }
    }
    return crossArr;
  }

  function splitSliceEdges(sliceEdges, crossPoints) {
    let points = getCrossPointsBySliceEdges(sliceEdges);
    crossPoints.push(...points);
    let splitted = [];
    sliceEdges.forEach(edge => splitted.push(...processSplitBy(edge)));
    return splitted;
  }

  //edges0edges1
  //
  //findUsefulEdges
  function findNextEdge4Slicing(curEdge, edges0, edges1) {
    let first = edges0, second = edges1;
    for (let i = 0; i < first.length; i++) {
      let edge = first[i];
      if (edge.from === curEdge.to && curEdge.reverseEdge() != edge) {
        return edge;
      }
    }
    let candidate = null;
    for (let i = 0; i < second.length; i++) {
      let edge = second[i];
      if (edge.from === curEdge.to) {
        if (edge.holeId != curEdge.holeId) {
          return edge;
        }
        candidate = edge;
      }
    }
    return candidate;
  }

  //123  45
  function findSharePointHolesLst(circles) {
    let target = [];
    for (let i = 0; i < circles.length; i++) {
      let circleI = circles[i];
      for (let j = i + 1; j < circles.length; j++) {
        let circleJ = circles[j];
        let count = mergeVertices(circleI, circleJ);
        if (count > 0) {
          let subLst = target.filter(arr => arr.indexOf(i) > -1 || arr.indexOf(j) > -1);
          let exists = subLst.length > 0;
          let sub = [];
          if (subLst.length > 0) {
            sub = subLst[0];
          }
          if (sub.indexOf(i) < 0)
            sub.push(i);
          if (sub.indexOf(j) < 0)
            sub.push(j);
          if (!exists) {
            target.push(sub);
          }
        }
      }
    }
    return target;
  }

  //edges
  // 01..., n n 
  //1.  
  //2.  0   0 
  //3.  n (n>0)   n (n>0) 
  //4.  0   n (n>0) n  0, sharePointHolesLst
  //5.  n (n>0)  m (m>0), n  msharePointHolesLst
  //n  mn  m n  mnhhmm, h, n
  function findUsefulEdges(edges, sharePointHolesLst) {
    let fromStack = [], fromIndices = [], fromPoints = [];
    for (let i = 0; i < edges.length; i++) {
      let edge = edges[i];
      if (edge.from.onCircle != undefined) {
        fromStack.push(edge.from.onCircle);
        fromIndices.push(i);
      }
      if (edge.to.onCircle != undefined) {
        let toHoleId = edge.to.onCircle;
        let index = fromStack.indexOf(toHoleId);
        let lst = sharePointHolesLst.filter(arr => arr.indexOf(toHoleId) > -1);
        if (index < 0 && lst.length > 0 && fromStack.length > 0) {
          let sharePointHoles = lst[0];
          for (let j = 0; j < sharePointHoles.length; j++) {//45
            let holeId = sharePointHoles[j];
            index = fromStack.indexOf(holeId);
            if (index >= 0)
              break;
          }
        }
        if (index >= 0) {
          return edges.slice(fromIndices[index], i + 1);
        }
      }

      fromPoints.push(edge.from);
      let ind = fromPoints.indexOf(edge.to);
      if (ind >= 0) { //, 1
        return edges.slice(ind, i + 1);
      }
    }
    return [];
  }

  function createHolesArr(edges4Circles) {
    let target = [];
    for (let i = 0; i < edges4Circles.length; i++) {
      let hole = edges4Circles[i];
      target[i] = {len: hole.length, used: 0, holeId: i};
    }
    return target;
  }
  function changeHolesArr(arr, edge) {
    if (edge.belong > -1) {
      arr[edge.holeId].used++;
    }
  }

  //sliceEdges  
  //
  //
  //
  function sliceOutOne(edges4CirclesFlatted, sliceEdges, firstEdge, edges4Circles) {
    let edges = [firstEdge], involvedHoles = [];
    let holesTracks = createHolesArr(edges4Circles);
    changeHolesArr(holesTracks, firstEdge);
    let nextEdge = findNextEdge4Slicing(firstEdge, sliceEdges, edges4CirclesFlatted);
    while (nextEdge != null) {
      if (nextEdge.holeId > -1 && involvedHoles.indexOf(nextEdge.holeId) < 0)
        involvedHoles.push(nextEdge.holeId);
      changeHolesArr(holesTracks, nextEdge);
      if (edges.indexOf(nextEdge) > -1) {//
        console.log("error happen in sliceOutOne");
        return {edges: edges, holes: involvedHoles, error:true, nextEdge: nextEdge};
      }
      edges.push(nextEdge);
      if (nextEdge.to == firstEdge.from)
        break;
      nextEdge = findNextEdge4Slicing(nextEdge, sliceEdges, edges4CirclesFlatted);
    }
    let arr = holesTracks.filter(it => it.len == it.used);
    if (arr.length > 0) {//
      let needlessHoles = arr.map(it => it.holeId);
      edges = edges.filter(edge => needlessHoles.indexOf(edge.holeId) < 0);
      arrAsubB(involvedHoles, needlessHoles);
    }
    return {edges: edges, holes: involvedHoles};
  }


  //sliceEdges 
  function slicePolygonWithEdges(edges4Circles, sliceEdges) {
    let remainedHoleIndices = []; // 
    let edges4CirclesFlatted = [];
    edges4Circles.forEach((it, index) => {
      edges4CirclesFlatted.push(...it);
      if (index > 0) {
        remainedHoleIndices.push(index);
      }
    });
    let sliceEdgesPair = [];
    sliceEdges.forEach(it => sliceEdgesPair.push(it.reverseEdge()));
    sliceEdgesPair = sliceEdges.concat(sliceEdgesPair);
    let newPolygonsEdges = [];
    while (sliceEdgesPair.length > 0) {
      let firstEdge = sliceEdgesPair[0];
      let obj = sliceOutOne(edges4CirclesFlatted, sliceEdgesPair, firstEdge, edges4Circles);
      if (obj.error)
        return [edges4Circles];
      newPolygonsEdges.push(obj.edges);
      arrAsubB(sliceEdgesPair, obj.edges);
      arrAsubB(remainedHoleIndices, obj.holes);
    }

    let holesExists = [], groups = [];
    remainedHoleIndices.forEach(index => holesExists.push(edges4Circles[index]) );
    let newPolygons = [], newHolePolygon = null;
    newPolygonsEdges.forEach(edges => {
      let polygon = new Polygon(edges);
      if (polygon.isClockwise) {  //slicePolygon
        // findUsefulEdgesslicePolygonWithEdges
        if (newHolePolygon) console.log("warning, new hole polygon already exists");
        newHolePolygon = polygon;
      } else {
        newPolygons.push(polygon);
      }
    });
    for (let i = 0; i < newPolygons.length; i++) {
      let polygon = newPolygons[i];
      groups.push([polygon.edges]);
      for (let j = 0; j < holesExists.length; j++) {
        let point = holesExists[j][0].getMidPoint();
        if (polygon.contain(point)) {
          groups[i].push(holesExists[j]);
        }
      }
      holesExists = holesExists.filter(it => groups[i].indexOf(it) < 0);
    }
    if (newHolePolygon) { //
      let outline = [edges4Circles[0] ];
      holesExists.forEach(edges => outline.push(edges) );
      outline.push(newHolePolygon.edges);
      return [outline].concat(groups);
    }
    return groups;
  }

  function getHoleId(pointsCircles, point) {
    for (let i = 0; i < pointsCircles.length; i++) {
      let circle = pointsCircles[i];
      if (circle.indexOf(point) > -1) {
        return i;
      }
    }
    return -2;
  }

  function edgesLst2PointsCircles(edges4Circles) {
    let pointsCircles = [];
    edges4Circles.forEach( (edges, index) => {
      pointsCircles.push(edges2Points(edges) );
      edges.forEach(edge => edge.holeId = index);
    } );
    return pointsCircles;
  }

  function slicePolygonRecursively(edges4Circles, sliceEdges) {
    let pointsCircles = edgesLst2PointsCircles(edges4Circles);
    //   
    let sharePointHolesLst = findSharePointHolesLst(pointsCircles); //
    if (sharePointHolesLst.length > 0) {
      let edges4CirclesOld = edges4Circles.slice();
      edges4Circles = createEdges4Circles(pointsCircles, 20);
      //belong
      for (let i = 0; i < edges4Circles.length; i++) {
        let edges = edges4Circles[i];
        for (let j = 0; j < edges.length; j++) {
          edges[j].belong = edges4CirclesOld[i][j].belong;
        }
      }
    }
    // onCircle
    let slicePoints = edges2Points(sliceEdges);
    for (let point of slicePoints) {
      point.onCircle = undefined;
      let holeId = getHoleId(pointsCircles, point);
      if (holeId >= 0)
        point.onCircle = holeId;
    }
    let polygons2 = [];
    pointsCircles.forEach(circle => {
      let polygons = [];
      concavToConvex(circle, polygons);
      polygons2.push(polygons);
    });
    // 
    let sliceEdgesCopy = sliceEdges.filter(it => isPointInPolygonWithHoles(it.getMidPoint(), polygons2));

    let usingEdges = findUsefulEdges(sliceEdgesCopy, sharePointHolesLst);
    if (usingEdges.length == 0)
      return [edges4Circles];
    let edgesLst = slicePolygonWithEdges(edges4Circles, usingEdges);

    sliceEdgesCopy = arrAsubB(sliceEdges.slice(), usingEdges);
    if (sliceEdgesCopy.length == 0) {
      return edgesLst;
    }
    let target = [];
    for (let edges2 of edgesLst) {
      let edges3 = slicePolygonRecursively(edges2, sliceEdgesCopy);
      target = target.concat(edges3);
    }
    return target;
  }

  function getArea(contour) {
    let n = contour.length;
    let a = 0.0;

    for (let p = n - 1, q = 0; q < n; p = q++) {

      a += contour[p].x * contour[q].y - contour[q].x * contour[p].y;

    }

    return a * 0.5;
  }

  function getPolygonArea(polygon) {
    let contour = polygon[0], holeIndices = polygon[1], holesVectices = [], vertices = contour;
    if (holeIndices.length > 0) {
      for (let i = 0; i < holeIndices.length; i++) {
        if (i == 0) {
          vertices = contour.slice(0, holeIndices[i]);
        } else {
          let arr = contour.slice(holeIndices[i - 1], holeIndices[i]);
          holesVectices.push(arr);
        }
      }
      holesVectices.push(contour.slice(holeIndices[holeIndices.length - 1], contour.length));
    }
    let area = Math.abs(getArea(vertices));
    holesVectices.forEach(arr => area -= Math.abs(getArea(arr)));
    return area;
  }
  function getPolygonsArea(polygons) {
    let area = 0;
    polygons.forEach(it => area += getPolygonArea(it));
    return area;
  }
  //
  function getSliceLinesPolygons(slicePoints) {
    let pts = slicePoints.map(p => {
      return {x: p.x, y: p.y}
    });
    let box = findBox2(pts);
    box.scale(2);
    let v0 = box.min, v1 = {x: box.max.x, y: box.min.y};
    let v2 = box.max, v3 = {x: box.min.x, y: box.max.y};
    //v0, v1, v2, v3 
    let edges3 = slicePolygon([[v0, v1, v2, v3], []], slicePoints, true);
    let polygonsFromSliceLines = [];
    for (let j = 0; j < edges3.length; j++) {
      let edges2 = edges3[j];
      let valid = true;
      for (let i = 0; i < edges2.length; i++) {
        let edges = edges2[i];
        for (let h = 0; h < edges.length; h++) {
          let edge = edges[h];
          if (edge.belong >= 0) {
            valid = false;
            break;
          }
        }
        if (!valid) {
          break;
        }
      }
      if (valid) {
        polygonsFromSliceLines.push(formatEdges2(edges2));
      }
    }
    return polygonsFromSliceLines;
  }
  // verticesHoles, 
  function slicePolygon(verticesHoles, slicePoints, noFormat) {
    let vertices0 = verticesHoles[0];
    mergeVertices(vertices0, slicePoints);
    let circles = parseToCircles(verticesHoles);
    let edges4Circles = createEdges4Circles(circles, 0);

    let slicedEdges0 = createEdges(slicePoints, -1, -1);
    if (!noFormat)
      slicedEdges0.pop();

    let slicedEdges = [];
    //  
    slicedEdges0.forEach(edge0 => {
      for (let i = 0; i < edges4Circles.length; i++) {
        for (let j = 0; j < edges4Circles[i].length; j++) {
          let edge = edges4Circles[i][j];
          let arr = edge0.subEdge(edge), len = arr.length;
          if (len == 0 || len == 2 || arr[0] !== edge0) {
            slicedEdges.push(...arr);
            return;
          }
        }
      }
      slicedEdges.push(edge0);
    });

    let crossPoints = [], segments = [];
    for (let i = 0; i < slicedEdges.length; i++) {  //
      let edgeFromSlice = slicedEdges[i];
      for (let j = 0; j < edges4Circles.length; j++) {
        let edges = edges4Circles[j];
        let points = [];
        let obj = sliceCircleEdges(edges, edgeFromSlice, points, true);//
        for (let sliceObj of obj.beSlice) {
          let beSliceEdge = sliceObj.originEdge, slicePoint = sliceObj.slicePoint;
          let index = edges.indexOf(beSliceEdge);
          let sliced = beSliceEdge.sliceBy(slicePoint); //
          edges.splice(index, 1, ...sliced);
        }
        crossPoints.push(...points);
      }
      segments.push(...processSplitBy(edgeFromSlice));  //  
    }
    //
    let cross4Sliced = [];
    segments = splitSliceEdges(segments, cross4Sliced);
    segments = mergeEdges(segments);
    let edges3 = slicePolygonRecursively(edges4Circles, segments);

    if (noFormat) {
      return edges3;
    }
    let formattedLst = [];
    edges3.forEach(edges2 => formattedLst.push(formatEdges2(edges2)));
    return formattedLst;
  }

  function slicePolygons(verticesHolesLst, slicePoints) {
    let target = [];
    for (let i = 0; i < verticesHolesLst.length; i++) {
      let verticesHoles = verticesHolesLst[i];
      let sliced = slicePolygon(verticesHoles, slicePoints);
      target.push(...sliced);
    }
    return target;
  }

  let that = {
    setPrecision(num) {
      precision = num;
      minimum = Math.pow(0.1, num);
    },
    arrAsubB: arrAsubB,
    triangulate(arr) {//
      let lst = [];
      for (let i = 0; i < arr.length; i += 2) {
        lst.push({x: arr[i], y: arr[i + 1], id: ~~(i / 2)});
      }
      let triangles = getTriangles4ConcavPolygon(lst);
      let target = [];
      for (let i = 0; i < triangles.length; i++) {
        let triangle = triangles[i];
        if (triangle.isClockwise()) {
          triangle.changeSequence();
        }
        target = target.concat(triangle.getIndices());
      }
      return target;
    },
    lineSegmentCross: lineSegmentCross,
    equals: equals,
    concavToConvex: concavToConvex,
    isPointInPolygonWithHoles: isPointInPolygonWithHoles,
    getAngleWithX_axis: getAngleWithX_axis,
    mergePolygons: mergePolygons,
    polygonAsubB(verticesHolesA, verticesHolesB) {
      return boolPolygons(verticesHolesA, verticesHolesB, bool_a_sub_b)
    },
    polygonAintersectB(verticesHolesA, verticesHolesB) {
      return boolPolygons(verticesHolesA, verticesHolesB, bool_intersection)
    },
    slicePolygon: slicePolygon,
    //
    getSliceLinesPolygons: getSliceLinesPolygons,
    getPolygonsArea: getPolygonsArea,
  };
  return that;
};

let TriCut = TriangleCut();
export default TriCut;

