<template>
<div class="boxInTab">
  <div>
    <span class="itemPropertyLabel"></span>
    <square-buttons-group v-model="activeShape" @change="onClickAddPolygon">
      <square-button icon-class="iconjuxing" value="0" v-tip.dark="tips[0]"></square-button>
      <square-button icon-class="iconyuanxing" value="1" v-tip.dark="tips[1]"></square-button>
      <square-button icon-class="iconduobianxing" value="2" v-tip.dark="tips[2]"></square-button>

    </square-buttons-group>
  </div>

  <div class="itemProperty">
    <div class="itemPropertyLabel" ></div>
    <div class="inputWidth">
      <el-input v-model="defaultHeight" @input="onInputDefaultHeight($event)" :numberOnly="true"/>
    </div>
  </div>

  <div class="itemProperty">
    <div class="itemPropertyLabel" ></div>
    <div class="inputWidth">
      <el-input v-model="defaultFillColor" @colorChange="updateDefaultFillColor"
                :colorAlphaEnabled="true" ref="fill"/>
    </div>
  </div>
  <div class="itemProperty">
    <div class="itemPropertyLabel" ></div>
    <div class="inputWidth">
      <el-input v-model="defaultLineColor" @colorChange="updateDefaultLineColor"
                :colorEnabled="true" ref="line"/>
    </div>
  </div>
  <div>
    <input type="button" value="" @click="onclickRandHeatmap()">
    <input type="button" value="" @click="onclickClearHeatmap()">
  </div>
  <div>
    <input type="button" value="" @click="applyHeatmapToSelected()">
    <input type="button" value="" @click="unapplyHeatmapToSelected()">
  </div>
  <div>
    <input type="button" :value="editHeatmapBtnText" @click="toggleHeatmapEdit()">
    <input type="text" v-model="heatmapStr">
    <input type="button" value="load" @click="loadHeatmap()">
  </div>
</div>
</template>

<script>
  import SquareButtonsGroup from "@/components/common/SquareButtonsGroup";
  import SquareButton from "@/components/common/SquareButton";
  import ElInput from '@/components/element/input';
  import MainScene from '@/3d/js/MainScene';
  import Layers from "../../../3d/js/Layers";
  import * as M from "../../../store/mutations-types";

  export default {
    name: "BoxDrawGraphic",
    mounted() {
      // let defaultFillColor = document.getElementById('defaultFillColor');
      // let defaultLineColor = document.getElementById('defaultLineColor');
      // defaultFillColor.onFineChange = this.updateDefaultLineColor;
      // defaultLineColor.onFineChange = this.updateDefaultLineColor;
      this.defaultHeight = Layers.getDefaultHeight();
      this.defaultFillColor = Layers.getDefaultFillColor();
      this.defaultLineColor = Layers.getDefaultLineColor();
      Layers.floorLayer.addEventListener("default color change", () => {
        this.defaultFillColor = Layers.getDefaultFillColor();
        this.defaultLineColor = Layers.getDefaultLineColor();
        this.defaultHeight = Layers.getDefaultHeight();
        this.$refs.fill.updateColor(this.defaultFillColor);
        this.$refs.line.updateColor(this.defaultLineColor);
      });
    },
    data() {
      return {
        activeShape: '',
        defaultHeight: 0.1,
        defaultFillColor: '1bbeff 0.5',
        defaultLineColor: 'ffcc00',
        tips: ["", "", ""],
        editingHeatmap: false,
        heatmapStr: "",
      }
    },
    computed: {
      editHeatmapBtnText() {
        return this.editingHeatmap ? "" : ""
      }
    },
    methods: {
      onClickAddPolygon() {
        // console.log("this.activeShape: " + this.activeShape);
        if (this.activeShape == 0) {
          MainScene.polgyonTool().startAddRectangle();
        } else if (this.activeShape == 1) {
          MainScene.polgyonTool().startAddCircle();
        } else if (this.activeShape == 2) {
          MainScene.polgyonTool().startAddPolygon();
        }
        let mousedown = (event) => {
          // console.log("event.button : " + event.button);
          if (event.button === 2) {
            MainScene.getDom().removeEventListener('mousedown', mousedown);
            this.activeShape = '';
          }
        }
        MainScene.getDom().addEventListener('mousedown', mousedown);
      },
      onInputDefaultHeight(val) {
        let valNum = parseFloat(val);
        if (isNaN(valNum))
          return;
        Layers.setDefaultHeight(valNum );
      },
      updateDefaultLineColor(jscolor) {
        let lineColor = parseInt('0x' + jscolor);
        Layers.setDefaultLineColor(lineColor);
      },
      updateDefaultFillColor(jscolor) {
        let arr = jscolor.split(' ');
        let fillColor = parseInt('0x' + arr[0]);
        let fillOpacity = parseFloat(arr[1]);
        Layers.setDefaultFillColor(fillColor, fillOpacity);
      },
      onclickRandHeatmap() {
        MainScene.showRandomHeatmap();
      },
      onclickClearHeatmap() {
        MainScene.clearHeatmap();
      },
      applyHeatmapToSelected() {
        MainScene.applyHeatmapToSelected();
      },
      unapplyHeatmapToSelected() {
        MainScene.unapplyHeatmapToSelected();
      },
      toggleHeatmapEdit() {
        this.editingHeatmap = !this.editingHeatmap;
        if (this.editingHeatmap) {
          this.heatmapStr = ""
          MainScene.polgyonTool().startEditHeatmap();
        } else {
          this.heatmapStr = JSON.stringify(MainScene.polgyonTool().getHeatMapEditObj() );
          MainScene.polgyonTool().endEditHeatmap();
        }
      },
      loadHeatmap() {
        MainScene.polgyonTool().loadFloorHeatMap(JSON.parse(this.heatmapStr));
      },
    },
    components: {
      SquareButton, SquareButtonsGroup, ElInput
    }
  }
</script>

<style scoped>
</style>
