<template>
  <div>
    <div id="WebGL-output" ref="webglDiv" style="position: relative">
    </div>
    <div class="contextmenu" id="contextSingle">
      <ul>
        <li @click="toggleEditPoints()"><span class="iconfont icontianjiajiedian"/><span>{{editTxt}}</span></li>
        <li @click="toggleScaleItem()"><span class="iconfont iconlashen"/><span>{{scaleTxt}}</span> </li>
        <li @click="toggleRotateItem()"><span class="iconfont iconxuanzhuan"/><span>{{rotateTxt}}</span></li>
        <li @click="sliceItem()"><span class="iconfont iconmuban"/><span></span></li>
        <li @click="copyItem()"><span class="iconfont iconfuzhi"/><span></span></li>
        <li @click="deleteItem()"><span class="iconfont iconshanchu1"/><span></span></li>
        <li @click="topItems()"><span class="iconfont iconlashen"/><span></span></li>
        <li @click="bottomItems()"><span class="iconfont iconlashen"/><span></span></li>
      </ul>
    </div>
    <div class="contextmenu" id="contextMulti">
      <ul>
        <li @click="copyItem()"><span class="iconfont iconfuzhi"/><span></span></li>
        <li @click="deleteItem()"><span class="iconfont iconshanchu1"/><span></span></li>
        <li @click="mergeItems()"><span class="iconfont iconhebing"/><span></span></li>
        <li @click="itemAsubB()"><span class="iconfont iconhebing"/><span>A-B</span></li>
        <li @click="itemAintersectB()"><span class="iconfont iconhebing"/><span>AB</span></li>
        <li @click="sliceItem()"><span class="iconfont iconmuban"/><span></span></li>
        <li @click="topItems()"><span class="iconfont iconlashen"/><span></span></li>
        <li @click="bottomItems()"><span class="iconfont iconlashen"/><span></span></li>
      </ul>
    </div>
    <div class="contextmenu" id="contextPathItem">
      <ul>
        <li @click="editPathPoints()"><span></span></li>
        <li @click="removeSelectedPaths()"><span></span></li>
      </ul>
    </div>
    <div class="contextmenu" id="contextRef">
      <ul>
        <li @click="toggleScaleItem()"><span class="iconfont iconlashen"/><span>{{scaleTxt}}</span> </li>
        <li @click="toggleRotateItem()"><span class="iconfont iconxuanzhuan"/><span>{{rotateTxt}}</span></li>
      </ul>
    </div>
    <div class="creatingDiv" id="createMeshDiv">
      <input type="number">
      <input type="number">
    </div>
    <div class="tip-measuring" style="display: none" ref="tipMeasuring"></div>
  </div>
</template>

<script>
  import Main from './js/MainScene';
  import Layers from "./js/Layers";
  import Config from "./js/configurable/Config";
  import Serializer from "./js/util/Serialization";
  import Notification from "../components/element/notification";
  import Shortcuts from "../3d/js/tool/Shortcuts";
  import {getCSSValue} from "../external_lib/util";

  export default {
    name: "Main3d",
    data() {
      this.tipDoms = [];
      return {
        scaleTxt: '',
        rotateTxt: '',
        editTxt: ''
      };
    },
    mounted() {
      Main.init(document.getElementById('createMeshDiv'));
      if (Config.code.loadSavingFromStart) {
        Serializer.loadFromLocal(false);
      }
      Main.scene().addEventListener("slice error", () => {
        Notification.info({message:"", position:'top-right'});
      });
      Main.scene().addEventListener("invalid num input", () => {
        Notification.info({message:"", position:'top-right'});
      });
      let _this = this;
      document.addEventListener('contextmenu', onContextMenu);
      function onContextMenu(env){
        _this.scaleTxt = Main.polgyonTool().isStatusScalingMesh() ? '' : '';
        _this.rotateTxt = Main.polgyonTool().isStatusRotatingMesh() ? '' : '';
        _this.editTxt = Main.polgyonTool().isStatusEditingPoints() ? '' : '';

        let context;
        let e = env || window.event;
        let pathItems = Main.pathTool().getSelectedPathItems();
        if (pathItems.length > 0 && Layers.navPathTabSelected()) {
          context = document.getElementById("contextPathItem");
        } else if (Main.polgyonTool().getSelectedRef() != null) {
          context = document.getElementById("contextRef");
        } else {
          let len = Main.polgyonTool().getSelectedMeshes().length;
          if (len == 0 || !Main.contextMenuEnabled()) {
            return false;
          }

          if (len == 1) {
            context = document.getElementById("contextSingle");
          } else {
            context = document.getElementById("contextMulti");
          }
        }
        context.style.display = "block";

        let x = e.clientX;
        let y = e.clientY;

        let w = window.innerWidth;
        let h = window.innerHeight;

        context.style.left = Math.min(w-202,x)+"px";
        context.style.top = Math.min(h-330,y)+"px";
        document.addEventListener('click', onContextMenuClick);
        // 
        return false;
      }

      //   
      function onContextMenuClick(event){
        // let contextmenu = event.target.parentElement.parentElement.parentElement;
        let contextMulti = document.getElementById("contextMulti");
        contextMulti.style.display = 'none';
        let contextSingle = document.getElementById("contextSingle");
        contextSingle.style.display = 'none';
        let contextPathItem = document.getElementById("contextPathItem");
        contextPathItem.style.display = 'none';
        let contextRef = document.getElementById("contextRef");
        contextRef.style.display = 'none';

        document.removeEventListener('click', onContextMenuClick);
      }

      Main.scene().addEventListener("update tip", (event) => {
        let div = _this.$refs.tipMeasuring;
        div.style.display = 'block';
        div.mapCoord = event.mapCoord;
        if (event.isArea) {
          div.innerHTML = event.area + "m<sup>2</sup>"
        } else {
          div.innerText = event.meters + "m";
        }
        _this.updateTipPosition(div);
      });
      Main.scene().addEventListener("cancel add tips", () => {
        _this.$refs.tipMeasuring.style.display = 'none';
      });
      Main.scene().addEventListener("add tip", (event) => {
        _this.$refs.tipMeasuring.style.display = 'none';
        let tipDiv = document.createElement('div');
        tipDiv.classList.add('tip-measuring');
        tipDiv.classList.add('tip-measured');
        if (event.isArea) {
          tipDiv.innerHTML = event.area + "m<sup>2</sup>";  //event.areaMeters
        } else {
          tipDiv.innerText = event.meters + "m";
        }
        _this.$refs.webglDiv.appendChild(tipDiv);
        _this.tipDoms.push(tipDiv);
        tipDiv.mapCoord = event.mapCoord;
        _this.updateTipPosition(tipDiv);
      });
      Main.scene().addEventListener("remove all tips", (event) => {
        _this.$refs.tipMeasuring.style.display = 'none';
        _this.tipDoms.forEach(dom => dom.parentNode.removeChild(dom));
        _this.tipDoms = [];
      });
      Main.cameraControl().addEventListener('change', () => {
        _this.tipDoms.forEach(dom => _this.updateTipPosition(dom));
      });
    },
    methods: {
      toggleEditPoints() {
        Main.polgyonTool().toggleStatusOfEditingPoints();
      },
      toggleRotateItem() {
        Main.polgyonTool().toggleStatusRotatingMesh();
      },
      toggleScaleItem() {
        Main.polgyonTool().toggleStatusScalingMesh();
      },
      deleteItem() {
        Main.polgyonTool().removeSelectedMesh();
      },
      copyItem() {
        Main.polgyonTool().startCopyingMesh();
      },
      sliceItem() {
        Main.polgyonTool().startSlicingMesh();
      },
      mergeItems() {
        Main.polgyonTool().mergeMeshes();
      },
      itemAsubB() {
        // Main.polgyonTool().meshAsubB()
        Main.polgyonTool().meshAsubItems()
      },
      itemAintersectB() {
        Main.polgyonTool().meshAintersectB()
      },
      editPathPoints() {
        Main.pathTool().startEditingPoint();
      },
      removeSelectedPaths() {
        Main.pathTool().removeSelectedPaths();
      },
      topItems() {
        Main.polgyonTool().selectedMeshesReorder(true);
      },
      bottomItems() {
        Main.polgyonTool().selectedMeshesReorder(false);
      },
      updateTipPosition(tipDiv) {
        let screenCoord = Main.transToScreenCoord(tipDiv.mapCoord.clone());
        let padding = 5;
        let w0 = parseInt(getCSSValue(tipDiv, 'width') );
        let h0 = parseInt(getCSSValue(tipDiv, 'height') );
        let w = w0 + padding*2, h = h0 + 5;
        tipDiv.style.top = screenCoord.y - h + "px";
        tipDiv.style.left = screenCoord.x - w0/2 + "px";
      }
    }
  }
</script>

<style scoped>

.creatingDiv {
  position: fixed;
  /*background-color: #eedab5;*/
  /*border: 1px solid #eee;*/

  /*width: 120px;*/
  z-index: 2;
  top: 220px;
  left: 310px;
  display: none;
}
.creatingDiv input {
  width: 54px;
}

.contextmenu {
  width: 130px;
  /*border: 1px solid #999;*/
  /*box-shadow: 3px 3px 3px #ccc;*/
  background-color: rgba(238,238,238,1);
  box-shadow:0px 0px 25px 2px rgba(0, 0, 0, 0.3);
  position: absolute;
  top: 10px;
  left: 10px;
  display: none;
  z-index: 99;
}
.contextmenu li {
  height: 40px;
  line-height: 40px;
  padding-left: 10px;
  cursor: pointer;
  border-bottom: solid 1px rgba(204,204,204,1);
}
.contextmenu li>span:nth-child(2) {
  display: inline-block;
  padding: 0 10px;
}
.contextmenu li:hover {
  background-color: #ccc;
  font-weight: bold;
  color: #fff;
}

.itemSliceTx {
  padding-left: 25px;
}
.selectBox {
  border: 1px solid #55aaff;
  background-color: rgba(75, 160, 255, 0.3);
  position: fixed;
}
</style>
