/*
GCJ-02(WGS-84)
 pi: 
 a: 
 ee: 
 x_pi: 
 transformLat(lat, lon): 
 transformLng(lat, lon): 
 wgs2gcj(lat, lon): WGSGCJ
 gcj2bd(lat, lon): GCJ
*/

let pi = 3.14159265358979324;
let a = 6378245.0;
let ee = 0.00669342162296594323;
let x_pi = 3.14159265358979324 * 3000.0 / 180.0;

export function wgs2bd(lat, lon) {
  let _wgs2gcj = wgs2gcj(lat, lon);
  let _gcj2bd = gcj2bd(_wgs2gcj[0], _wgs2gcj[1]);
  return _gcj2bd;
}
export function bd2wgs(lat, lon) {
  let _bd2gcj = bd2gcj(lat, lon);
  let _gcj2wgs = gcj02towgs84(_bd2gcj[0], _bd2gcj[1]);
  return _gcj2wgs;
}
//wgs2bd(30.563119999999998, 104.04921080000001); [30.566882476258105, 104.05808460983931]
//bd2wgs(30.566882476258105, 104.05808460983931); [30.56063101298715, 104.05167698078606]
//30.56063101298715 - 30.563119999999998 = -0.0024889870128461666
//104.05167698078606 - 104.04921080000001 = 0.002466180786043992

export function gcj2bd(lat, lon) {
  let x = lon, y = lat;
  let z = Math.sqrt(x * x + y * y) + 0.00002 * Math.sin(y * x_pi);
  let theta = Math.atan2(y, x) + 0.000003 * Math.cos(x * x_pi);
  let bd_lon = z * Math.cos(theta) + 0.0065;
  let bd_lat = z * Math.sin(theta) + 0.006;
  return [ bd_lat, bd_lon ];
}

function bd2gcj(lat, lon) {
  let x = lon - 0.0065, y = lat - 0.006;
  let z = Math.sqrt(x * x + y * y) - 0.00002 * Math.sin(y * x_pi);
  let theta = Math.atan2(y, x) - 0.000003 * Math.cos(x * x_pi);
  let gg_lon = z * Math.cos(theta);
  let gg_lat = z * Math.sin(theta);
  return [ gg_lat, gg_lon ];
}

function wgs2gcj(lat, lon) {
  let dLat = transformLat(lon - 105.0, lat - 35.0);
  let dLon = transformLng(lon - 105.0, lat - 35.0);
  let radLat = lat / 180.0 * pi;
  let magic = Math.sin(radLat);
  magic = 1 - ee * magic * magic;
  let sqrtMagic = Math.sqrt(magic);
  dLat = (dLat * 180.0) / ((a * (1 - ee)) / (magic * sqrtMagic) * pi);
  dLon = (dLon * 180.0) / (a / sqrtMagic * Math.cos(radLat) * pi);
  let mgLat = lat + dLat;
  let mgLon = lon + dLon;
  return [ mgLat, mgLon ];
}

/**
 * GCJ-02  WGS-84
 * @param lng
 * @param lat
 * @returns {*[]}
 */
function gcj02towgs84(lng, lat) {
  if (outOfChina(lng, lat)) {
    return [lng, lat]
  } else {
    let dlat = transformLat(lng - 105.0, lat - 35.0);
    let dlng = transformLng(lng - 105.0, lat - 35.0);
    let radlat = lat / 180.0 * pi;
    let magic = Math.sin(radlat);
    magic = 1 - ee * magic * magic;
    let sqrtmagic = Math.sqrt(magic);
    dlat = (dlat * 180.0) / ((a * (1 - ee)) / (magic * sqrtmagic) * pi);
    dlng = (dlng * 180.0) / (a / sqrtmagic * Math.cos(radlat) * pi);
    let mglat = lat + dlat;
    let mglng = lng + dlng;
    return [lng * 2 - mglng, lat * 2 - mglat]
  }
};

//
function outOfChina(lng, lat) {
  if (lng < 72.004 || lng > 137.8347) {
    return true;
  }
  if (lat < 0.8293 || lat > 55.8271) {
    return true;
  }
  return false;
}

function transformLat(lng, lat) {
  let ret = -100.0 + 2.0 * lng + 3.0 * lat + 0.2 * lat * lat +
        0.1 * lng * lat + 0.2 * Math.sqrt(Math.abs(lng))
  ret += (20.0 * Math.sin(6.0 * lng * pi) + 20.0 *
      Math.sin(2.0 * lng * pi)) * 2.0 / 3.0
  ret += (20.0 * Math.sin(lat * pi) + 40.0 *
      Math.sin(lat / 3.0 * pi)) * 2.0 / 3.0
  ret += (160.0 * Math.sin(lat / 12.0 * pi) + 320 *
      Math.sin(lat * pi / 30.0)) * 2.0 / 3.0
  return ret
}

function transformLng(lng, lat) {
  let ret = 300.0 + lng + 2.0 * lat + 0.1 * lng * lng +
        0.1 * lng * lat + 0.1 * Math.sqrt(Math.abs(lng));
  ret += (20.0 * Math.sin(6.0 * lng * pi) + 20.0 *
      Math.sin(2.0 * lng * pi)) * 2.0 / 3.0;
  ret += (20.0 * Math.sin(lng * pi) + 40.0 *
      Math.sin(lng / 3.0 * pi)) * 2.0 / 3.0;
  ret += (150.0 * Math.sin(lng / 12.0 * pi) + 300.0 *
      Math.sin(lng / 30.0 * pi)) * 2.0 / 3.0
  return ret
}
//----------------------
/**
 * 
 * @param {Object} poi {y:, x:}
 */
export function getMercator(poi) {//{lat:11732811, lng:3361629.5}
  var mercator = {};
  var earthRad = 6378137.0;
  mercator.x = poi.x * Math.PI / 180 * earthRad;
  var a = poi.y * Math.PI / 180;
  mercator.y = earthRad / 2 * Math.log((1.0 + Math.sin(a)) / (1.0 - Math.sin(a)));
  return mercator;
}
/**
 * 
 * @param {Object} poi {x:11732811, y:3361629.5}
 */
export function getLngLat(poi){
  var lnglat = {};
  lnglat.x = poi.x/20037508.34*180;
  var mmy = poi.y/20037508.34*180;
  lnglat.y = 180/Math.PI*(2*Math.atan(Math.exp(mmy*Math.PI/180))-Math.PI/2);
  return lnglat;
}
