<template>
  <div class="boxInTab">
    <div class="settingItem">
      <div class="itemPropertyLabel" ></div>
      <div class="inputWidth" style="width: 200px">
        <el-input type="text" v-model="fastNameRule"
                  @input="onInputFastNum" placeholder="" clearable/>
      </div>
    </div>

    <div class="settingItem">
      <div class="itemPropertyLabel">3d</div>
      <switch-item active-color="#409eff" inactive-color="#ff4949" v-model="editing3d" @change="toggle3dEditing"/>
    </div>
  </div>

</template>

<script>
  import ElInput from '@/components/element/input';
  import MainScene from '@/3d/js/MainScene';
  import SwitchItem from '@/components/element/switch/component';
  import {Editing_For_3d} from "../../../store/mutations-types";
  import EventBus from "../../../store/EventBus";

  export default {
    name: "BoxMisc",
    components: {
      ElInput, SwitchItem
    },
    mounted() {
      MainScene.scene().addEventListener("fast name changed", (event) => {
        this.fastNameRule = event.value;
      });
      // this.$refs.backColor.updateColor(this.sceneBackColor);
    },
    data() {
      return {
        fastNameRule: "",
        editing3d: false
      }
    },
    methods: {
      onInputFastNum() {
        if (this.fastNameRule.trim().length == 0) {
          MainScene.polgyonTool().disableFastNaming();
        } else {
          MainScene.polgyonTool().enableFastNaming(this.fastNameRule);
        }
      },
      toggle3dEditing() {
        MainScene.toggle3dEditing(this.editing3d);
        this.$store.commit(Editing_For_3d, this.editing3d);
        EventBus.$emit("editing for 3d", this.editing3d);
      }
    }
  }
</script>

<style scoped>

  .settingItem {
    padding: 5px 0 0 0;
    display: flex;
    font-size: 14px;
    color: #333;
    justify-content: space-between;
  }
</style>
