<template>
  <div class="tabBarItem" @click="itemClick">
    <div class="icon"><slot name="icon" ></slot></div>
    <div :style="activeStyle" class="txt"><slot name="txt" ></slot></div>
  </div>
</template>

<script>
  export default {
    name: "TabBarItem",
    props: {
      index: String,
      cur: String,
      activeColor: {
        type: String,
        default: 'red'
      }
    },
    data() {
      return {
        // isActive: true
      }
    },
    computed: {
      isActive() {
        return this.index == this.cur;
      },
      activeStyle() {
        return this.isActive ? {'border-bottom': 'solid 2px red'} : {}
      }
    },
    methods: {
      itemClick() {
        this.$emit('tabClick', this.index);
        // this.$router.replace(this.path)
      }
    }
  }
</script>

<style scoped>
  .tabBarItem {
    flex: 1;
    height: 49px;
    font-size: 14px;
    padding: 5px 0;
    cursor: pointer;
  }
  .tabBarItem>div {
    text-align: center;
    color: white;
  }
  .tabBarItem>.icon {
    margin-bottom: 3px;
  }
  .tabBarItem>.txt {
    padding-bottom: 5px;
  }
</style>
