let Config = function () {
  let configByUI = {
    modelTextVisible: true,
    refOpacityPathTab: 0.8,
    isPublished() {
      let pathHash = window.location.hash;
      return pathHash && pathHash.startsWith("#/preview");
    },
    enableEditPointAdhere: true,
    defaultFillColor: [0x1bbeff, 0.9],
    defaultLineColor: [0x2b51a9, 1],
    defaultFillColorFloor: [0xdbdbdb, 1],
    defaultLineColorFloor: [0x2b51a9, 1],
    defaultHeightFloor: 0.1,
    defaultHeight: 0.3,
    txtHeightExtra: 0.55,
    showFps: false, //true  

    noLight: false,

    defaultFillColorStr() {
      return configByUI.defaultFillColor[0].toString(16).toUpperCase() + " " + configByUI.defaultFillColor[1];
    },
    defaultFillColorFloorStr() {
      return configByUI.defaultFillColorFloor[0].toString(16).toUpperCase() + " " + configByUI.defaultFillColorFloor[1];
    }
  };

  let configByCode = {
    adhereRadiusForCreating: 0.5,
    adhereRadiusForEditing: 0.4,
    adhereEdgeDistance: 0.5,
    moveSlightlyStep: 0.01,
    moveStep:0.5,
    rotateStep: 90, //rotate in degree
    loadSavingFromStart: true,
    smartGuideSpan: 0.5,
    extraSpan: 1,
    instancingRect: true, //true Mesh intancing
    textOutline: true, //true 
    autoSaveInterval: 0, //localStorage

    pathAdhereSpanSq: 1.5 * 1.5,
    pointAdhereSpanSq: 0.1 * 0.1,
    defaultSaveForPreview: "mapPreview",
    defaultSaveForEditor: "wuboMap2",
    lngLatBind: false,  //true: , 
    TYPE_WGS84: 0,
    TYPE_BaiDu: 2,
    coordType: 2, // 0wgs84 12

    getPrecision() {
      let str = configByCode.moveSlightlyStep + "";
      return str.substr(str.indexOf(".") + 1).length;
    }
  };

  let remote = {
    baseUrl: "http://192.168.0.154:8151/",
    encKey: "1234",
    token: null,
    mapId: null,
    previewToken: null,
    previewMapId: null,
    heatmap: undefined,
    track: undefined,
    trackOwner: null, // 
    userId: null,
    timeStart: null,
    timeEnd: null,
    version: null,
    // userId: null,
    updateByVue(router) {
      Config.remote.previewToken = router.query.token;
      Config.remote.previewMapId = router.query.mapId;
      Config.remote.heatmap = router.query.heatmap;
      Config.remote.track = router.query.track;
      Config.remote.trackOwner = router.query.owner;
      Config.remote.userId = router.query.userId;
      Config.remote.mapId = router.query.mapId;
      Config.remote.timeStart = router.query.beginTime;
      Config.remote.timeEnd = router.query.endTime;
      Config.remote.version = router.query.version;
      if (Config.remote.timeStart && Config.remote.timeStart.trim().indexOf(" ") > 3) {
        Config.remote.timeStart = Config.remote.timeStart.split(" ")[0]
      }
      if (Config.remote.timeEnd && Config.remote.timeEnd.trim().indexOf(" ") > 3) {
        Config.remote.timeEnd = Config.remote.timeEnd.split(" ")[0]
      }
    }
  };

  let that = {
    ui: configByUI,
    code: configByCode,
    remote: remote,
    mathConsts: {
      radianToDegree: 180 / Math.PI,
      degreeToRadian: Math.PI / 180,
    }
  };
  return that;
} ();

export default Config;
