import UndoTypes from "./UndoTypes";
import Layers from "../Layers";

let CommandExecutor = function() {
  let replaced, replacement;
  let angleInRadian;

  function removeItem(arr, item) {
    let ind = arr.indexOf(item);
    if (ind > -1) {
      arr.splice(ind, 1);
    }
  }
  function replaceItem(arr, oldItem, newItem) {
    let ind = arr.indexOf(oldItem);
    if (ind > -1) {
      arr[ind] = newItem;
    }
  }
  let that = {
    execute(type, obj) {
      switch (type) {
        case UndoTypes.add_polygons:
          obj.meshes.forEach(mesh => {
            obj.selectedMeshes.push(mesh);
            obj.parentGroup.add(mesh.holder.getHolderGroup());
          } );
          // obj.tool.dispatchSelectionChange();
          break;
        case UndoTypes.remove_polygons:
          obj.meshes.forEach(mesh => {
            removeItem(obj.selectedMeshes, mesh);
            obj.parentGroup.remove(mesh.holder.getHolderGroup());
          } );
          // obj.tool.dispatchSelectionChange();
          break;
        case UndoTypes.add_paths:
          obj.addLines.forEach(line => {
            line.holder.getItems().forEach(it => Layers.navPathLayer.add(it) );
          } );
          obj.removeLines.forEach(line => Layers.navPathLayer.remove(line) );
          Layers.removeUnusedSegmentRings();

          if (obj.relatedHolders) {
            obj.ring.position.copy(obj.position);
            obj.relatedHolders.forEach(it => it != null && it.updateLine() );
          }
          break;
        case UndoTypes.remove_paths:
          obj.addLines.forEach(line => Layers.navPathLayer.remove(line) );
          obj.removeLines.forEach(line => Layers.navPathLayer.add(line) );
          Layers.removeUnusedSegmentRings();

          if (obj.relatedHolders) {
            obj.ring.position.copy(obj.oldPosition);
            obj.relatedHolders.forEach(it => it != null && it.updateLine() );
          }
          break;

        case UndoTypes.merge_models:
        case UndoTypes.slice_model:
          obj.appendLst.forEach(mesh => {
            obj.parentGroup.add(mesh.holder.getHolderGroup());
          } );
          obj.removeLst.forEach(mesh => {
            obj.parentGroup.remove(mesh.holder.getHolderGroup());;
          } );
          break;
        case UndoTypes.merge_models_undo:
        case UndoTypes.slice_model_undo:
          obj.removeLst.forEach(mesh => {
            obj.parentGroup.add(mesh.holder.getHolderGroup());
          } );
          obj.appendLst.forEach(mesh => {
            obj.parentGroup.remove(mesh.holder.getHolderGroup());
            removeItem(obj.selectedMeshes, mesh);
          } );
          break;

        case UndoTypes.update_model_shape:
          obj.holder.startEditCtrlPoint(obj.parentGroup);
          if (obj.add) {
            obj.holder.addCtrlPoint(obj.adhereObj);
          } else {
            obj.holder.removeCtrlPoint(obj.adhereObj);
          }
          obj.holder.endEditCtrlPoint();

          replaced = obj.holder.getMesh();
          replacement = obj.holder.getMesh();
          break;
        case UndoTypes.update_model_shape_undo:
          obj.holder.startEditCtrlPoint(obj.parentGroup);
          if (obj.add) {
            obj.holder.removeCtrlPoint(obj.adhereObj);
          } else {
            obj.holder.addCtrlPoint(obj.adhereObj);
          }
          obj.holder.endEditCtrlPoint();

          replaced = obj.holder.getMesh();
          replacement = obj.holder.getMesh();
          break;
        case UndoTypes.update_model_shape_slightly:
          obj.mesh.holder.updateCurPointIndex(obj.ctrlPointObj);
          obj.mesh.holder.startEditCtrlPoint(obj.parentGroup);
          obj.ctrlPointObj.position.copy(obj.position);
          obj.mesh.holder.moveCtrlPoint(obj.ctrlPointObj);
          obj.mesh.holder.endEditCtrlPoint();
          break;
        case UndoTypes.update_model_shape_slightly_undo:
          obj.mesh.holder.updateCurPointIndex(obj.ctrlPointObj);
          obj.mesh.holder.startEditCtrlPoint(obj.parentGroup);
          obj.ctrlPointObj.position.copy(obj.oldPosition);
          obj.mesh.holder.moveCtrlPoint(obj.ctrlPointObj);
          obj.mesh.holder.endEditCtrlPoint();
          break;

        case UndoTypes.shortcut_move:
          obj.meshes.forEach((mesh, index) => {
            mesh.holder.move(obj.moveVector);
            mesh.holder.updatePosition();
          } );
          break;
        case UndoTypes.shortcut_move_undo:
          let move = obj.moveVector.clone().negate();
          obj.meshes.forEach((mesh, index) => {
            mesh.holder.move(move);
            mesh.holder.updatePosition();
          } );
          break;

        case UndoTypes.shortcut_rotate:
          angleInRadian = obj.degree / 180 * Math.PI;
          obj.mesh.rotation.z += angleInRadian;
          break;
        case UndoTypes.shortcut_rotate_undo:
          angleInRadian = obj.degree / 180 * Math.PI;
          obj.mesh.rotation.z -= angleInRadian;
          break;

        case UndoTypes.update_models_position:
          obj.meshes.forEach((mesh, index) => {
            mesh.holder.move(obj.positions[index].clone().sub(obj.oldPositions[index]) );
            // mesh.position.copy(obj.positions[index]);
            mesh.holder.updatePosition();
          } );
          break;
        case UndoTypes.update_models_position_undo:
          obj.meshes.forEach((mesh, index) => {
            mesh.holder.move(obj.oldPositions[index].clone().sub(obj.positions[index]) );
            // mesh.position.copy(obj.oldPositions[index]);
            mesh.holder.updatePosition();
          } );
          break;
        case UndoTypes.update_model_scale:
          obj.mesh.scale.copy(obj.scale);
          obj.mesh.userData.oldScale.copy(obj.mesh.scale);
          break;
        case UndoTypes.update_model_scale_undo:
          obj.mesh.scale.copy(obj.oldScale);
          obj.mesh.userData.oldScale.copy(obj.mesh.scale);
          break;
        case UndoTypes.update_model_rotation:
          obj.mesh.rotation.copy(obj.rotation);
          obj.mesh.userData.oldRotation.copy(obj.mesh.rotation);
          break;
        case UndoTypes.update_model_rotation_undo:
          obj.mesh.rotation.copy(obj.oldRotation);
          obj.mesh.userData.oldRotation.copy(obj.mesh.rotation);
          break;

        case UndoTypes.update_model_fillcolors:
          obj.meshes.forEach((mesh, index) => {
            if (obj.preColor) {
              mesh.material.color.setHex(obj.preColor);
              mesh.userData.bacOpacity = mesh.material.opacity = obj.preOpacity;
            } else {
              mesh.material.color.copy(obj.colors[index]);
              mesh.material.opacity = obj.opacities[index];
              mesh.userData.bacOpacity = mesh.material.opacity;
            }
          } );
          break;
        case UndoTypes.unify_model_fillcolors:
          obj.meshes.forEach(mesh => {
            mesh.material.color.setHex(obj.unifyColor);
            mesh.material.opacity = obj.unifyOpacity;
            mesh.userData.bacOpacity = obj.unifyOpacity;
          } );
          break;
        case UndoTypes.update_model_linecolors:
          obj.meshes.forEach((mesh, index) => {
            let outlines = mesh.holder.getOutlines();
            if (obj.preColor) {
              outlines.forEach(outline => outline.material.color.setHex(obj.preColor) );
            } else {
              outlines.forEach(outline => outline.material.color.setHex(obj.lineColors[index]) );
            }
          } );
          break;
        case UndoTypes.unify_model_linecolors:
          obj.meshes.forEach(mesh => {
            let outlines = mesh.holder.getOutlines();
            outlines.forEach(outline => outline.material.color.setHex(obj.unifyColor) );
          } );
          break;

        case UndoTypes.update_models_names:
          obj.meshes.forEach((mesh, index) => {
            if (obj.preText) {
              mesh.holder.setName(obj.preText);
            } else {
              mesh.holder.setName(obj.texts[index]);
            }
          });
          break;
        case UndoTypes.unify_models_names:
          obj.meshes.forEach(mesh => mesh.holder.setName(obj.unifyText));
          break;

        case UndoTypes.update_models_height:
          obj.meshes.forEach((mesh, index) => {
            if (obj.preHeight) {
              mesh.holder.setHeight(obj.preHeight);
            } else {
              mesh.holder.setHeight(obj.heights[index]);
            }
          });
          break;
        case UndoTypes.unify_models_height:
          obj.meshes.forEach(mesh => mesh.holder.setHeight(obj.unifyHeight));
          break;
        case UndoTypes.update_models_text_height:
          obj.meshes.forEach((mesh, index) => {
            if (obj.preTextHeight) {
              mesh.holder.setTextHeight(obj.preTextHeight);
            } else {
              mesh.holder.setTextHeight(obj.textHeights[index]);
            }
          });
          break;
        case UndoTypes.unify_models_text_height:
          obj.meshes.forEach(mesh => mesh.holder.setTextHeight(obj.unifyTextHeight));
          break;

        case UndoTypes.update_path_point:
          obj.pathPointObj.position.copy(obj.position);
          obj.relatedHolders.forEach(it => it != null && it.updateLine());
          break;
        case UndoTypes.update_path_point_undo:
          obj.pathPointObj.position.copy(obj.oldPosition);
          obj.relatedHolders.forEach(it => it != null && it.updateLine());
          break;
        case UndoTypes.replace_paths_point:
          obj.relatedHolders.forEach(it => it != null && it.replaceCircle(obj.replaced, obj.replacement));
          obj.ringGroup.remove(obj.replaced);
          obj.relatedHolders.forEach(it => it != null && it.updateLine());
          break;
        case UndoTypes.replace_paths_point_undo:
          obj.relatedHolders.forEach(it => it != null && it.replaceCircle(obj.replacement, obj.replaced));
          obj.ringGroup.add(obj.replaced);
          obj.replaced.position.copy(obj.oldPosition);
          obj.relatedHolders.forEach(it => it != null && it.updateLine());
          break;

        case UndoTypes.update_paths_type:
          obj.pathHolders.forEach((holder, index) => {
            if (obj.preType) {
              holder.setPathType(obj.preType);
            } else {
              holder.setPathType(obj.pathTypes[index]);
            }
          });
          break;
        case UndoTypes.unify_paths_type:
          obj.pathHolders.forEach(holder => holder.setPathType(obj.unifyType) );
          break;
        case UndoTypes.update_paths_direction:
          obj.pathHolders.forEach((holder, index) => {
            if (obj.preDirection) {
              holder.setPathDirection(obj.preDirection);
            } else {
              holder.setPathDirection(obj.pathDirections[index]);
            }
          });
          break;
        case UndoTypes.unify_paths_direction:
          obj.pathHolders.forEach(holder => holder.setPathDirection(obj.unifyDirection) );
          break;
        default: break;
      }

    }
  };
  return that;
};

let cmdExecutor = CommandExecutor();
let Action = function (undoTypeStr, context) {
  this.undoObj = UndoTypes.getAction(undoTypeStr);
  this.name = undoTypeStr;
  let done = true;
  this.context = function () {
    return context;
  };
  this.undo = function () {
    done = false;
    cmdExecutor.execute(this.undoObj.undoNum, context);
  };
  this.redo = function () {
    done = true;
    cmdExecutor.execute(this.undoObj.num, context);
  }
};
export default Action;
