import {Vector3} from 'three'
import Config from "../configurable/Config"
import Main from "../MainScene"
import History from "../undo/History";

let Shortcuts = function () {
  if (Config.ui.isPublished()) return;
  let smallStep = Config.code.moveSlightlyStep;
  let bigStep = Config.code.moveStep;
  let moveVector = new Vector3();
  let canvasSelected = false;
  document.addEventListener('mousedown', function (event) {
    canvasSelected = event.target === Main.getDom();
    if (!canvasSelected && Main.isEditingIn3D() )
      Main.cameraControl().enabled = false;
  });

  window.addEventListener('keydown', function(event) {
    if (!canvasSelected) {
      return;
    }
    let step = event.shiftKey ? bigStep : smallStep;
    switch ( event.key.toLowerCase() ) {
      case 'backspace':
        // event.preventDefault(); // prevent browser back
        // fall-through
      case 'delete':
        Main.polgyonTool().removeSelectedMesh();
        Main.pathTool().removeSelectedPaths();
        Main.lightTool().deleteSelectedLight();
        Main.markerTool().removeSelected();
        break;
      case 'arrowup':
      case 'w':
        if (event.altKey) {
          Main.cameraControl().moveCamera(0, -1);
        } else {
          moveVector.set(0, step, 0);
        }

        break;
      case 'arrowdown':
      case 's':
        if (event.altKey) {
          Main.cameraControl().moveCamera(0, 1);
        } else {
          moveVector.set(0, -step, 0);
        }
        break;
      case 'arrowleft':
      case 'a':
        if (event.altKey) {
          Main.cameraControl().moveCamera(1, 0);
        } else {
          moveVector.set( -step, 0,0);
        }
        break;
      case 'arrowright':
      case 'd':
        if (event.altKey) {
          Main.cameraControl().moveCamera(-1, 0);
        } else {
          moveVector.set( step, 0,0);
        }
        break;
      case 'r':
        Main.polgyonTool().rotateSingleMesh();
        break;
      case 'z':
        if (event.ctrlKey) {
          if (event.shiftKey) {
            History.redoAction();
          } else {
            History.undoAction();
          }
        }
        break;
      case 'f4':
        if (event.shiftKey) {
          let removeAll = confirm(", ");
          if (removeAll) {
            Main.removeAll();
            History.removeAll();
          }
        }
        break;
    }
    if (moveVector.lengthSq() > 0) {
      Main.polgyonTool().moveSelectedMesh(moveVector);
      moveVector.set(0,0,0);
    }
  });
} ();

export default Shortcuts;
