import {Vector3, Line3} from 'three';
import {getPathSegment, getPathCtrlPoint, getPathArrow, getPathArrowRotation} from '../util/StaticMethods';
import * as Constants from '../consts/Constants'
import Config from "../configurable/Config";

let PathSegment = function (from, to, lineType, scalar0) {
//from.starts, from.ends, to.starts, to.ends = [element of PathSegment];
  let pathType = lineType;
  let pathDirection = 0;
  let lineObj, fromCircle, toCircle, arrowObj;
  let selectedLine = getPathSegment(new Vector3(0,0,0), new Vector3(0,1,0), Constants.pathTypeSelected);
  let findedLine = getPathSegment(new Vector3(0,0,0), new Vector3(0,1,0), Constants.pathTypeFinded);
  // let selectedLine = getPolygonLineMesh([new Vector3(0,0,0), new Vector3(0,3,0)], null, false);
  findedLine.renderOrder = 4;
  selectedLine.renderOrder = 3;
  const edgeAdhere = Config.code.adhereEdgeDistance;
  const adhereDistance = Config.code.adhereRadiusForCreating;
  let edgeAdhereDistanceSq = edgeAdhere * edgeAdhere;
  let adhereDistanceSq = adhereDistance * adhereDistance;
  let cost = 0;
  if (scalar0 != undefined) {
    updateScaleFactor(scalar0);
  }

  if (from instanceof Vector3) {
    fromCircle = getPathCtrlPoint(from, Constants.pointTypeRing);
  } else {
    fromCircle = from;
  }
  if (to instanceof Vector3) {
    toCircle = getPathCtrlPoint(to, Constants.pointTypeRing);
  } else {
    toCircle = to;
  }
  fromCircle.renderOrder = toCircle.renderOrder = 2;
  lineObj = getPathSegment(fromCircle.position, toCircle.position, lineType);
  lineObj.renderOrder = 1;

  let tmp = new Vector3();
  let line = new Line3(fromCircle.position, toCircle.position);

  function getPosition() {
    let pos = fromCircle.position.clone().lerp(toCircle.position, 0.5);
    return pos;
  }
  function updateScaleFactor(scalar) {
    edgeAdhereDistanceSq = edgeAdhere * edgeAdhere * scalar * scalar;
    adhereDistanceSq = adhereDistance * adhereDistance * scalar * scalar;
  }
  let that = {
    splitBy:[],
    splitCircles: [],
    processSplitBy() {
      let p0 = fromCircle.position;
      let distanceSqs = [];
      that.splitBy.forEach(it => distanceSqs.push(p0.distanceToSquared(it)) );
      let distanceSqsSorted = distanceSqs.slice().sort((a,b) => a - b);
      let pFrom = p0;
      let segments = [];
      for (let i = 0; i < distanceSqsSorted.length; i++) {
        let index = distanceSqs.indexOf(distanceSqsSorted[i]);
        let pTo = that.splitBy[index];
        let circle = that.splitCircles[index];
        let segment;
        if (i == 0) {
          segment = PathSegment(fromCircle, circle || pTo, pathType);
        } else {
          segment = PathSegment(pFrom, circle || pTo, pathType);
        }
        segments.push(segment);
        pFrom = circle || pTo;
      }
      segments.push( PathSegment(pFrom, toCircle, pathType) );
      return segments;
    },
    fromCircle() {
      return fromCircle;
    },
    toCircle() {
      return toCircle;
    },
    length() {
      return fromCircle.position.distanceTo(toCircle.position);
    },
    getPathType() {
      return pathType;
    },
    setPathType(type) {
      if (pathType == type) {
        return;
      }
      let parent = lineObj.parent;
      parent.remove(lineObj);
      lineObj = getPathSegment(fromCircle.position, toCircle.position, type);
      parent.add(lineObj);
      if (arrowObj) {
        lineObj.add(arrowObj);
      }
      lineObj.holder = that;
      pathType = type;
    },
    setPathDirection(direction) {
      if (pathDirection == direction) {
        return;
      }
      if (arrowObj && arrowObj.parent) {
        arrowObj.parent.remove(arrowObj);
      }
      if (direction !== Constants.pathDirBoth) {
        arrowObj = getPathArrow(fromCircle.position, toCircle.position, direction);
        lineObj.add(arrowObj);
      } else {
        arrowObj = null;
      }
      pathDirection = direction;
    },
    getPathDirection() {
      return pathDirection;
    },
    getLine() {
      return lineObj;
    },
    updateLine() {
      let pf = fromCircle.position, pt = toCircle.position;
      lineObj.geometry.setPositions([pf.x, pf.y, 0, pt.x, pt.y, 0]);
      lineObj.computeLineDistances();
      if (arrowObj && arrowObj.parent) {
        arrowObj.position.copy(pf.clone().lerp(pt, 0.5));
        arrowObj.material.rotation = getPathArrowRotation(pf, pt, pathDirection);
      }
    },
    replaceCircle(oldCircle, newCircle) {
      if (oldCircle == fromCircle) {
        fromCircle = newCircle;
      } else if (oldCircle == toCircle) {
        toCircle = newCircle;
      }
    },
    selectFinding() {
      let pf = fromCircle.position, pt = toCircle.position;
      findedLine.geometry.setPositions([pf.x, pf.y, 0, pt.x, pt.y, 0]);
      findedLine.computeLineDistances();
      lineObj.add(findedLine);
    },
    unselectFinding() {
      if (findedLine.parent) lineObj.remove(findedLine);
    },
    select() {
      let pf = fromCircle.position, pt = toCircle.position;
      selectedLine.geometry.setPositions([pf.x, pf.y, 0, pt.x, pt.y, 0]);
      selectedLine.computeLineDistances();
      lineObj.add(selectedLine);
    },
    unselect() {
      lineObj.remove(selectedLine);
    },
    getItems() {
      return [lineObj, fromCircle, toCircle];
    },
    updateScaleFactor: updateScaleFactor,
    updateCost() {
      cost = fromCircle.position.distanceTo(toCircle.position);
    },
    getCost() {
      return cost;
    },
    getAdhereObj(point, skipEdge) {
      let target = { ctrlPoint: null, posOnEdge: null, distanceSq:-1, holder: that};
      let distance = fromCircle.position.distanceToSquared(point);
      if (distance <= adhereDistanceSq) {
        target.ctrlPoint = fromCircle;
        target.distanceSq = distance;
        return target;
      }
      distance = toCircle.position.distanceToSquared(point);
      if (toCircle.position.distanceToSquared(point) <= adhereDistanceSq) {
        target.ctrlPoint = toCircle;
        target.distanceSq = distance;
        return target;
      }
      if (skipEdge) {
        return target;
      }

      line.start.copy(fromCircle.position);
      line.end.copy(toCircle.position);
      let t = line.closestPointToPointParameter(point, false);
      if (t > 1 || t < 0) {
        return target;
      }
      line.closestPointToPoint(point, false, tmp);
      let distanceSq = tmp.distanceToSquared(point);
      if (distanceSq <= edgeAdhereDistanceSq) {
        target.posOnEdge = tmp;
        target.distanceSq = distanceSq;
      }
      return target;
    }
  };
  lineObj.holder = that;
  return that;
}

export default PathSegment;
