import {TextureLoader} from 'three'
import * as Consts from '../consts/Constants'

let TextureStore = function () {
  let startMarker, endMarker;
  let pointHollow, pointRed, pointWhiteDisc, pointDefault;
  let crossOnPath, arrowOnPath;
  let pathIndicators = {};
  let pathTrackMarkerImg = new Image();
  pathTrackMarkerImg.src = "img_tabs/pathTrackMarker.png";

  function drawArrow(edgeLen, backgroundColor, arrowColor) {
    let canvas = document.createElement( 'canvas' );
    let ctx = canvas.getContext( '2d' );
    canvas.width = edgeLen;
    canvas.height = edgeLen; // fontsize * 1.5
    ctx.rect(0,0,edgeLen,edgeLen);
    ctx.fillStyle = backgroundColor;    //'#35a610';
    ctx.fill();

    let p0 = {x: 0.2 * edgeLen, y: 0.1 * edgeLen};
    let p1 = {x: 0.8 * edgeLen, y: 0.5 * edgeLen}, p2 = {x: 0.2 * edgeLen, y: 0.9 * edgeLen};
    ctx.beginPath();
    ctx.lineWidth = 4;
    ctx.moveTo(p0.x, p0.y);
    ctx.lineTo(p1.x, p1.y);
    ctx.lineTo(p2.x, p2.y);
    ctx.lineCap="round";
    ctx.lineJoin="round";
    ctx.strokeStyle = "#ffffff";
    ctx.stroke();
    return canvas;
  }

  let that = {
    pathTrackMarkerImg() {
      return pathTrackMarkerImg;
    },
    startMarker() {
      if (!startMarker) {
        startMarker = new TextureLoader().load( 'img_tabs/startPoint.png' );
      }
      return startMarker;
    },
    endMarker() {
      if (!endMarker) {
        endMarker = new TextureLoader().load( 'img_tabs/endPoint.png' );
      }
      return endMarker;
    },
    crossOnPath() {
      if (!crossOnPath) {
        crossOnPath = new TextureLoader().load('img_tabs/crossGray.png');
      }
      return crossOnPath;
    },
    arrowOnPath() {
      if (!arrowOnPath) {
        arrowOnPath = new TextureLoader().load('img_tabs/arrow1.png');
      }
      return arrowOnPath;
    },
    pointTexture(pointType) {
      if (pointType == Consts.pointTypeRing) {
        if (!pointHollow) {
          pointHollow = new TextureLoader().load( 'img_tabs/circle2.png' );
        }
        return pointHollow;
      } else if (pointType == Consts.pointTypeRingSelected) {
        if (!pointRed) {
          pointRed = new TextureLoader().load( 'img_tabs/circleRed.png' );
        }
        return pointRed;
      } else if (pointType == Consts.pointTypeCircleWhite) {
        if (!pointWhiteDisc) {
          pointWhiteDisc = new TextureLoader().load( 'img_tabs/disc.png' );
        }
        return pointWhiteDisc;
      } else {
        if (!pointDefault) {
          pointDefault = new TextureLoader().load( 'img_tabs/circle3.png' );
        }
        return pointDefault;
      }
    },
    getPathIndicatorCanvas(backgroundColor) {
      let backColor = '#35a610';
      if (backgroundColor != null) {
        backColor = backgroundColor;
      }
      if (!pathIndicators[backColor]) {
        let canvas = drawArrow(32, backColor, '#fff');
        pathIndicators[backColor] = canvas;
      }
      return pathIndicators[backColor];
    }
  };
  return that;
} ();

export default TextureStore;
