<template>
  <div class="squareBtn iconfont"  :class="[{'is-active': isActive}, iconClass]" @click="handleClick">

  </div>
</template>

<script>
  import Emitter from '@/components/element/mixins/emitter'

  export default {
    name: "SquareButton",
    mixins: [Emitter],

    inject: ['group'],
    props: {
      iconClass: {
        type: String,
        default: 'iconjuxing'
      },
      value: {
        type: [String, Number],
        default() {
          return this.iconClass;
        }
      }
    },
    computed: {
      isActive() {
        // console.log(this.value)
        return this.group.activeNames.indexOf(this.value) > -1;
      }
    },
    methods: {
      handleClick() {
        this.$emit('itemClick', this);
        this.dispatch('SquareButtonsGroup', 'itemClick', this);
      }
    }
  }
</script>

<style scoped>
  .squareBtn {
    display: inline-block;
    box-sizing: border-box;
    margin-right: 6px;
    padding: 5px;
    text-align: center;
    font-size: 15px;
    width: 36px;
    height: 36px;
    border: solid 1px silver;
    color: black;
    cursor: pointer;
  }
  .squareBtn:hover {
    background-color: #999;
    color: white;
  }
  .is-active {
    background-color: var(--color-tint);
    color: white;
  }
  .is-active:hover {
    background-color: #2842dd;
  }
</style>
