import {Vector3} from 'three';
import Layers from "../Layers";
import {getMeasurePoint, getMeasuringLineSegment, getMeasuredLines, getMeasuredAreaMesh, getPolygonsAreaMesh, ShapeUtils} from "../util/StaticMethods"
import Main from "../MainScene";
import Config from "../configurable/Config";
import TriCut from "../lib/TriangleCut";

let MeasureTool = function () {
  let eventUpdateTip = {type: "update tip", meters:0, distance:0, areaMeters: 0, area: 0, mapCoord:null};
  let eventAddTip = {type: "add tip", meters:0, distance:0, areaMeters: 0, area: 0, mapCoord:null};
  let eventRemoveTips = {type: "remove all tips"};
  let eventCancelAddTip = {type: "cancel add tips"};
  let STATUS_FREE = 0;
  let STATUS_MEASURE_LENGTH = 1;
  let STATUS_MEASURE_AREA = 2;
  let status = STATUS_FREE;
  let measurePoint = getMeasurePoint(new Vector3(), '#3b3b3b', true);
  let currentSegment, lastSegment;
  let meshes = Layers.availableMeshes();
  let points = [], pointObjs = [];
  let segments = [], measuredLst = [];
  let dom = Main.getDom();
  let scene = Main.scene();
  let dashSize = 0.4, gapSize = 0.3, _scalar = 1;
  let measuredLen = 0;

  dom.addEventListener('mousedown', mousedown);
  function mousedown(event) {
    if (event.button === 2) {
      // 
      if (status == STATUS_MEASURE_LENGTH || status == STATUS_MEASURE_AREA) {
        endMeasuring()
      }
    }
  }

  let tempVector = new Vector3()
  function mousemove4Measure(event) {
    let lst = Main.getIntersectsOnPlane(event.clientX, event.clientY);
    if (lst.length == 0) {
      return;
    }
    let pos = lst[0].point;
    if (Config.ui.enableEditPointAdhere) {
      let targets = [];
      meshes.forEach(mesh => {
        let obj = mesh.holder.getPointOnEdges(pos, true);
        if (obj.ctrlPoint) {
          targets.push(obj);
        }
      });
      if (targets.length > 0) {
        targets.sort((a, b) => a.distanceSq - b.distanceSq );
        pos = targets[0].ctrlPos;
      }
    }

    measurePoint.position.copy(pos);
    if (!measurePoint.parent) {
      Layers.editingLayer.add(measurePoint);
    }
    if (!currentSegment) {
      return;
    }
    let attr = currentSegment.geometry.attributes.position;
    attr.setXYZ(1, pos.x, pos.y, pos.z);
    attr.needsUpdate = true;
    currentSegment.computeLineDistances();
    if (lastSegment) {
      attr = lastSegment.geometry.attributes.position;
      attr.setXYZ(0, pos.x, pos.y, pos.z);
      attr.needsUpdate = true;
      lastSegment.computeLineDistances();
    }

    eventUpdateTip.mapCoord = pos;
    if (points.length>0) {
      if (status == STATUS_MEASURE_AREA) {
        tempVector.set(0,0,0);
        points.forEach(pt => tempVector.add(pt));
        tempVector.add(pos).divideScalar(points.length + 1);
        eventUpdateTip.mapCoord = tempVector;
        if (points.length > 1) {
          let area = Math.abs(ShapeUtils.area(points.concat([pos])) );
          eventUpdateTip.area = area.toFixed(2);
          eventUpdateTip.areaMeters = (area * Main.mapScale() * Main.mapScale()).toFixed(2);
        } else {
          eventUpdateTip.area = 0;
          eventUpdateTip.areaMeters = 0;
        }
      } else if (status == STATUS_MEASURE_LENGTH) {
        let curLen = points[points.length-1].distanceTo(pos);
        eventUpdateTip.distance = (measuredLen + curLen).toFixed(2);
        eventUpdateTip.meters = ((measuredLen + curLen) * Main.mapScale()).toFixed(2);
      }

      scene.dispatchEvent(eventUpdateTip);
    }
  }
  function mouseclick4Measure(event) {
    if (!measurePoint.parent) {
      return;
    }
    let pos = measurePoint.position;
    if (points.length > 0) {
      let latest = points[points.length - 1];
      if (latest.manhattanDistanceTo(pos) < 0.01) {
        return;
      }
    }
    points.push(pos.clone());

    currentSegment = getMeasuringLineSegment(pos, pos, dashSize * _scalar, gapSize * _scalar);
    segments.push(currentSegment);
    Layers.editingLayer.add(currentSegment);
    if (points.length > 1) {
      if (status == STATUS_MEASURE_LENGTH) {
        measuredLen += points[points.length-2].distanceTo(points[points.length-1]);
      } else if (status == STATUS_MEASURE_AREA && lastSegment == null) {
        lastSegment = getMeasuringLineSegment(pos, points[0], dashSize * _scalar, gapSize * _scalar);
        segments.push(lastSegment);
        Layers.editingLayer.add(lastSegment);
      }
    }
  }
  function mousedblclick4Measure() {
    if (points.length < 2)
      return;
    let measuredLines = getMeasuredLines(points, points.length > 2 && status == STATUS_MEASURE_AREA);
    Layers.editingLayer.add(measuredLines);
    measuredLst.push(measuredLines);
    // if (status == STATUS_MEASURE_AREA) {
    //   let areaMesh = getMeasuredAreaMesh(points);
    //   Layers.editingLayer.add(areaMesh);
    //   measuredLst.push(areaMesh);
    // }

    points.forEach(it => {
      let pointObj = getMeasurePoint(it, '#5f5f5f');
      Layers.editingLayer.add(pointObj);
      pointObjs.push(pointObj);
    })

    if (points.length > 1) {
      eventAddTip.distance = measuredLen.toFixed(2);
      eventAddTip.meters = (measuredLen * Main.mapScale()).toFixed(2);
      eventAddTip.mapCoord = eventUpdateTip.mapCoord.clone();
      if (status == STATUS_MEASURE_AREA) {
        let polygons = TriCut.getSliceLinesPolygons(points);
        let area = TriCut.getPolygonsArea(polygons);
        eventAddTip.areaMeters = (area * Main.mapScale() * Main.mapScale()).toFixed(2);
        eventAddTip.area = area.toFixed(2);

        let areaMesh = getPolygonsAreaMesh(polygons);
        Layers.editingLayer.add(areaMesh);
        measuredLst.push(areaMesh);
      }
      scene.dispatchEvent(eventAddTip);
    }
    clearTempMeasuring();
  }
  function clearTempMeasuring() {
    segments.forEach(it => {
      if (it.parent)
        it.parent.remove(it)
    });
    segments.splice(0, segments.length);
    points.splice(0, points.length);
    lastSegment = currentSegment = null;
    measuredLen = 0;
  }
  function endMeasuring() {
    status = STATUS_FREE;
    dom.removeEventListener('mousemove', mousemove4Measure);
    dom.removeEventListener('click', mouseclick4Measure);
    dom.removeEventListener('dblclick', mousedblclick4Measure);

    clearTempMeasuring();
    if (measurePoint.parent) {
      measurePoint.parent.remove(measurePoint);
    }
    scene.dispatchEvent(eventCancelAddTip);
  }

  function startMeasuring() {
    meshes = Layers.availableMeshes();
    dom.addEventListener('mousemove', mousemove4Measure);
    dom.addEventListener('click', mouseclick4Measure);
    dom.addEventListener('dblclick', mousedblclick4Measure);
  }
  let that = {
    updateScaleFactor(scalar) {
      _scalar = scalar;
      segments.forEach(it => {
        it.material.dashSize = dashSize * _scalar;
        it.material.gapSize = gapSize * _scalar;
      });
    },
    startMeasureLength() {
      status = STATUS_MEASURE_LENGTH;
      startMeasuring()
      eventUpdateTip.isArea = eventAddTip.isArea = false;
    },
    startMeasureArea() {
      status = STATUS_MEASURE_AREA;
      startMeasuring()
      eventUpdateTip.isArea = eventAddTip.isArea = true;
    },
    clearMeasures() {
      if (status == STATUS_MEASURE_LENGTH || status == STATUS_MEASURE_AREA) {
        endMeasuring()
      }
      status = STATUS_FREE;

      pointObjs.forEach(it => it.parent.remove(it));
      pointObjs = [];
      measuredLst.forEach(it => it.parent.remove(it));
      measuredLst = [];
      scene.dispatchEvent(eventRemoveTips);
    },
  }
  return that;
}

export default MeasureTool;
