<template>
  <div class="buttonsGroup">
    <slot></slot>
  </div>
</template>

<script>
  export default {
    name: "SquareButtonsGroup",
    componentName: "SquareButtonsGroup",
    props: {
      multi: Boolean,
      value: {
        type: [String, Number, Array],
        default() {
          return [];
        }
      }
    },
    data() {
      return {
        activeNames: [].concat(this.value)
      };
    },
    provide() {
      return {
        group: this
      };
    },
    created() {
      this.$on('itemClick', this.handleItemClick);
    },
    watch: {
      value(value) {
        this.activeNames = [].concat(value);
      }
    },
    methods: {
      setActiveNames(activeNames) {
        activeNames = [].concat(activeNames);
        let value = !this.multi ? activeNames[0] : activeNames;
        this.activeNames = activeNames;
        this.$emit('input', value);
        this.$emit('change', value);
      },

      handleItemClick(item) {
        // console.log("button group, handle click: " + item.value);
        if (!this.multi) {
          this.setActiveNames(
              item.value
          );
        } else {
          let activeNames = this.activeNames.slice(0);
          let index = activeNames.indexOf(item.value);

          if (index > -1) {
            activeNames.splice(index, 1);
          } else {
            activeNames.push(item.value);
          }
          this.setActiveNames(activeNames);
        }
      }
    }
  }
</script>

<style scoped>
  .buttonsGroup {
    display: inline-block;
  }
</style>
